import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ModalFormConfig, DynamicService, RowControl, ComponentControl, SubmitAction, CancelAction, ModalFormComponent } from 'codera-shared';
import { TmsTaskPriorityPickerComponent } from '../tms-task-priorities/tms-task-priority-picker/tms-task-priority-picker.component';

@Component({
  selector: 'update-tms-task-priority',
  template: ''
})
export class UpdateTmsTaskPriorityComponent implements OnInit {
  @Input()
  value: any;
  @Input()
  objectType: any;
  @Input()
  objectId: any;

  @Output()
  onSubmit: EventEmitter<string>;

  model: any;
  modalFormConfig: ModalFormConfig;
  constructor(
    private dynamicService: DynamicService,
    private viewContainerRef: ViewContainerRef
  ) {
    this.model = {
      TaskPriority: null
    };
    this.onSubmit = new EventEmitter<string>();
  }

  ngOnInit() {
    const that = this;
    if (this.value)
      this.model.Owner = this.value;

    this.modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].TaskPriority.Edit",
      [
        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskPriorityPickerComponent,
            field: "TaskPriority",
            data: [
              { objectType: this.objectType },
              { objectId: this.objectId }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            if (that.model && that.model.TaskPriority)
              that.onSubmit.emit(that.model.TaskPriority.Id);
            else
              that.onSubmit.emit(null);

            that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainerRef, ModalFormComponent, [
      { model: that.model },
      { config: that.modalFormConfig },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }
}
