import { Component, OnInit, Input, ViewContainerRef, ComponentRef, Output, EventEmitter } from '@angular/core';
import { ApplicationService, CancelAction, ComponentControl, DynamicService, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl } from 'codera-shared';
import { WorkflowStage } from '../types';
import { TranslateService } from '@ngx-translate/core';
import { ChecklistGroupService } from '../../checklist-groups/checklist-group.service';
import { PreviewChecklistGroupComponent } from '../../checklist-groups/checklist-groups-preview/checklist-groups-preview.component';
import { SelectedChecklistGroupsModel, ChecklistGroup } from '../../checklist-groups/types';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'workflow-stage-matrix',
  templateUrl: './workflow-stage-matrix.component.html',
  styleUrls: ['./workflow-stage-matrix.component.scss']
})
export class WorkflowStageMatrixComponent implements OnInit {
  private _submitted: boolean;
  private _componentInitiated: boolean;
  @Input() form: FormGroup;
  @Input() model: WorkflowStage[];

  @Input() set submitted(value: boolean) {
    this._submitted = value;
  }

  get submitted() {
    return this._submitted;
  }

  modalFormConfig: ModalFormConfig;
  component: ComponentRef<ModalFormComponent>;
  siteKey: string;
  formStatus: any;
  constructor(
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private checklistGroupService: ChecklistGroupService) {
    this.siteKey = this.applicationService.getCurrentSiteKey();
  }

  ngOnInit() {
    this.formStatus = {};
    this.formStatus["InitialApprovalFirst"] = false;
    this.formStatus["ManagementApprovalFirst"] = false;
    this.formStatus["ManagementApprovalSecond"] = false;
    this.formStatus["ManagementApprovalThird"] = false;
    this.formStatus["ApprovalFirst"] = true;
    this.formStatus["ApprovalSecond"] = true;
    this.formStatus["ApprovalThird"] = true;
    this.formStatus["ApprovalAnalysis"] = false;
    this.formStatus["Analysis"] = false;
    this._componentInitiated = true;
    this.validate();
  }

  getStageName(stageName: string) {
    let translatedLabel = "-";
    let requestTranslation = `[MOCQuest].[WorkflowStage].${stageName}.Label.${this.siteKey}`;
    this.translate.get(requestTranslation).subscribe((translation: string) => {
      if (requestTranslation === translation) {
        this.translate.get(`[MOCQuest].[WorkflowStage].${stageName}.Default`).subscribe((translation: string) => {
          translatedLabel = translation;
          return translation;
        });
      } else {
        translatedLabel = translation;
        return translation;
      }
    });

    return translatedLabel;
  }

  getLevelName(levelName: string) {
    let translatedLabel = "-";
    let requestTranslation = `[MOCQuest].[SystemControls].SeverityLevel.Level.${levelName}.Label.${this.siteKey}`;
    this.translate.get(requestTranslation).subscribe((translation: string) => {
      if (requestTranslation === translation) {
        this.translate.get(`[MOCQuest].[SystemControls].SeverityLevel.Level.${levelName}.Label.Default`).subscribe((translation: string) => {
          translatedLabel = translation;
          return translation;
        });
      } else {
        translatedLabel = translation;
        return translation;
      }
    });

    return translatedLabel;
  }

  previewChecklistGroups(checklistGroups: LookupViewModel[], hideOutcomes: boolean) {

    let model = new SelectedChecklistGroupsModel();
    model.ChecklistGroups = checklistGroups;

    this.checklistGroupService.getSelectedItems(model).subscribe((result: ChecklistGroup[]) => {
      this.modalFormConfig = new ModalFormConfig(
        "[MOCQuest].[ChecklistGroup].Preview",
        [
          new RowControl([
            new ComponentControl({
              componentClass: PreviewChecklistGroupComponent,
              field: "ChecklistGroups",
              label: "",
              data: [
                { checklistGroups: result },
                { hideOutcomes: hideOutcomes }
              ]
            })
          ])
        ],
        [
          new CancelAction({
            field: "OK",
            value: "[ISOQuest].[Common].Buttons.OK",
            callback(formValueSnapshot) { },
            css: "btn btn-primary"
          })
        ]
      );


      this.component = this.dynamicService.addComponent(
        this.viewContainer,
        ModalFormComponent,
        [
          { "config": this.modalFormConfig },
          { "model": result },
          {
            "modalOptions":
              { "size": "md" }
          }
        ]) as ComponentRef<ModalFormComponent>;
    })
  }

  validate() {
    let initialApprovalStage = this.model.find(stage => stage.Name == "InitialApproval");
    let managementApprovalStage = this.model.find(stage => stage.Name == "ManagementApproval");
    let approvalStage = this.model.find(stage => stage.Name == "Approval");
    let analysisStage = this.model.find(stage => stage.Name == "Analysis");
    if (initialApprovalStage.First) {
      if (initialApprovalStage.ApproversFirstLevel.length == 0) {
        this.formStatus["InitialApprovalFirst"] = false;
      } else {
        this.formStatus["InitialApprovalFirst"] = true;
      }
    } else {
      this.formStatus["InitialApprovalFirst"] = true;
    }

    if (managementApprovalStage.First) {
      if (managementApprovalStage.ApproversFirstLevel.length == 0) {
        this.formStatus["ManagementApprovalFirst"] = false;
      } else {
        this.formStatus["ManagementApprovalFirst"] = true;
      }
    } else {
      this.formStatus["ManagementApprovalFirst"] = true;
    }

    if (managementApprovalStage.Second) {
      if (managementApprovalStage.ApproversSecondLevel.length == 0) {
        this.formStatus["ManagementApprovalSecond"] = false;
      } else {
        this.formStatus["ManagementApprovalSecond"] = true;
      }
    } else {
      this.formStatus["ManagementApprovalSecond"] = true;
    }

    if (managementApprovalStage.Third) {
      if (managementApprovalStage.ApproversThirdLevel.length == 0) {
        this.formStatus["ManagementApprovalThird"] = false;
      } else {
        this.formStatus["ManagementApprovalThird"] = true;
      }
    } else {
      this.formStatus["ManagementApprovalThird"] = true;
    }

    // if (approvalStage.First) {
    //   if (approvalStage.ApproversFirstLevel.length == 0) {
    //     this.formStatus["ApprovalFirst"] = false;
    //   } else {
    //     this.formStatus["ApprovalFirst"] = true;
    //   }
    // } else {
    //   this.formStatus["ApprovalFirst"] = true;
    // }

    // if (approvalStage.Second) {
    //   if (approvalStage.ApproversSecondLevel.length == 0) {
    //     this.formStatus["ApprovalSecond"] = false;
    //   } else {
    //     this.formStatus["ApprovalSecond"] = true;
    //   }
    // } else {
    //   this.formStatus["ApprovalSecond"] = true;
    // }

    // if (approvalStage.Third) {
    //   if (approvalStage.ApproversThirdLevel.length == 0) {
    //     this.formStatus["ApprovalThird"] = false;
    //   } else {
    //     this.formStatus["ApprovalThird"] = true;
    //   }
    // } else {
    //   this.formStatus["ApprovalThird"] = true;
    // }

    // if (approvalStage.First || approvalStage.Second || approvalStage.Third) {
    //   if (approvalStage.ChecklistGroups.length == 0)
    //     this.formStatus["ApprovalAnalysis"] = false;
    //   else
    //     this.formStatus["ApprovalAnalysis"] = true;
    // } else {
    //   this.formStatus["ApprovalAnalysis"] = true;
    // }

    // if (analysisStage.First || analysisStage.Second || analysisStage.Third) {
    //   if (analysisStage.ChecklistGroups.length == 0)
    //     this.formStatus["Analysis"] = false;
    //   else
    //     this.formStatus["Analysis"] = true;
    // } else {
    //   this.formStatus["Analysis"] = true;
    // }

    let validationResult = Object.keys(this.formStatus).every(key => this.formStatus[key]);
    if (validationResult)
      this.form.setErrors(null);
    else
      this.form.setErrors({ error: true });
  }
}
