import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, AttachmentViewModel, BulkOperationExecutionResultComponent, DynamicService, IsoQuestEventManagerService, ModalFormComponent, ModalFormConfig } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ChangeRequestService } from '../../change-request.service';
import { ChangeRequestStatusStateViewModel } from '../../types';
import { FollowUpFactory } from './follow-up.factory';
import { FollowUpService } from './follow-up.service';
import { AddEditFollowUpViewModel, FollowUpPermissionsViewModel, FollowUpViewModel } from './types';

@Component({
  selector: 'follow-up',
  templateUrl: './follow-up.component.html',
  styleUrls: ['./follow-up.component.scss']
})
export class FollowUpComponent implements OnInit {
  private _changeRequestId: string;

  get changeRequestId(): string {
    return this._changeRequestId;
  }
  @Input() set changeRequestId(newValue: string) {
    if (newValue && this._changeRequestId != newValue) {
      //todo: fetch
    }

    this._changeRequestId = newValue;
  }

  private _canConductFollowUp: boolean;

  get canConductFollowUp(): boolean {
    return this._canConductFollowUp;
  }
  @Input() set canConductFollowUp(newValue: boolean) {
    if (newValue && this._canConductFollowUp != newValue) {
      //todo: fetch
    }

    this._canConductFollowUp = newValue;
  }

  @Output() onConductFollowUp: EventEmitter<void>;
  @Output() onSetupFollowUp: EventEmitter<void>;
  @Output() onPermissionsChanged: EventEmitter<FollowUpPermissionsViewModel>;

  model: FollowUpViewModel[];
  currentFollowUp: FollowUpViewModel;
  pastFollowUps: FollowUpViewModel[];
  assignedToCurrentUser: boolean;
  permissions: FollowUpPermissionsViewModel;
  subscriptions: Subscription[];
  conductFollowUpModalConfig: ModalFormConfig;
  constructor(
    private followUpService: FollowUpService,
    private followUpFactory: FollowUpFactory,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private changeRequestService: ChangeRequestService,
    private isoQuestEventManager: IsoQuestEventManagerService
  ) {
    this.onConductFollowUp = new EventEmitter();
    this.onSetupFollowUp = new EventEmitter();
    this.onPermissionsChanged = new EventEmitter();
    this.subscriptions = [];
  }

  ngOnInit() {
    this.fetchData();
    this.getPermissions();
    let statusChangeSubscription = this.changeRequestService.statusChange$.subscribe((changeRequestStatusState) => {
      this.onChangeRequestStatusChange(changeRequestStatusState);
    });
    this.subscriptions.push(statusChangeSubscription);
  }

  fetchData() {
    var currentUserId = this.applicationService.getCurrentUser().PersonelId;
    this.followUpService.getChangeRequestFollowUps(this.changeRequestId).subscribe((response: FollowUpViewModel[]) => {
      this.model = response;
      if (this.model.length > 0) {
        this.currentFollowUp = this.model[0];
      }

      if (this.model.length > 1) {
        this.pastFollowUps = this.model.slice(1, this.model.length);
      }

      if (this.currentFollowUp) {
        var followUpAssignees = this.currentFollowUp.AssignedTo;
        followUpAssignees.filter(assignee => {
          if (assignee.Personel.Id == currentUserId)
            this.assignedToCurrentUser = true;
        })
      }
    }
    );
  }

  conductFollowUp(): void {
    const that = this;
    if (that.model.length > 0) {
      this.followUpService.getById(that.changeRequestId, that.model[0].Id).subscribe((response: FollowUpViewModel) => {
        const model = response as AddEditFollowUpViewModel;
        this.conductFollowUpModalConfig = that.followUpFactory.getFollowUpModalConfig(model, that.dynamicService, that.viewContainer, (model) => {
          this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
          this.fetchData();
          this.uploadAttachments(model.Id);
        });
        that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
          { "config": that.conductFollowUpModalConfig },
          { "model": model },
          {
            "modalOptions": {
              size: "md"
            }
          }
        ], true);
      });
    }
  }

  setupFollowUp() {
    const that = this;

    const model = new AddEditFollowUpViewModel();
    model.ChangeRequestId = this.changeRequestId;
    const modalConfig = this.followUpFactory.getNewFollowUpModalConfig(
      model,
      (payload: AddEditFollowUpViewModel) => {
        that.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemCreated"));
        that.fetchData();
        that.getPermissions();
        that.onSetupFollowUp.emit();
      },
      () => { });
    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { "config": modalConfig },
      { "model": model },
      { "modalOptions": { size: "md" } }
    ], true);
  }

  getPermissions(): void {
    this.followUpService.getPermissions(this.changeRequestId).subscribe(permissions => {
      this.permissions = permissions;
      this.onPermissionsChanged.emit(permissions);
    });
  }

  onChangeRequestStatusChange(changeRequestStatusState: ChangeRequestStatusStateViewModel): void {
    this.fetchData();
    this.getPermissions();
  }

  ngOnDestroy() {
    //uncomment when IsoQuestEventManager is fixed
    //this.subscriptions.forEach(subscription => subscription.unsubscribe());
  }

  uploadAttachments(followUpId: string) {
    const that = this;
    const attachmentsModel = new AttachmentViewModel();
    this.isoQuestEventManager.subscribe(this, "iq-no-attachments-provided", () => {
      this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
      this.conductFollowUpModalConfig.modalRef && this.conductFollowUpModalConfig.modalRef.close();
      this.onConductFollowUp.emit();
    });

    let succeeded = [];
    let failed = [];
    this.isoQuestEventManager.subscribe(this, "iq-single-attachment-success", (payload) => {
      if (payload.data.title) {
        succeeded.push({ Name: payload.data.title, Reason: '', IsSucceed: true });
      }
    });
    this.isoQuestEventManager.subscribe(this, "iq-single-attachment-error", (payload) => {
      if (payload.data.title) {
        var errorParsed = JSON.parse(payload.data.error);
        failed.push({ Name: payload.data.title, Reason: errorParsed.Errors[""][0], IsSucceed: false });
      }
    });

    this.isoQuestEventManager.subscribe(this, "attachmentsUploadFinished", () => {
      this.isoQuestEventManager.unsubscribe(this, "iq-no-attachments-provided");
      this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-success");
      this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-error");
      this.isoQuestEventManager.unsubscribe(this, "attachmentsUploadFinished");

      if (failed.length) {
        var all = { Succeed: succeeded, Failed: failed };
        this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
          { "model": all },
          { "modalOptions": { size: "sm" } },
          {
            "callback": () => {
              this.conductFollowUpModalConfig.modalRef && this.conductFollowUpModalConfig.modalRef.close();
            }
          }
        ]);

        this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
      }
      else {
        this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
        this.conductFollowUpModalConfig.modalRef && this.conductFollowUpModalConfig.modalRef.close();
        this.onConductFollowUp.emit();
      }
      that.fetchData();
    });

    attachmentsModel.ObjectId = followUpId;
    this.isoQuestEventManager.notify("attachmentsSubmitted", attachmentsModel);
  }

  onAttachmentDeleted(): void {
    this.fetchData();
  }
}
