import { HttpClient } from '@angular/common/http';
import { Component, Input, OnInit } from '@angular/core';
import { NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ApplicationService } from 'codera-shared';
import { UpdateCommentsAndNotesModel } from '../../change-request/types';

@Component({
    selector: 'comments-and-notes-info',
    templateUrl: './comments-and-notes-info.component.html',
    styleUrls: ['./comments-and-notes-info.component.scss']
})
export class CommentsAndNotesInfoComponent implements OnInit {
    @Input() changeRequestId: string;
    model: UpdateCommentsAndNotesModel;
    private modalRef: NgbModalRef;

    constructor(
        private http: HttpClient,
        private applicationService: ApplicationService,
    ) { }

    ngOnInit() {
    };

    onInit(modalRef) {
        this.modalRef = modalRef;
        this.http.post(`${this.applicationService.getHostUrl()}/mocquest/api/sites/${this.applicationService.getCurrentSiteKey()}/ChangeRequest/GetCommentsAndNotesById`, { changeRequestId: this.changeRequestId })
            .subscribe((response: UpdateCommentsAndNotesModel) => {
                this.model = response;
            });
    };

    onClose() {
        this.modalRef.dismiss();
    };

}
