import { Component, ComponentRef, Input, OnInit, ViewContainerRef, OnDestroy } from '@angular/core';
import { BulkChangeStatusComponent, BulkDeleteComponent, ComponentControl, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderItemsConfig, InputControl, IqStatusPickerComponent, ModalFormConfig, RenderButtonsPosition, RowControl, TextConfirmationModalComponent, IsoQuestEventManagerService } from 'codera-shared';
import { ChangeRequestTypeGridModel } from 'src/app/system-controls/change-request-type/types';
import { CreateUpdateTmsTaskCategoryComponent } from './create-update/create-update-tms-task-category.component';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { TMSTaskCategoryViewModel } from './types';
import { TmsTaskCategoryService } from './tms-task-category.service';
import { TaskManagementSystemService } from '../task-management-system.service';

@Component({
    selector: 'tms-task-categories',
    templateUrl: './tms-task-categories.component.html',
    styleUrls: ['./tms-task-categories.component.scss']
})
export class TmsTaskCategoriesComponent implements OnInit, OnDestroy {
    @Input()
    objectType: string;
    @Input()
    objectId: string;

    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    tableSource: string;
    modalConfig: ModalFormConfig;
    model: TMSTaskCategoryViewModel;
    renderButtonsTypes = RenderButtonsPosition;
    headerItemsConfig: HeaderItemsConfig;
    canEditCategories: boolean;

    constructor(
        private router: Router,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private isoQuestEventManager: IsoQuestEventManagerService,
        private tmsTaskCategoryServiceService: TmsTaskCategoryService,
        private taskManagementSystemService: TaskManagementSystemService
    ) {
        this.model = new TMSTaskCategoryViewModel();
    }

    ngOnInit() {
        if (!this.objectType)
            throw new Error("objectType is required");

        if (!this.objectId)
            throw new Error("objectId is required");

        const that = this;

        this.isoQuestEventManager.subscribe(this, "workflow-changed", () => {
            this.getPermissions();
        });

        this.tableSource = `TMSTaskCategory/Grid?objectType=${this.objectType}&objectId=${this.objectId}`;

        this.getPermissions();

        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqStatusPickerComponent,
                        field: "Status",
                        label: "[ISOQuest].[Common].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });
    }

    getPermissions() {
        const that = this;

        this.fluentTableConfig = null;
        this.taskManagementSystemService.getPermissions(this.objectType, this.objectId).subscribe(permissions => {
            this.canEditCategories = permissions.CanEditCategories;
            that.fluentTableConfig = that.getFluentTableConfig();
        });
    }

    getFluentTableConfig() {
        const that = this;

        return new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "string",
                    flexGrow: 2
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "status"
                },
                {
                    field: "ModifiedBy",
                    title: "[ISOQuest].[Common].ModifiedBy",
                    sortable: "ModifiedBy",
                    type: "personnel"
                },
                {
                    field: "ModifiedAt",
                    title: "[ISOQuest].[Common].ModifiedAt",
                    sortable: "ModifiedAt",
                    type: "date"
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: TMSTaskCategoryViewModel) {
                        const component = that.dynamicService.addComponent(that.viewContainer, CreateUpdateTmsTaskCategoryComponent,
                            [
                                { id: row.Id },
                                { objectType: that.objectType },
                                { objectId: that.objectId },
                                {
                                    onCreateUpdate: () => {
                                        that.fluentTableConfig.notifyDataChanged("item-updated");
                                    }
                                }
                            ]
                        ) as ComponentRef<CreateUpdateTmsTaskCategoryComponent>;
                    },
                    render(row: TMSTaskCategoryViewModel) {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
                    },
                    flexGrow: 0.28,
                    show: that.canEditCategories
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: TMSTaskCategoryViewModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.tmsTaskCategoryServiceService.delete(row.Id)
                                        .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                                }
                            }
                        ], true);
                    },
                    render(row: ChangeRequestTypeGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.28,
                    show: that.canEditCategories
                }
            ],
            selection: {
                mode: that.canEditCategories ? "checkbox" : "none"
            },
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });
    }

    summonDialog = () => {
        const component = this.dynamicService.addComponent(this.viewContainer, CreateUpdateTmsTaskCategoryComponent, [
            {
                onCreateUpdate: () => {
                    this.fluentTableConfig.notifyDataChanged("item-created");
                }
            },
            {
                objectType: this.objectType
            },
            {
                objectId: this.objectId
            }
        ]) as ComponentRef<CreateUpdateTmsTaskCategoryComponent>;
    }

    bulkChangeStatus = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "TMSTaskCategory/BulkChangeStatus" },
            { "modalOptions": { size: "sm" } }
        ]);
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "TMSTaskCategory/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }

    ngOnDestroy(): void { }
}
