import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { MaturityStatus } from '../../types';
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'change-request-maturity-status-picker',
  templateUrl: './change-request-maturity-status-picker.component.html'
})
export class ChangeRequestMaturityStatusPickerComponent implements OnInit {
  @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
  @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

  @Input() label: string;
  @Input() controllerName: string;
  @Input() placeholder: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;

  maturityStatus = MaturityStatus;
  staticData: IdentityLookupViewModel[];
  constructor(
    private translate: TranslateService
  ) {
    this.modelChange = new EventEmitter();
  }

  ngOnInit() {
    if (!this.label) {
      this.translate.get("[MOCQuest].[ChangeRequest].MaturityStatus").subscribe(localized => this.label = localized);
    }

    this.staticData = Object.keys(MaturityStatus).filter((item) => {
      return isNaN(Number(item));
    }).map(maturityStatus => {
      return new IdentityLookupViewModel(MaturityStatus[maturityStatus], this.translate.instant("[MOCQuest].[ChangeRequest].MaturityStatus." + maturityStatus), true);
    }).sort((left, right) => Number(left.Id) - Number(right.Id));
  }


  onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
