    getStageTranslation(workflowStage: WorkflowStages): string {
        var translatedLabel = "-";
        var requestLabelLocalization = `[MOCQuest].[WorkflowStage].${WorkflowStages[workflowStage]}.Label.${this.siteKey}`;
        this.translate.get(requestLabelLocalization).subscribe((translation: string) => {
            if (requestLabelLocalization !== translation) {
                translatedLabel = translation;
            }
            else {
                this.translate.get(`[MOCQuest].[WorkflowStage].${WorkflowStages[workflowStage]}.Default`).subscribe((translation: string) => {
                    translatedLabel = translation;
                });
            }
        });
        return translatedLabel;
    }

