import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, IqDepartmentPickerComponent, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';

@Component({
  selector: 'update-tms-task-department',
  template: ''
})
export class UpdateTmsTaskDepartmentComponent implements OnInit {
  @Input()
  value: any;
  @Input()
  objectType: any;
  @Input()
  objectId: any;
  @Input()
  facility: LookupViewModel;

  @Output()
  onSubmit: EventEmitter<string>;

  model: any;
  modalFormConfig: ModalFormConfig;
  constructor(
    private dynamicService: DynamicService,
    private viewContainerRef: ViewContainerRef
  ) {
    this.model = {
      Department: null
    };
    this.onSubmit = new EventEmitter<string>();
  }

  ngOnInit() {
    const that = this;
    if (this.value)
      this.model.Department = this.value;

    this.modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[Departments].EditItem.DialogTitle",
      [
        new RowControl([
          new ComponentControl({
            componentClass: IqDepartmentPickerComponent,
            field: "Department",
            data: [
              { objectType: that.objectType },
              { objectId: that.objectId },
              { facility: that.facility },
              { label: '[ISOQuest].[Common].Department' }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            if (that.model && that.model.Department)
              that.onSubmit.emit(that.model.Department.Id);
            else
              that.onSubmit.emit(null);

            that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainerRef, ModalFormComponent, [
      { model: that.model },
      { config: that.modalFormConfig },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }
}
