import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ISOQuestModuleType, ISOQuestRoles, WorkflowPathsModel } from 'codera-shared';
import { ChecklistGroup } from '../../types';
import { ManageChecklistGroupService } from '../manage-checklist-group.service';

@Component({
  selector: 'manage-checklist-group-approval',
  templateUrl: './manage-checklist-group-approval.component.html',
  styleUrls: ['./manage-checklist-group-approval.component.scss']
})
export class ManageChecklistGroupApprovalComponent implements OnInit, OnDestroy {
  @Input() checklistGroup: ChecklistGroup;
  moduleRoles = ISOQuestRoles;
  moduleType = ISOQuestModuleType;
  paths: WorkflowPathsModel;
  objectType: string;

  constructor(private manageChecklistGroupService: ManageChecklistGroupService) { }

  ngOnDestroy(): void { }

  ngOnInit() {
    this.objectType = "MOCQuest.ChecklistGroup";
    this.paths = {
      GetWorkflowDetailsSettings: "MOCQuestWorkflow/GetWorkflowDetailsSettings",
      GetWorkflowSettings: "MOCQuestWorkflow/GetWorkflowSettings",
      GetWorkflowDefinitionByTag: "Workflow/GetWorkflowDefinitionByTag",
      UpdateWorkflowSettings: "MOCQuestWorkflow/UpdateWorkflowSettings",
      ResetWorkflowSettings: "MOCQuestWorkflow/ResetWorkflowSettings",
      GetApprovalHistory: "MOCQuestWorkflow/GetApprovalHistory",
      GetApprovalHistoryDetails: "MOCQuestWorkflow/GetApprovalHistoryDetails",
      GetApprovalHistoryDetailsAll: "MOCQuestWorkflow/GetApprovalHistoryDetailsAll",
      GetWorkflowUiOptions: "Workflow/GetChecklistGroupApprovalWorkflowUiOptions",
      ExecuteTransition: "Workflow/ExecuteTransition",
      PreExecuteTransition: "Workflow/PreExecuteTransition",
      GetAvailableTransitions: "Workflow/GetAvailableTransitions",
      RequestApproval: "ChangeRequest/RequestChecklistGroupApproval",
    };
  }

  approvalEnabledChange(newValue: boolean) {
    this.manageChecklistGroupService.saveSubject.next();
  }
}
