import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { TMSTaskMaturityStatus } from '../types';

@Component({
  selector: 'tms-task-maturity-status-picker',
  templateUrl: './tms-task-maturity-status-picker.component.html'
})
export class TmsTaskMaturityStatusPickerComponent implements OnInit {
  @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
  @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() searchMode: boolean;
  taskMaturityStatus = TMSTaskMaturityStatus;

  staticData: IdentityLookupViewModel[];
  statusConfig: { [key: string]: any }
  constructor() {
    this.modelChange = new EventEmitter<IdentityLookupViewModel>();
    this.staticData = [];
  }

  ngOnInit() {
    if (!this.label)
      this.label = "[ISOQuest].[TaskManagementSystem].Task.MaturityStatus";
      
    this.staticData = Object.keys(TMSTaskMaturityStatus).filter((item) => {
      return isNaN(Number(item));
    }).map(maturityStatus => {
      return new IdentityLookupViewModel(TMSTaskMaturityStatus[maturityStatus], "[ISOQuest].[TaskManagementSystem].Task.MaturityStatus." + maturityStatus, true);
    }).sort((left, right) => Number(left.Id) - Number(right.Id));
  }

  onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
