import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SettingsService } from '../settings.service';
import { MOCQuestSettingsViewModel } from '../types';

@Component({
    selector: 'app-notifications',
    templateUrl: './notifications.component.html',
    styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {
    @Input() viewModel: MOCQuestSettingsViewModel;
    @Output() viewModelChanged: EventEmitter<MOCQuestSettingsViewModel>;

    constructor(private settingsService: SettingsService) {
        this.viewModelChanged = new EventEmitter<MOCQuestSettingsViewModel>();
    }

    ngOnInit() {
    }

    updateSettings() {
        this.settingsService.setSettings(this.viewModel).subscribe((viewModel: MOCQuestSettingsViewModel) => {
            this.viewModelChanged.emit(viewModel);
            window.location.reload();
        });
    }
}
