import { Component, ComponentRef, Input, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, BulkOperationExecutionResult, BulkOperationExecutionResultComponent, BulkOperationExecutionResultItem, CancelAction, ComponentControl, DynamicService, FluentTableConfig, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IqUploaderComponent, IsoQuestEventManagerService, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextConfirmationModalComponent } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { AnalysisChoice, AnalysisChoiceAttachmentPreviewViewModel, AnalysisChoiceAttachmentUploadViewModel, AnalysisConfigModel, ChoiceAction, DeleteAnalysisChoiceAttachmentModel, SaveAnalysisModel } from 'src/app/change-request/types';
import { OutcomeConfigEvent } from 'src/app/system-controls/checklist-groups/manage-checklist-group/outcome-config/types';
import { ChecklistGroup, Choice, ChoiceOutcomeConfigUpdateModel, ChoiceOutcomeType, OutcomeTypeConfigViewModel, Question } from 'src/app/system-controls/checklist-groups/types';
import { AnalysisService } from '../../analysis.service';

@Component({
  selector: 'answer-mode-checklist-group',
  templateUrl: './answer-mode-checklist-group.component.html',
  styleUrls: ['./answer-mode-checklist-group.component.scss']
})
export class AnswerModeChecklistGroupComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;

  choiceOutcomeType = ChoiceOutcomeType;
  choiceAction = ChoiceAction;
  currentSiteKey: string;
  uploadUrl: string;
  fluentTableConfig: FluentTableConfig;
  backendErrors: [];
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private analysisService: AnalysisService,
    private applicationService: ApplicationService,
    private changeRequestService: ChangeRequestService,
    private isoQuestEventManager: IsoQuestEventManagerService
  ) { }

  ngOnInit() {
    const that = this;

    this.currentSiteKey = this.applicationService.getCurrentSiteKey();
    this.uploadUrl = `${this.applicationService.getApiUrl()}/${this.analysisConfig.UploadAnalysisChoiceAttachmentUrl}`;
    this.fluentTableConfig = new FluentTableConfig({
      columns: [{
        field: "Title",
        title: "[ISOQuest].[Common].Attachment.Filename",
        type: "string",
        show: true
      }, {
        field: "Size",
        title: "[ISOQuest].[Common].Fields.Size",
        type: "string",
        show: true
      }, {
        field: "Download",
        title: "",
        type: "custom",
        show: true,
        render(row) {
          return `<a class="btn btn-sm btn-default blue-stripe" href='${row.DownloadUrl}'><i class="fa fa-download"></i></a>`;
        },
        flexGrow: 0.15
      }, {
        field: "Delete",
        title: "",
        type: "action",
        show: true,
        handler(row: AnalysisChoiceAttachmentPreviewViewModel) {
          that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
            { "modalOptions": { size: "sm" } },
            {
              "successCallback": () => {
                let model = new DeleteAnalysisChoiceAttachmentModel(row.Id, row.ChangeRequestId, row.GroupId, row.QuestionId, row.ChoiceId, row.NestedQuestionId, row.NestedChoiceId, row.Object);
                that.changeRequestService.deleteAnalysisChoiceAttachment(that.analysisConfig.DeleteAnalysisChoiceAttachmentUrl, model).subscribe(() => {
                  that.analysisService.fetchChecklistGroups();
                });
              }
            }
          ], true);
        },
        render() {
          return `<a class="btn btn-sm btn-default red-stripe"><i class="far fa-trash-alt"></i></a>`;
        },
        flexGrow: 0.15
      }],
      footer: {
        show: true,
        showExports: false,
        showRefreshButton: false,
        showPaging: false
      },
      sorting: { "": "asc" }
    });
  }

  viewPersonnelInfo(id: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
      { personnelId: id }
    ]);
  }

  viewPersonnelGroupInfo(id: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
      { groupId: id }
    ]);
  }

  questionsTrackByFn(index: number, item: Question) {
    return item.Id;
  }

  choicesTrackByFn(index: number, item: Choice) {
    return item.Id;
  }

  outcomeTypeConfingsTrackByFn(index: number, item: OutcomeTypeConfigViewModel) {
    return item.Id;
  }

  hasSelected(choiceId?: string): boolean {
    if (typeof choiceId !== 'undefined' && choiceId !== '00000000-0000-0000-0000-000000000000' && choiceId !== null)
      return true;
    return false;
  }

  clearChoice(qId: number, ncqId?: number, outcomeTypeConfigIndex?: number): void {
    if (typeof ncqId !== 'undefined') {
      const selectedChoice = this.group.Questions[qId].SelectedChoice;
      const choiceIndex = this.group.Questions[qId].Choices.findIndex(x => x.Id === selectedChoice.Id);
      this.group.Questions[qId].SelectedChoice.OutcomeTypes[outcomeTypeConfigIndex].NestedChecklistConfig.Checklist.Questions[ncqId].SelectedChoice = new Choice();
      this.group.Questions[qId].Choices[choiceIndex].OutcomeTypes[outcomeTypeConfigIndex].NestedChecklistConfig.Checklist.Questions[ncqId].SelectedChoice = new Choice();
    } else {
      this.group.Questions[qId].SelectedChoice = new Choice();
    }
    let analysisChoice = new AnalysisChoice();
    analysisChoice.Action = ChoiceAction.Remove;
    analysisChoice.GroupId = this.group.Id;
    analysisChoice.QuestionId = this.group.Questions[qId].Id;
    if (!(ncqId === undefined || ncqId === null)) {
      analysisChoice.ChoiceId = this.group.Questions[qId].SelectedChoice.Id;
      analysisChoice.OutcomeTypeConfigId = this.group.Questions[qId].SelectedChoice.OutcomeTypes[outcomeTypeConfigIndex].Id;
      analysisChoice.NestedQuestionId = this.group.Questions[qId].SelectedChoice.OutcomeTypes[outcomeTypeConfigIndex].NestedChecklistConfig.Checklist.Questions[ncqId].Id;
    }
    this.saveAnalysis(analysisChoice);
  }

  saveAnalysis(analysisChoice?: AnalysisChoice) {
    const model = new SaveAnalysisModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, analysisChoice);
    this.changeRequestService.saveAnalysis(this.analysisConfig.SaveAnalysisUrl, model).subscribe(() => {
      // this.onAnalysisSave.emit();
      this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'));
      this.analysisService.fetchChecklistGroups();
      this.changeRequestService.headerItems.next();
    }, (error) => {
      const errors = error.error.Errors;
      // Object.keys(errors).forEach((key: string) => this.backendErrors = this.backendErrors.concat(errors[key]));
    });
  }

  selectAnswer2(oldChoice: Choice, newChoice: Choice, analysisChoice: AnalysisChoice) {
    if (oldChoice.Attachments.length > 0 && oldChoice.Id !== newChoice.Id) {
      const modalForm = this.getConfirmDialog(() => {
        this.saveAnalysis(analysisChoice);
      }, () => {
        this.analysisService.fetchChecklistGroups();
      });
      const component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
        { config: modalForm },
        { model: {} },
        {
          modalOptions: {
            size: "md"
          }
        }
      ]) as ComponentRef<ModalFormComponent>;
    } else {
      this.saveAnalysis(analysisChoice);
    }
  }

  getConfirmDialog(onConfirm: () => void, onClose: () => void): ModalFormConfig {
    const modalForm = new ModalFormConfig(
      "[ISOQuest].[Common].ConfirmDialog.Title",
      [
        new RowControl([
          new LabelControl({
            text: `[MOCQuest].[ChecklistGroup].AttachmentsWillBeDeleted`
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.OK",
          css: "btn btn-primary",
          callback: () => {
            onConfirm();
          }
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          css: "btn btn-default",
          callback: () => {
            onClose();
          }
        })
      ]
    );
    return modalForm;
  }

  closePopover(event: OutcomeConfigEvent, popover: any, groupId: string, questionId: string, choiceId: string, nestedQuestionId: string, nestedChoiceId: string, outcomeTypeConfigId: string) {
    if (!event.Cancel) {
      const choiceModel = event.Model;
      const model = new ChoiceOutcomeConfigUpdateModel(this.analysisConfig.ObjectId, groupId, questionId);
      model.OutcomeType = choiceModel.OutcomeType.Id;
      model.NewDocumentConfig = choiceModel.NewDocumentConfig || null;
      model.UpdateDocumentsConfig = choiceModel.UpdateDocumentsConfig || null;
      model.SpecificationConfig = choiceModel.SpecificationConfig || null;
      model.ChoiceId = choiceId;
      model.NestedQuestionId = nestedQuestionId;
      model.NestedChoiceId = nestedChoiceId;

      if ((nestedQuestionId == undefined || nestedQuestionId == null) && (nestedChoiceId === undefined || nestedChoiceId === null))
        model.OutcomeTypeConfigId = choiceModel.Id;

      else if (!(nestedQuestionId == undefined || nestedQuestionId == null) && !(nestedChoiceId === undefined || nestedChoiceId === null)) {
        model.OutcomeTypeConfigId = outcomeTypeConfigId;
        model.NestedOutcomeTypeConfigId = choiceModel.Id;
      }

      this.changeRequestService.updateOutcomeConfig(this.analysisConfig.UpdateOutcomeConfigUrl, model)
        .subscribe(() => {
          popover.close();
          this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
          this.analysisService.fetchChecklistGroups();
        });
    } else
      popover.close();
  }

  uploadAttachments(groupId: string, questionId: string, choiceId: string, nestedQuestionId: string, nestedChoiceId: string) {
    const that = this;
    this.changeRequestService.getAnalysis(this.analysisConfig.GetAnalysisUrl, this.analysisConfig.ObjectId).subscribe((response: ChecklistGroup[]) => {
      var bulkOperationExecutionResult = new BulkOperationExecutionResult([], [], []);
      let attachmentModel = new AnalysisChoiceAttachmentUploadViewModel();
      attachmentModel.ObjectId = this.analysisConfig.ObjectId;
      attachmentModel.GroupId = groupId;
      attachmentModel.QuestionId = questionId;
      attachmentModel.ChoiceId = choiceId;
      attachmentModel.NestedQuestionId = nestedQuestionId;
      attachmentModel.NestedChoiceId = nestedChoiceId;

      const modalFormConfig = new ModalFormConfig(
        "[ISOQuest].[Common].UploadAttachments",
        [
          new ComponentControl({
            componentClass: IqUploaderComponent,
            field: "Attachments",
            data: [
              { uploadUrl: that.uploadUrl },
              { multiple: true },
              {
                onUploadErrorItem: (item) => {
                  var errorMessage = that.translate.instant(JSON.parse(item._xhr.response).Errors[""][0]);
                  bulkOperationExecutionResult.Failed.push(new BulkOperationExecutionResultItem(item.file.name, false, errorMessage));
                }
              },
              {
                onUploadSuccessItem: (item) => {
                  bulkOperationExecutionResult.Succeed.push(new BulkOperationExecutionResultItem("", true, ""));
                }
              },
              {
                onUploadFinished: () => {
                  that.analysisService.fetchChecklistGroups();
                  modalFormConfig.modalRef && modalFormConfig.modalRef.close();
                  that.dynamicService.addComponent(that.viewContainer, BulkOperationExecutionResultComponent, [
                    { model: bulkOperationExecutionResult },
                    {
                      modalOptions: {
                        size: "md"
                      }
                    }
                  ], true);
                }
              }
            ]
          })
        ],
        [
          new SubmitAction({
            field: "Submit",
            value: "[ISOQuest].[Common].Buttons.Submit",
            callback(formValueSnapshot) {
              bulkOperationExecutionResult = new BulkOperationExecutionResult([], [], []);
              that.isoQuestEventManager.notify("iq-uploader-start-upload", attachmentModel);
            },
            css: "btn btn-primary"
          }),
          new CancelAction({
            field: "Cancel",
            value: "[ISOQuest].[Common].Buttons.Cancel",
            callback(formValueSnapshot) { },
            css: "btn btn-default"
          })
        ]
      );

      this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
        { model: {} },
        { config: modalFormConfig },
        {
          modalOptions: {
            size: "md"
          }
        }
      ]);
    });
  }
}
