import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, DynamicService, ISOQuestRoles, LookupViewModel, ModalFormComponent } from 'codera-shared';
import { MethodOfFollowUpFactory } from '../method-of-follow-up.factory';
import { MethodOfFollowUpViewModel } from '../types';

@Component({
  selector: 'method-of-follow-up-picker',
  templateUrl: './method-of-follow-up-picker.component.html'
})
export class MethodOfFollowUpPickerComponent implements OnInit {

  @Input() model: LookupViewModel | LookupViewModel[];
  @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() disableAnnotations?: boolean;

  controllerName: string;
  showNewItemButton: boolean;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private methodOfFollowUpFactory: MethodOfFollowUpFactory
  ) {
    this.modelChange = new EventEmitter();
  }

  ngOnInit() {
    this.controllerName = "/MethodOfFollowUp/GetAvailableMethodsOfFollowUp";

    if (!this.label) {
      this.label = this.isMultiple
        ? "[MOCQuest].[SiteMap].SystemControls.MethodsOfFollowUp.Multiple"
        : "[MOCQuest].[SiteMap].SystemControls.MethodsOfFollowUp.Single";
    }

    if (this.applicationService.hasAnyRole([ISOQuestRoles[ISOQuestRoles.ISOQuestSuperAdministrator], ISOQuestRoles[ISOQuestRoles.MOCQuestAdministrator]]))
      this.showNewItemButton = true;
  }

  onChange(newValue: LookupViewModel | LookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }

  onNewItemClick() {
    var onSave = (result: LookupViewModel) => {
      if (this.isMultiple) {
        if (this.model) {
          this.model = (this.model as LookupViewModel[]).concat([result]);
        } else {
          this.model = [result];
        }
      } else {
        this.model = result;
      }

      this.modelChange.emit(this.model);
    };

    let model = new MethodOfFollowUpViewModel();
    let config = this.methodOfFollowUpFactory.getModalConfig(model, onSave);

    this.dynamicService.addComponent(this.viewContainer,
      ModalFormComponent,
      [
        { model: model },
        { config: config },
        { modalOptions: { size: 'sm' } }
      ], true
    );
  }

}
