export class UpdateChoiceOnChangeModel {
    constructor(
        public ChangeRequestId?: string,
        public AnalysisType?: AnalysisObjectType,
        public GroupId?: string,
        public QuestionId?: string,
        public ChoiceId?: string,
        public OutcomeTypeConfigId?: string,
        public ChangeType?: UpdateOnChangeType,
        public Value?: boolean | string | IdentityLookupViewModel | LookupViewModel,
        protected StringInput?: string,
        protected BooleanInput?: boolean,
        protected IdentityInput?: IdentityLookupViewModel,
        protected LookupInput?: LookupViewModel
    ) {

        switch (this.ChangeType) {
            case UpdateOnChangeType.GroupTitle:
            case UpdateOnChangeType.QuestionTitle:
            case UpdateOnChangeType.ChoiceTitle:
            case UpdateOnChangeType.ChoiceUpdateDocumentsTitle:
            case UpdateOnChangeType.ChoiceNewDocumentTitle:
            case UpdateOnChangeType.ChoiceSpecificationTitle:
                this.StringInput = this.Value as string;
                break;

            case UpdateOnChangeType.GroupAllowApproval:
            case UpdateOnChangeType.ChoiceComments:
            case UpdateOnChangeType.ChoiceAttachments:
            case UpdateOnChangeType.ChoiceDefaultChoice:
                this.BooleanInput = this.Value as boolean;
                break;

            case UpdateOnChangeType.ChoiceOutcome:
            case UpdateOnChangeType.ChoiceSeverity:
                this.IdentityInput = this.Value as IdentityLookupViewModel;
                break;

            case UpdateOnChangeType.ChoiceChecklist:
                this.LookupInput = this.Value as LookupViewModel;
                break;
        }
    }
}

