import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { LookupViewModel } from 'codera-shared';

@Component({
    selector: 'checklist-group-picker',
    templateUrl: './checklist-group-picker.component.html'
})
export class ChecklistGroupPickerComponent implements OnInit {
    @Input() model: LookupViewModel | LookupViewModel[];
    @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

    @Input() label: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;
    @Input() enableItemCreation: boolean;
    @Input() itemsToExclude: any[];
    @Input() allowOutcome: boolean;

    controllerName: string;
    hasPermission: boolean;
    showNewItemButton: boolean;

    constructor() {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        this.controllerName = "/ChecklistGroup/GetAvailableItems";
        if (!(this.allowOutcome === undefined || this.allowOutcome === null)) {
            this.controllerName += `?allowOutcome=${this.allowOutcome}`
        }

        if (!this.label) {
            this.label = this.isMultiple
                ? "[MOCQuest].[SiteMap].SystemControls.ChecklistGroup.Multiple"
                : "[MOCQuest].[SiteMap].SystemControls.ChecklistGroup.Single";
        }

        if (this.enableItemCreation && this.hasPermission)
            this.showNewItemButton = true;
    }

    onChange(newValue: LookupViewModel | LookupViewModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }
}
