import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, DynamicService, IsoQuestEventManagerService } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { ChangeRequestService } from '../../change-request.service';
import { ChangeRequestViewModel, UpdateCommentsAndNotesModel } from '../../types';

@Component({
    selector: 'comments-and-notes',
    templateUrl: './comments-and-notes.component.html',
    styleUrls: ['./comments-and-notes.component.scss']
})
export class CommentsAndNotesComponent implements OnInit {
    @Input() changeRequestId: string;

    @Output() onChangeRequestLoad: EventEmitter<ChangeRequestViewModel>;

    editModeCommentsAndNotes: boolean;
    expandedPrimary: boolean;
    commentsAndNotesModel: UpdateCommentsAndNotesModel;
    model: ChangeRequestViewModel = new ChangeRequestViewModel();
    configTinyMce: any;
    tinyMceInstance: any;

    constructor(
        private http: HttpClient,
        private toastr: ToastrService,
        private translate: TranslateService,
        private changeRequestService: ChangeRequestService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.configTinyMce = {
            "height": 500,
            "theme": 'modern',
            "plugins": 'searchreplace link codesample table hr anchor insertdatetime advlist lists textcolor wordcount imagetools colorpicker contextmenu textpattern autolink charmap print preview visualblocks code fullscreen paste code help',
            "toolbar1": 'formatselect | bold italic strikethrough forecolor backcolor | link | alignleft aligncenter alignright alignjustify | numlist bullist outdent indent  | removeformat',
            "image_advtab": true,
            "branding": false,
            "file_picker_types": 'image',
            "automatic_uploads": false,
            "images_upload_handler": function (blobInfo, success, failure) {
                success(`data:${blobInfo.blob().type};base64,${blobInfo.base64()}`);
            },
            "convert_urls": false,
            "relative_urls": false,
            "target_list": false,
            "default_link_target": '_blank',
            "setup": (editor) => {
                this.tinyMceInstance = editor;
            },
        };
        this.onChangeRequestLoad = new EventEmitter<ChangeRequestViewModel>();
    }

    ngOnInit() {
        const that = this;
        this.getById();
        this.expandedPrimary = this.model.CommentsAndNotes && this.model.CommentsAndNotes.length > 0;

        this.isoQuestEventManager.subscribe(this, "fetch-change-request", () => {
            that.getById();
        });
    }

    getById() {
        this.changeRequestService.getById(this.changeRequestId).subscribe((response: ChangeRequestViewModel) => {
            this.model = response;
            this.onChangeRequestLoad.emit(this.model);
        });
    }

    editCommentsAndNotes(): void {
        this.changeRequestService.getCommentsAndNotesById(this.changeRequestId)
            .subscribe((model: UpdateCommentsAndNotesModel) => {
                this.commentsAndNotesModel = new UpdateCommentsAndNotesModel(model.Id, model.CommentsAndNotes);
                this.editModeCommentsAndNotes = true;
            });
    }

    updateCommentsAndNotes(): void {
        this.changeRequestService.updateCommentsAndNotes(this.commentsAndNotesModel)
            .subscribe(() => {
                this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                this.editModeCommentsAndNotes = false;
                this.changeRequestService.getCommentsAndNotesById(this.changeRequestId)
                    .subscribe((model: UpdateCommentsAndNotesModel) => {
                        this.model.CommentsAndNotes = model.CommentsAndNotes;
                    });
            });
    }

    ngOnDestroy(): void {
        const notifications = this.tinyMceInstance && this.tinyMceInstance.notificationManager && this.tinyMceInstance.notificationManager.getNotifications();
        if (notifications && notifications.length) {
            notifications.forEach(notification => {
                const element = document.getElementById(notification._id);
                if (element)
                    element.remove();
            });
        }
    }
}
