import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import * as _ from 'lodash';
import { ChoiceOutcomeType, DocumentActivityModel, OutcomeTypeConfigViewModel } from '../../types';
import { OutcomeConfigEvent } from './types';

@Component({
    selector: 'outcome-config',
    styleUrls: ['./outcome-config.component.scss'],
    templateUrl: './outcome-config.component.html',
    encapsulation: ViewEncapsulation.None
})
export class OutcomeConfigComponent implements OnInit {
    @Input() choice: OutcomeTypeConfigViewModel;
    @Input() objectToUse: string;
    @Input() requiredConfig: boolean;

    @Output() closePopover: EventEmitter<OutcomeConfigEvent>;

    outcomeType = ChoiceOutcomeType;
    internalChoice: OutcomeTypeConfigViewModel;
    constructor(private translate: TranslateService) {
        this.closePopover = new EventEmitter();
    }

    ngOnInit() {
        this.internalChoice = _.cloneDeep(this.choice);
    }

    validateConfigTitle(title: string): string {
        if (this.requiredConfig) {
            if (!title)
                return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

            if (title.length == 0)
                return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
        }

        return null;
    }

    saveConfig(): void {
        if (this.requiredConfig) {
            switch (this.internalChoice.OutcomeType.Id) {
                //Update Documents
                case ChoiceOutcomeType.DocumentUpdate:
                    if (this.validateConfigTitle(this.internalChoice.UpdateDocumentsConfig.Title))
                        return;
                    break;

                //New Document
                case ChoiceOutcomeType.NewDocument:
                    if (this.validateConfigTitle(this.internalChoice.NewDocumentConfig.Title))
                        return;
                    break;

                //Specification
                case ChoiceOutcomeType.Specify:
                    if (this.validateConfigTitle(this.internalChoice.SpecificationConfig.Title))
                        return;
                    break;
            }
        }

        this.closePopover.emit(new OutcomeConfigEvent(false, this.internalChoice));
    }

    cancelConfig(): void {
        this.closePopover.emit(new OutcomeConfigEvent(true, this.internalChoice));
    }

    manageDocuments(documents: DocumentActivityModel[]): void {
        this.internalChoice.UpdateDocumentsConfig.Documents = [];

        documents.forEach(x => {
            this.internalChoice.UpdateDocumentsConfig.Documents.push(x);
        });

        (this.internalChoice.UpdateDocumentsConfig.Documents as any).documents = this.internalChoice.UpdateDocumentsConfig.Documents;
    }
}
