import { Injectable } from '@angular/core';
import { CancelAction, InputControl, LookupViewModel, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { MethodOfFollowUpService } from './method-of-follow-up.service';
import { MethodOfFollowUpViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class MethodOfFollowUpFactory {

  constructor(
    private methodOfFollowUpService: MethodOfFollowUpService
  ) { }

  getModalConfig(model: MethodOfFollowUpViewModel, onSave?: (result: LookupViewModel) => void): ModalFormConfig {
    const that = this;

    return new ModalFormConfig(
      "[MOCQuest].[Common].Buttons.MethodOfFollowUp.New",
      [
        new RowControl([
          new InputControl({
            type: "text",
            field: "Title",
            label: "[ISOQuest].[Common].Title",
            required: true,
            css: "col-md-12",
            min: 0,
            max: 250
          })
        ]),
        new RowControl([
          new InputControl({
            type: "checkbox",
            field: "Status",
            label: "[ISOQuest].[Common].Choices.Active",
            css: "col-md-12"
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback() {
            that.methodOfFollowUpService.saveOrUpdate(model).subscribe((methodOfFollowUpId: string) => {
              if (onSave) {
                onSave(new LookupViewModel(methodOfFollowUpId, model.Title, model.Status));
              }
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );
  }
}
