import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, IdentityLookupViewModel, InputControl, IqStatusPickerComponent, IsoQuestEventManagerService, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { TmsTaskColorColorComponent } from '../tms-task-color-color/tms-task-color-color.component';
import { TmsTaskColorPreviewColorComponent } from '../tms-task-color-preview-color/tms-task-color-preview-color.component';
import { TmsTaskColorService } from '../tms-task-color.service';
import { TMSColorViewModel } from '../types';

@Component({
  selector: 'create-update-tms-task-color',
  templateUrl: './create-update-tms-task-color.component.html',
  styleUrls: ['./create-update-tms-task-color.component.scss']
})
export class CreateUpdateTmsTaskColorComponent implements OnInit {
  @Input()
  id: string;
  @Input()
  objectType: string;
  @Input()
  objectId: string;

  @Output()
  onCreate: EventEmitter<TMSColorViewModel>;
  @Output()
  onUpdate: EventEmitter<TMSColorViewModel>;

  modalFormConfig: ModalFormConfig;
  model: TMSColorViewModel;
  private component: ComponentRef<ModalFormComponent>;

  constructor(
    private tmsTaskColorService: TmsTaskColorService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private isoQuestEventManager: IsoQuestEventManagerService
  ) {
    this.model = new TMSColorViewModel();
    this.onCreate = new EventEmitter<TMSColorViewModel>();
    this.onUpdate = new EventEmitter<TMSColorViewModel>();
  }

  ngOnInit() {
    if (!this.objectType)
      throw new Error("objectType is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    this.summonDialog();
    if (this.id)
      this.getById();
  }

  summonDialog(): void {
    const that = this;

    this.model.ObjectType = this.objectType;
    this.model.ObjectId = this.objectId;

    let title = this.id
      ? "[ISOQuest].[TaskManagementSystem].Color.EditColor"
      : "[ISOQuest].[TaskManagementSystem].Color.NewColor";

    this.modalFormConfig = new ModalFormConfig(
      title,
      [
        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskColorColorComponent,
            field: "Color",
            label: "[ISOQuest].[Common].Color",
            css: "col-8",
            data: [
              {
                modelChange: (color: string) => {
                  const previewColorComponentControl = that.modalFormConfig.getControlByField("PreviewColor") as ComponentControl;
                  previewColorComponentControl.getDataByKey("color").color = new IdentityLookupViewModel(0, color, false);
                }
              }
            ]
          }),
          new ComponentControl({
            componentClass: TmsTaskColorPreviewColorComponent,
            field: "PreviewColor",
            label: "[ISOQuest].[Common].Preview",
            css: "col-4",
            data: [
              { color: that.model.Color },
              {
                onColorPreviewChange: (colorTitle: string) => {
                  that.model.Title = colorTitle;
                }
              }
            ]
          })
        ]),
        new RowControl([
          new InputControl({
            type: "checkbox",
            field: "Status",
            label: "[ISOQuest].[Common].Choices.Active",
            css: "col-12"
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            if (!that.model.Color)
              return;

            that.tmsTaskColorService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
              if (typeof response === "string")
                that.onCreate.emit(new TMSColorViewModel(response, that.model.Title, that.model.Status, that.model.Color, that.objectId, that.objectType));
              else if (typeof response === "boolean")
                that.onUpdate.emit(new TMSColorViewModel(that.model.Id, that.model.Title, that.model.Status, that.model.Color, that.objectId, that.objectType));
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
      [
        { config: this.modalFormConfig },
        { model: this.model },
        {
          modalOptions: {
            size: "md"
          }
        }
      ]) as ComponentRef<ModalFormComponent>;
  }

  getById(): void {
    this.tmsTaskColorService.getById(this.id).subscribe(color => {
      this.model = color;
      this.component.instance.model = color;

      const previewColorComponentControl = this.modalFormConfig.getControlByField("PreviewColor") as ComponentControl;
      previewColorComponentControl.getDataByKey("color").color = new IdentityLookupViewModel(0, this.model.Color, false);
      this.isoQuestEventManager.notify("invalidateData", null);
    });
  }
}
