import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, CancelAction, ComponentControl, DynamicService, FluentTabConfig, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IqPersonnelPickerComponent, IsoQuestEventManagerService, ModalFormComponent, ModalFormConfig, RequestApprovalViewModel, RowControl, SubmitAction, TextareaControl, TransitionViewModel, WorkflowPathsModel, WorkflowService } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { AnalysisConfigModel } from 'src/app/change-request/types';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroup, UpdateChecklistGroupAssigneesModel } from 'src/app/system-controls/checklist-groups/types';
import { AnalysisService } from '../../analysis.service';
import { AnswerModeChecklistGroupApprovalsComponent } from '../answer-mode-checklist-group-approvals/answer-mode-checklist-group-approvals.component';
import { AnswerModeChecklistGroupComponent } from '../answer-mode-checklist-group/answer-mode-checklist-group.component';

@Component({
  selector: 'answer-mode-checklist-group-wrapper',
  templateUrl: './answer-mode-checklist-group-wrapper.component.html',
  styleUrls: ['./answer-mode-checklist-group-wrapper.component.scss']
})
export class AnswerModeChecklistGroupWrapperComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;
  @Input() workflowObjectType: string;
  @Input() workflowPaths: WorkflowPathsModel;

  fluentTabs: FluentTabConfig[];
  settings: MOCQuestSettingsViewModel;
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private analysisService: AnalysisService,
    private workflowService: WorkflowService,
    private applicationService: ApplicationService,
    private changeRequestService: ChangeRequestService,
    private isoQuestEventManager: IsoQuestEventManagerService,
  ) { }

  ngOnInit() {
    const that = this;

    this.fluentTabs = [
      new FluentTabConfig({
        componentClass: AnswerModeChecklistGroupComponent,
        title: "[ISOQuest].[Common].General",
        data: [
          { group: that.group },
          { analysisConfig: that.analysisConfig }
        ]
      }),
      new FluentTabConfig({
        componentClass: AnswerModeChecklistGroupApprovalsComponent,
        title: (tab) => {
          return `<span><i class='fa fa-sitemap'></i>&nbsp;${that.translate.instant("[ISOQuest].[Workflow].Common.Approvals")}</span>`;
        },
        data: [
          { group: that.group },
          { objectId: that.group.Id },
          { objectType: that.workflowObjectType },
          { workflowPaths: that.workflowPaths }
        ],
        show: (that.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel).AllowChecklistApproval
      })
    ];

    this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;
  }

  summonUpdateAssigneesModal(checklistGroupId: string) {
    this.changeRequestService.getChecklistGroupAssignees(this.analysisConfig.ObjectId, checklistGroupId, this.analysisConfig.ObjectType)
      .subscribe(assignees => {
        const model = new UpdateChecklistGroupAssigneesModel(this.analysisConfig.ObjectId, checklistGroupId, this.analysisConfig.ObjectType, assignees);
        const modalConfig = new ModalFormConfig(
          "[MOCQuest].[ChangeRequest].UpdateChecklistGroupAssignees",
          [
            new RowControl([
              new ComponentControl({
                componentClass: IqPersonnelPickerComponent,
                field: "Assignees",
                css: "col-12",
                data: [
                  { targetModule: "MOCQuest" },
                  { label: "[ISOQuest].[Common].Assignees" },
                  { isMultiple: true },
                  { onlyActive: true }
                ]
              })
            ])
          ],
          [
            new SubmitAction({
              field: "Submit",
              value: "[ISOQuest].[Common].Buttons.Submit",
              css: "btn btn-primary",
              callback: () => {
                this.changeRequestService.updateChecklistGroupAssignees(model).subscribe(() => {
                  this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
                  this.analysisService.fetchChecklistGroups();
                }, (error) => {
                  var errorMessage = error.error.Errors[""][0];
                  this.toastr.error(this.translate.instant(errorMessage));
                });
              }
            }),
            new CancelAction({
              field: "Cancel",
              value: "[ISOQuest].[Common].Buttons.Cancel",
              css: "btn btn-default",
              callback: () => { }
            })
          ]
        );

        this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
          { model: model },
          { config: modalConfig },
          {
            modalOptions: {
              size: "md"
            }
          }
        ]);
      }, (error) => {
        var errorMessage = error.error.Errors[""][0];
        this.toastr.error(this.translate.instant(errorMessage));
      });
  }


  requestApproval = () => {
    const that = this;

    const transitionViewModel = new TransitionViewModel();
    const requestApprovalConfig = new ModalFormConfig(
      "[MOCQuest].[Transition].ChecklistGroup.NOTSTARTED_WORKFLOWAPPROVAL",
      [
        new RowControl([
          new TextareaControl({
            field: "Comment",
            css: "col-md-12",
            label: "[ISOQuest].[Common].OptionalComment"
          })
        ]),
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formValueSnapshot) {
            transitionViewModel.Id = that.analysisConfig.ObjectId;
            transitionViewModel.ObjectId = that.group.Id;
            transitionViewModel.ObjectType = this.objectType;

            this.transitionViewModel = [];
            let requestApproval = new RequestApprovalViewModel();
            requestApproval.Id = transitionViewModel.ObjectId;
            requestApproval.ObjectType = transitionViewModel.ObjectType;
            requestApproval.Comment = transitionViewModel.Comment;

            that.workflowService.requestApproval(requestApproval, that.workflowPaths.RequestApproval).subscribe(() => {
              that.isoQuestEventManager.notify("workflow-changed", {});
              that.analysisService.fetchChecklistGroups();
              that.onWorkflowChanged(true);
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback(formValueSnapshot) { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { "config": requestApprovalConfig },
      { "model": transitionViewModel },
      {
        "modalOptions": {
          size: "sm"
        }
      }
    ], true);
  }

  viewPersonnelInfo(id: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
      { personnelId: id }
    ]);
  }

  viewPersonnelGroupInfo(id: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
      { groupId: id }
    ]);
  }

  onWorkflowChanged(value: boolean) {
    this.changeRequestService.headerItems.next();
  }
}
