import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { BulkOperationExecutionResultComponent, CancelAction, ComponentControl, DynamicService, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { TmsTaskColorPickerComponent } from '../../tms-task-colors/tms-task-color-picker/tms-task-color-picker.component';
import { BulkChangeColorViewModel } from '../../types';

@Component({
  selector: 'bulk-change-tms-task-color',
  template: ''
})
export class BulkChangeTmsTaskColorComponent implements OnInit {
  @Input()
  objectId: string;
  @Input()
  objectType: string;
  @Input()
  selectedIds: string[];

  @Output()
  onSuccess: EventEmitter<void>;
  model: BulkChangeColorViewModel;
  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private taskManagementSystemService: TaskManagementSystemService
  ) {
    this.onSuccess = new EventEmitter<void>();
  }


  ngOnInit() {
    const that = this;

    if (this.selectedIds == null || this.selectedIds.length == 0)
      throw new Error("Please provide TMS Task ids");

    this.model = new BulkChangeColorViewModel(this.selectedIds, null);
    const modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].ChangeTasksColor",
      [
        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskColorPickerComponent,
            field: "Color",
            data: [
              { objectId: this.objectId },
              { objectType: this.objectType },
              { onlyActive: true }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formValueSnapshot) {
            if (that.model.Color == null) {
              const modalForm = new ModalFormConfig(
                "[ISOQuest].[Common].ConfirmDialog.Title",
                [
                  new RowControl([
                    new LabelControl({
                      text: `[ISOQuest].[TaskManagementSystem].ConfirmDialog.RemoveColorMessage`
                    })
                  ])
                ],
                [
                  new SubmitAction({
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.OK",
                    css: "btn btn-primary",
                    callback: () => {
                      that.updateTmsTasksColors();
                    }
                  }),
                  new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    css: "btn btn-default",
                    callback: () => {

                    }
                  })
                ]
              );

              that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
                { config: modalForm },
                { model: {} },
                {
                  modalOptions: {
                    size: "md"
                  }
                }
              ]);
            } else {
              that.updateTmsTasksColors();
            }
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback(formValueSnapshot) { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { config: modalFormConfig },
      { model: this.model },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }

  updateTmsTasksColors() {
    this.taskManagementSystemService.bulkChangeTaskColor(this.model).subscribe((response) => {
      this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
        { model: response },
        {
          modalOptions: {
            size: "md"
          }
        }
      ]);
      this.onSuccess.emit();
    });
  }
}
