import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { ApplicationService, DynamicService, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent } from 'codera-shared';
import { AnalysisConfigModel } from 'src/app/change-request/types';
import { ChecklistGroup, Choice, ChoiceOutcomeType } from 'src/app/system-controls/checklist-groups/types';

@Component({
  selector: 'view-mode-checklist-group',
  templateUrl: './view-mode-checklist-group.component.html',
  styleUrls: ['./view-mode-checklist-group.component.scss']
})
export class ViewModeChecklistGroupComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;
  choiceOutcomeType = ChoiceOutcomeType;
  currentSiteKey: string;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService
  ) { }

  ngOnInit() {
    this.currentSiteKey = this.applicationService.getCurrentSiteKey();
  }

  viewPersonnelInfo(personnelId: string): void {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
      { personnelId: personnelId }
    ]);
  }

  viewPersonnelGroupInfo(groupId: string): void {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
      { groupId: groupId }
    ]);
  }

  hasSelected(choice: Choice): boolean {
    if (choice && choice.Id !== '00000000-0000-0000-0000-000000000000' && choice.Id !== null)
      return true;
    return false;
  }
}
