import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, BulkDeleteComponent, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderDropdownItem, HeaderItemsConfig, InputControl, LookupViewModel, ModalFormComponent, ModalFormConfig, RenderButtonsPosition, RowControl, TextConfirmationModalComponent } from 'codera-shared';
import { ChangeRequestTagInfoComponent } from '../../info-components/change-request-tag-info/change-request-tag-info.component';
import { ChangeRequestTagFactory } from './change-request-tag.factory';
import { ChangeRequestTagService } from './change-request-tag.service';
import { ChangeRequestTagGridModel, ChangeRequestTagViewModel } from './types';

@Component({
  selector: 'change-request-tag',
  templateUrl: './change-request-tag.page.html',
  styleUrls: ['./change-request-tag.page.scss']
})
export class ChangeRequestTagPage implements OnInit {
    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    tableSource: string;
    modalConfig: ModalFormConfig;
    model: ChangeRequestTagViewModel;
    renderButtonsTypes = RenderButtonsPosition;
    headerItemsConfig: HeaderItemsConfig;
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;

    constructor(
        private route: ActivatedRoute,
        private translate: TranslateService,
        private titleService: Title,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private changeRequestTagService: ChangeRequestTagService,
        private changeRequestTagFactory: ChangeRequestTagFactory
    ) { }

    ngOnInit() {
        const that = this;

        this.tableSource = `ChangeRequestTag/Grid`;
        this.pageTitle = this.route.snapshot.data["pageTitle"];

        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });

        // set the title in the tab
        this.translate.get(this.pageTitle).subscribe(translated => {
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translated}`);
        });

         // table config
        this.headerItemsConfig = new HeaderItemsConfig([
            new HeaderDropdownItem({
                name: "bulk-actions",
                icon: "",
                label: "[ISOQuest].[Common].Buttons.WithSelected",
                dropdownActions: [
                    new HeaderButtonItem({
                        name: "bulk-delete",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.Delete",
                        action: that.bulkDelete
                    })
                ]
            })
        ]);

        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "string",
                    flexGrow: 4
                },
                {
                    field: "viewAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestTagViewModel) {
                        that.dynamicService.addComponent(that.viewContainer, ChangeRequestTagInfoComponent, [
                            { tag: new LookupViewModel(row.Id, row.Title, true) }
                        ], true);
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default blue-stripe pull-right btn-overlay"><i class="fa fa-eye"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.View")}</span></a>`;
                    },  
                    flexGrow: 0.25
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestTagViewModel) {
                        that.changeRequestTagService.getById(row.Id).subscribe((response: ChangeRequestTagViewModel) => {
                            that.model = response;
                            that.modalConfig = that.changeRequestTagFactory.getModalConfig(that.model, () => { that.fluentTableConfig.notifyDataChanged('item-updated'); });

                            that.modalConfig.modalHeader = "[MOCQuest].[Common].Buttons.ChangeRequestTag.Edit";
                            that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
                                { "config": that.modalConfig },
                                { "model": that.model },
                                {
                                    "modalOptions": {
                                        size: "sm"
                                    }
                                }
                            ], true);
                        });
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
                    },
                    flexGrow: 0.25
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestTagViewModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.changeRequestTagService.delete(row.Id)
                                        .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                                }
                            }
                        ], true);
                    },
                    render(row: ChangeRequestTagGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.25
                }
            ],
            selection: {
                mode: "checkbox"
            },
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "ChangeRequestTag/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }

}
