import { IdentityLookupViewModel, LookupViewModel, IqLanguage } from 'codera-shared';

export class UpdateLocalizationItemViewModel {
    constructor(
        public Key?: string,
        public JavaScript?: boolean,
        public IsExternal?: boolean,
        public Module?: IdentityLookupViewModel,
        public Section?: LookupViewModel,
        public Values?: ValuePerLanguage[]
    ) {
        this.JavaScript = true;
        this.Values = [];
    }
}

export class ValuePerLanguage {
    constructor(
        public Id?: string,
        public LanguageId?: string,
        public Value?: string,
        public ShortTitle?: string
    ) { }
}

export class LocalizationItemViewModel {
    constructor(
        public Id?: string,
        public Key?: string,
        public Value?: string,
        public JavaScript?: boolean,
        public IsExternal?: boolean,
        public Module?: IdentityLookupViewModel,
        public Section?: LookupViewModel,
        public Language?: IqLanguage,
    ) { }
}
