import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from "@ngx-translate/core";
import { FluentTabConfig, IsoQuestEventManagerService, WorkflowPathsModel } from 'codera-shared';
import { Subscription } from 'rxjs';
import { AnalysisObjectType } from 'src/app/system-controls/checklist-groups/types';
import { WorkflowStages } from '../../../settings/types';
import { AnalysisConfigModel } from '../../types';
import { AnalysisComponent } from '../analysis/analysis.component';
import { AnalysisService } from '../analysis/analysis.service';
import { ProjectWorkflowComponent } from '../approval/project-workflow.component';
import { RecommendedActionComponent } from './recommended-action/recommended-action.component';
import { RecommendedActionService } from './recommended-action/recommended-action.service';
import { RecommendedActionsCountViewModel } from './recommended-action/types';

@Component({
    selector: 'review-approval-tabs',
    templateUrl: './review-approval-tabs.component.html',
    styleUrls: ['./review-approval-tabs.component.scss']
})
export class ReviewApprovalTabsComponent implements OnInit, OnDestroy {
    changeRequestId: string;
    tabsConfig: FluentTabConfig[];

    @Input() objectId: string;
    @Input() objectType: string;
    @Input() paths: WorkflowPathsModel;
    recommendedActionsCount: RecommendedActionsCountViewModel;
    subscriptions: Subscription[];
    constructor(
        private route: ActivatedRoute,
        private translate: TranslateService,
        private analysisService: AnalysisService,
        private recommendedActionService: RecommendedActionService,
        private isoQuestEventManager: IsoQuestEventManagerService) {
    }

    ngOnDestroy(): void {
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
    }

    ngOnInit() {
        const that = this;

        this.subscriptions = [];
        this.route.paramMap.subscribe(paramMap => {
            this.changeRequestId = paramMap.get("id");
        });
        this.getRecommendedActionsCount();
        this.prepareTabs();

        this.subscriptions.push(
            this.analysisService.fetchPreStartupReviewAnalysisAlerts$.subscribe(() => {
                this.isoQuestEventManager.notify("workflow-changed", {});
                this.isoQuestEventManager.notify("get-change-request-alerts", {});
            })
        )
    }

    getRecommendedActionsCount() {
        this.recommendedActionService.getRecommendedActionsCount(this.changeRequestId).subscribe(recommendedActionsCount => {
            this.recommendedActionsCount = recommendedActionsCount;
        });
    }

    prepareTabs() {
        const that = this;

        this.tabsConfig = [
            new FluentTabConfig({
                title: () => {
                    return `<span><i class='fal fa-print-search'></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ReviewApproval.Review")}</span>`;
                },
                show: true,
                componentClass: AnalysisComponent,
                data: [
                    { "changeRequestId": this.changeRequestId },
                    {
                        analysisConfig: new AnalysisConfigModel(
                            that.changeRequestId,
                            AnalysisObjectType.PreStartupReviewAnalysis,
                            "ChangeRequest/GetPreStartupReviewAnalysis",
                            "ChangeRequest/SaveAnalysis",
                            "ChangeRequest/UploadPreStartupReviewAnalysisChoiceAttachment",
                            "ChangeRequest/GetChangeRequestStatus",
                            "ChangeRequest/UpdateChangeRequestAnalysis",
                            "ChangeRequest/RemoveAnalysisQuestionGroup",
                            "ChangeRequest/UpdatePreStartupReviewAnalysisGroupsOrder",
                            "ChangeRequest/DeleteAnalysisChoiceAttachments",
                            "ChangeRequest/UpdateOutcomeConfig",
                            "ChangeRequest/RemoveAnalysisQuestion",
                            "ChangeRequest/AddAnalysisChoice",
                            "ChangeRequest/RemoveAnalysisChoice",
                            "ChangeRequest/UpdateChoiceOnChange",
                            "ChangeRequest/UpdatePreStartupReviewAnalysisQuestionsOrder",
                            "ChangeRequest/UpdatePreStartupReviewAnalysisChoicesOrder",
                            "ChangeRequest/DeleteAnalysisChoiceAttachment",
                            false,
                            "ChangeRequest/AddAnalysisQuestion",
                            "ChangeRequest/GetPreStartupReviewAnalysisState"
                        )
                    }
                ]
            }),
            new FluentTabConfig({
                title: (tab: FluentTabConfig) => {
                    var recommendedActionsCount = that.recommendedActionsCount;
                    var element = ``;
                    if (recommendedActionsCount) {
                        var recommendedActionsCompleted = recommendedActionsCount.All === recommendedActionsCount.Completed;
                        var recommendedActionsClass = recommendedActionsCompleted ? "badge badge-success" : "badge badge-warning";
                        if (recommendedActionsCompleted) {
                            element = `<span><i class="fas fa-share-all"></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ReviewApproval.RecommendedActions")}</span>&nbsp;&nbsp;<span class="${recommendedActionsClass}" style="border-radius:50%">${recommendedActionsCount.All}</span>`
                        } else {
                            element = `<span><i class="fas fa-share-all"></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ReviewApproval.RecommendedActions")}</span>&nbsp;&nbsp;<span class="${recommendedActionsClass}" style="border-radius:50%">${recommendedActionsCount.Completed}/${recommendedActionsCount.All}</span>`
                        }
                    } else {
                        element = `<span><i class="fas fa-share-all"></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ReviewApproval.RecommendedActions")}</span>`;
                    }
                    return element;
                },
                show: true,
                componentClass: RecommendedActionComponent,
                data: [
                    { "changeRequestId": this.changeRequestId },
                    {
                        "refreshRecommendedActionCounter": () => {
                            that.getRecommendedActionsCount();
                        }
                    }
                ]
            }),
            new FluentTabConfig({
                componentClass: ProjectWorkflowComponent,
                title: (tab: FluentTabConfig) => {
                    return `<span><i class='fad fa-calendar-check'></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ReviewApproval.Approval")}</span>`;
                },
                show: true,
                data: [
                    { "objectId": this.objectId },
                    { "objectType": this.objectType },
                    { "paths": this.paths }
                ],
                key: WorkflowStages[WorkflowStages.Approval]
            })
        ]
    }
}
