import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { FormControl, ValidationErrors } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { ApplicationService, ComponentControl, IdentityLookupViewModel, IqSelectPickerComponent } from 'codera-shared';
import { CancelAction, CustomAction, DynamicService, FluentTableConfig, InputControl, IsoQuestEventManagerService, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextConfirmationModalComponent } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { SettingsService } from '../settings.service';
import { SidebarReportLinkViewModel, MOCQuestSettingsViewModel } from '../types';

@Component({
    selector: 'report-settings',
    templateUrl: './reports.component.html',
    styles: []
})
export class ReportsComponent implements OnInit {
    @Output() onReportActionEvent: EventEmitter<null>;
    settings: MOCQuestSettingsViewModel;

    pageTitle: string;
    fluentTableConfig: FluentTableConfig;
    tableSource: any;
    modalConfig: ModalFormConfig;
    report: SidebarReportLinkViewModel;

    office365Enabled: boolean;
    constructor(
        private settingsService: SettingsService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private translate: TranslateService,
        private isoQuestEventManager: IsoQuestEventManagerService,
        private applicationService: ApplicationService
    ) {
        this.tableSource = [];
        this.onReportActionEvent = new EventEmitter();
    }

    ngOnInit() {
        const that = this;

        this.office365Enabled = this.applicationService.getApplicationSettings().Office365Auth;

        this.getSettings(true);
    }
    getSettings(initializeModals: boolean = false) {
        this.settingsService.getSettings().subscribe(
            (viewModel: MOCQuestSettingsViewModel) => {

                this.settings = viewModel;
                this.tableSource = viewModel.Reports;
                if (initializeModals) {
                    this.setModalConfig();
                    this.setTableConfig();
                }
            }
        );
    }

    setModalConfig() {
        const that = this;
        this.modalConfig = new ModalFormConfig("",
            [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        required: true,
                        max: 50,
                        validators: [
                            (control: FormControl): (ValidationErrors | null) => {

                                const value = control.value as string;
                                var unique = this.settings.Reports.findIndex(x => x.Title === value && x.Id != this.report.Id) ? true : false;
                                if (!unique) {
                                    return {
                                        custom: {
                                            message: "[ISOQuest].[Exceptions].ReportTitleNotUnique"
                                        }
                                    };
                                };

                                return null;
                            }
                        ]
                    })
                ]),
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Link",
                        label: "[ISOQuest].[Common].Link",
                        required: true,
                        max: 160,
                        validators: [
                            (control: FormControl): (ValidationErrors | null) => {
                                const value = control.value as string;
                                const regex = new RegExp("^(http|https):\/\/app.powerbi.com\/groups\/(?:me|([{]?[0-9a-fA-F]{8}-([0-9a-fA-F]{4}-){3}[0-9a-fA-F]{12}[}]?))\/(?:dashboards|reports)\/[{]?[0-9a-fA-F]{8}-([0-9a-fA-F]{4}-){3}[0-9a-fA-F]{12}[}]?(?:$|\/)");
                                if (!regex.test(value)) {
                                    return {
                                        custom: { message: "Wrong Url Format" }
                                    };
                                };
                                var unique = this.settings.Reports.findIndex(x => x.Link === value && x.Id != this.report.Id) == -1;
                                if (!unique) {
                                    return {
                                        custom: {
                                            message: "[ISOQuest].[Exceptions].ReportUrlAlreadyExists"
                                        }
                                    };
                                };
                                return null;
                            }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: IqSelectPickerComponent,
                        field: 'IsUserPermissionIdentity',
                        show: this.office365Enabled,
                        data: [
                            { "isRequired": true },
                            { "label": "[ISOQuest].[Common].AccessType" },
                            {
                                "staticData": [
                                    new IdentityLookupViewModel(0, that.translate.instant("[ISOQuest].[Common].AccessType.Public"), true),
                                    new IdentityLookupViewModel(1, that.translate.instant("[ISOQuest].[Common].AccessType.UserPermission"), true)
                                ]
                            },
                            {
                                "modelChange": (accessType: IdentityLookupViewModel) => {
                                    if (accessType != null) {
                                        that.report.IsUserPermission = accessType.Id == 1;
                                    } else
                                        that.report.IsUserPermission = false;
                                }
                            }
                        ],
                        css: 'col-4',
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    callback: () => {
                        var newReport = new SidebarReportLinkViewModel(this.report.Id, that.report.Title, that.report.Link, that.report.IsUserPermission);
                        var found = false;
                        if (this.settings.Reports.find(x => x.Id == newReport.Id) != null)
                            found = true;

                        if (!found)
                            this.settings.Reports.push(newReport);
                        else {
                            var report = this.settings.Reports.find(x => x.Id == newReport.Id)
                            report.Link = newReport.Link;
                            report.Title = newReport.Title;
                            report.IsUserPermission = newReport.IsUserPermission;
                        }
                        this.tableSource = this.settings.Reports;
                        this.settingsService.setSettings(this.settings).subscribe(
                            (result) => {
                                this.getSettings(false);
                                this.fluentTableConfig.notifyDataChanged(this.report.Id ? "item-updated" : "item-created");
                                that.modalConfig.modalRef.close();

                            }, (error) => {
                                that.isoQuestEventManager.notify("manuallyTriggerServerValidation", error);
                            }
                        )
                    },
                    css: "btn btn-primary"
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    callback: () => {

                    },
                    css: "btn btn-default"
                })
            ]
        );
    }

    setTableConfig() {
        const that = this;

        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    type: "custom",
                    title: "[ISOQuest].[Common].Title",
                    render(row: SidebarReportLinkViewModel) {
                        return `<span>${row.Title}</span>`;
                    }
                },
                {
                    field: "Link",
                    type: "custom",
                    title: "[ISOQuest].[Common].Link",
                    render(row: SidebarReportLinkViewModel) {
                        return "<a href='" + row.Link + "' target='_blank'>" + row.Link + "</a>";
                    }
                },
                {
                    field: "IsUserPermission",
                    type: "custom",
                    show: that.office365Enabled,
                    title: "[ISOQuest].[Common].AccessType",
                    flexGrow: 0.25,
                    render(row: SidebarReportLinkViewModel) {
                        if (row.IsUserPermission)
                            return `<span>${that.translate.instant("[ISOQuest].[Common].AccessType.UserPermission")}</span>`;
                        return `<span>${that.translate.instant("[ISOQuest].[Common].AccessType.Public")}</span>`;
                    }
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: SidebarReportLinkViewModel) {
                        that.modalConfig.modalHeader = "[ISOQuest].[Common].EditReport.DialogTitle";
                        that.report = JSON.parse(JSON.stringify(row));
                        if (row.IsUserPermission) {
                            that.report.IsUserPermissionIdentity = new IdentityLookupViewModel(1, that.translate.instant("[ISOQuest].[Common].AccessType.UserPermission"), true);
                            that.report.IsUserPermission = true;
                        }
                        else {
                            that.report.IsUserPermissionIdentity = new IdentityLookupViewModel(0, that.translate.instant("[ISOQuest].[Common].AccessType.Public"), true);
                            that.report.IsUserPermission = false;
                        }
                        that.dynamicService.addComponent(
                            that.viewContainer,
                            ModalFormComponent,
                            [
                                { model: that.report },
                                { config: that.modalConfig },
                                { modalOptions: { size: "lg" } }
                            ]
                        )
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant(
                            "[ISOQuest].[Common].Buttons.Edit"
                        )}</span></a>`;
                    },
                    flexGrow: 0.2
                },
                {
                    field: 'deleteAction',
                    title: '',
                    type: 'action',
                    handler(row: SidebarReportLinkViewModel) {

                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent,
                            [
                                { modalHeader: '[ISOQuest].[Common].ConfirmDialog.Title' },
                                { modalText: '[ISOQuest].[Common].ConfirmDialog.DeleteMessage' },
                                {
                                    modalOptions: {
                                        size: 'sm'
                                    }
                                },
                                {
                                    successCallback: () => {
                                        var index = that.settings.Reports.findIndex(x => x.Id == row.Id);
                                        if (index != null)
                                            that.settings.Reports.splice(index, +1);

                                        that.settingsService.setSettings(that.settings).subscribe(
                                            (result) => {
                                                that.fluentTableConfig.notifyDataChanged("item-deleted");
                                            }
                                        )
                                    }
                                }
                            ],
                            true
                        );
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant('[ISOQuest].[Common].Buttons.Delete')}</span></a>`;
                    },
                    flexGrow: 0.2
                }
            ],
            selection: {
                mode: "none"
            },
            sorting: {
                Title: "asc"
            },
            filter: null,
            footer: {
                show: true,
                showExports: false,
                showPageSize: true,
                showPaging: true,
                showRefreshButton: true
            },
        });
    }

    openAdd() {

        this.report = new SidebarReportLinkViewModel();
        this.report.IsUserPermissionIdentity = new IdentityLookupViewModel(0, this.translate.instant("[ISOQuest].[Common].AccessType.Public"), true);
        this.report.IsUserPermission = false;

        this.modalConfig.modalHeader = "[ISOQuest].[Common].NewReport.DialogTitle";
        this.dynamicService.addComponent(
            this.viewContainer,
            ModalFormComponent,
            [
                { model: this.report },
                { config: this.modalConfig },
                { modalOptions: { size: "lg" } }
            ]
        )

    }


}

