import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { IsoQuestEventManagerService, WorkflowPathsModel, WorkflowService, WorkflowUiOptionsModel } from 'codera-shared';
import { ChangeRequestService } from '../../change-request.service';
import { ChangeRequestPermissionsModel } from '../../types';

@Component({
    templateUrl: './project-workflow.component.html'
})
export class ProjectWorkflowComponent implements OnInit, OnDestroy {
    @Input() objectId: string;
    @Input() objectType: string;
    @Input() paths: WorkflowPathsModel;

    permissions: ChangeRequestPermissionsModel;
    workflowUiOptionsModel: WorkflowUiOptionsModel;

    constructor(
        private projectService: ChangeRequestService,
        private workflowService: WorkflowService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) { }

    ngOnInit() {
        const that = this;
        this.getPermissions();
        this.getWorkflowUiOptions();

        this.isoQuestEventManager.subscribe(this, 'workflow-definition-changed', () => {
            this.workflowService.getWorkflowDetailsSettings(this.objectId, this.objectType, this.paths.GetWorkflowDetailsSettings).subscribe();
        });

        this.isoQuestEventManager.subscribe(this, 'workflow-changed', () => {
            that.getWorkflowUiOptions();
        });

        this.isoQuestEventManager.subscribe(this, "project-details-changed", () => {
            this.getPermissions();
        });
    }

    getPermissions = () => {
        this.projectService.getPermissions(this.objectId, this.objectType).subscribe((permissions) => {
            this.permissions = permissions;
        });
    }

    getWorkflowUiOptions = () => {
        this.workflowService.getWorkflowUiOptions(this.objectId, this.objectType, this.paths.GetWorkflowUiOptions).subscribe((result) => {
            this.workflowUiOptionsModel = result;
        });
    }

    ngOnDestroy() { }
}
