import { Component, Input, OnInit } from '@angular/core';
import { ApplicationService, ISOQuestModuleType, ISOQuestRoles, WorkflowPathsModel } from 'codera-shared';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroup } from 'src/app/system-controls/checklist-groups/types';

@Component({
  selector: 'view-mode-checklist-group-approvals',
  templateUrl: './view-mode-checklist-group-approvals.component.html',
  styleUrls: ['./view-mode-checklist-group-approvals.component.scss']
})
export class ViewModeChecklistGroupApprovalsComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() objectId: string;
  @Input() objectType: string;
  @Input() workflowPaths: WorkflowPathsModel;

  moduleType = ISOQuestModuleType;
  moduleRoles = ISOQuestRoles;
  settings: MOCQuestSettingsViewModel;

  constructor(private applicationService: ApplicationService) { }

  ngOnInit() {
    this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;
  }

}
