import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, DynamicService, LookupViewModel, ModalFormComponent } from 'codera-shared';
import { ChangeRequestTypeFactory } from '../change-request-type.factory';
import { ChangeRequestTypeViewModel } from '../types';

@Component({
    selector: 'change-request-type-picker',
    templateUrl: './change-request-type-picker.component.html'
})
export class ChangeRequestTypePickerComponent implements OnInit {
    @Input() model: any;
    @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

    @Input() classNames: string[] = null;
    @Input() label: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;
    @Input() enableItemCreation: boolean;
    @Input() itemsForWorkflowMatrix: boolean;
    @Input() itemsForChangeRequest: boolean;
    @Input() set facility(value: LookupViewModel) {
        this.data.facility = value;
    }

    controllerName: string;
    hasPermission: boolean;
    showNewItemButton: boolean;
    data: { [key: string]: any } = {};

    constructor(
        private applicationService: ApplicationService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private changeRequestTypeFactory: ChangeRequestTypeFactory
    ) {
        this.modelChange = new EventEmitter();
        this.facility = new LookupViewModel("", "", false);
    }

    ngOnInit() {
        this.hasPermission = this.applicationService.hasAnyRole(["ISOQuestSuperAdministrator", "MOCQuestAdministrator"]);
        this.updateControllerName();

        if (!this.label) {
            this.label = this.isMultiple
                ? "[MOCQuest].[SiteMap].SystemControls.ChangeRequestType.Multiple"
                : "[MOCQuest].[SiteMap].SystemControls.ChangeRequestType.Single";
        }

        if (this.enableItemCreation && this.hasPermission)
            this.showNewItemButton = true;
    }

    onChange(newValue: LookupViewModel | LookupViewModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }

    onNewItemClick() {
        let onSave = (result: LookupViewModel) => {
            if (this.isMultiple) {
                if (this.model) {
                    this.model = this.model.concat([result]);
                } else {
                    this.model = [result];
                }
            } else {
                this.model = result;
            }

            this.modelChange.emit(this.model);
        };

        let model = new ChangeRequestTypeViewModel();
        let config = this.changeRequestTypeFactory.getModalConfig(model, onSave);

        this.dynamicService.addComponent(this.viewContainer,
            ModalFormComponent,
            [
                { model: model },
                { config: config },
                { modalOptions: { size: 'sm' } }
            ], true
        );
    }

    private updateControllerName() {
        let controllerName = "/ChangeRequestType/GetAvailableItems";
        if (this.itemsForWorkflowMatrix) {
            controllerName = "/ChangeRequestType/GetAvailableItemsForWorkflowMatrix";
        } else if (this.itemsForChangeRequest) {
            controllerName = "/ChangeRequestType/GetAvailableItemsForChangeRequest";
        }
        this.controllerName = controllerName;
    }
}
