import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { MethodOfFollowUpViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class MethodOfFollowUpService {

  private apiUrl: string;

  constructor(
    private http: HttpClient,
    private applicationService: ApplicationService
  ) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  saveOrUpdate(model: MethodOfFollowUpViewModel): Observable<string> {
    const url = `${this.apiUrl}/MethodOfFollowUp/CreateOrUpdate`;
    return this.http.post<string>(url, model);
  }

  getById(methodOfFollowUpId: string): Observable<MethodOfFollowUpViewModel> {
    const url = `${this.apiUrl}/MethodOfFollowUp/GetById`;
    return this.http.post<MethodOfFollowUpViewModel>(url, { id: methodOfFollowUpId });
  }

  delete(methodOfFollowUpId: string): Observable<boolean> {
    const url = `${this.apiUrl}/MethodOfFollowUp/Delete`;
    return this.http.post<boolean>(url, { id: methodOfFollowUpId });
  }
}
