import { Injectable } from "@angular/core";
import { CancelAction, InputControl, LookupViewModel, ModalFormConfig, RowControl, SubmitAction } from "codera-shared";
import { ChangeRequestTypeService } from './change-request-type.service';
import { ChangeRequestTypeViewModel } from './types';

@Injectable({ providedIn: "root" })
export class ChangeRequestTypeFactory {

    constructor(
        private changeRequestTypeService: ChangeRequestTypeService
    ) { }

    getModalConfig(model: ChangeRequestTypeViewModel, onSave?: (result: LookupViewModel) => void): ModalFormConfig {
        const that = this;

        return new ModalFormConfig(
            '[MOCQuest].[Common].Buttons.ChangeRequestType.New',
            [
                new RowControl([
                    new InputControl({
                        type: 'text',
                        field: 'Title',
                        label: '[ISOQuest].[Common].Title',
                        required: true,
                        css: 'col-md-12',
                        min: 0,
                        max: 255
                    })
                ]),
                new RowControl([
                    new InputControl({
                        type: 'checkbox',
                        field: 'AttachmentRequiredBeforeClose',
                        label: '[MOCQuest].[SystemControls].ChangeRequestType.AttachmentRequiredBeforeClose',
                        css: 'col-md-12'
                    })
                ]),
                new RowControl([
                    new InputControl({
                        type: 'checkbox',
                        field: 'Status',
                        label: '[ISOQuest].[Common].Choices.Active',
                        css: 'col-md-12'
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: 'Submit',
                    value: '[ISOQuest].[Common].Buttons.Submit',
                    callback() {
                        that.changeRequestTypeService.saveOrUpdate(model).subscribe((changeRequestTypeId) => {
                            if (onSave) {
                                onSave(new LookupViewModel(changeRequestTypeId as string, model.Title, model.Status));
                            }
                        });
                    },
                    css: 'btn btn-primary'
                }),
                new CancelAction({
                    field: 'Cancel',
                    value: '[ISOQuest].[Common].Buttons.Cancel',
                    callback() { },
                    css: 'btn btn-default'
                })
            ]
        );
    }
}
