    updateChangeRequestTab(fluentTabsComponent: FluentTabsComponent, changeRequestStatus: ChangeRequestStatus) {
        var tabToFindExpression = (x: FluentTabConfig) => false;

        switch (changeRequestStatus) {
            case ChangeRequestStatus.InitialApproval:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.InitialApproval];
                break;
            case ChangeRequestStatus.Analysis:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.Analysis];
                break;
            case ChangeRequestStatus.ManagementApproval:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.ManagementApproval];
                break;
            case ChangeRequestStatus.Implementation:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.Implementation];
                break;
            case ChangeRequestStatus.Approval:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.Approval];
                break;
            case ChangeRequestStatus.FollowUp:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.FollowUp];
                break;
            case ChangeRequestStatus.Requested:
            case ChangeRequestStatus.Launch:
            case ChangeRequestStatus.Closed:
            case ChangeRequestStatus.Terminate:
                tabToFindExpression = x => x.key === WorkflowStages[WorkflowStages.Requested];
                break;
            default:
                break;
        }

        let tabIndex = fluentTabsComponent.config.findIndex(tabToFindExpression);
        if (tabIndex !== -1)
            fluentTabsComponent.setActiveTab(tabIndex);
    }

