import { ChangeDetectorRef, Component, Input, NgZone, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FluentTableConfig } from 'codera-shared';
import { TMSColumnItemViewModel } from '../types';

@Component({
  selector: 'tms-gantt-manage-columns-popover',
  templateUrl: './tms-gantt-manage-columns-popover.component.html',
  styleUrls: ['./tms-gantt-manage-columns-popover.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TmsGanttManageColumnsPopoverComponent implements OnInit {
  @Input() model;
  @Input() settings;
  @ViewChild('table') table;
  tmsColumsConfig: FluentTableConfig;

  columns = [{ name: 'Custom', sortable: false, width: "50" }, { name: 'Field', sortable: false }, { name: 'Width', sortable: false }];
  constructor(private zone: NgZone) { }

  ngOnInit() {

    this.tmsColumsConfig = new FluentTableConfig({
      columns: [
        {
          field: "",
          title: "",
          type: "custom",
          render: () => {
            return "<i class='fa fa-arrows'></i>"
          },
          flexGrow: 0.15
        },
        {
          field: "Field",
          title: "[ISOQuest].[TaskManagementSystem].Field",
          type: "custom",
          render: () => {
            return "";
          }
        },
        {
          field: "Width",
          title: "[ISOQuest].[Common].Width",
          type: "custom",
          flexGrow: 0.4,
          render: () => {
            return "";
          }
        }
      ],
      sorting: {
        "Position": "asc"
      },
      footer: {
        show: false
      }
    });
  }

  onDragulaModelChange(model: TMSColumnItemViewModel[]): void {
    model.forEach((column, index) => {
      column.Position = index;
    });
    this.model.localSearch.ColumnConfig = model;
  }

  resetColumnsWidthToDefault(): void {
    for (var i = 0; i < this.model.localSearch.ColumnConfig.length; i++) {
      var currentColumn = this.model.localSearch.ColumnConfig[i];
      var defaultValueForColumn = this.settings.DefaultColumnsWidth[currentColumn.Field];
      if (defaultValueForColumn == null)
        throw "No default value for field:" + currentColumn.Field;

      currentColumn.Width = defaultValueForColumn;
    }
  }
}
