import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, CancelAction, DynamicService, FluentTableConfig, IsoQuestEventManagerService, LookupViewModel, TextConfirmationModalComponent } from 'codera-shared';
import { ChangeRequestTagService } from '../../system-controls/change-request-tag/change-request-tag.service';
import { ChangeRequestsWithTagGridModel } from './types';

@Component({
  selector: 'change-request-tag-info',
  templateUrl: './change-request-tag-info.component.html',
  styleUrls: ['./change-request-tag-info.component.scss']
})

export class ChangeRequestTagInfoComponent implements OnInit {

    @Input()
    tag: LookupViewModel;

    tableSource: string;
    tableConfig: FluentTableConfig;

    private modalRef: NgbModalRef;

    constructor(
        private router: Router,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private changeRequestTagService: ChangeRequestTagService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) { }

    ngOnInit() {
        const that = this;
        this.tableSource = "ChangeRequest/ChangeRequestsWithTag";
        this.tableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "action",
                    flexGrow: 1.6,
                    handler(row: ChangeRequestsWithTagGridModel) {
                        that.router.navigate(["/change-request/details", row.Id]);
                        that.modalRef.close();
                    },
                    render(row: ChangeRequestsWithTagGridModel) {
                        return `<a href="javascript:void(0);">${row.Title}</a>`;
                    }
                },
                {
                    field: "ChangeRequestId",
                    title: "[MOCQuest].[ChangeRequest].ChangeRequestId",
                    sortable: "ChangeRequestId",
                    type: "string",
                    render(row: ChangeRequestsWithTagGridModel) {
                        return row.ChangeRequestId
                    }
                },
                {
                    field: "Status",
                    title: "[MOCQuest].[ChangeRequest].Status",
                    sortable: "Status",
                    type: "custom",
                    render(row: ChangeRequestsWithTagGridModel) {
                        return that.translate.instant(row.Status.Name);
                    },
                    flexGrow: 1,
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestsWithTagGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.changeRequestTagService.unlinkChangeRequestTag(that.tag.Id, row.Id)
                                        .subscribe(() => {
                                            that.tableConfig.notifyDataChanged("item-deleted");
                                        });
                                }
                            }
                        ], false);
                    },
                    render(row: ChangeRequestsWithTagGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.5
                }
            ],
            filter: [
                { TagId: this.tag.Id }
            ],
            sorting: {
                Title: "asc"
            },
            selection: {
                mode: "none"
            },
            footer: {
                show: false
            }
        });
    }

    onInit(modalRef) {
        this.modalRef = modalRef;
    }

    onCancel(result) {
        this.modalRef.dismiss();
    }

}
