import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { DynamicService, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, ModalFormConfig, PersonnelGroupPickerViewModel } from 'codera-shared';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { AnalysisObjectType } from '../types';

@Component({
  selector: 'checklist-group-assignees',
  templateUrl: './checklist-group-assignees.component.html',
  styleUrls: ['./checklist-group-assignees.component.scss']
})
export class ChecklistGroupAssigneesComponent implements OnInit {
  @Input() changeRequestId: string;
  @Input() checklistGroupId: string;
  @Input() analysisObjectType: AnalysisObjectType;

  checklistGroupAssignees: PersonnelGroupPickerViewModel[];
  modalFormConfig: ModalFormConfig;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private changeRequestService: ChangeRequestService
  ) {
    this.checklistGroupAssignees = [];
  }

  ngOnInit() {
    this.changeRequestService
      .getChecklistGroupAssignees(this.changeRequestId, this.checklistGroupId, this.analysisObjectType)
      .toPromise()
      .then(response => {
        this.checklistGroupAssignees = response;
      });
  }

  showPersonnelDetails(personnelId: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
      { personnelId: personnelId }
    ]);
  }

  showPersonnelGroupDetails(groupId: string) {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
      { groupId: groupId }
    ]);
  }
}
