import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { ChangeRequestTagViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class ChangeRequestTagService {

    private apiUrl: string;

    constructor(
        private http: HttpClient,
        private applicationService: ApplicationService
    ) {
        this.apiUrl = this.applicationService.getApiUrl();
    }

    saveOrUpdate(model: ChangeRequestTagViewModel): Observable<string | boolean> {
        const url = `${this.apiUrl}/ChangeRequestTag/Update`;
        return this.http.post<string | boolean>(url, model);
    }

    getById(changeTagId: string): Observable<ChangeRequestTagViewModel> {
        const url = `${this.apiUrl}/ChangeRequestTag/GetById`;
        return this.http.post<ChangeRequestTagViewModel>(url, { id: changeTagId });
    }

    delete(changeTagId: string): Observable<boolean> {
        const url = `${this.apiUrl}/ChangeRequestTag/Delete`;
        return this.http.post<boolean>(url, { id: changeTagId });
    }

    unlinkChangeRequestTag(id: string, changeRequestId: string): Observable<boolean> {
        const url = `${this.apiUrl}/ChangeRequestTag/UnlinkTagChangeRequest`;
        return this.http.post<boolean>(url, { id: id, changeRequestId: changeRequestId });
    }
}
