import { Component, OnInit, Input, ViewContainerRef } from '@angular/core';
import { ApplicationService, DynamicService, IdentityLookupViewModel, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, ISOQuestModuleType, ISOQuestRoles, LookupViewModel, WorkflowPathsModel } from 'codera-shared';
import { ChecklistGroup, ChoiceOutcomeType } from '../types';
import { TranslateService } from "@ngx-translate/core";
import { SeverityLevelLevel } from '../../severity-level/types';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
@Component({
    selector: 'checklist-groups-preview',
    templateUrl: './checklist-groups-preview.component.html',
    styleUrls: ['./checklist-groups-preview.component.scss']
})
export class PreviewChecklistGroupComponent implements OnInit {
    @Input() checklistGroups: ChecklistGroup[];
    @Input() hideOutcomes: boolean;
    moduleType = ISOQuestModuleType;
    moduleRoles = ISOQuestRoles;
    objectType: string;
    paths: WorkflowPathsModel;
    choiceOutcomeType = ChoiceOutcomeType;
    settings: MOCQuestSettingsViewModel;
    constructor(
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService
    ) {
    }

    ngOnInit() {
        this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;
        this.objectType = "MOCQuest.ChecklistGroup";
        this.paths = {
            GetWorkflowDetailsSettings: "MOCQuestWorkflow/GetWorkflowDetailsSettings",
            GetWorkflowSettings: "MOCQuestWorkflow/GetWorkflowSettings",
            GetWorkflowDefinitionByTag: "Workflow/GetWorkflowDefinitionByTag",
            UpdateWorkflowSettings: "MOCQuestWorkflow/UpdateWorkflowSettings",
            ResetWorkflowSettings: "MOCQuestWorkflow/ResetWorkflowSettings",
            GetApprovalHistory: "MOCQuestWorkflow/GetApprovalHistory",
            GetApprovalHistoryDetails: "MOCQuestWorkflow/GetApprovalHistoryDetails",
            GetApprovalHistoryDetailsAll: "MOCQuestWorkflow/GetApprovalHistoryDetailsAll",
            GetWorkflowUiOptions: "Workflow/GetChecklistGroupApprovalWorkflowUiOptions",
            ExecuteTransition: "Workflow/ExecuteTransition",
            PreExecuteTransition: "Workflow/PreExecuteTransition",
            GetAvailableTransitions: "Workflow/GetAvailableTransitions",
            RequestApproval: "ChangeRequest/RequestChecklistGroupApproval",
        };
    }

    getSeverityLevelTranslation(severityLevel: IdentityLookupViewModel) {
        //because items are serialized we need to get severity level by id and than localize it
        let severityLevelName = "";
        if (severityLevel.Id == SeverityLevelLevel.First)
            severityLevelName = SeverityLevelLevel[SeverityLevelLevel.First];
        else if (severityLevel.Id == SeverityLevelLevel.Second)
            severityLevelName = SeverityLevelLevel[SeverityLevelLevel.Second];
        else if (severityLevel.Id == SeverityLevelLevel.Third)
            severityLevelName = SeverityLevelLevel[SeverityLevelLevel.Third];

        let key = `[MOCQuest].[SystemControls].SeverityLevel.Level.${severityLevelName}.Label.${this.applicationService.getCurrentSiteKey()}`;
        let defaultKey = `[MOCQuest].[SystemControls].SeverityLevel.Level.${severityLevelName}.Label.Default`;
        let severityLevelTranslation = this.translate.instant(key);
        if (severityLevelTranslation == key)
            severityLevelTranslation = this.translate.instant(defaultKey);

        return severityLevelTranslation;
    }

    viewPersonnelInfo(id: string) {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
            { personnelId: id }
        ]);
    }

    viewPersonnelGroupInfo(id: string) {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
            { groupId: id }
        ]);
    }
}
