import { IdentityLookupViewModel } from 'codera-shared';

export class MOCQuestSettingsViewModel {
    constructor(
        //General
        public ContactPhone?: string,
        public ContactEmail?: string,
        public ChangeRequestIdFormat?: string,
        public MaximumFollowUpLevel: number = 1,
        public CanManageAnalysisFacilityAdministrators?: boolean,
        public CanManageAnalysisCoordinators?: boolean,
        public EnableProjectNumber?: boolean,
        public AllowDefaultChoice?: boolean,
        public AllowChecklistApproval?: boolean,
        public AllowAdditionalImpactedFacilities?: boolean,
        public FunctionalLocationsEnabled?: boolean,
        //General

        //Notification
        public EnableEmail?: boolean,
        public SendChangeRequestNotificationsAt?: NotificationsViewModel,
        public SendSummaryForImplementationNotificationsAt?: NotificationsViewModel,
        //Notification

        public TemporaryDurationDays?: number,

        public AllowParallelApprovalType?: boolean,
        public AllowParallelCancelApprovalType?: boolean,
        public AllowSerialApprovalType?: boolean,
        public AllowVoteApprovalType?: boolean,
        public Reports?: SidebarReportLinkViewModel[]
    ) {
        this.SendChangeRequestNotificationsAt = new NotificationsViewModel();
        this.SendSummaryForImplementationNotificationsAt = new NotificationsViewModel();
        this.Reports = [];
    }
}

export class SidebarReportLinkViewModel {
    constructor(
        public Id?: string,
        public Title?: string,
        public Link?: string,
        public IsUserPermission?: boolean,
        public IsUserPermissionIdentity?:IdentityLookupViewModel
    ) {
    }
}

export class WorkflowStageGridModel {
    constructor(
        public StageName?: string,
        public LabelId?: string
    ) { }
}

export enum WorkflowStages {
    Requested = 1,
    InitialApproval,
    Analysis,
    ManagementApproval,
    Implementation,
    Approval,
    Launch,
    FollowUp,
    Closure,
    Terminate
}

export class NotificationsViewModel {
    constructor(
        public Type?: IdentityLookupViewModel,
        public Day?: IdentityLookupViewModel,
        public Time?: IdentityLookupViewModel,
        public DayOfMonth?: number
    ) {
        this.Day = { Id: 0, Name: "Sunday", Status: true };
    }
}

export class CanDisableApprovalTypeResultModel {
    constructor(
        public ObjectTypeName?: string,
        public Objects?: ObjectWithDynamicApprovalTypeResultModel[]
    ) { }
}

export class ObjectWithDynamicApprovalTypeResultModel {
    constructor(
        public Title?: string,
        public Url?: string
    ) { }
}
