import { Component, HostListener, Input, OnInit, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, ModalFormComponent, ModalFormConfig, RowControl, StaticInfoComponent } from 'codera-shared';
import { ChangeRequestTypeService } from '../change-request-type.service';
import { ChangeRequestTypeViewModel } from '../types';

@Component({
    selector: 'change-request-type-info',
    templateUrl: './change-request-type-info.component.html'
})
export class ChangeRequestTypeInfoComponent implements OnInit {
    @Input() id: string;

    private modalConfig: ModalFormConfig;
    model: ChangeRequestTypeViewModel;

    @ViewChild('statusTemplate') public statusTemplate: TemplateRef<any>;

    constructor(
        private changeRequestTypeService: ChangeRequestTypeService,
        private viewContainer: ViewContainerRef,
        private dynamicService: DynamicService
    ) { }

    ngOnInit() {
        this.modalConfig = new ModalFormConfig("[MOCQuest].[SiteMap].SystemControls.ChangeRequestType.Single", [
            new RowControl([
                new ComponentControl({
                    componentClass: StaticInfoComponent,
                    field: "Title",
                    css: "col-sm-6",
                    data: [
                        { label: "[ISOQuest].[Common].Title" }
                    ]
                }),
                new ComponentControl({
                    componentClass: StaticInfoComponent,
                    field: "Status",
                    css: "col-sm-6",
                    data: [
                        { label: "[ISOQuest].[Common].Status" },
                        { template: this.statusTemplate }
                    ]
                })
            ])
        ], [
            new CancelAction({
                field: "Cancel",
                value: "[ISOQuest].[Common].Buttons.OK",
                callback() { },
                css: "btn btn-primary"
            })
        ]);
    }

    @HostListener("click")
    onClick() {
        this.changeRequestTypeService.getById(this.id).subscribe(model => {
            this.model = model;

            this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
                { "config": this.modalConfig },
                { "model": model },
                { "modalOptions": { size: "sm" } }
            ], true);
        });
    }
}
