import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { AttachmentsService, AttachmentViewModel, DynamicService, IqPersonnelInfoComponent, TextConfirmationModalComponent } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { FollowUpViewModel } from '../types';
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: 'follow-up-info',
  templateUrl: './follow-up-info.component.html'
})
export class FollowUpInfoComponent implements OnInit {

  @Input() model: FollowUpViewModel;
  @Output() attachmentDeleted: EventEmitter<void>;
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private attachmentService: AttachmentsService
  ) {
    this.attachmentDeleted = new EventEmitter<void>();
  }

  ngOnInit() {
  }

  internalPersonnelInfo(id: string): void {
    this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
      { personnelId: id },
      { modalOptions: { size: "md" } }
    ], false);
  }

  deleteAttachment(followUpAttachment: AttachmentViewModel): void {
    const that = this;

    this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
      { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
      { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
      { "modalOptions": { size: "sm" } },
      {
        "successCallback": () => {
          that.attachmentService.delete(followUpAttachment.Id, "ChangeRequest/DeleteFollowUpAttachment").subscribe(() => {
            that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemDeleted"));
            that.attachmentDeleted.emit();
          });
        }
      }
    ], true);
  }

}
