import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, DynamicService, FluentTableConfig } from 'codera-shared';
import { TMSTaskInfoComponent } from '../task/info/tms-task-info.component';
import { TmsTaskDocumentGridModel } from '../types';

@Component({
  selector: 'tms-task-documents',
  templateUrl: './tms-task-documents.component.html',
  styleUrls: ['./tms-task-documents.component.scss']
})
export class TmsTaskDocumentsComponent implements OnInit {
  @Input() objectId: string;
  @Input() objectType: string;

  fluentTableConfig: FluentTableConfig;
  tableSource: string;
  constructor(
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService
  ) { }

  ngOnInit() {
    const that = this;

    this.tableSource = `TaskManagementSystem/TaskDocumentGrid?objectType=${this.objectType}&objectId=${this.objectId}`;
    this.fluentTableConfig = new FluentTableConfig({
      columns: [
        {
          field: "Task",
          sortable: "Task",
          title: "[MOCQuest].[Common].TaskTitle",
          type: "action",
          render: (row: TmsTaskDocumentGridModel) => {
            return `<a href='javascript:void(0);'>${row.Task.Name}</a>`;
          },
          handler: (row: TmsTaskDocumentGridModel) => {
            that.dynamicService.addComponent(that.viewContainer, TMSTaskInfoComponent, [
              { id: row.Task.Id }
            ]);
          }
        },
        {
          field: "ReferenceRevisionNumber",
          sortable: "ReferenceRevisionNumber",
          title: "[ISOQuest].[Common].Document.ReferenceNumber",
          type: "custom",
          render: (row: TmsTaskDocumentGridModel) => {
            let extension = "";
            if (row.Extension && row.Extension.indexOf(".") > -1) {
              extension = row.Extension.slice(1).toLowerCase();
            }

            return `<span class="file-icon file-icon-sm file-icon-${extension}"><i>${extension}</i></span>&nbsp;` +
              `<a target="_blank" href="/docuquest/sites/${that.applicationService.getCurrentSiteKey()}/documents/details/${row.Document.Id}">${row.ReferenceNumber} / ${row.RevisionNumber}</a>&nbsp;` +
              `<a target="_blank" href="/docuquest/api/sites/${that.applicationService.getCurrentSiteKey()}/download/document/${row.Document.Id}" style=""><i class="fa fa-download"></i></a>`;
          }
        },
        {
          field: "Document",
          sortable: "DocumentTitle",
          title: "[MOCQuest].[Common].DocumentTitle",
          type: "custom",
          render: (row: TmsTaskDocumentGridModel) => {
            return row.Document.Name;
          }
        },
        {
          field: "DocumentFacility",
          sortable: "DocumentFacility",
          title: "[MOCQuest].[Common].DocumentFacility",
          type: "custom",
          render: (row: TmsTaskDocumentGridModel) => {
            return row.DocumentFacility.Name;
          },
          show: that.applicationService.getCurrentSite().IsMultiSite
        },
        {
          field: "DocumentDepartment",
          sortable: "DocumentDepartment",
          title: "[MOCQuest].[Common].DocumentDepartment",
          type: "custom",
          render: (row: TmsTaskDocumentGridModel) => {
            return row.DocumentDepartment.Name;
          }
        },
        {
          field: "DocumentType",
          sortable: "DocumentType",
          title: "[ISOQuest].[Common].Document.DocumentType",
          type: "custom",
          render: (row: TmsTaskDocumentGridModel) => {
            return row.DocumentType.Name;
          }
        },
        {
          field: "IssuedDate",
          sortable: "DocumentIssuedDate",
          title: "[ISOQuest].[Common].Document.IssuedDate",
          type: "date"
        }
      ],
      sorting: {
        Task: "asc"
      },
      footer: {
        show: true,
        showExports: false,
        showPageSize: true,
        showPaging: true,
        showRefreshButton: true
      },
      grouping: {
        field: "Task",
        render: (row) => {
          if (row.value[0] && row.value[0].Task && row.value[0].Task.Name) {
            return row.value[0].Task.Name;
          } else {
            return that.translate.instant("[ISOQuest].[Common].NotAvailable");
          }
        },
        property: "Id"
      }
    })
  }

}
