import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { Subject } from 'rxjs';
import { shareReplay, startWith, switchMap, map } from 'rxjs/operators';
import { ChecklistGroupService } from '../checklist-group.service';
import { ChecklistGroup } from '../types';

@Injectable({
  providedIn: 'root'
})
export class ManageChecklistGroupService {
  private fetchChecklistGroupSubject = new Subject<string>();
  cancelSubject = new Subject<void>();
  saveSubject = new Subject<ChecklistGroup>();
  copySubject = new Subject<void>();

  checklistGroup$ = this.fetchChecklistGroupSubject.pipe(
    switchMap((id) => {
      return this.checklistGroupService.getById(id);
    }),
    shareReplay(1),
    startWith(new ChecklistGroup(null, '', [], true))
  )


  constructor(private checklistGroupService: ChecklistGroupService) { }


  fetchChecklistGroup(id: string) {
    this.fetchChecklistGroupSubject.next(id);
  }
}
