import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, IdentityLookupViewModel } from 'codera-shared';
import { SeverityLevelLevel } from '../../types';
@Component({
  selector: 'severity-level-level-picker',
  templateUrl: './severity-level-level-picker.component.html'
})
export class SeverityLevelLevelPickerComponent implements OnInit {
  @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
  @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() enableItemCreation: boolean;

  staticData: IdentityLookupViewModel[];
  controllerName: string;

  constructor(
    private translate: TranslateService,
    private applicationService: ApplicationService
  ) {
    this.modelChange = new EventEmitter();
  }

  ngOnInit() {
    if (!this.label)
      this.label = "[MOCQuest].[SystemControls].SeverityLevel";

    this.translate.get("[ISOQuest].[Common].Title").subscribe(() => {
      this.staticData = Object.keys(SeverityLevelLevel).filter((item) => {
        return isNaN(Number(item));
      }).map(level => {
        let translatedLabel = "-";
        let requestTranslation = `[MOCQuest].[SystemControls].SeverityLevel.Level.${level}.Label.${this.applicationService.getCurrentSiteKey()}`;

        let translation = this.translate.instant(requestTranslation);
        if (requestTranslation === translation) {
          translation = this.translate.instant(`[MOCQuest].[SystemControls].SeverityLevel.Level.${level}.Label.Default`);
        }

        translatedLabel = translation;
        return new IdentityLookupViewModel(
          SeverityLevelLevel[level],
          translatedLabel,
          true);
      }).sort((left, right) => Number(left.Id) - Number(right.Id));
    });
  }

  onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
