import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { ApplicationService, CancelAction, ComponentControl, CustomAction, DynamicService, FrequencySchedulePickerComponent, InputControl, ModalFormComponent, ModalFormConfig, RowControl, StaticInfoComponent } from 'codera-shared';
import { TemplateControlComponent } from 'src/app/template/template.component';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { TMSTaskStatus, TMSTaskViewModel } from '../../types';
import { CreateUpdateTmsTaskComponent } from '../create-update/create-update-tms-task.component';

@Component({
  selector: 'tms-task-info',
  templateUrl: './tms-task-info.component.html'
})
export class TMSTaskInfoComponent implements OnInit {
  @Input()
  id: string;
  modalFormConfig: ModalFormConfig;
  model: TMSTaskViewModel;
  component: ComponentRef<ModalFormComponent>;
  statusConfig: any;
  @Output()
  onUpdateTask: EventEmitter<void>;
  @ViewChild("statusTemplate") statusTemplate: TemplateRef<any>;
  @ViewChild("lookupTemplate") lookupTemplate: TemplateRef<any>;
  @ViewChild("taskPriorityTemplate") taskPriorityTemplate: TemplateRef<any>;
  @ViewChild("colorTemplate") colorTemplate: TemplateRef<any>;
  @ViewChild("personnelTemplate") personnelTemplate: TemplateRef<any>;
  @ViewChild("dateTemplate") dateTemplate: TemplateRef<any>;
  @ViewChild("percentTemplate") percentTemplate: TemplateRef<any>;
  @ViewChild("htmlTemplate") htmlTemplate: TemplateRef<any>;
  @ViewChild("attachmentsTemplate") attachmentsTemplate: TemplateRef<any>;
  @ViewChild("recurringTaskTemplate") recurringTaskTemplate: TemplateRef<any>;
  @ViewChild("scheduleTemplate") scheduleTemplate: TemplateRef<any>;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private taskManagementSystem: TaskManagementSystemService,
    private applicationService: ApplicationService
  ) {
    this.onUpdateTask = new EventEmitter<void>();
  }

  ngOnInit() {
    if (!this.id)
      throw new Error("Id is required");

    const that = this;

    this.statusConfig = {};
    this.statusConfig[TMSTaskStatus.NotStarted] = { icon: "fa fa-circle-o-notch", color: "#9e9e9e;" };
    this.statusConfig[TMSTaskStatus.InProgress] = { icon: "fa fa-spinner", color: "#337ab7;" };
    this.statusConfig[TMSTaskStatus.Completed] = { icon: "fa fa-check-circle", color: "#26c281;" };
    this.statusConfig[TMSTaskStatus.Archived] = { icon: "fa fa-archive", color: "#f1c40f;" };
    this.statusConfig[TMSTaskStatus.NotCompleted] = { icon: "fa fa-circle-o-notch", color: "#295170;" };

    this.taskManagementSystem.getTMSTaskAttachments(this.id).subscribe(attachments => {
      this.taskManagementSystem.getTMSTaskById(this.id).subscribe(task => {
        this.model = task;
        this.model.Attachments = attachments;
        this.summonDialog();
      });
    });
  }

  summonDialog(): void {
    const that = this;

    this.modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].Task.Details",
      [
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Title",
            data: [
              { label: "[ISOQuest].[Common].Title" }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Status",
            data: [
              { label: "[ISOQuest].[Common].Status" },
              { template: this.statusTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Tag",
            data: [
              { label: "[ISOQuest].[TaskManagementSystem].Task.Topic" },
              { template: this.lookupTemplate }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "TaskCategory",
            data: [
              { label: "[ISOQuest].[Common].TMSTaskCategory" },
              { template: this.lookupTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "TaskPriority",
            data: [
              { label: "[ISOQuest].[Common].TaskPriority" },
              { template: this.taskPriorityTemplate }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Color",
            data: [
              { label: "[ISOQuest].[Common].Color" },
              { template: this.colorTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Owner",
            data: [
              { label: "[ISOQuest].[Common].Owner" },
              { template: this.personnelTemplate }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "AssignedTo",
            data: [
              { label: "[ISOQuest].[Common].Assignee" },
              { template: this.personnelTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Facility",
            data: [
              { label: "[ISOQuest].[Common].Facility" },
              { template: this.lookupTemplate }
            ]
          })
        ], that.applicationService.getCurrentSite().IsMultiSite),
        new RowControl([
          new ComponentControl({
            componentClass: TemplateControlComponent,
            field: "RecurringTaskTemplate",
            data: [
              { template: that.recurringTaskTemplate }
            ]
          })
        ]),
        new RowControl([
          new InputControl({
            field: "MarkAsRecurringTask",
            type: "checkbox",
            label: "[ISOQuest].[BryntumGantt].MarkAsRecurringTask",
            disabled: true
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: FrequencySchedulePickerComponent,
            field: "FrequencySchedule",
            data: [
              { label: "[ISOQuest].[FrequencySchedule].Label.Frequency" },
              { isDisabled: true },
            ],
            validators: [FrequencySchedulePickerComponent.validator]
          })
        ], that.model.MarkAsRecurringTask),
        new RowControl([
          new ComponentControl({
            componentClass: TemplateControlComponent,
            field: "ScheduleTemplate",
            data: [
              { template: that.scheduleTemplate }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "StartDate",
            data: [
              { label: '[ISOQuest].[Common].StartDate' },
              { template: this.dateTemplate }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "EndDate",
            data: [
              { label: '[ISOQuest].[Common].DueDate' },
              { template: this.dateTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "ActualStartDate",
            data: [
              { label: '[ISOQuest].[Common].ActualStartDate' },
              { template: this.dateTemplate }
            ],
            css: "col"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "CompletionDate",
            data: [
              { label: '[ISOQuest].[Common].CompletionDate' },
              { template: this.dateTemplate }
            ],
            css: "col"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Duration",
            data: [
              { label: '[ISOQuest].[Common].Duration' },
            ],
            css: "col-6 offset-6"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "ComingDueDays",
            data: [
              { label: '[ISOQuest].[Common].ComingDueDays' },
            ],
            css: "col-6"
          }),
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "PercentDone",
            data: [
              { label: '[ISOQuest].[TaskManagementSystem].Columns.PercentDone' },
              { template: this.percentTemplate }
            ],
            css: "col-6"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Notes",
            data: [
              { label: "[ISOQuest].[Common].Notes" },
              { template: this.htmlTemplate }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: StaticInfoComponent,
            field: "Attachments",
            data: [
              { label: "[ISOQuest].[Common].Attachments" },
              { template: this.attachmentsTemplate }
            ]
          })
        ]),
      ],
      [
        new CustomAction({
          actionType: "button",
          callback: () => {
            that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
            that.dynamicService.addComponent(that.viewContainer, CreateUpdateTmsTaskComponent, [
              { objectId: that.model.ObjectId },
              { objectType: that.model.ObjectType },
              { id: that.model.Id },
              {
                onUpdateTask: () => {
                  that.onUpdateTask.emit();
                }
              }
            ]);
          },
          field: "Edit",
          value: "[ISOQuest].[Common].Buttons.Edit",
          css: "btn btn-primary",
          show: that.model.Permissions.CanEdit
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]);

    this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { config: this.modalFormConfig },
      { model: this.model },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]) as ComponentRef<ModalFormComponent>;
  }

}
