import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'duration-type-picker',
  templateUrl: './duration-type-picker.component.html',
  styleUrls: ['./duration-type-picker.component.scss']
})
export class DurationTypePickerComponent implements OnInit {
    @Input()
    model: any;
    @Output()
    modelChange: EventEmitter<any>;

    @Input()
    label: string;
    @Input()
    isDisabled: boolean;

    staticData: IdentityLookupViewModel[];

    constructor(private translate: TranslateService) {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        const that = this;
        this.staticData = [];

        this.staticData.push({
            Id: 1,
            Name: this.translate.instant("[MOCQuest].[ChangeRequest].DurationType.Permanent"),
            Status: true
        });
        this.staticData.push({
            Id: 2,
            Name: this.translate.instant("[MOCQuest].[ChangeRequest].DurationType.Temporary"),
            Status: true
        });
    }

    onSelect(newValue: any) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }

}
