import { Injectable } from "@angular/core";
import { CancelAction, InputControl, LookupViewModel, ModalFormConfig, RowControl, SubmitAction } from "codera-shared";
import { ChangeRequestTagService } from './change-request-tag.service';
import { ChangeRequestTagViewModel } from './types';

@Injectable({ providedIn: "root" })
export class ChangeRequestTagFactory {

    constructor(
        private changeRequestTagService: ChangeRequestTagService
    ) { }

    getModalConfig(model: ChangeRequestTagViewModel, onSave?: (result: LookupViewModel) => void): ModalFormConfig {
        const that = this;

        return new ModalFormConfig(
            '[MOCQuest].[Common].Buttons.ChangeRequestTag.New',
            [
                new RowControl([
                    new InputControl({
                        type: 'text',
                        field: 'Title',
                        label: '[ISOQuest].[Common].Title',
                        required: true,
                        css: 'col-md-12',
                        min: 0,
                        max: 255
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: 'Submit',
                    value: '[ISOQuest].[Common].Buttons.Submit',
                    callback() {
                        that.changeRequestTagService.saveOrUpdate(model).subscribe((changeRequestTagId) => {
                            if (onSave) {
                                onSave(new LookupViewModel(changeRequestTagId as string, model.Title, true));
                            }
                        });
                    },
                    css: 'btn btn-primary'
                }),
                new CancelAction({
                    field: 'Cancel',
                    value: '[ISOQuest].[Common].Buttons.Cancel',
                    callback() { },
                    css: 'btn btn-default'
                })
            ]
        );
    }
}
