import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, CancelAction, ComponentControl, DynamicService, IqPersonnelPickerComponent, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';

@Component({
  selector: 'update-tms-task-owner',
  template: ''
})
export class UpdateTmsTaskOwnerComponent implements OnInit {
  @Input()
  value: any;

  @Output()
  onSubmit: EventEmitter<string>;

  model: any;
  modalFormConfig: ModalFormConfig;
  constructor(
    private dynamicService: DynamicService,
    private viewContainerRef: ViewContainerRef,
    private applicationService: ApplicationService
  ) {
    this.model = {
      Owner: null
    };
    this.onSubmit = new EventEmitter<string>();
  }

  ngOnInit() {
    const that = this;
    if (this.value)
      this.model.Owner = this.value;

    this.modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].Owner.Edit",
      [
        new RowControl([
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "Owner",
            data: [
              { label: "[ISOQuest].[Common].Owner" },
              { targetModule: that.applicationService.getCurrentModuleKey() },
              { type: "Personnel" },
              { onlyActive: true }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            if (that.model && that.model.Owner && that.model.Owner.Personel)
              that.onSubmit.emit(that.model.Owner.Personel.Id);
            else
              that.onSubmit.emit(null);

            that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainerRef, ModalFormComponent, [
      { model: that.model },
      { config: that.modalFormConfig },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }
}
