import { IdentityLookupViewModel, LookupViewModel, PersonnelGroupPickerViewModel } from 'codera-shared';

export class WorkflowMatrixViewModel {
    constructor(
        public Id?: string,
        public Facility?: LookupViewModel,
        public ChangeRequestType?: LookupViewModel,
        public Status?: boolean,
        public SeverityLevel?: LookupViewModel,
        public AllowImplementationAndPssrToBeConductedAtSameTime?: boolean,
        public WorkflowStageMatrix?: WorkflowStage[],
        public ChangeCoordinators?: LookupViewModel[],
    ) {
        this.Status = true;
        this.WorkflowStageMatrix = [];
        this.ChangeCoordinators = [];
    }
}

export class WorkflowMatrixGridModel {
    constructor(
        public Id?: string,
        public Facility?: LookupViewModel,
        public ChangeRequestType?: LookupViewModel,
        public Status?: boolean
    ) { }
}

export class WorkflowStage {
    constructor(
        public Id?: string,
        public StageId?: number,
        public Name?: string,
        public Enabled?: boolean,
        public First?: boolean,
        public Second?: boolean,
        public Third?: boolean,
        public Required?: boolean,
        public ApprovalTypeFirstLevel?: IdentityLookupViewModel,
        public DaysUntilDueDateFirstLevel?: number,
        public MinNumbersOfApproversFirstLevel?: number,
        public ApprovalTypeSecondLevel?: IdentityLookupViewModel,
        public DaysUntilDueDateSecondLevel?: number,
        public MinNumbersOfApproversSecondLevel?: number,
        public ApprovalTypeThirdLevel?: IdentityLookupViewModel,
        public DaysUntilDueDateThirdLevel?: number,
        public MinNumbersOfApproversThirdLevel?: number,
        public ApproversFirstLevel?: PersonnelGroupPickerViewModel[],
        public ApproversSecondLevel?: PersonnelGroupPickerViewModel[],
        public ApproversThirdLevel?: PersonnelGroupPickerViewModel[],
        public ChecklistGroupsFirstLevel?: LookupViewModel[],
        public ChecklistGroupsSecondLevel?: LookupViewModel[],
        public ChecklistGroupsThirdLevel?: LookupViewModel[]
    ) {
        this.ApproversFirstLevel = [];
        this.ApproversSecondLevel = [];
        this.ApproversThirdLevel = [];
        this.ChecklistGroupsFirstLevel = [];
        this.ChecklistGroupsSecondLevel = [];
        this.ChecklistGroupsThirdLevel = [];
    }
}

export class CopyWorkflowMatrixViewModel {
    constructor(
        public Id?: string,
        public OriginalWorkflowMatrixFacility?: LookupViewModel,
        public Facilities?: LookupViewModel[],
        public AllFacilities?: boolean,
        public ChangeRequestType?: LookupViewModel,
        public Status?: boolean,
        public SeverityLevel?: LookupViewModel,
        public AllowImplementationAndPssrToBeConductedAtSameTime?: boolean,
        public WorkflowStageMatrix?: WorkflowStage[],
        public ChangeCoordinators?: LookupViewModel[]
    ) {
        this.Status = true;
        this.AllFacilities = false;
        this.WorkflowStageMatrix = [];
        this.ChangeCoordinators = [];
        this.Facilities = [];
    }
}

