import { Component, Input, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService } from 'codera-shared';
import * as powerbi from "powerbi-client";
import { DomSanitizer } from '@angular/platform-browser';
@Component({
	selector: 'app-dynamic-reports',
	templateUrl: './dynamic-reports.component.html',
	styles: []
})
export class DynamicReportsComponent implements OnInit {
	pageTitle: string;
	workspaceId: string;
	reportId: string;
	isDashboard: boolean;
	section: string;
	isUserPermission: boolean;

	embedSrcUrl: any = null;

	private apiUrl: string;
	constructor(
		private http: HttpClient,
		private applicationService: ApplicationService,
		private activatedRoute: ActivatedRoute,
		private translate: TranslateService,
		private dom: DomSanitizer
	) {
		this.apiUrl = this.applicationService.getApiUrl();
	}

	ngOnInit() {
		const that = this;
		this.activatedRoute.data.subscribe(data => {
			this.pageTitle = data.pageTitle;
			this.workspaceId = data.workspaceId;
			this.reportId = data.reportId;
			this.isDashboard = data.isDashboard;
			this.section = data.section;
			this.isUserPermission = data.isUserPermission;
		})
		var office365Enabled = this.applicationService.getApplicationSettings().Office365Auth;
		if (!office365Enabled)
			this.isUserPermission = false; //everything set to public if office 365 is disabled

		const url = `${this.apiUrl}/PowerBi/GetConfig`;

		var reportContainer = <HTMLElement>document.getElementById('powerBiContainer');
		var models = powerbi.models;
		var model = { WorkspaceId: this.workspaceId, ReportId: this.reportId, IsDashboard: this.isDashboard, IsUserPermission: this.isUserPermission };

		this.http.post<any>(url, { model: model }).subscribe(
			(response) => {

				if (!model.IsDashboard && this.isUserPermission) {

					var urlConfig = {
						"clusterUrl": "https://WABI-US-EAST2-redirect.analysis.windows.net",
						"embedFeatures": { "modernEmbed": true, "certifiedTelemetryEmbed": true, "usageMetricsVNext": true }

					};
					var url64Config = "";
					if (response.AccessToken != null)
						url64Config = `&config=${btoa(JSON.stringify(urlConfig))}&token=${response.AccessToken}`;
					else url64Config = `&config=${btoa(JSON.stringify(urlConfig))}`;

					this.embedSrcUrl = this.dom.bypassSecurityTrustResourceUrl(`https://app.powerbi.com/reportEmbed?reportId=${model.ReportId}&pageName=${this.section}&autoAuth=true&ctid=${response.TenantId}&` + url64Config);
				}

				if (this.isDashboard || (!this.isDashboard && !this.isUserPermission)) {
					var config = {
						type: this.isDashboard ? 'dashboard' : 'report',
						tokenType: model.IsUserPermission && this.isDashboard ? models.TokenType.Aad : !model.IsUserPermission && this.isDashboard ? models.TokenType.Embed : models.TokenType.Embed, //embed for reports
						accessToken: model.IsUserPermission && this.isDashboard ? response.AccessToken : !model.IsUserPermission && this.isDashboard ? response.EmbedToken.token : response.EmbedToken.token, //embed for reports
						embedUrl: response.EmbedUrl,
						pageName: this.section,
						id: response.Id,
						permissions: models.Permissions.All,
						settings: {
							//Enable this setting to remove gray shoulders from embedded report
							background: models.BackgroundType.Transparent,
							filterPaneEnabled: true,
							navContentPaneEnabled: true
						}
					};

					window.powerbi.embed(reportContainer, config);
				}

			});
	}
}
