import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Category } from './category';
import { CategoryModel } from './change-request/details/review-approval-tabs/recommended-action/types';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

    constructor(
        private http: HttpClient
    ) {}

    createOrUpdate(model: Category): Observable<string | boolean>{
        model.Status = true;
        if(model.Id){
            return this.http.post<string | boolean>("Category/Update", model);
        }else{
            return this.http.post<string | boolean>("Category/Create", model);
        }
    }
    getCategory(id: string): Observable<Category>{
        return this.http.get<Category>("Category/Get/" + id);
    }

    getCategories(): Observable<Category[]> {
        return this.http
        .get<Category[]>("Category/GetAll")
      }

    delete(id: string){
        return this.http.post("Category/DeletePOST", {id: id});
    }
}
