import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, IdentityLookupViewModel, InputControl, IqNumberComponent, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { ColorPickerComponent } from 'ngx-color-picker';
import { TmsTaskPriorityColorComponent } from '../tms-task-priority-color/tms-task-priority-color.component';
import { TmsTaskPriorityIconPickerComponent } from '../tms-task-priority-icon-picker/tms-task-priority-icon-picker.component';
import { TmsTaskPriorityPreviewIconComponent } from '../tms-task-priority-preview-icon/tms-task-priority-preview-icon.component';
import { TmsTaskPriorityService } from '../tms-task-priority.service';
import { TMSTaskPriorityViewModel } from '../types';

@Component({
  selector: 'app-create-update-tms-task-priority',
  templateUrl: './create-update-tms-task-priority.component.html',
  styleUrls: ['./create-update-tms-task-priority.component.scss']
})
export class CreateUpdateTmsTaskPriorityComponent implements OnInit {
  @Input()
  id: string;
  @Input()
  objectType: string;
  @Input()
  objectId: string;

  @Output()
  onCreateUpdate: EventEmitter<LookupViewModel>;

  modalFormConfig: ModalFormConfig;
  model: TMSTaskPriorityViewModel;
  private componentRef: ComponentRef<ModalFormComponent>;

  constructor(
    private tmsTaskPriorityService: TmsTaskPriorityService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef
  ) {
    this.onCreateUpdate = new EventEmitter<LookupViewModel>();
    this.model = new TMSTaskPriorityViewModel();
    this.model.ObjectId = this.objectId;
    this.model.ObjectType = this.objectType;
  }

  ngOnInit() {
    if (!this.objectType)
      throw new Error("objectType is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    let title = this.id
      ? "[ISOQuest].[Common].Buttons.EditTaskPriority"
      : "[ISOQuest].[Common].Buttons.NewTaskPriority";

    this.prepareModalFormConfig(title);
    this.componentRef = this.summonDialog();

    if (this.id)
      this.getById();
  }

  prepareModalFormConfig(title: string) {
    const that = this;

    this.modalFormConfig = new ModalFormConfig(
      title,
      [
        new RowControl([
          new InputControl({
            type: "text",
            field: "Title",
            label: "[ISOQuest].[Common].Title",
            required: true,
            css: "col-10",
            min: 0,
            max: 250
          }),
          new ComponentControl({
            componentClass: IqNumberComponent,
            field: "Rank",
            label: "[ISOQuest].[Common].Rank",
            required: true,
            validators: [
              IqNumberComponent.validator.min(0)
            ],
            data: [
              { isReqired: true }
            ],
            css: "col-2"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskPriorityColorComponent,
            field: "Color",
            label: "[ISOQuest].[Common].Color",
            css: "col-7",
            data: [
              {
                modelChange: (color: IdentityLookupViewModel) => {
                  let previewIconComponentControl = that.modalFormConfig.getControlByField("PreviewIcon") as ComponentControl;
                  previewIconComponentControl.getDataByKey("color").color = color;
                }
              }
            ]
          }),
          new ComponentControl({
            componentClass: TmsTaskPriorityIconPickerComponent,
            field: "Icon",
            label: "[ISOQuest].[Common].Icon",
            css: "col-3",
            required: true,
            data: [
              { isRequired: true },
              {
                modelChange: (icon: IdentityLookupViewModel) => {
                  let previewIconComponentControl = that.modalFormConfig.getControlByField("PreviewIcon") as ComponentControl;
                  previewIconComponentControl.getDataByKey("icon").icon = icon;
                }
              }
            ]
          }),
          new ComponentControl({
            componentClass: TmsTaskPriorityPreviewIconComponent,
            field: "PreviewIcon",
            label: "[ISOQuest].[Common].Preview",
            css: "col-2",
            data: [
              { icon: that.model.Icon },
              { color: that.model.Color }
            ]
          })
        ]),
        new RowControl([
          new InputControl({
            type: "checkbox",
            field: "Status",
            label: "[ISOQuest].[Common].Choices.Active",
            css: "col-md-12"
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            that.tmsTaskPriorityService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
              if (typeof response === "string")
                that.onCreateUpdate.emit(new LookupViewModel(response, that.model.Title, that.model.Status));
              else if (typeof response === "boolean")
                that.onCreateUpdate.emit(new LookupViewModel(that.model.Id, that.model.Title, that.model.Status));
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );
  }

  summonDialog(): ComponentRef<ModalFormComponent> {
    this.model = new TMSTaskPriorityViewModel();
    this.model.ObjectId = this.objectId;
    this.model.ObjectType = this.objectType;
    this.model.Rank = 0;
    this.model.Color = new IdentityLookupViewModel(0, "#000000", false);

    let component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
      [
        { config: this.modalFormConfig },
        { model: this.model },
        {
          modalOptions: {
            size: "sm"
          }
        }
      ]
    );
    return component as ComponentRef<ModalFormComponent>;
  }

  getById() {
    this.tmsTaskPriorityService.getById(this.id).subscribe((response: TMSTaskPriorityViewModel) => {
      let previewIconComponentControl = this.modalFormConfig.getControlByField("PreviewIcon") as ComponentControl;
      let iconComponentControl = this.modalFormConfig.getControlByField("Icon") as ComponentControl;
      this.model = response;
      this.componentRef.instance.model = response;
      previewIconComponentControl.getDataByKey("icon").icon = response.Icon;
      previewIconComponentControl.getDataByKey("color").color = response.Color;
    });
  }
}
