import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { LookupViewModel } from 'codera-shared';

@Component({
  selector: 'tms-task-color-picker',
  templateUrl: './tms-task-color-picker.component.html'
})
export class TmsTaskColorPickerComponent implements OnInit {
  @Input() model: any;
  @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() enableItemCreation: boolean;
  @Input() excludeUsedItems: boolean;
  @Input() objectType: string;
  @Input() objectId: string;

  controllerName: string;
  hasPermission: boolean;
  showNewItemButton: boolean;

  constructor(
  ) {
    this.modelChange = new EventEmitter();
  }

  ngOnInit() {
    this.controllerName = `/TMSColor/GetAvailableColors?objectType=${this.objectType}&objectId=${this.objectId}`;

    if (!this.label)
      this.label = "[ISOQuest].[TaskManagementSystem].Task.TaskColor";
  }

  onChange(newValue: LookupViewModel | LookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
