import { Component, Input, OnInit } from '@angular/core';
import { WorkflowPathsModel } from 'codera-shared';
import { AnalysisConfigModel } from 'src/app/change-request/types';
import { ChecklistGroup } from '../../../../system-controls/checklist-groups/types';
import { ChangeRequestService } from '../../../change-request.service';
import { AnalysisService } from '../analysis.service';

@Component({
    selector: 'view-mode-analysis',
    templateUrl: './view-mode-analysis.component.html',
    styleUrls: ['./view-mode-analysis.component.scss']
})
export class ViewModeAnalysisComponent implements OnInit {
    @Input() groups: ChecklistGroup[];
    @Input() analysisConfig: AnalysisConfigModel;
    @Input() workflowObjectType: string;
    @Input() workflowPaths: WorkflowPathsModel;

    constructor(
        private analysisService: AnalysisService,
        private changeRequestService: ChangeRequestService,
    ) { }

    ngOnInit() { }

    exportAnalysis() {
        this.changeRequestService.exportChangeRequestAnalysisXlsx(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);
    }
}
