import { Input } from "@angular/core";
import {
    LookupViewModel,
    PersonnelGroupPickerViewModel,
    IdentityLookupViewModel,
    AttachmentViewModel,
} from "codera-shared";

export class CategoryModel {
    @Input()
    Id: string;
    @Input()
    Title: string;
    @Input()
    Status: IdentityLookupViewModel;
    constructor() {}
}
export class CategoryViewModel{
    public Id?: string;
        public Title?: string;
        public Status?: IdentityLookupViewModel;
    constructor(
        Id?: string,
        Title?: string,
        Status?: IdentityLookupViewModel
    ){
        this.Id = Id;
        this.Title = Title;
        this.Status = Status;
    }
}
export class RecommendedActionViewModel {
    constructor(
        public Id?: string,
        public Title?: string,
        public Status?: IdentityLookupViewModel,
        public AssignedTo?: PersonnelGroupPickerViewModel,
        public Description?: string,
        public DueDate?: Date,
        public CompletionDate?: Date,
        public RequiredBeforeLaunch?: boolean,
        public Attachments?: AttachmentViewModel[]
    ) {}
}

export class RecommendedActionGridModel extends RecommendedActionViewModel {
    constructor(
        public ModifiedBy?: LookupViewModel,
        public ModifiedAt?: Date,
        public CanEdit?: boolean,
        public HasAttachments?: boolean
    ) {
        super();
    }
}

export class ChangeRequestRecommendedActionGridModel extends RecommendedActionGridModel {
    constructor(
        public ChangeRequest?: LookupViewModel,
        public ChangeRequestFacility?: LookupViewModel,
        public ChangeRequestStatus?: IdentityLookupViewModel,
        public CanDelete?: boolean
    ) {
        super();
    }
}

export class BulkChangeStatusIdentityLookupViewModel {
    constructor(
        public SelectedIds?: string[],
        public Status?: IdentityLookupViewModel
    ) {}
}

export class RecommendedActionsCountViewModel {
    constructor(public All: number, public Completed: number) {}
}

export enum Status {
    NotStarted = 0,
    InProgress = 1,
    Completed = 2,
}
