import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';

@Component({
  selector: 'tms-task-color-color',
  templateUrl: './tms-task-color-color.component.html',
  styleUrls: ['./tms-task-color-color.component.scss']
})
export class TmsTaskColorColorComponent implements OnInit {
  @Input()
  model: string;

  @Output()
  modelChange: EventEmitter<string>;

  constructor() {
    this.modelChange = new EventEmitter<string>();
  }

  ngOnInit() {
  }

  changeColor(color: string): void {
    this.model = color;
    this.modelChange.emit(this.model);
  }

}
