import { LookupViewModel, PersonnelGroupPickerViewModel } from 'codera-shared';

export class AddEditFollowUpViewModel {
    constructor(
        public Id?: string,
        public ChangeRequestId?: string,
        public DueDate?: Date,
        public DateCompleted?: Date,
        public AssignedTo?: PersonnelGroupPickerViewModel[],
        public MethodOfFollowUp?: LookupViewModel,
        public FollowUpResult?: LookupViewModel,
        public Comment?: string,
        public HasNextFollowUp?: boolean,
        public NextFollowUp?: AddEditFollowUpViewModel,
        public NextFollowUpDueDate?: Date,
        public NextFollowUpMethodOfFollowUp?: LookupViewModel,
        public NextFollowUpAssignedTo?: PersonnelGroupPickerViewModel[]
    ) {
        if (this.AssignedTo == null) {
            this.AssignedTo = [];
        }
        this.NextFollowUpAssignedTo = [];
    }
}

export class FollowUpViewModel extends AddEditFollowUpViewModel {
    constructor(
        public Id?: string,
        public ChangeRequestId?: string,
        public DueDate?: Date,
        public DateCompleted?: Date,
        public AssignedTo?: PersonnelGroupPickerViewModel[],
        public MethodOfFollowUp?: LookupViewModel,
        public FollowUpResult?: LookupViewModel,
        public Comment?: string,
        public HasNextFollowUp?: boolean,
        public NextFollowUp?: AddEditFollowUpViewModel,
        public NextFollowUpDueDate?: Date,
        public NextFollowUpMethodOfFollowUp?: LookupViewModel,
        public NextFollowUpAssignedTo?: PersonnelGroupPickerViewModel[],
        public Actor?: LookupViewModel,
        public Attachments?: any[]
    ) {
        super(Id, ChangeRequestId, DueDate, DateCompleted, AssignedTo, MethodOfFollowUp, FollowUpResult, Comment, HasNextFollowUp, NextFollowUp, NextFollowUpDueDate, NextFollowUpMethodOfFollowUp, NextFollowUpAssignedTo);
    }
}

export class AttachmentBaseModel {
    constructor(
        public Title?: string,
        public Description?: string,
        public FileName?: string
    ) { }
}

export class AttachmentPreviewViewModel extends AttachmentBaseModel {
    constructor(
        public Title?: string,
        public Description?: string,
        public FileName?: string,
        public Id?: string,
        public ContentType?: string,
        public ContentLength?: number,
        public DownloadUrl?: string
    ) {
        super(Title, Description, FileName);
    }
}

export class FollowUpPermissionsViewModel {
    constructor(
        public CanSetupFollowUp?: boolean,
        public CanDeleteFollowUp?: boolean,
        public CanConductFollowUp?: boolean
    ) { }
}

export class FollowUpCountViewModel {
    constructor(
        public All: number,
        public Completed: number
    ) { }
}