import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextEditorComponent } from 'codera-shared';

@Component({
  selector: 'update-tms-task-notes',
  template: ''
})
export class UpdateTmsTaskNotesComponent implements OnInit {
  @Input()
  value: any;

  @Output()
  onSubmit: EventEmitter<string>;

  model: any;
  modalFormConfig: ModalFormConfig;
  constructor(
    private dynamicService: DynamicService,
    private viewContainerRef: ViewContainerRef
  ) {
    this.model = {
      Notes: null
    };
    this.onSubmit = new EventEmitter<string>();
  }

  ngOnInit() {
    const that = this;
    if (this.value)
      this.model.Notes = this.value;

    this.modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].Notes.Edit",
      [
        new RowControl([
          new ComponentControl({
            componentClass: TextEditorComponent,
            field: "Notes",
            data: [
              { label: "[ISOQuest].[Common].Notes" },
              { editorId: "notes" }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            if (that.model && that.model.Notes)
              that.onSubmit.emit(that.model.Notes);
            else
              that.onSubmit.emit(null);

            that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainerRef, ModalFormComponent, [
      { model: that.model },
      { config: that.modalFormConfig },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);

  }

}
