import { ViewportScroller } from '@angular/common';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { WorkflowPathsModel } from 'codera-shared';
import { Subscription } from 'rxjs';
import { AnalysisObjectType, ChecklistGroup, Choice, Question } from '../../../../system-controls/checklist-groups/types';
import { ChangeRequestService } from '../../../change-request.service';
import { AnalysisConfigModel, ChangeRequestAnalysisAlertViewModel } from '../../../types';
import { AnalysisService } from '../analysis.service';

@Component({
    selector: 'manage-mode-analysis',
    templateUrl: './manage-mode-analysis.component.html',
    styleUrls: ['./manage-mode-analysis.component.scss']
})
export class ManageModeAnalysisComponent implements OnInit, OnDestroy {
    @Input() groups: ChecklistGroup[];
    @Input() analysisConfig: AnalysisConfigModel;
    @Input() workflowObjectType: string;
    @Input() workflowPaths: WorkflowPathsModel;

    analysisAlerts: ChangeRequestAnalysisAlertViewModel[];
    analysisObjectType = AnalysisObjectType;
    collapseAllGroups: boolean;
    subscriptions: Subscription[];

    constructor(
        private analysisService: AnalysisService,
        private viewportScroller: ViewportScroller,
        private changeRequestService: ChangeRequestService,
    ) { }

    ngOnDestroy(): void {
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
    }

    ngOnInit() {
        this.subscriptions = [];
        this.analysisAlerts = [];
        this.subscriptions.push(this.analysisService.alerts$.subscribe(alerts => this.analysisAlerts = alerts));
    }

    collapseExpandAllGroups(): void {
        this.collapseAllGroups = !this.collapseAllGroups;
        for (let i = 0; i < this.groups.length; i++) {
            this.groups[i].expanded = !this.collapseAllGroups;
            for (let j = 0; j < this.groups[i].Questions.length; j++) {
                this.groups[i].Questions[j].expanded = !this.collapseAllGroups;
            }
        }
    }


    goToChoice(groupId: string, questionId: string, choiceId: string) {
        const group: ChecklistGroup = this.groups.find(x => x.Id == groupId);
        if (group === undefined)
            throw new Error("Group not found");

        const question: Question = group.Questions.find(x => x.Id == questionId);
        if (question === undefined) {
            this.viewportScroller.scrollToAnchor(`group-${groupId}`);
            return;
        }

        question.expanded = true;
        const choice: Choice = question.Choices.find(x => x.Id == choiceId);
        if (choice === undefined) {
            this.viewportScroller.scrollToAnchor(`group-${groupId}-question-${questionId}`);
            return;
        }

        choice.expanded = true;
        setTimeout(() => {
            this.viewportScroller.scrollToAnchor(`group-${groupId}-question-${questionId}-choice-${choiceId}`);
        });
    }

    exportAnalysis() {
        this.changeRequestService.exportChangeRequestAnalysisXlsx(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);
    }

    exportAnalysisForPrint() {
        this.changeRequestService.exportChangeRequestAnalysisForPrintXlsx(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);
    }
}
