import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { TMSTaskStatus } from '../../types';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'task-status-picker',
  templateUrl: './task-status-picker.component.html'
})
export class TaskStatusPickerComponent implements OnInit {
  @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
  @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() searchMode: boolean;

  staticData: IdentityLookupViewModel[];
  statusConfig: { [key: string]: any }
  constructor(
    private translate: TranslateService
  ) {
    this.modelChange = new EventEmitter();
    this.staticData = [];
  }

  ngOnInit() {
    if (!this.label) {
      this.label = "[ISOQuest].[BryntumGantt].TaskStatus";
    }

    this.statusConfig = {};
    this.statusConfig[TMSTaskStatus.NotStarted] = { icon: "fa fa-circle-notch", color: "#9e9e9e;" };
    this.statusConfig[TMSTaskStatus.InProgress] = { icon: "fa fa-spinner", color: "#337ab7;" };
    this.statusConfig[TMSTaskStatus.Completed] = { icon: "fa fa-check-circle", color: "#26c281;" };
    this.statusConfig[TMSTaskStatus.Archived] = { icon: "fa fa-archive", color: "#f1c40f;" };
    this.statusConfig[TMSTaskStatus.NotCompleted] = { icon: "fa fa-circle-notch", color: "#295170;" };

    this.staticData = Object.keys(TMSTaskStatus).filter((item) => {
      if (!this.searchMode && item === TMSTaskStatus[TMSTaskStatus.NotCompleted])
        return false;

      return isNaN(Number(item));
    }).map(taskStatus => {
      return new IdentityLookupViewModel(TMSTaskStatus[taskStatus], this.translate.instant("[ISOQuest].[Common].TMS.Status." + taskStatus), true);
    }).sort((left, right) => Number(left.Id) - Number(right.Id));

    if (this.model == null && this.isRequired)
      this.onChange(this.staticData[0]);
  }

  onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
