import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, ComponentControl, DynamicService, FacilityInfoComponent, FacilityPickerComponent, FluentSearchConfig, FluentTableComponent, FluentTableConfig, IdentityLookupViewModel, InputControl, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IqPersonnelPickerComponent, RowControl, TextConfirmationModalComponent } from 'codera-shared';
import { ChangeRequestTmsGroupByPickerComponent } from 'src/app/tasks/pickers/change-request-tms-group-by-picker/change-request-tms-group-by-picker.component';
import { ChangeRequestTmsGrouping } from 'src/app/tasks/types';
import { CreateUpdateRecommendedActionComponent } from '../details/review-approval-tabs/recommended-action/create-update/create-update-recommended-action.component';
import { RecommendedActionStatusPickerComponent } from '../details/review-approval-tabs/recommended-action/recommended-action-status-picker/recommended-action-status-picker.component';
import { RecommendedActionService } from '../details/review-approval-tabs/recommended-action/recommended-action.service';
import { ChangeRequestRecommendedActionGridModel } from '../details/review-approval-tabs/recommended-action/types';
import { ChangeRequestTypePickerComponent } from '../../system-controls/change-request-type/picker/change-request-type-picker.component';

@Component({
    selector: 'change-request-pssr-tasks-grid',
    templateUrl: './change-request-pssr-tasks-grid.component.html',
    styleUrls: ['./change-request-pssr-tasks-grid.component.scss']
})
export class ChangeRequestPssrTasksGridComponent implements OnInit {
    @Input()
    source: string;
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    currentFilter: { [key: string]: any }[] = [];
    @ViewChild(FluentTableComponent) fluentTableComponent: FluentTableComponent;
    @Output() onDeleteTask: EventEmitter<void>;
    constructor(
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private recommendedActionService: RecommendedActionService,
        private translate: TranslateService,
        private router: Router,
        private applicationService: ApplicationService
    ) {
        this.onDeleteTask = new EventEmitter<void>();
    }

    ngOnInit() {
        const that = this;
        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        field: "ChangeRequest",
                        type: "text",
                        label: "[MOCQuest].[SiteMap].ChangeRequest",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "Assignee",
                        data: [
                            { targetModule: "MOCQuest" },
                            { type: "Personnel" },
                            { label: "[ISOQuest].[Common].Assignee" }
                        ],
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "ChangeRequestFacility",
                        data: [
                            { label: "[ISOQuest].[Common].Facility" }
                        ],
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: RecommendedActionStatusPickerComponent,
                        field: "TaskStatus",
                        label: "[MOCQuest].[RecommendedAction].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTmsGroupByPickerComponent,
                        field: "GroupBy",
                        data: [
                            {
                                modelChange: (model: IdentityLookupViewModel) => {
                                    let field = "";
                                    let property = "";
                                    if (model != null && model.Id != null) {

                                        switch (model.Id) {
                                            case ChangeRequestTmsGrouping.ChangeRequest:
                                                field = "ChangeRequest";
                                                break;

                                            case ChangeRequestTmsGrouping.Facility:
                                                field = "ChangeRequestFacility";
                                                break;

                                            case ChangeRequestTmsGrouping.Assignee:
                                                field = "AssignedToGrouping";
                                                break;

                                            case ChangeRequestTmsGrouping.TaskStatus:
                                                field = "Status";
                                                break;

                                            default:
                                                field = undefined;
                                                property = undefined;
                                                break;
                                        }

                                        that.fluentTableConfig.grouping = {
                                            field: field,
                                            property: property,
                                            render: (row) => {
                                                if (row.key) {
                                                    return that.translate.instant(row.key);
                                                } else {
                                                    return that.translate.instant("[ISOQuest].[Common].NotAvailable");
                                                }
                                            },
                                            expandGroups: true
                                        };
                                    } else {
                                        that.fluentTableConfig.grouping = null;
                                        that.fluentTableConfig.sorting = { Title: "asc" };
                                    }

                                    that.fluentTableConfig.notifyDataChanged();
                                },
                            },
                            { defaultSelected: ChangeRequestTmsGrouping.ChangeRequest }
                        ],
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new InputControl({
                        type: "text",
                        field: "ChangeRequestId",
                        label: "[MOCQuest].[ChangeRequest].ChangeRequestId",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "isMultiple": true }
                        ]
                    }),
                    new InputControl({
                        type: "text",
                        field: "ProjectNumber",
                        label: "[MOCQuest].[ChangeRequest].ProjectNumber",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: that.applicationService.getCurrentModuleSettings().EnableProjectNumber
                    }),
                ])
            ],
            searchAction: () => {
                that.fluentTableConfig.applyFilterAndGrouping(that.currentFilter);
            },
            resetAction: () => {
                that.fluentTableConfig.grouping = undefined;
                that.fluentTableConfig.sorting = [{ Title: "asc" }]
                that.fluentTableConfig.applyFilterAndGrouping(that.currentFilter);
                that.fluentTableComponent.table.groupedRows = undefined;
                that.fluentTableComponent.table.sorts = [{ prop: "Title", dir: "asc" }];
            }
        });

        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "",
                    title: " ",
                    type: "custom",
                    render(row: ChangeRequestRecommendedActionGridModel) {
                        if (row.HasAttachments) {
                            return `<i class="fa fa-paperclip pull-right"></i>`;
                        }
                        return "";
                    },
                    flexGrow: 0.25
                },
                {
                    field: "ChangeRequestFacility",
                    type: "action",
                    sortable: "ChangeRequestFacility",
                    title: "[MOCQuest].[ChangeRequest].ChangeRequestFacility",
                    render: (row: ChangeRequestRecommendedActionGridModel) => {
                        return `<a href="javascript:void(0);">${row.ChangeRequestFacility.Name}</a>`
                    },
                    handler: (row: ChangeRequestRecommendedActionGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, FacilityInfoComponent, [
                            { facilityId: row.ChangeRequestFacility.Id }
                        ]);
                    },
                    show: that.applicationService.getCurrentSite().IsMultiSite
                },
                {
                    field: "ChangeRequest",
                    type: "action",
                    sortable: "ChangeRequestName",
                    title: "[MOCQuest].[SiteMap].ChangeRequest",
                    render: (row: ChangeRequestRecommendedActionGridModel) => {
                        const siteKey = that.applicationService.getCurrentSiteKey();
                        if (row && row.ChangeRequest && row.ChangeRequest.Name)
                            return `<a href="/mocquest/sites/${siteKey}/change-request/details/${row.ChangeRequest.Id}">${row.ChangeRequest.Name}</a>`;
                        return "";
                    },
                    handler: (row: ChangeRequestRecommendedActionGridModel) => {
                        that.router.navigate(["change-request/details", row.ChangeRequest.Id]);
                    }
                },
                {
                    field: "ChangeRequestStatus",
                    type: "custom",
                    sortable: "ChangeRequestStatus",
                    title: "[MOCQuest].[ChangeRequest].ChangeRequestStatus",
                    render: (row: ChangeRequestRecommendedActionGridModel) => {
                        return `${that.translate.instant(row.ChangeRequestStatus.Name)}`
                    }
                },
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "string",
                    cellClass: "inline-column"
                },
                {
                    field: "AssignedTo",
                    title: "[ISOQuest].[Common].Assignee",
                    sortable: "AssignedTo",
                    type: "action",
                    handler(row: ChangeRequestRecommendedActionGridModel) {
                        if (row.AssignedTo !== null) {
                            if (!row.AssignedTo.IsGroup) {
                                const personnelId = row.AssignedTo.Personel.Id;
                                that.dynamicService.addComponent(that.viewContainer, IqPersonnelInfoComponent, [
                                    { "personnelId": personnelId }
                                ], false);
                            }
                            else {
                                const groupId = row.AssignedTo.Group.Id;
                                that.dynamicService.addComponent(that.viewContainer, IqPersonnelGroupInfoComponent, [
                                    { "groupId": groupId }
                                ], false);
                            }
                        }
                    },
                    render(row: ChangeRequestRecommendedActionGridModel) {
                        if (row.AssignedTo !== null) {
                            if (!row.AssignedTo.IsGroup) {
                                return `<a title="${row.AssignedTo.Personel.FullName}" href="javascript:void(0);">${row.AssignedTo.Personel.FullName}</a>`;
                            }
                            else {
                                return `<a title="${row.AssignedTo.Group.Title}" href="javascript:void(0);">${row.AssignedTo.Group.Title}</a>`;
                            }
                        }
                        else {
                            return "";
                        }
                    }
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "custom",
                    render: (row: ChangeRequestRecommendedActionGridModel) => {
                        if (!row.Status) {
                            return null;
                        } else {
                            const translatedStatus = that.translate.instant(row.Status.Name);
                            switch (row.Status.Id) {
                                case 0:
                                    return `<i class='far fa-circle-notch' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                case 1:
                                    return `<i class='fa fa-spinner' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                case 2:
                                    return `<i class='fa fa-check-circle' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                default:
                                    return null;
                            }
                        }
                    },
                    flexGrow: 1
                },
                {
                    field: "DueDate",
                    title: "[ISOQuest].[Common].DueDate",
                    sortable: "DueDate",
                    type: "date"
                },
                {
                    field: "Description",
                    title: "[ISOQuest].[Common].Description",
                    sortable: "Description",
                    type: "string",
                    cellClass: "inline-column"
                },
                {
                    field: "RequiredBeforeLaunch",
                    title: "[MOCQuest].[RecommendedAction].RequiredBeforeLaunch",
                    sortable: "RequiredBeforeLaunch",
                    type: "yesNo"
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestRecommendedActionGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, CreateUpdateRecommendedActionComponent, [
                            { changeRequestId: row.ChangeRequest.Id },
                            { recommendedActionId: row.Id },
                            {
                                onCreateUpdate: () => {
                                    that.fluentTableConfig.notifyDataChanged();
                                }
                            }
                        ], true);
                    },
                    render(row: ChangeRequestRecommendedActionGridModel) {
                        return row.CanEdit ? `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>` : "";
                    },
                    flexGrow: 0.4
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: ChangeRequestRecommendedActionGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.recommendedActionService.delete(row.Id, row.ChangeRequest.Id).subscribe(() => {
                                        that.fluentTableConfig.notifyDataChanged("item-deleted");
                                        that.onDeleteTask.emit();
                                    });
                                }
                            }
                        ], true);
                    },
                    render(row: ChangeRequestRecommendedActionGridModel) {
                        return row.CanDelete ? `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>` : "";
                    },
                    flexGrow: 0.4,
                }
            ],
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ],
            grouping: {
                field: "ChangeRequest",
                render: (row) => {
                    if (row.key) {
                        return that.translate.instant(row.key);
                    } else {
                        return that.translate.instant("[ISOQuest].[Common].NotAvailable");
                    }
                },
                expandGroups: true
            },
        });
    }

}
