import { Component, ComponentRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, AttachmentsMultipleModeComponent, AttachmentViewModel, BulkOperationExecutionResultComponent, CancelAction, ComponentControl, CustomAction, DynamicService, FacilityPickerComponent, InputControl, IqDatePickerComponent, IqPersonnelPickerComponent, IsoQuestEvent, IsoQuestEventManagerService, LabelControl, LookupViewModel, ModalFormComponent, ModalFormConfig, RadioControl, RowControl, TextareaControl } from 'codera-shared';
import { ToastrService } from "ngx-toastr";
import { FunctionalLocationPickerComponent } from 'src/app/functional-location-picker/functional-location-picker.component';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChangeRequestTypePickerComponent } from 'src/app/system-controls/change-request-type/picker/change-request-type-picker.component';
import { ReasonTypePickerComponent } from 'src/app/system-controls/reason-type/picker/reason-type-picker.component';
import { ChangeRequestTagPickerComponent } from '../../system-controls/change-request-tag/picker/change-request-tag-picker.component';
import { WorkflowMatrixService } from '../../system-controls/workflow-matrix/workflow-matrix.service';
import { ChangeRequestService } from '../change-request.service';
import { ChangeRequestViewModel, DurationType } from '../types';

@Component({
    selector: 'create-update-change-request',
    templateUrl: './create-update-change-request.component.html',
    styleUrls: ['./create-update-change-request.component.scss']
})
export class CreateUpdateChangeRequestComponent implements OnInit, OnDestroy {
    @Input() changeRequestId: string;

    @Output() onCreateUpdate: EventEmitter<string | boolean>;

    model: ChangeRequestViewModel;
    createChangeModalFormConfig: ModalFormConfig;
    component: ComponentRef<ModalFormComponent>;
    attachmentsModel: AttachmentViewModel;

    constructor(
        private router: Router,
        private toastr: ToastrService,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private changesService: ChangeRequestService,
        private applicationService: ApplicationService,
        private workflowMatrixService: WorkflowMatrixService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.onCreateUpdate = new EventEmitter<string | boolean>();
    }

    ngOnDestroy(): void {
    }

    ngOnInit() {
        const modalTitle = this.changeRequestId
            ? "[MOCQuest].[ChangeRequest].Edit"
            : "[MOCQuest].[ChangeRequest].New";

        this.prepareModalFormConfig(modalTitle);
        this.component = this.summonCreateChangeDialog();
        var changerRequestTagsControl = this.createChangeModalFormConfig.getControlByField("ChangeRequestTags");

        if (this.changeRequestId) {
            this.changesService.getById(this.changeRequestId).subscribe((response: ChangeRequestViewModel) => {
                this.model = response;
                this.component.instance.model = response;

                if (response.DurationType === 2) {
                    const dateFromControl = this.component.instance.config.getControlByField("DateFrom");
                    const dateToControl = this.component.instance.config.getControlByField("DateTo");
                    const infoLabelControl = (this.createChangeModalFormConfig.controls[6] as RowControl).controls[0] as LabelControl;
                    if (this.component.instance.model.DurationType === DurationType.Temporary) {
                        dateFromControl.show = true;
                        dateToControl.show = true;
                        infoLabelControl.show = true;
                    } else {
                        dateFromControl.show = false;
                        dateToControl.show = false;
                        infoLabelControl.show = false;
                    }
                }

                const additionalImpactedFacilities = this.createChangeModalFormConfig.getControlByField("AdditionalImpactedFacilities") as ComponentControl;
                additionalImpactedFacilities.getDataByKey("itemsToExclude").itemsToExclude.push(response.Facility);

                const functionalLocations = this.createChangeModalFormConfig.getControlByField("FunctionalLocations") as ComponentControl;
                functionalLocations.getDataByKey("facilityId").facilityId = this.model.Facility.Id;
            });
        }
    }

    prepareModalFormConfig(title: string) {
        const that = this;
        this.model = new ChangeRequestViewModel();
        this.createChangeModalFormConfig = new ModalFormConfig(title, [
            new RowControl([ //0
                new InputControl({
                    type: "text",
                    field: "Title",
                    label: "[ISOQuest].[Common].Title",
                    required: true,
                    min: 1,
                    max: 255
                })]),
            new RowControl([ //1
                new InputControl({
                    type: "text",
                    field: "ProjectNumber",
                    label: "[MOCQuest].[ChangeRequest].ProjectNumber",
                    min: 1,
                    max: 255,
                    show: that.applicationService.getCurrentModuleSettings().EnableProjectNumber
                })]),
            new RowControl([ //2
                new ComponentControl({
                    componentClass: FacilityPickerComponent,
                    field: "Facility",
                    data: [
                        { "label": "[ISOQuest].[Common].Facility" },
                        { "isRequired": true },
                        { "onlyActive": true },
                        { "currentUserFacilities": true },
                        {
                            "modelChange": (newValue: LookupViewModel) => {
                                this.onFacilityChange(newValue);
                            }
                        },
                        { isDisabled: that.changeRequestId }
                    ],
                    show: that.applicationService.getCurrentSite().IsMultiSite
                })
            ]),
            new RowControl([ //3
                new ComponentControl({
                    componentClass: FacilityPickerComponent,
                    field: "AdditionalImpactedFacilities",
                    css: "col-md-10",
                    data: [
                        { "label": "[MOCQuest].[ChangeRequest].AdditionalImpactedFacilities" },
                        { "isRequired": false },
                        { "onlyActive": true },
                        { "currentUserFacilities": true },
                        { "itemsToExclude": [] },
                        { isMultiple: true },
                        { isDisabled: false }
                    ],
                    show: that.applicationService.getCurrentSite().IsMultiSite && that.applicationService.getCurrentModuleSettings().AllowAdditionalImpactedFacilities
                }),
                new InputControl({
                    type: 'checkbox',
                    field: 'AllAdditionalImpactedFacilities',
                    label: '[MOCQuest].[ChangeRequest].AllAdditionalImpactedFacilities',
                    css: 'col-md-2',
                    events: [
                        {
                            eventName: "change",
                            eventCallback: () => {
                                that.model.AdditionalImpactedFacilities = [];
                                const additionalImpactedFacilities = this.createChangeModalFormConfig.getControlByField("AdditionalImpactedFacilities") as ComponentControl;
                                if (additionalImpactedFacilities.getDataByKey("isDisabled").isDisabled === true) {
                                    additionalImpactedFacilities.getDataByKey("isDisabled").isDisabled = false;
                                }
                                else {
                                    additionalImpactedFacilities.getDataByKey("isDisabled").isDisabled = true;
                                }
                                this.isoQuestEventManager.notify("invalidateData", null);
                            }
                        }
                    ]
                })
            ]),
            new RowControl([ //4
                new ComponentControl({
                    componentClass: ChangeRequestTypePickerComponent,
                    field: "ChangeRequestType",
                    data: [
                        { "isRequired": true },
                        { "onlyActive": true },
                        { "facility": null },
                        { "itemsForChangeRequest": true },
                        {
                            "modelChange": (newValue: LookupViewModel) => {
                                this.onChangeRequestTypeChange(newValue);
                            }
                        },
                        { isDisabled: that.changeRequestId }
                    ]
                }),
            ]),
            new RowControl([ //5
                new ComponentControl({
                    componentClass: ReasonTypePickerComponent,
                    field: "ReasonType",
                    css: "col-md-6",
                    data: [
                        { "isRequired": true },
                        { "onlyActive": true },
                        { "enableItemCreation": true }
                    ]
                }),
                new ComponentControl({
                    field: "TargetDueDate",
                    componentClass: IqDatePickerComponent,
                    css: "col-md-6",
                    data: [
                        { "label": "[MOCQuest].[ChangeRequest].TargetDueDate" },
                        { "isRequired": true }
                    ]
                })
            ]),
            new RowControl([ //6
                new RadioControl({
                    field: "DurationType",
                    label: "[MOCQuest].[ChangeRequest].DurationType",
                    css: "col-md-12",
                    options: [
                        {
                            label: "[MOCQuest].[ChangeRequest].DurationType.Permanent",
                            value: DurationType.Permanent
                        },
                        {
                            label: "[MOCQuest].[ChangeRequest].DurationType.Temporary",
                            value: DurationType.Temporary
                        }
                    ],
                    events: [
                        {
                            eventName: "click",
                            eventCallback() {
                                setTimeout(function () {
                                    const dateFromControl = that.createChangeModalFormConfig.getControlByField("DateFrom");
                                    const dateToControl = that.createChangeModalFormConfig.getControlByField("DateTo");
                                    const infoLabelControl = (that.createChangeModalFormConfig.controls[8] as RowControl).controls[0] as LabelControl;
                                    if (that.model.DurationType === DurationType.Temporary) {
                                        dateFromControl.show = true;
                                        that.model.DateFrom = null;
                                        dateToControl.show = true;
                                        that.model.DateTo = null;
                                        infoLabelControl.show = true;
                                    } else {
                                        dateFromControl.show = false;
                                        that.model.DateFrom = null;
                                        dateToControl.show = false;
                                        that.model.DateTo = null;
                                        infoLabelControl.show = false;
                                    }
                                });
                            }
                        }
                    ]
                })
            ]),
            new RowControl([
                new ComponentControl({
                    field: "DateFrom",
                    componentClass: IqDatePickerComponent,
                    css: "col-md-6",
                    data: [
                        { "label": "[MOCQuest].[ChangeRequest].DateFrom" },
                        { "isRequired": true },
                        {
                            "modelChange": (newValue: Date) => {
                                this.onTemporaryDurationDateChange(that.createChangeModalFormConfig, newValue, "DateFrom");
                            }
                        },
                        { "minDate": null },
                        { "maxDate": null }
                    ],
                    show: this.model.DurationType === 2
                }),
                new ComponentControl({
                    field: "DateTo",
                    componentClass: IqDatePickerComponent,
                    css: "col-md-6",
                    data: [
                        { "label": "[MOCQuest].[ChangeRequest].DateTo" },
                        { "isRequired": true },
                        {
                            "modelChange": (newValue: Date) => {
                                this.onTemporaryDurationDateChange(that.createChangeModalFormConfig, newValue, "DateTo");
                            }
                        },
                        { "minDate": null },
                        { "maxDate": null }
                    ],
                    show: this.model.DurationType === 2
                })
            ]),
            new RowControl([
                new LabelControl({
                    text: "[MOCQuest].[ChangeRequest].TemporaryDuration.Tip",
                    css: "col-md-12 text-info",
                    show: this.model.DurationType === 2
                })
            ]),
            new RowControl([
                new ComponentControl({
                    componentClass: IqPersonnelPickerComponent,
                    field: "ChangeCoordinators",
                    data: [
                        { targetModule: "MOCQuest" },
                        { label: "[MOCQuest].[ChangeRequest].ChangeCoordinators" },
                        { isMultiple: true },
                        { roles: ['MOCQuestAdministrator', 'MOCQuestContributor'] }
                        //{ "isRequired": true },
                        //{ "onlyActive": true },
                        //{ "type": "Personnel" }
                    ]
                })
            ]),
            new RowControl([
                new ComponentControl({
                    componentClass: ChangeRequestTagPickerComponent,
                    field: "ChangeRequestTags",
                    show: true,
                    data: [
                        { "isRequired": false },
                        { "isMultiple": true },
                        { "allowTags": true }
                    ]
                }),
            ]),
            new RowControl([
                new ComponentControl({
                    componentClass: FunctionalLocationPickerComponent,
                    field: "FunctionalLocations",
                    show: that.changeRequestId != null && (that.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel).FunctionalLocationsEnabled,
                    data: [
                        { isMultiple: true },
                        { facilityId: null },
                        { onlyActive: true }
                    ]
                })
            ]),
            new RowControl([
                new TextareaControl({
                    field: "Description",
                    label: "[ISOQuest].[Common].Description"
                })
            ]),
            new RowControl([
                new TextareaControl({
                    field: "Reason",
                    label: "[ISOQuest].[Common].Reason"
                })
            ]),
            new RowControl([
                new ComponentControl({
                    componentClass: AttachmentsMultipleModeComponent,
                    field: "Attachments",
                    show: !that.changeRequestId,
                    data: [
                        { "isMultiple": true },
                        { "url": "/ChangeRequest/UploadAttachment" }
                    ]
                })
            ])
        ], [
            new CustomAction({
                actionType: "submit",
                field: "Submit",
                value: "[ISOQuest].[Common].Buttons.Submit",
                callback: () => {
                    that.changesService.createOrUpdate(that.model).subscribe((response) => {
                        that.onCreateUpdate.emit(response);
                        if (typeof response === "string") {
                            setTimeout(() => {
                                this.uploadAttachments(response);
                            });
                        }
                        else {
                            that.createChangeModalFormConfig.modalRef.close();
                            that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                        }
                    },
                        (error) => {
                            that.isoQuestEventManager.notify("manuallyTriggerServerValidation", error);
                        });
                },
                css: "btn btn-primary"
            }),
            new CancelAction({
                field: "Cancel",
                value: "[ISOQuest].[Common].Buttons.Cancel",
                callback: () => {

                },
                css: "btn btn-default"
            })
        ]);

        if (this.applicationService.getCurrentSite().IsMultiSite) {
            this.onFacilityChange(null);
        }
    }

    summonCreateChangeDialog(): ComponentRef<ModalFormComponent> {
        let component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
            [
                { config: this.createChangeModalFormConfig },
                { model: this.model },
                {
                    modalOptions: {
                        size: "md"
                    }
                }
            ]
        );
        return component as ComponentRef<ModalFormComponent>;
    }

    uploadAttachments(changeRequestId: string) {
        this.attachmentsModel = new AttachmentViewModel();
        this.isoQuestEventManager.subscribe(this, "iq-no-attachments-provided", () => {
            this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
            this.createChangeModalFormConfig.modalRef.close();
            this.router.navigate(["/change-request/details", changeRequestId]);
        });

        let succeeded = [];
        let failed = [];
        this.isoQuestEventManager.subscribe(this, "iq-single-attachment-success", (payload: IsoQuestEvent) => {
            if (payload.data.title) {
                succeeded.push({ Name: payload.data.title, Reason: '', IsSucceed: true });
            }
        });
        this.isoQuestEventManager.subscribe(this, "iq-single-attachment-error", (payload: IsoQuestEvent) => {
            if (payload.data.title) {
                var errorParsed = JSON.parse(payload.data.error);
                failed.push({ Name: payload.data.title, Reason: errorParsed.Errors[""][0], IsSucceed: false });
            }
        });

        this.isoQuestEventManager.subscribe(this, "attachmentsUploadFinished", () => {
            this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-success");
            this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-error");
            this.isoQuestEventManager.unsubscribe(this, "attachmentsUploadFinished");

            if (failed.length) {
                var all = { Succeed: succeeded, Failed: failed };
                this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
                    { "model": all },
                    { "modalOptions": { size: "sm" } },
                    {
                        "callback": () => {
                            this.createChangeModalFormConfig.modalRef.close();
                            this.router.navigate(["/change-request/details", changeRequestId]);
                        }
                    }
                ]);

                this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
            }
            else {
                this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
                this.createChangeModalFormConfig.modalRef.close();
                this.router.navigate(["/change-request/details", changeRequestId]);
            }
        });

        this.attachmentsModel.ObjectId = changeRequestId;
        this.isoQuestEventManager.notify("attachmentsSubmitted", this.attachmentsModel);
    }

    /**
    * This method syncs the min/max dates on the pickers so invalid date exception (occurs when the dateTo is greater than the dateFrom) would be avoid from the back-end
    * @param modalConfig 
    * @param newValue 
    * @param fieldName 
    */
    private onTemporaryDurationDateChange(modalConfig: ModalFormConfig, newValue: Date, fieldName: string): void {
        const temporaryDurationDays = this.applicationService.getCurrentModuleSettings().TemporaryDurationDays;
        const dateFromComponentControl = modalConfig.getControlByField("DateFrom") as ComponentControl;
        const dateToComponentControl = modalConfig.getControlByField("DateTo") as ComponentControl;
        if (temporaryDurationDays !== 0 && newValue) {
            if (fieldName === "DateFrom") {
                const maxDate = new Date(newValue.getTime());
                maxDate.setDate(newValue.getDate() + temporaryDurationDays);
                dateToComponentControl.getDataByKey("minDate").minDate = newValue;
                dateToComponentControl.getDataByKey("maxDate").maxDate = maxDate;
            }
            else if (fieldName === "DateTo") {
                const minDate = new Date(newValue.getTime());
                minDate.setDate(newValue.getDate() - temporaryDurationDays);
                dateFromComponentControl.getDataByKey("minDate").minDate = minDate;
                dateFromComponentControl.getDataByKey("maxDate").maxDate = newValue;
            }
        }
        else {
            if (fieldName === "DateFrom") {
                dateToComponentControl.getDataByKey("minDate").minDate = newValue;
            }
            else if (fieldName === "DateTo") {
                dateFromComponentControl.getDataByKey("maxDate").maxDate = newValue;
            }
        }
    }

    /**
    * This method updates the input on change request type change on the change request type dependent pickers
    * @param newValue 
    */
    private onChangeRequestTypeChange(newValue: LookupViewModel): void {
        if (newValue) {
            const facilityId = this.model.Facility ? this.model.Facility.Id : null;
            this.workflowMatrixService.getWorkflowMatrixByChangeRequestTypeId(newValue.Id, facilityId).subscribe((response) => {
                if (response) {
                    this.model.ChangeCoordinators = response.ChangeCoordinators;
                }
                else {
                    this.model.ChangeCoordinators = [];
                }
            });
        }
        else {
            this.model.ChangeCoordinators = [];
        }
    }

    /**
    * This method updates the input on facility change on the facility dependent pickers
    * @param newValue 
    */
    private onFacilityChange(newValue: LookupViewModel): void {
        if (this.model) {
            this.model.ChangeRequestType = null;
            this.onChangeRequestTypeChange(null);
        }

        const changeRequestType = this.createChangeModalFormConfig.getControlByField("ChangeRequestType") as ComponentControl;
        const additionalImpactedFacilities = this.createChangeModalFormConfig.getControlByField("AdditionalImpactedFacilities") as ComponentControl;

        changeRequestType.getDataByKey("facility").facility = newValue;

        if (newValue) {
            changeRequestType.getDataByKey("isDisabled").isDisabled = false;
            if (changeRequestType.formControl && changeRequestType.formControl.disabled) {
                changeRequestType.formControl.enable();
            }
            additionalImpactedFacilities.getDataByKey("itemsToExclude").itemsToExclude = [];
            additionalImpactedFacilities.getDataByKey("itemsToExclude").itemsToExclude.push(newValue);
        }
        else {
            changeRequestType.getDataByKey("isDisabled").isDisabled = true;
        }
    }

}
