import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, ComponentControl, DynamicService, FacilityInfoComponent, FacilityPickerComponent, FluentSearchConfig, FluentTableConfig, InputControl, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IqPersonnelPickerComponent, ModalFormConfig, RowControl } from 'codera-shared';
import { FunctionalLocationPickerComponent } from 'src/app/functional-location-picker/functional-location-picker.component';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChangeRequestTypePickerComponent } from 'src/app/system-controls/change-request-type/picker/change-request-type-picker.component';
import { ReasonTypePickerComponent } from 'src/app/system-controls/reason-type/picker/reason-type-picker.component';
import { SeverityLevelLevelPickerComponent } from 'src/app/system-controls/severity-level/picker/severity-level-level-picker/severity-level-level-picker.component';
import { CommentsAndNotesInfoComponent } from '../../info-components/comments-and-notes-info/comments-and-notes-info.component';
import { ChangeRequestTagPickerComponent } from '../../system-controls/change-request-tag/picker/change-request-tag-picker.component';
import { ChangeRequestMaturityStatusPickerComponent } from '../picker/maturity-status-picker/change-request-maturity-status-picker.component';
import { ChangeRequestStatusPickerComponent } from '../picker/status-picker/status-picker.component';
import { ChangeRequestGridModel, MaturityStatus } from '../types';
import { DurationTypePickerComponent } from '../../pickers/duration-type-picker/duration-type-picker.component';

@Component({
    selector: 'change-request-grid',
    templateUrl: './change-request-grid.component.html',
    styleUrls: ['./change-request-grid.component.scss']
})
export class ChangeRequestGridComponent implements OnInit {
    @Input()
    tableSource: string;
    @Input()
    disableSearch: boolean;
    @Input()
    showPendingApproversColumn: boolean;

    currentFilter: { [key: string]: any }[] = [];
    fluentTableConfig: FluentTableConfig;
    fluentSearchConfig: FluentSearchConfig;
    commentsAndNotesModalConfig: ModalFormConfig;

    constructor(
        private router: Router,
        private translate: TranslateService,
        private applicationService: ApplicationService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef
    ) { }

    ngOnInit() {
        const that = this;

        if (this.tableSource === undefined || this.tableSource === null)
            throw new Error("tableSource is required");

        this.fluentTableConfig = new FluentTableConfig(
            {
                columns: [
                    {
                        field: "RejectedDate",
                        title: "[MOCQuest].[ChangeRequest].RejectDate",
                        type: "custom",
                        render(row) {
                            return "";
                        },
                    },
                    {
                        field: "ChangeRequestId",
                        title: "[MOCQuest].[ChangeRequest].ChangeRequestId",
                        sortable: "ChangeRequestId",
                        type: "string"
                    },
                    {
                        field: "Title",
                        title: "[ISOQuest].[Common].Title",
                        sortable: "Title",
                        type: "action",
                        render: (row: ChangeRequestGridModel) => {
                            const siteKey = that.applicationService.getCurrentSiteKey();
                            return `<a href="/mocquest/sites/${siteKey}/change-request/details/${row.Id}">${row.Title}</a>`;
                        },
                        handler: (row: ChangeRequestGridModel) => {
                            that.router.navigate(["/change-request/details", row.Id]);
                        }
                    },
                    {
                        field: "ProjectNumber",
                        title: "[MOCQuest].[ChangeRequest].ProjectNumber",
                        sortable: "ProjectNumber",
                        type: "string",
                        show: that.applicationService.getCurrentModuleSettings().EnableProjectNumber
                    },
                    {
                        field: "Status",
                        title: "[ISOQuest].[Common].Status",
                        sortable: "Status",
                        type: "custom",
                        render: (row: ChangeRequestGridModel) => {
                            var translatedStatus = that.translate.instant(row.Status.Name);
                            return translatedStatus;
                        }
                    },
                    {
                        field: "Facility",
                        title: "[ISOQuest].[Common].Facility",
                        sortable: "Facility",
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        type: "action",
                        render: (row: ChangeRequestGridModel) => {
                            return `<a href="javascript:void(0);">${row.Facility.Name}</a>`;
                        },
                        handler: (row: ChangeRequestGridModel) => {
                            that.dynamicService.addComponent(that.viewContainer, FacilityInfoComponent, [{ "facilityId": row.Facility.Id }]);
                        }
                    },
                    {
                        field: "ChangeRequestType",
                        title: "[MOCQuest].[SiteMap].SystemControls.ChangeRequestType.Single",
                        sortable: "ChangeRequestType",
                        type: "custom",
                        render: (row: ChangeRequestGridModel) => {
                            return "";
                        }
                    },
                    {
                        field: "ReasonType",
                        title: "[MOCQuest].[SiteMap].SystemControls.ReasonType.Single",
                        sortable: "ReasonType",
                        type: "custom",
                        render: (row: ChangeRequestGridModel) => {
                            return "";
                        }
                    },
                    {
                        field: "DurationType",
                        title: "[MOCQuest].[ChangeRequest].DurationType",
                        sortable: "DurationType",
                        type: "custom",
                        render: (row: ChangeRequestGridModel) => {
                            var translatedDurationType = that.translate.instant(row.DurationType.Name);
                            return translatedDurationType;
                        }
                    },
                    {
                        field: "CreatedAt",
                        title: "[ISOQuest].[Common].CreatedAt",
                        sortable: "CreatedAt",
                        type: "date"
                    },
                    {
                        field: "Duration",
                        title: "[MOCQuest].[ChangeRequest].Duration",
                        sortable: "Duration",
                        type: "string",
                        flexGrow: 0.4
                    },
                    {
                        field: "DateTo",
                        title: "[MOCQuest].[ChangeRequest].DateTo",
                        sortable: "DateTo",
                        type: "date",
                        flexGrow: 0.9
                    },
                    {
                        field: "SeverityLevel",
                        title: "[MOCQuest].[SystemControls].SeverityLevel",
                        sortable: "SeverityLevel",
                        type: "custom",
                        render: (row: ChangeRequestGridModel) => {
                            let key = `[MOCQuest].[SystemControls].SeverityLevel.Level.${row.SeverityLevel.Name}.Label.${this.applicationService.getCurrentSiteKey()}`;
                            let defaultKey = `[MOCQuest].[SystemControls].SeverityLevel.Level.${row.SeverityLevel.Name}.Label.Default`;
                            let severityLevelTranslation = "";
                            this.translate.get(key).subscribe(translation => {
                                if (translation === key) {
                                    this.translate.get(defaultKey).subscribe(defaultTranslation => {
                                        severityLevelTranslation = defaultTranslation;
                                    });
                                } else
                                    severityLevelTranslation = translation;
                            });
                            return severityLevelTranslation;
                        }
                    },
                    {
                        field: "MaturityStatus",
                        title: "[MOCQuest].[ChangeRequest].MaturityStatus",
                        sortable: "MaturityStatus",
                        type: "custom",
                        render(row: ChangeRequestGridModel) {
                            if (!row.MaturityStatus) {
                                return null;
                            } else {
                                var translatedMaturityStatus = that.translate.instant(row.MaturityStatus.Name);
                                switch (row.MaturityStatus.Id) {
                                    case MaturityStatus.OnSchedule:
                                        return `<i class='fa fa-circle' style='color: #000000; font-size: 15px;'></i>&ensp;${translatedMaturityStatus}`;
                                    case MaturityStatus.Overdue:
                                        return `<i class='fa fa-circle' style='color: #D9534F; font-size: 15px;'></i>&ensp;${translatedMaturityStatus}`;
                                    case MaturityStatus.CompletedOnTime:
                                        return `<i class='fa fa-circle' style='color: #00FF00; font-size: 15px;'></i>&ensp;${translatedMaturityStatus}`;
                                    case MaturityStatus.CompletedLate:
                                        return `<i class='fa fa-circle' style='color: #000000; font-size: 15px;'></i>&ensp;${translatedMaturityStatus}`;
                                    case MaturityStatus.None:
                                        return `<i class='fal fa-circle' style='color: #000000; font-size: 15px;'></i>&ensp;${translatedMaturityStatus}`;
                                    default:
                                        return null;
                                }
                            }
                        }
                    },
                    {
                        field: "TargetDueDate",
                        title: "[MOCQuest].[ChangeRequest].TargetDueDate",
                        sortable: "TargetDueDate",
                        type: "date",
                        flexGrow: 0.9
                    },
                    {
                        field: "ChangeCoordinators",
                        title: "[MOCQuest].[ChangeRequest].ChangeCoordinators",
                        type: "custom",
                        render() {
                            return "";
                        },
                        flexGrow: 1.2,
                        show: true
                    },
                    {
                        field: "PendingApprovers",
                        title: "[MOCQuest].[Common].PendingApprovers",
                        type: "custom",
                        render() {
                            return "";
                        },
                        flexGrow: 1.2,
                        show: that.showPendingApproversColumn == true
                    },
                    {
                        field: "CommentsAndNotes",
                        title: "[MOCQuest].[ChangeRequest].CommentsAndNotes",
                        show: true,
                        type: "action",
                        render: (row: ChangeRequestGridModel) => {
                            return `<a class="btn btn-sm btn-default blue-stripe btn-overlay"><i class="fa fa-eye"></i>&nbsp;<span>${that.translate.instant("[MOCQuest].[ChangeRequest].ShowCommentsAndNotes")}</span></a>`;
                        },
                        handler: (row: ChangeRequestGridModel) => {
                            that.dynamicService.addComponent(that.viewContainer, CommentsAndNotesInfoComponent, [{ "changeRequestId": row.Id }]);
                        },
                        flexGrow: 1
                    }
                ],
                sorting: {
                    "ChangeRequestId": "asc"
                },
                selection: {
                    mode: "none"
                },
                export: [
                    {
                        exportType: 2,
                        label: "[ISOQuest].[Common].ExportToExcel"
                    }
                ]
            });

        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "ChangeRequestId",
                        label: "[MOCQuest].[ChangeRequest].ChangeRequestId",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestStatusPickerComponent,
                        field: "Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "Facility",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        data: [
                            { "label": "[ISOQuest].[Common].Facility" },
                            { "currentUserFacilities": this.tableSource === "ChangeRequest/MyFacilityChangeRequestsGrid" },
                            { isMultiple: true },
                        ]
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "isMultiple": true }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: ReasonTypePickerComponent,
                        field: "ReasonType",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "isMultiple": true }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: SeverityLevelLevelPickerComponent,
                        field: "SeverityLevel",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestMaturityStatusPickerComponent,
                        field: "MaturityStatus",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTagPickerComponent,
                        field: "ChangeRequestTag",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "isMultiple": true }
                        ]
                    }),
                    new InputControl({
                        type: "text",
                        field: "ProjectNumber",
                        label: "[MOCQuest].[ChangeRequest].ProjectNumber",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: that.applicationService.getCurrentModuleSettings().EnableProjectNumber
                    }),
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "ChangeCoordinator",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: true,
                        data: [
                            { "label": "[MOCQuest].[ChangeRequest].ChangeCoordinators" },
                            { "isMultiple": true },
                            { "moduleName": 'MOCQuest' },
                            { "targetModule": 'MOCQuest' },
                            { "type": "Personnel" },
                            { "ignoreOnlyCurrentFacility": true }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "AdditionalImpactedFacilities",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "label": "[MOCQuest].[ChangeRequest].AdditionalImpactedFacilities" },
                            { "isRequired": false },
                            { "onlyActive": true },
                            { "currentUserFacilities": true },
                            { isMultiple: true },
                            { isDisabled: false },
                        ],
                        show: that.applicationService.getCurrentSite().IsMultiSite && that.applicationService.getCurrentModuleSettings().AllowAdditionalImpactedFacilities
                    }),
                    new ComponentControl({
                        componentClass: FunctionalLocationPickerComponent,
                        field: "FunctionalLocations",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: (that.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel).FunctionalLocationsEnabled,
                        data: [
                            { isMultiple: true }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: DurationTypePickerComponent,
                        css: "col-md-2",
                        field: "DurationType",
                        data: [
                            { "label": "[MOCQuest].[ChangeRequest].DurationType" },
                            { "onlyActive": true },
                            { "isMultiple": true }
                        ]
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });
    }

    personnelDetails(event, personnelId: string): void {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
            { "personnelId": personnelId },
            { "modalOptions": { size: "lg" } }
        ], false);
    }

    personnelGroupDetails(event, groupId: string): void {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
            { groupId: groupId }
        ]);
    }
}
