import { Component, Input, OnInit } from '@angular/core';
import { ApplicationService, ApplicationSite, FluentTabConfig } from 'codera-shared';
import { TranslateService } from '@ngx-translate/core';
import { TmsTaskCategoriesComponent } from './tms-task-categories/tms-task-categories.component';
import { TmsTaskPrioritiesComponent } from './tms-task-priorities/tms-task-priorities.component';
import { TmsTaskColorsComponent } from './tms-task-colors/tms-task-colors.component';
import { TmsTaskTopicsComponent } from './tms-task-topics/tms-task-topics.component';
import { BryntumGanttInstanceConfig } from '../bryntum-gantt/types';
import { TMSSearchModel, TMSPermissionsModel, TMSSettingsViewModel, TMSSearchModelHelper } from './types';
import { TmsGanttComponent } from './tms-gantt/tms-gantt.component';

@Component({
  selector: 'task-management-system',
  templateUrl: './task-management-system.component.html',
  styleUrls: ['./task-management-system.component.scss']
})
export class TaskManagementSystemComponent implements OnInit {
  tabsConfig: FluentTabConfig[];

  @Input()
  url: string;
  @Input()
  objectId: string;
  @Input()
  objectType: string;

  instanceConfig: BryntumGanttInstanceConfig;
  defaultSearch: TMSSearchModel;
  permissions: TMSPermissionsModel;
  settings: TMSSettingsViewModel;
  warningMessage: string;
  model: TMSSearchModelHelper;
  showing: boolean;
  currentModule: string;
  submitted: boolean;
  currentSite: ApplicationSite;
  searchCache: TMSSearchModelHelper;
  createMode: boolean;

  constructor(
    private applicationService: ApplicationService,
    private translate: TranslateService
  ) {
    this.currentModule = this.applicationService.getCurrentModuleKey();
    this.currentSite = this.applicationService.getCurrentSite();
  }

  ngOnInit() {
    if (!this.url)
      throw new Error("url is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    if (!this.objectType)
      throw new Error("objectType is required");

    this.tabsConfig = [
      new FluentTabConfig({
        componentClass: TmsGanttComponent,
        title: () => {
          let translatedTitle = '';
          this.translate.get("[ISOQuest].[Common].TMS.Tasks").subscribe(translation => translatedTitle = translation);
          return `<i class='fa fa-tasks' style='color:Mediumslateblue'></i>${translatedTitle}`;
        },
        data: [
          { url: this.url },
          { objectId: this.objectId },
          { objectType: this.objectType },
          { instanceConfig: this.instanceConfig }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskCategoriesComponent,
        title: () => {
          let translatedTitle = '';
          this.translate.get("[ISOQuest].[SiteMap].SystemControls.TMSTaskCategory").subscribe(translation => translatedTitle = translation);
          return `<i class='fa fa-th-large' style='color: #26C281;'></i>${translatedTitle}`;
        },
        data: [
          { objectId: this.objectId },
          { objectType: this.objectType }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskPrioritiesComponent,
        title: () => {
          let translatedTitle = '';
          this.translate.get("[ISOQuest].[SiteMap].SystemControls.TaskPriorities").subscribe(translation => translatedTitle = translation);
          return `<i class='fa fa-arrow-up' style='color: Tomato;'></i>${translatedTitle}`;
        },
        data: [
          { objectId: this.objectId },
          { objectType: this.objectType }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskTopicsComponent,
        title: () => {
          let translatedTitle = '';
          this.translate.get("[ISOQuest].[TaskManagementSystem].Task.Topics").subscribe(translation => translatedTitle = translation);
          return `<i class='fa fa-pencil-alt' style='color: color: #53853A;'></i>${translatedTitle}`;
        },
        data: [
          { objectId: this.objectId },
          { objectType: this.objectType }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskColorsComponent,
        title: () => {
          let translatedTitle = '';
          this.translate.get("[ISOQuest].[TaskManagementSystem].Task.Colors").subscribe(translation => translatedTitle = translation);
          return `<i class='fa fa-paint-brush' style='color: Mediumslateblue;''></i>${translatedTitle}`;
        },
        data: [
          { objectId: this.objectId },
          { objectType: this.objectType }
        ]
      })
    ]
  }
}
