import { Component, Input, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { ApplicationService } from 'codera-shared';
import { BryntumGanttInstanceConfig } from './types';
@Component({
  selector: 'bryntum-gantt',
  templateUrl: './bryntum-gantt.component.html',
  styleUrls: ['./bryntum-gantt.component.scss']
})
export class BryntumGanttComponent implements OnInit {
  @Input()
  url: string;
  @Input()
  objectId: string;
  @Input()
  objectType: string;
  @Input()
  instanceConfig: BryntumGanttInstanceConfig;

  src: SafeResourceUrl;

  constructor(
    private sanitizer: DomSanitizer,
    private applicationService: ApplicationService
  ) { }

  ngOnInit() {
    if (!this.url)
      throw new Error("url is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    if (!this.objectType)
      throw new Error("objectType is required");

    let src = `${this.url}index.html?object-id=${this.objectId}`;
    this.src = this.sanitizer.bypassSecurityTrustResourceUrl(`${this.applicationService.getHostUrl()}/${src}`);

    window['bryntumGanttCharts'] = {};
    window['bryntumGanttCharts'][this.objectId] = this.instanceConfig;
  }
}
