import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { FluentTabConfig } from 'codera-shared';
import { ChangeRequestService } from '../../change-request.service';
import { ChangeRequestDocumentsComponent } from '../documents/change-request-documents.component';

@Component({
    selector: 'documents-tabs',
    templateUrl: './documents-tabs.component.html'
})
export class DocumentsTabsComponent implements OnInit {
    @Input() objectId: string;
    requestDocumentsCount: number;
    tabsConfig: FluentTabConfig[];

    constructor(private translate: TranslateService,
                private changeRequestService: ChangeRequestService) { }

    ngOnInit() {
        const that = this;
        this.getDocumentsCount();
        this.prepareTabs();
    }

    getDocumentsCount() {
        this.changeRequestService.getChangeRequestCounts(this.objectId).subscribe(changeRequestCounts => {
            this.requestDocumentsCount = changeRequestCounts.DocumentsCount;
        });
    }

    prepareTabs() {
        const that = this;

        this.tabsConfig = [
            new FluentTabConfig({
                title: () => {
                    return `<span><i class='fas fa-file-alt'></i>${that.translate.instant("[MOCQuest].[ChangeRequest].ChangeRequestDocuments")}</span> &nbsp;<span class='badge badge-success' style='border-radius: 50% !important; min-width: 21px !important;'>${that.requestDocumentsCount}</span>`;
                },
                show: true,
                componentClass: ChangeRequestDocumentsComponent,
                data: [
                    { "objectId": that.objectId },
                    {
                        "refreshDocumentsCounter": () => {
                            that.getDocumentsCount();
                        }
                    }
                ]
            })            
        ]
    }

}
