import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser/';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, ComponentControl, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderItemsConfig, InputControl, IqStatusPickerComponent, RenderButtonsPosition, RowControl, TextConfirmationModalComponent, HeaderDropdownItem, BulkChangeStatusComponent, BulkDeleteComponent, ModalFormConfig, SubmitAction, CancelAction, ModalFormComponent } from 'codera-shared';
import { ChecklistGroupService } from './checklist-group.service';
import { ChecklistGroupGridModel, CopyChecklistGroupViewModel } from './types';

@Component({
    selector: 'checklist-groups',
    templateUrl: './checklist-groups.page.html'
})
export class ChecklistGroupsPage implements OnInit {
    pageTitle: string;
    tableSource: string;
    headerItemsConfig: HeaderItemsConfig;
    fluentTableConfig: FluentTableConfig;
    fluentSearchConfig: FluentSearchConfig;
    renderButtonsTypes = RenderButtonsPosition;
    currentFilter: { [key: string]: any }[] = [];

    constructor(
        private router: Router,
        private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private checklistGroupService: ChecklistGroupService) {

        this.pageTitle = this.route.snapshot.data["pageTitle"];
        let translatedTitle;

        this.translate.get(this.pageTitle).subscribe(translated => {
            translatedTitle = translated
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translatedTitle}`);
        });
    }

    ngOnInit() {
        const that = this;
        this.headerItemsConfig = new HeaderItemsConfig([
            new HeaderButtonItem({
                name: "new-checklist-group",
                icon: "fa fa-plus",
                label: "[MOCQuest].[ChecklistGroup].New",
                action: that.createNew.bind(this)
            }),
            new HeaderDropdownItem({
                name: "bulk-actions",
                icon: "",
                label: "[ISOQuest].[Common].Buttons.WithSelected",
                dropdownActions: [
                    new HeaderButtonItem({
                        name: "change-status",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.ChangeStatus",
                        action: that.bulkChangeStatus
                    }),
                    new HeaderButtonItem({
                        name: "bulk-delete",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.Delete",
                        action: that.bulkDelete
                    })
                ]
            })
        ]);

        this.tableSource = `ChecklistGroup/Grid`;

        // #region Filter Config

        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqStatusPickerComponent,
                        field: "Status",
                        label: "[ISOQuest].[Common].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });

        // #endregion Filter Config

        // #region Table Config

        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: 'action',
                    render(row: ChecklistGroupGridModel) {
                        return `<a href="javascript:void(0);">${row.Title}</a>`;
                    },
                    handler(row: ChecklistGroupGridModel) {
                        that.router.navigate(["/system-controls/edit-checklist-group", row.Id]);
                    },
                    flexGrow: 2
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "status"
                },
                {
                    field: "ModifiedBy",
                    title: "[ISOQuest].[Common].ModifiedBy",
                    sortable: "ModifiedBy",
                    type: "personnel"
                },
                {
                    field: "ModifiedAt",
                    title: "[ISOQuest].[Common].ModifiedAt",
                    sortable: "ModifiedAt",
                    type: "date"
                },
                {
                    field: "copyAction",
                    title: "",
                    type: "action",
                    handler(row: ChecklistGroupGridModel) {
                        that.copy(row.Id);
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default blue-stripe pull-right btn-overlay"><i class="fas fa-copy"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Copy")}</span></a>`;
                    },
                    flexGrow: 0.28
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: ChecklistGroupGridModel) {
                    that.router.navigate(['/system-controls/edit-checklist-group', row.Id]);
                        //that.dynamicService.addComponent(that.viewContainer, CreateUpdateChecklistGroupComponent,
                        //    [
                        //        { "checklistGroupId": row.Id },
                        //        {
                        //            "onCreateUpdate": () => {
                        //                that.fluentTableConfig.notifyDataChanged("item-updated");
                        //            }
                        //        }
                        //    ]
                        //);
                    },
                    render(row: ChecklistGroupGridModel) {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
                    },
                    flexGrow: 0.28
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: ChecklistGroupGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.checklistGroupService.delete(row.Id)
                                        .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                                }
                            }
                        ], true);
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.33
                }
            ],
            selection: {
                mode: "checkbox"
            },
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });

        // #endregion Table Config
    }


    bulkChangeStatus = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "ChecklistGroup/BulkChangeStatus" },
            { "modalOptions": { size: "sm" } }
        ]);
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "ChecklistGroup/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }

    copy = ( checklistGroupId) => {
        const that = this;

        const model = new CopyChecklistGroupViewModel();
        model.ChecklistGroupId =checklistGroupId;

        const modalConfig = new ModalFormConfig(
            "[MOCQuest].[ChecklistGroup].CopyChecklistGroup",
            [
                new RowControl([
                    new InputControl({
                        field: "Title",
                        type: "text",
                        label: "[ISOQuest].[Common].Title",
                        required: true
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    css: "btn btn-primary",
                    callback: () => {
                        that.checklistGroupService.copy(model).subscribe(id => {
                            that.router.navigate(["/system-controls/edit-checklist-group", id]);
                        });
                    }
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    css: "btn btn-default",
                    callback: () => {

                    }
                })
            ]
        );

        that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
            { model: model },
            { config: modalConfig },
            {
                modalOptions: {
                    size: "sm"
                }
            }
        ]);
    }

    createNew() {
        this.router.navigate(['/system-controls/new-checklist-group']);
    }
}
