import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, DynamicService, InputControl, IqDatePickerComponent, IsoQuestEventManagerService, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextareaControl } from 'codera-shared';
import { LaunchService } from '../launch.service';
import { LaunchViewModel } from '../types';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { LaunchConfirmationComponent } from '../launch-confirmation/launch-confirmation.component';

@Component({
    selector: 'update-launch',
    templateUrl: './update-launch.component.html',
    styleUrls: ['./update-launch.component.scss']
})
export class UpdateLaunchComponent implements OnInit {
    @Input()
    changeRequestId: string;

    @Output()
    onUpdate: EventEmitter<any>;

    model: LaunchViewModel;
    private component: ComponentRef<ModalFormComponent>;

    constructor(
        private launchService: LaunchService,
        private translate: TranslateService,
        private toastr: ToastrService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.onUpdate = new EventEmitter();
    }

    ngOnInit() {
        if (this.changeRequestId === undefined || this.changeRequestId === null)
            throw new Error("changeRequestId is required");

        const that = this;

        this.model = new LaunchViewModel(this.changeRequestId);

        const modalFormConfig =
            new ModalFormConfig(
                "[MOCQuest].[ChangeRequest].Launch.Update",
                [
                    new RowControl([
                        new ComponentControl({
                            componentClass: IqDatePickerComponent,
                            field: "EffectiveImplementationDate",
                            css: "col-6",
                            data: [
                                { label: "[MOCQuest].[ChangeRequest].Launch.EffectiveImplementationDate" },
                                { isRequired: true }
                            ]
                        }),
                        new InputControl({
                            field: "EffectiveImplementationTime",
                            type: "text",
                            css: "col-6",
                            label: "[MOCQuest].[ChangeRequest].Launch.EffectiveImplementationTime",
                            required: false
                        })
                    ]),
                    new RowControl([
                        new InputControl({
                            type: "text",
                            field: "Shift",
                            label: "[MOCQuest].[ChangeRequest].Launch.Shift",
                            css: "col"
                        })
                    ]),
                    new RowControl([
                        new TextareaControl({
                            field: "Comments",
                            label: "[ISOQuest].[Common].Comments",
                            css: "col"
                        })
                    ])
                ],
                [
                    new SubmitAction({
                        field: "Submit",
                        value: "[ISOQuest].[Common].Buttons.Submit",
                        callback: () => {
                            const modalForm = new ModalFormConfig(
                                "[ISOQuest].[Common].ConfirmDialog.Title",
                                [
                                    new RowControl([
                                        new ComponentControl({
                                            componentClass: LaunchConfirmationComponent,
                                            field: "Confirmation"
                                        })
                                    ])
                                ],
                                [
                                    new SubmitAction({
                                        field: "Submit",
                                        value: "[ISOQuest].[Common].Buttons.OK",
                                        css: "btn btn-primary",
                                        callback: () => {
                                            that.model.ProceedToNextStage = true;
                                            that.launchService.updateLaunch(that.model).subscribe(response => {
                                                modalFormConfig.modalRef && modalFormConfig.modalRef.close();
                                                that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                                                that.onUpdate.emit();
                                                that.isoQuestEventManager.notify("refresh-change-request-details", null);
                                                that.isoQuestEventManager.notify("fetch-change-request", null);
                                            });
                                        }
                                    }),
                                    new SubmitAction({
                                        field: "Cancel",
                                        value: "[ISOQuest].[Common].Buttons.Cancel",
                                        css: "btn btn-default",
                                        callback: () => {
                                            that.model.ProceedToNextStage = false;
                                            that.launchService.updateLaunch(that.model).subscribe(response => {
                                                modalFormConfig.modalRef && modalFormConfig.modalRef.close();
                                                that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                                                that.onUpdate.emit();
                                                that.isoQuestEventManager.notify("refresh-change-request-details", null);
                                                that.isoQuestEventManager.notify("fetch-change-request", null);
                                            });
                                        }
                                    })
                                ]
                            );
                            that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
                                { config: modalForm },
                                { model: {} },
                                { modalOptions: { size: "md" } }
                            ]);
                        },
                        css: "btn btn-primary"
                    }),
                    new CancelAction({
                        field: "Cancel",
                        value: "[ISOQuest].[Common].Buttons.Cancel",
                        callback: () => {

                        },
                        css: "btn btn-default"
                    })
                ]
            );

        this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
            { config: modalFormConfig },
            { model: that.model },
            {
                modalOptions: {
                    size: "md"
                }
            }
        ]) as ComponentRef<ModalFormComponent>;

        this.launchService.getLaunch(this.changeRequestId).subscribe(launch => {
            this.model = launch;
            this.component.instance.model = launch;
        });
    }

}
