import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, FluentTabConfig, WorkflowPathsModel } from 'codera-shared';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { AnalysisConfigModel } from 'src/app/change-request/types';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroup } from 'src/app/system-controls/checklist-groups/types';
import { ViewModeChecklistGroupApprovalsComponent } from '../view-mode-checklist-group-approvals/view-mode-checklist-group-approvals.component';
import { ViewModeChecklistGroupComponent } from '../view-mode-checklist-group/view-mode-checklist-group.component';

@Component({
  selector: 'view-mode-checklist-group-wrapper',
  templateUrl: './view-mode-checklist-group-wrapper.component.html',
  styleUrls: ['./view-mode-checklist-group-wrapper.component.scss']
})
export class ViewModeChecklistGroupWrapperComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;
  @Input() workflowObjectType: string;
  @Input() workflowPaths: WorkflowPathsModel;

  settings: MOCQuestSettingsViewModel;
  fluentTabs: FluentTabConfig[];

  constructor(
    private translate: TranslateService,
    private applicationService: ApplicationService,
    private changeRequestService: ChangeRequestService
  ) { }

  ngOnInit() {
    const that = this;

    this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;

    this.fluentTabs = [
      new FluentTabConfig({
        componentClass: ViewModeChecklistGroupComponent,
        title: "[ISOQuest].[Common].General",
        data: [
          { group: that.group },
          { analysisConfig: that.analysisConfig }
        ]
      }),
      new FluentTabConfig({
        componentClass: ViewModeChecklistGroupApprovalsComponent,
        title: (tab) => {
          return `<span><i class='fa fa-sitemap'></i>&nbsp;${that.translate.instant("[ISOQuest].[Workflow].Common.Approvals")}</span>`;
        },
        data: [
          { group: that.group },
          { objectId: that.group.Id },
          { objectType: that.workflowObjectType },
          { workflowPaths: that.workflowPaths }
        ],
        show: that.settings.AllowChecklistApproval
      })
    ];
  }

  onWorkflowChanged(value: boolean) {
    this.changeRequestService.headerItems.next();
  }
}
