import { Component, Input, OnDestroy, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, DynamicService, IdentityLookupViewModel, IsoQuestEventManagerService, ISOQuestModuleType, ISOQuestRoles, LookupViewModel, TextConfirmationModalComponent } from 'codera-shared';
import { DragulaService, Group } from 'ng2-dragula';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroup, Choice, ChoiceOutcomeType, OutcomeTypeConfigViewModel, Question } from '../../types';
import { OutcomeConfigEvent } from '../outcome-config/types';

@Component({
    selector: 'manage-checklist-group-general',
    templateUrl: './manage-checklist-group-general.component.html',
    styleUrls: ['./manage-checklist-group-general.component.scss']
})
export class ManageChecklistGroupGeneralComponent implements OnInit, OnDestroy {
    @Input() checklistGroup: ChecklistGroup;
    @Input() submitted: boolean;

    dragulaChoices: string;
    dragulaQuestions: string;
    checklistGroupId: string;
    collapseAllGroups: boolean;
    questionsGroup: Group;
    choicesGroup: Group;
    settings: MOCQuestSettingsViewModel;
    moduleType = ISOQuestModuleType;
    moduleRoles = ISOQuestRoles;
    subscriptions: Subscription[];
    constructor(
        private toastr: ToastrService,
        private translate: TranslateService,
        private dragulaService: DragulaService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private isoQuestEventManager: IsoQuestEventManagerService,
    ) {

    }

    ngOnInit(): void {
        this.subscriptions = [];
        this.dragulaQuestions = "Questions";
        this.dragulaChoices = "Choices";
        this.collapseAllGroups = true;
        this.settings = this.applicationService.getCurrentModuleSettings();

        this.questionsGroup = this.dragulaService.find(this.dragulaQuestions);
        if (this.questionsGroup == null) {
            this.questionsGroup = this.dragulaService.createGroup(this.dragulaQuestions, {
                moves: (el, container, handle) => {
                    return handle.className.includes("question-handle-class");
                }
            });
        }

        this.choicesGroup = this.dragulaService.find(this.dragulaChoices);
        if (this.choicesGroup == null) {
            this.choicesGroup = this.dragulaService.createGroup(this.dragulaChoices, {
                moves: (el, container, handle) => {
                    return handle.className.includes("choice-handle-class");
                },
                accepts: (el, target, source) => {
                    return target.id === source.id;
                }
            });
        }
    }

    ngOnDestroy(): void {
        this.dragulaService.destroy(this.dragulaQuestions);
        this.dragulaService.destroy(this.dragulaChoices);
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
    }

    addQuestion(): void {
        if (this.checklistGroup.Questions.length > 0 && this.checklistGroup.Questions.filter(item => { if (item.Title.length == 0) { return item } }).length)
            return;
            
        let question = new Question('', '', this.checklistGroup.Questions.length);
        question.expanded = true;
        this.checklistGroup.Questions.push(question);
    }

    removeQuestion(qId: number): void {
        this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
            { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
            {
                "modalOptions": {
                    size: "sm"
                }
            },
            {
                successCallback: () => {
                    this.checklistGroup.Questions.splice(qId, 1);
                    this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'));
                    this.isoQuestEventManager.notify('on-response-success', null);
                }
            }
        ], true);
    }

    addChoice(qId: number): void {
        if (this.checklistGroup.Questions[qId].Choices.length > 0 && this.checklistGroup.Questions[qId].Choices.filter(item => { if (item.Title.length == 0) { return item } }).length)
            return;

        let outcomeTypes: OutcomeTypeConfigViewModel[] = [];
        let outcomeType: OutcomeTypeConfigViewModel = new OutcomeTypeConfigViewModel();
        outcomeTypes.push(outcomeType);
        this.checklistGroup.Questions[qId].Choices.push(new Choice('', '', this.checklistGroup.Questions[qId].Choices.length, false, false, true, [], outcomeTypes));
    }

    removeChoice(qId: number, cId: number): void {
        this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
            { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
            {
                "modalOptions": {
                    size: "sm"
                }
            },
            {
                successCallback: () => {
                    this.checklistGroup.Questions[qId].Choices.splice(cId, 1);
                    this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'));
                    this.isoQuestEventManager.notify('on-response-success', null);
                }
            }
        ], true);
    }

    removeOutcomeType(questionIndex: number, choiceIndex: number, outcomeTypeConfigIndex: number) {
        const that = this;
        this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
            { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
            {
                "modalOptions": {
                    size: "sm"
                }
            },
            {
                successCallback: (modalRef) => {
                    that.checklistGroup.Questions[questionIndex].Choices[choiceIndex].OutcomeTypes.splice(outcomeTypeConfigIndex, 1);
                    that.toastr.success(that.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'));
                    modalRef && modalRef.dismiss();
                }
            }
        ], true);
    }

    addOutcomeType(questionIndex: number, choiceIndex: number) {
        this.checklistGroup.Questions[questionIndex].Choices[choiceIndex].OutcomeTypes.push(new OutcomeTypeConfigViewModel());
    }

    closePopover(popover: any, config: OutcomeConfigEvent, outcomeTypeConfig: OutcomeTypeConfigViewModel) {
        if (!config.Cancel) {
            const model = config.Model;
            switch (outcomeTypeConfig.OutcomeType.Id) {
                case ChoiceOutcomeType.None:
                    break;

                case ChoiceOutcomeType.NestedChecklist:
                    outcomeTypeConfig.NestedChecklistConfig = model.NestedChecklistConfig;
                    break;

                case ChoiceOutcomeType.SeverityLevel:
                    outcomeTypeConfig.SeverityLevelConfig = model.SeverityLevelConfig;
                    break;

                case ChoiceOutcomeType.DocumentUpdate:
                    outcomeTypeConfig.UpdateDocumentsConfig = model.UpdateDocumentsConfig;
                    break;

                case ChoiceOutcomeType.NewDocument:
                    outcomeTypeConfig.NewDocumentConfig = model.NewDocumentConfig;
                    break;

                case ChoiceOutcomeType.Specify:
                    outcomeTypeConfig.SpecificationConfig = model.SpecificationConfig;
                    break;

                case ChoiceOutcomeType.Notification:
                    outcomeTypeConfig.NotificationConfig = model.NotificationConfig;
                    break;
            }
        }
        popover.close();
    }

    // #region Validation

    validateQuestions(): string {
        const newList = this.checklistGroup.Questions.map(item => {
            return this.checklistGroup.Questions.filter(item1 => {
                if (item.Title == item1.Title) { return item1 };
            }).length;
        })

        const hasDuplicateQuestionTitles = newList.filter(item => { if (item > 1) { return item } }).length;

        if (!this.checklistGroup.Questions.length || hasDuplicateQuestionTitles)
            return this.translate.instant('[MOCQuest].[Exceptions].Question.ValidationError');

        return '';
    }

    validateChecklistGroupTitle(title: string): string {
        if (title.length === 0) {
            return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
        }

        return '';
    }

    validateQuestion(question: Question): string {
        let title = question.Title;
        if (title.length === 0)
            return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

        let hasDuplicateTitles = this.checklistGroup.Questions.filter(item => {
            if (item.Title == title) {
                return item;
            }
        }).length;

        if (hasDuplicateTitles > 1)
            return this.translate.instant('[MOCQuest].[Exceptions].Question.AlreadyExists');

        if (question.Choices.length === 0)
            return this.translate.instant('[MOCQuest].[Exceptions].Question.EmptyChoices');

        return null;
    }

    validateChoice(title: string, qId: number): string {
        if (title.length === 0) {
            return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
        }

        let hasDuplicateTitles = this.checklistGroup.Questions[qId].Choices.filter(item => {
            if (item.Title == title) {
                return item;
            }
        }).length;

        if (hasDuplicateTitles > 1) {
            return this.translate.instant('[MOCQuest].[Exceptions].Choice.AlreadyExists');
        }

        return null;
    }

    validateChecklist(checklist: IdentityLookupViewModel): string {
        if (!checklist)
            return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

        return null;
    }

    validateSeverityLevel(severityLevel: LookupViewModel): string {
        if (!severityLevel)
            return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

        return null;
    }

    validateOutcomeTypes(outcomeTypes: OutcomeTypeConfigViewModel[]): string {
        if (!Array.isArray(outcomeTypes) || outcomeTypes.length == 0)
            return this.translate.instant("[MOCQuest].[Exceptions].PleaseAddAtLeastOneOutcomeType");

        return null;
    }

    // #endregion Validation

    collapseExpandAllGroups(): void {
        this.collapseAllGroups = !this.collapseAllGroups;
        for (let i = 0; i < this.checklistGroup.Questions.length; i++) {
            this.checklistGroup.Questions[i].expanded = !this.collapseAllGroups;
            for (let j = 0; j < this.checklistGroup.Questions[i].Choices.length; j++) {
                this.checklistGroup.Questions[i].Choices[j].expanded = !this.collapseAllGroups;
            }
        }
    }

    onQuestionsOrderChange(questions: Question[]) {
        questions.forEach((question, index) => {
            question.Position = index;
        });
        this.checklistGroup.Questions = questions;
    }

    onChoicesOrderChange(choices: Choice[], question: Question) {
        choices.forEach((choice, index) => {
            choice.Position = index;
        });
        question.Choices = choices;
    }

    onDefaultChoiceClick(question: Question, choice: Choice) {
        if (!choice.IsDefault) {
            question.Choices.forEach((item) => {
                item.IsDefault = false;
            });
        }
        choice.IsDefault = !choice.IsDefault;
    }
}
