import { Component, OnDestroy, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, CancelAction, DynamicService, FluentTabConfig, HeaderButtonItem, HeaderItemsConfig, InputControl, ModalFormComponent, ModalFormConfig, RenderButtonsPosition, RowControl, SubmitAction } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroupService } from '../checklist-group.service';
import { ChecklistGroup, ChoiceOutcomeType, CopyChecklistGroupViewModel } from '../types';
import { ManageChecklistGroupApprovalComponent } from './manage-checklist-group-approval/manage-checklist-group-approval.component';
import { ManageChecklistGroupGeneralComponent } from './manage-checklist-group-general/manage-checklist-group-general.component';
import { ManageChecklistGroupService } from './manage-checklist-group.service';
@Component({
    selector: 'manage-checklist-group-page',
    templateUrl: './manage-checklist-group.page.html',
    styleUrls: ['./manage-checklist-group.page.scss'],
    providers: [ManageChecklistGroupService]
})
export class ManageChecklistGroupPage implements OnInit, OnDestroy {
    submitted: boolean;
    serverError: boolean;
    errorMessage: string;
    checklistGroupId: string;
    checklistGroup: ChecklistGroup;
    headerItemsConfig: HeaderItemsConfig;
    fluentTabs: FluentTabConfig[];
    pageTitle: string;
    settings: MOCQuestSettingsViewModel;
    subscriptions: Subscription[];

    renderButtonsTypes = RenderButtonsPosition;

    constructor(
        private title: Title,
        private router: Router,
        private toastr: ToastrService,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private checklistGroupService: ChecklistGroupService,
        private manageChecklistGroupService: ManageChecklistGroupService
    ) { }

    ngOnDestroy(): void {
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
    }

    ngOnInit(): void {
        const that = this;

        this.subscriptions = [];
        this.checklistGroup = new ChecklistGroup('', '', [], true);
        this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;

        this.translate.get(this.route.snapshot.data["pageTitle"]).subscribe((translation) => {
            this.title.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translation}`);
            this.pageTitle = translation;
        });

        this.fluentTabs = [
            new FluentTabConfig({
                componentClass: ManageChecklistGroupGeneralComponent,
                title: "[ISOQuest].[Common].General",
                data: [
                    { checklistGroup: that.checklistGroup },
                    { submitted: that.submitted }
                ]
            }),
            new FluentTabConfig({
                componentClass: ManageChecklistGroupApprovalComponent,
                title: "[ISOQuest].[Workflow].Common.Approvals",
                data: [
                    { checklistGroup: that.checklistGroup },
                    { submitted: that.submitted }
                ],
                show: false
            }),
        ];

        this.headerItemsConfig = new HeaderItemsConfig([
            new HeaderButtonItem({
                name: 'back',
                icon: 'fas fa-reply',
                label: '[MOCQuest].[ChecklistGroups].Button.BackToList',
                action: that.cancel
            }),
            new HeaderButtonItem({
                name: "copy",
                icon: "fas fa-copy",
                label: "[ISOQuest].[Common].Buttons.Copy",
                action: that.copy,
                show: false
            }),
            new HeaderButtonItem({
                name: 'save',
                icon: 'fas fa-save',
                label: '[ISOQuest].[Common].Buttons.Save',
                action: that.saveOrUpdate,
                show: true
            })
        ]);

        this.subscriptions.push(
            this.manageChecklistGroupService.checklistGroup$.subscribe(checklistGroup => {
                this.checklistGroup = checklistGroup;
                this.updateSubmitted(this.fluentTabs, false);
                this.updateChecklistGroup(this.fluentTabs, checklistGroup);
                this.headerItemsConfig.getItemByName("copy").show = checklistGroup.Id != null;
                const tab = this.fluentTabs.find(x => x.componentClass == ManageChecklistGroupApprovalComponent);
                if (tab)
                    tab.show = this.settings.AllowChecklistApproval && checklistGroup.Id != null;
            })
        );

        this.subscriptions.push(
            this.route.paramMap.subscribe(paramMap => {
                this.checklistGroupId = paramMap.get("id");
                if (this.checklistGroupId) {
                    this.manageChecklistGroupService.fetchChecklistGroup(this.checklistGroupId);
                }
            })
        );

        this.subscriptions.push(
            this.manageChecklistGroupService.saveSubject.subscribe(() => {
                this.saveOrUpdate();
            })
        )
    }

    cancel = () => {
        this.router.navigate(['/system-controls/checklist-groups']);
    }

    saveOrUpdate = () => {
        this.submitted = true;
        this.updateSubmitted(this.fluentTabs, true);

        if (!this.isValidChecklistGroup(this.checklistGroup)) {
            return;
        }

        this.checklistGroupService.saveOrUpdate(this.checklistGroup).subscribe(
            (result: string | boolean) => {
                if (typeof result === 'string') {
                    this.updateSubmitted(this.fluentTabs, false);
                    this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'));
                    this.router.navigate(['/system-controls/edit-checklist-group', result]);
                } else {
                    
                    this.manageChecklistGroupService.fetchChecklistGroup(this.checklistGroupId);
                    this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemUpdated'));
                }
            },
            (error) => {
                this.serverError = true;
                this.errorMessage = error.error.Errors[''][0];
            }
        );
    }

    copy = () => {
        const that = this;

        const model = new CopyChecklistGroupViewModel();
        model.ChecklistGroupId = this.checklistGroup.Id;

        const modalConfig = new ModalFormConfig(
            "[MOCQuest].[ChecklistGroup].CopyChecklistGroup",
            [
                new RowControl([
                    new InputControl({
                        field: "Title",
                        type: "text",
                        label: "[ISOQuest].[Common].Title"
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    css: "btn btn-primary",
                    callback: () => {
                        that.checklistGroupService.copy(model).subscribe(id => {
                            that.router.navigate(["/system-controls/edit-checklist-group", id]);
                        });
                    }
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    css: "btn btn-default",
                    callback: () => {

                    }
                })
            ]
        );

        that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
            { model: model },
            { config: modalConfig },
            {
                modalOptions: {
                    size: "md"
                }
            }
        ]);
    }

    onTabChange(event): void {
        this.updateChecklistGroup(this.fluentTabs, this.checklistGroup);
    }

    updateChecklistGroup(fluentTabs: FluentTabConfig[], checklistGroup: ChecklistGroup) {
        fluentTabs.forEach(fluentTab => {
            fluentTab.componentInstance.checklistGroup = checklistGroup;
            const checklistGroupData = fluentTab.data && fluentTab.data[0] && fluentTab.data[0].checklistGroup;
            if (checklistGroupData)
                fluentTab.data[0].checklistGroup = checklistGroup;
        });
    }

    updateSubmitted(fluentTabs: FluentTabConfig[], value: boolean) {
        fluentTabs.forEach(fluentTab => {
            fluentTab.componentInstance.submitted = value;
            const submittedData = fluentTab.data && fluentTab.data[0] && fluentTab.data[0].submitted;
            if (submittedData)
                fluentTab.data[0].submitted = value;
        });
    }

    isValidChecklistGroup(checklistGroup: ChecklistGroup): boolean {
        let hasQuestionWithoutChoices = false;
        let hasEmptyChecklist = false;
        let hasChoiceWithoutTitle = false;

        if (!checklistGroup.Questions.length)
            return;

        for (let i = 0; i < checklistGroup.Questions.length; i++) {
            let question = checklistGroup.Questions[i];

            if (!question.Choices.length) {
                checklistGroup.Questions[i].expanded = true;
                hasQuestionWithoutChoices = true;
            }

            question.Choices.forEach((choice, index) => {
                choice.OutcomeTypes.forEach((outcomeType) => {
                    if (outcomeType.OutcomeType.Id == ChoiceOutcomeType.NestedChecklist && !outcomeType.NestedChecklistConfig.PickerChecklist) {
                        checklistGroup.Questions[i].expanded = true;
                        checklistGroup.Questions[i].Choices[index].expanded = true;
                        hasEmptyChecklist = true;
                    }
                });
            });

            question.Choices.filter((item, index) => {
                if (item.Title.length == 0) {
                    checklistGroup.Questions[i].expanded = true;
                    checklistGroup.Questions[i].Choices[index].expanded = true;
                    hasChoiceWithoutTitle = true;
                }
            }).length;
        }

        const hasQuestionWithoutTitle = checklistGroup.Questions.filter(item => { if (item.Title.length == 0) { return item; } }).length;

        const newList = checklistGroup.Questions.map(item => {
            return checklistGroup.Questions.filter(item1 => {
                if (item.Title == item1.Title) { return item1 };
            }).length;
        })

        const hasDuplicateQuestionTitles = newList.filter(item => { if (item > 1) { return item } }).length;

        let hasEmptyChoiceOutcomes: boolean = false;
        for (let i = 0; i < checklistGroup.Questions.length; i++) {
            const question = checklistGroup.Questions[i];
            for (let j = 0; j < question.Choices.length; j++) {
                const choice = question.Choices[j];
                if (!Array.isArray(choice.OutcomeTypes) || choice.OutcomeTypes.length == 0) {
                    checklistGroup.Questions[i].expanded = true;
                    checklistGroup.Questions[i].Choices[j].expanded = true;
                    hasEmptyChoiceOutcomes = true;
                }
            }
        }

        let hasEmptySeverityLevel: boolean = false;
        for (let i = 0; i < checklistGroup.Questions.length; i++) {
            const question = checklistGroup.Questions[i];
            for (let j = 0; j < question.Choices.length; j++) {
                const choice = question.Choices[j];
                for (let k = 0; k < choice.OutcomeTypes.length; k++) {
                    const outcomeType = choice.OutcomeTypes[k];
                    if (outcomeType.OutcomeType.Id == ChoiceOutcomeType.SeverityLevel) {
                        if (outcomeType.SeverityLevelConfig == null || outcomeType.SeverityLevelConfig.SeverityLevel == null) {
                            checklistGroup.Questions[i].expanded = true;
                            checklistGroup.Questions[i].Choices[j].expanded = true;
                            hasEmptySeverityLevel = true;
                        }
                    }
                }
            }
        }

        if (hasQuestionWithoutChoices
            || hasQuestionWithoutTitle
            || hasDuplicateQuestionTitles
            || hasEmptyChecklist
            || hasChoiceWithoutTitle
            || hasEmptyChoiceOutcomes
            || hasEmptySeverityLevel)
            return false;

        return true;
    }
}
