import { AfterViewInit, Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, DynamicService, FluentTabConfig, FluentTableComponent, HeaderButtonItem, HeaderComponentItem, HeaderItemsConfig, LookupViewModel, RenderButtonsPosition } from "codera-shared";
import { ChangeRequestService } from '../change-request/change-request.service';
import { CreateUpdateChangeRequestComponent } from '../change-request/create-update/create-update-change-request.component';
import { ChangeRequestPickerComponent } from '../change-request/picker/change-request-picker/change-request-picker.component';
import { ChangeRequestPermissionsModel, DashboardCountsViewModel } from '../change-request/types';

@Component({
    templateUrl: './dashboard.page.html',
    styleUrls: ['./dashboard.page.scss']
})
export class DashboardPage implements OnInit, AfterViewInit {
    pageTitle: string;
    renderButtonsTypes = RenderButtonsPosition;
    tabsConfig: FluentTabConfig[];
    headerItemsConfig: HeaderItemsConfig;
    msie: boolean;
    activeTab: number;
    dashboardCounts: DashboardCountsViewModel;

    constructor(
        private router: Router,
        private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private changeRequestService: ChangeRequestService
    ) {
        this.activeTab = 0;
    }
    ngAfterViewInit(): void {
    }

    ngOnInit() {
        const that = this;
        this.pageTitle = this.route.snapshot.data["pageTitle"];
        let translatedTitle;

        this.translate.get(this.pageTitle).subscribe(translated => {
            translatedTitle = translated
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translatedTitle}`);
        });

        this.getDashboardCounts();

        this.setupHeaderItems();

        this.tabsConfig = [
            //new FluentTabConfig({
            //title: (tab) => {
            //    return `<span>${that.translate.instant("[CIPQuest].[Common].MyActiveProjects")}</span> &nbsp;<span class='badge badge-danger' style='border-radius: 50% !important; min-width: 21px !important;'>${that.MyActiveProjectsCount}</span>`;
            //},
            //show: true,
            //componentClass: MyActiveProjectsPage,
            //data: [
            //    { "disableHeader": true }
            //]
            //}),

        ];
    }

    getDashboardCounts(): void {
        this.changeRequestService.getDashboardCounts().toPromise().then(response => {
            this.dashboardCounts = response;
        });
    }

    setupHeaderItems() {
        const that = this;

        this.changeRequestService.getPermissions().subscribe((permissions: ChangeRequestPermissionsModel) => {
            this.headerItemsConfig = new HeaderItemsConfig([
                new HeaderButtonItem({
                    name: "my-facility-change-request",
                    label: "[MOCQuest].[SiteMap].MyFacilityChangeRequests",
                    icon: "fab fa-gg",
                    action: () => { this.router.navigate(["/change-request/my-facility-change-requests"]); },
                    show: this.applicationService.getCurrentSite().IsMultiSite
                }),
                new HeaderComponentItem({
                    componentClass: ChangeRequestPickerComponent,
                    name: "search-change-requset",
                    label: "",
                    data: [
                        {
                            modelChange: (model: LookupViewModel) => {
                                if (!Array.isArray(model))
                                    that.router.navigate(["/change-request/details", model.Id]);
                            }
                        },
                        { placeholder: "[MOCQuest].[ChangeRequest].Dashboard.SearchByTitleOrChangeRequestId" },
                        { searchMode: true }
                    ]
                }),
                new HeaderButtonItem({
                    name: "new-change-request",
                    icon: "fa fa-plus",
                    label: "[MOCQuest].[ChangeRequest].New",
                    action: this.summonDialog.bind(this),
                    show: permissions.CanCreateChangeRequest
                })
            ]);
        });
    }

    summonDialog() {
        this.dynamicService.addComponent(this.viewContainer, CreateUpdateChangeRequestComponent, [{}], true);
    }
}
