import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';

@Component({
  selector: 'tms-task-color-preview-color',
  templateUrl: './tms-task-color-preview-color.component.html',
  styleUrls: ['./tms-task-color-preview-color.component.scss']
})
export class TmsTaskColorPreviewColorComponent implements OnInit {
  private _color: IdentityLookupViewModel;
  @Input()
  set color(value: IdentityLookupViewModel) {
    this._color = value;
    this.setColorTitle();
  }

  @Output()
  onColorPreviewChange: EventEmitter<string>;

  colorTitle: string;
  constructor() {
    this.onColorPreviewChange = new EventEmitter<string>();
  }

  ngOnInit() {
  }

  setColorTitle(): void {
    if (this._color == null)
      return;

    if (this._color.Name === '#FF0000') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Red";
    } else if (this._color.Name === '#FFFF00') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Yellow";
    } else if (this._color.Name === '#008000') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Green";
    } else if (this._color.Name === '#FFC0CB') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Pink";
    } else if (this._color.Name === '#0000FF') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Blue";
    } else if (this._color.Name === '#808080') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Gray";
    }

    else if (this._color.Name === '#800000') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Maroon";
    } else if (this._color.Name === '#00FF00') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Lime";
    } else if (this._color.Name === '#808000') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Olive";
    } else if (this._color.Name === '#00FFFF') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Aqua";
    } else if (this._color.Name === '#000080') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Navy";
    } else if (this._color.Name === '#C0C0C0') {
      this.colorTitle = "[ISOQuest].[Common].Checklist.Colors.Silver";
    }
    this.onColorPreviewChange.emit(this.colorTitle);
  }
}
