export class ChecklistGroup {
    constructor(
        public Id?: string,
        public Title?: string,
        public Questions?: Question[],
        public Status?: boolean,
        public expanded?: boolean,
        public AllowOutcome: boolean = true,
        public Position?: number,
        public Assignees?: [],
        public ApprovalEnabled?: boolean,
        public ApprovalStatus?: IdentityLookupViewModel,
        public AnsweredQuestions?: number,
        public TotalQuestions?: number,
        public CanManageAssignees?: boolean,
        public CanAnswer?: boolean,
        public CanRequestApproval?: boolean

    ) {
        this.Questions = [];
        this.Assignees = [];
    }
}

