export class DocumentActivityModel extends LookupViewModel {
    constructor(
        public Id: string,
        public Title: string,
        public Status: boolean,
        public RootDocumentId?: string,
        public ReferenceNumber?: string,
        public RevisionNumber?: number,
        public ContentType?: string,
        public Department?: string,
        public DocumentType?: string,
        public IssuedDate?: Date,
        public Facility?: FacilityViewModel
    ) {
        super(Id, Title, Status);
    }
}

export class OutcomeTypeConfigViewModel {
    constructor(
        public Id?: string,
        public OutcomeType?: IdentityLookupViewModel,
        public NestedChecklistConfig?: ChoiceNestedChecklistConfig,
        public SeverityLevelConfig?: ChoiceSeverityLevelConfig,
        public UpdateDocumentsConfig?: ChoiceUpdateDocumentsConfig,
        public NewDocumentConfig?: ChoiceNewDocumentConfig,
        public SpecificationConfig?: ChoiceSpecificationConfig,
        public NotificationConfig?: ChoiceNotificationConfig
    ) {
        this.NestedChecklistConfig = new ChoiceNestedChecklistConfig();
        this.SeverityLevelConfig = new ChoiceSeverityLevelConfig();
        this.UpdateDocumentsConfig = new ChoiceUpdateDocumentsConfig();
        this.NewDocumentConfig = new ChoiceNewDocumentConfig();
        this.SpecificationConfig = new ChoiceSpecificationConfig();
        this.NotificationConfig = new ChoiceNotificationConfig();
    }
}

