export enum ChoiceOutcomeType {
    None = 1,
    NestedChecklist = 2,
    SeverityLevel = 3,
    DocumentUpdate = 4,
    NewDocument = 5,
    Specify = 6,
    Notification = 7
}

export enum AnalysisObjectType {
    Analysis,
    PreStartupReviewAnalysis
}
