import { Component, OnInit, Output, Input, EventEmitter, HostListener } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { ChoiceOutcomeType, OutcomeTypeConfigViewModel } from '../types';

@Component({
    selector: 'choice-outcome-type-picker',
    templateUrl: './choice-outcome-type-picker.component.html'
})
export class ChoiceOutcomeTypePickerComponent implements OnInit {
    @Input() model: IdentityLookupViewModel;
    @Input() label: string | undefined;
    @Input() disableAnnotations: boolean;
    @Input() isRequired: boolean | undefined;
    @Input() isDisabled: boolean | undefined;

    private _selectedOutcomeTypes: OutcomeTypeConfigViewModel[];
    @Input() set selectedOutcomeTypes(value: OutcomeTypeConfigViewModel[]) {
        this._selectedOutcomeTypes = value;
    }
    get selectedOutcomeTypes() {
        return this._selectedOutcomeTypes;
    }

    @Output() modelChange: EventEmitter<any>;

    staticData: IdentityLookupViewModel[];
    availableOutcomeTypes: IdentityLookupViewModel[];

    constructor() {
        this.modelChange = new EventEmitter();
        this.staticData = [
            new IdentityLookupViewModel(ChoiceOutcomeType.None, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.None', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.NestedChecklist, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.NestedChecklist', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.SeverityLevel, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.SeverityLevel', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.DocumentUpdate, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.DocumentUpdate', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.NewDocument, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.NewDocument', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.Specify, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.Specify', true),
            new IdentityLookupViewModel(ChoiceOutcomeType.Notification, '[MOCQuest].[ChecklistGroup].ChoiceOutcome.Notification', true)
        ];
        this.availableOutcomeTypes = [].concat(this.staticData);
    }

    ngOnInit() {
        if (this.isRequired && !this.model)
            this.onSelect(this.staticData[0]);

        this.filterStaticDataBasedOnCurrentlySelectedOutcomeTypes();
    }

    onSelect(newValue: any) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }

    private filterStaticDataBasedOnCurrentlySelectedOutcomeTypes(): void {
        if (Array.isArray(this.availableOutcomeTypes)) {
            let severityLevelSelected = this.selectedOutcomeTypes.map(x => x.OutcomeType.Id).find(x => x === ChoiceOutcomeType.SeverityLevel) !== undefined;
            if (severityLevelSelected)
                this.staticData = this.availableOutcomeTypes.filter(x => x.Id !== ChoiceOutcomeType.SeverityLevel);
            else
                this.staticData = [].concat(this.availableOutcomeTypes);
        }
    }

    @HostListener("click")
    handleFocus() {
        this.filterStaticDataBasedOnCurrentlySelectedOutcomeTypes();
    }
}
