import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { ApplicationService, CancelAction, ComponentControl, DynamicService, InputControl, IqPersonnelPickerComponent, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextareaControl } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { SendTaskLinkOrEmailViewModel } from '../../types';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'send-as-link',
  templateUrl: './send-task-as-link.component.html',
  styleUrls: ['./send-task-as-link.component.scss']
})
export class SendTaskAsLinkComponent implements OnInit {
  model: SendTaskLinkOrEmailViewModel;

  @Input()
  taskId: string;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private taskManagementSystemService: TaskManagementSystemService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) { }

  ngOnInit() {
    const that = this;

    if (!this.taskId)
      throw new Error("taskId is required");

    this.model = new SendTaskLinkOrEmailViewModel();
    let currentUser = this.applicationService.getCurrentUser().FullName;
    let currentModule = this.applicationService.getCurrentModuleKey();
    this.model.SentByPersonnel = currentUser;

    const modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].SendAsLink",
      [
        new RowControl([
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "Personnel",
            data: [
              { label: "[ISOQuest].[SiteMap].SystemControls.Personel" },
              { isMultiple: true },
              { type: "All" },
              { targetModule: currentModule },
              { isRequired: true }
            ]
          })
        ]),
        new RowControl([
          new TextareaControl({
            field: "Comment",
            label: "[ISOQuest].[Common].Comment"
          })
        ]),
        new RowControl([
          new InputControl({
            field: "SentByPersonnel",
            label: "[ISOQuest].[Common].SentBy",
            type: "text",
            disabled: true
          })
        ])
      ],
      [new SubmitAction({
        field: "Submit",
        value: "[ISOQuest].[Common].Buttons.Submit",
        callback(formSnapshot) {

          that.model.TaskId = that.taskId;
            that.model.SelectedPersonelIds = [];
            var personnel = that.model.Personnel.filter(x => !x.IsGroup);
            var groups = that.model.Personnel.filter(x => x.IsGroup);
            that.model.SelectedPersonelIds = personnel.map(x => x.Personel.Id);
            that.model.SelectedGroupIds = groups.map(x => x.Group.Id);
          that.taskManagementSystemService.sendTaskAsLink(that.model).subscribe(response => {
            that.translate.get("[ISOQuest].[TaskManagementSystem].LinkSuccessfullySent").subscribe(translation => {
              that.toastr.success(translation);
            })
          });
        },
        css: "btn btn-primary"
      }),
      new CancelAction({
        field: "Cancel",
        value: "[ISOQuest].[Common].Buttons.Cancel",
        callback() { },
        css: "btn btn-default"
      })]
    );

    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
      [
        { config: modalFormConfig },
        {
          modalOptions: {
            size: "sm"
          }
        },
        { model: this.model }
      ]);
  }

}
