import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { LookupViewModel } from 'codera-shared';

@Component({
    selector: 'functional-location-picker',
    templateUrl: './functional-location-picker.component.html',
    styleUrls: ['./functional-location-picker.component.scss']
})
export class FunctionalLocationPickerComponent implements OnInit {
    private _facilityId: string;

    @Input() model: LookupViewModel | LookupViewModel[];
    @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

    @Input() label: string;
    @Input() controllerName: string;
    @Input() placeholder: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;

    @Input() set facilityId(value: string) {
        this._facilityId = value;
        if (value)
            this.controllerName = "/FacilityMapItem/GetLocationsByFacility?facilityId=" + value;
    }

    get facilityId() {
        return this._facilityId;
    }

    constructor() {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        if (!this.label)
            this.label = "[ISOQuest].[Common].FunctionalLocations";

        this.controllerName = "/FacilityMapItem/GetLocationsByFacility";
        if (this.facilityId) {
            this.controllerName = "/FacilityMapItem/GetLocationsByFacility?facilityId=" + this.facilityId;
        }
    }

    onChange(newValue: LookupViewModel | LookupViewModel[]) {
        this.modelChange.emit(this.model);
    }
}
