import { Component, ComponentRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, DynamicService, FacilityInfoComponent, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IsoQuestEventManagerService, LookupViewModel } from 'codera-shared';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChangeRequestTagInfoComponent } from '../../../info-components/change-request-tag-info/change-request-tag-info.component';
import { ChangeRequestService } from '../../change-request.service';
import { CreateUpdateChangeRequestComponent } from '../../create-update/create-update-change-request.component';
import { ChangeRequestStatus, ChangeRequestViewModel, DurationType, MaturityStatus } from '../../types';

@Component({
    selector: 'app-request',
    templateUrl: './request.component.html',
    styleUrls: ['./request.component.scss']
})
export class RequestComponent implements OnInit, OnDestroy {
    @Input()
    changeRequestId: string;

    @Output()
    onChangeRequestLoad: EventEmitter<ChangeRequestViewModel>;

    model: ChangeRequestViewModel;
    isMultSite: boolean;
    durationType = DurationType;
    maturityStatus = MaturityStatus;
    changeRequestStatus = ChangeRequestStatus;
    enableProjectNumber: boolean;
    enableAdditionalImpactedFacilities: boolean;
    functionalLocationsEnabled: boolean;

    constructor(
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private changesService: ChangeRequestService,
        private applicationService: ApplicationService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.onChangeRequestLoad = new EventEmitter<ChangeRequestViewModel>();
    }

    ngOnDestroy(): void {
    }

    ngOnInit() {
        const that = this;
        this.getById();
        this.isMultSite = this.applicationService.getCurrentSite().IsMultiSite;
        this.isoQuestEventManager.subscribe(this, "fetch-change-request", () => {
            that.getById();
        });
        this.enableProjectNumber = this.applicationService.getCurrentModuleSettings().EnableProjectNumber;
        this.enableAdditionalImpactedFacilities = this.applicationService.getCurrentModuleSettings().AllowAdditionalImpactedFacilities;
        this.functionalLocationsEnabled = (this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel).FunctionalLocationsEnabled;
    }

    getById() {
        this.changesService.getById(this.changeRequestId).subscribe((response: ChangeRequestViewModel) => {
            this.model = response;
            this.onChangeRequestLoad.emit(this.model);
        });
    }

    editChange() {
        const component = this.dynamicService.addComponent(this.viewContainer, CreateUpdateChangeRequestComponent, [
            { "changeRequestId": this.model.Id },
            {
                "onCreateUpdate": () => {
                    this.getById();
                }
            }], true) as ComponentRef<CreateUpdateChangeRequestComponent>;
    }

    showFacilityDetails(id: string) {
        this.dynamicService.addComponent(this.viewContainer, FacilityInfoComponent,
            [{ "facilityId": id }]);
    }

    showPersonnelDetails(personnelId: string): void {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelInfoComponent, [
            { "personnelId": personnelId },
            { "modalOptions": { size: "lg" } }
        ], false);
    }

    showPersonnelGroupDetails(groupId: string): void {
        this.dynamicService.addComponent(this.viewContainer, IqPersonnelGroupInfoComponent, [
            { "groupId": groupId },
            { "modalOptions": { size: "sm" } }
        ], false);
    }

    getSeverityLevelTranslation(level: string) {
        let key = `[MOCQuest].[SystemControls].SeverityLevel.Level.${level}.Label.${this.applicationService.getCurrentSiteKey()}`;
        let defaultKey = `[MOCQuest].[SystemControls].SeverityLevel.Level.${level}.Label.Default`;
        let severityLevelTranslation = "";
        this.translate.get(key).subscribe(translation => {
            if (translation === key) {
                this.translate.get(defaultKey).subscribe(defaultTranslation => {
                    severityLevelTranslation = defaultTranslation;
                });
            } else
                severityLevelTranslation = translation;
        });
        return severityLevelTranslation;
    }

    changeRequestLinkedForTag(tag: LookupViewModel): void {
        this.dynamicService.addComponent(this.viewContainer, ChangeRequestTagInfoComponent, [
            { tag: tag },
            { modalOptions: { size: "md" } }
        ], false);
    }
}
