import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DepartmentAdministratorType, InsufficientPermissionsPage, ISOQuestModuleType, ISOQuestRoles, MyProfileComponent, PageNotFoundComponent, PersonnelGroupPage, RetakeSelfProvisioningPage } from 'codera-shared';
import { AllChangeRequestPage } from './change-request/all-change-request/all-changes-request.page';
import { ChangeRequestDetailsComponent } from './change-request/details/change-request-details.component';
import { MyChangeRequestPage } from './change-request/my-change-request/my-change-request.page';
import { MyFacilityChangeRequestPage } from './change-request/my-facility-change-request/my-facility-change-request.page';
import { MyOverdueChangeRequestPage } from './change-request/my-overdue-change-request/my-overdue-change-request.page';
import { MyWaitingForApprovalChangeRequestComponent } from './change-request/my-waiting-for-approval-change-request/my-waiting-for-approval-change-request.component';
import { DashboardPage } from './dashboard/dashboard.page';
import { SettingsComponent } from './settings/settings.component';
import { ChangeRequestTagPage } from './system-controls/change-request-tag/change-request-tag.page';
import { ChangeRequestTypePage } from './system-controls/change-request-type/change-request-type.page';
import { ChecklistGroupsPage } from './system-controls/checklist-groups/checklist-groups.page';
import { ManageChecklistGroupPage } from './system-controls/checklist-groups/manage-checklist-group/manage-checklist-group.page';
import { FollowUpResultPage } from './system-controls/follow-up-result/follow-up-result.page';
import { MethodOfFollowUpPage } from './system-controls/method-of-follow-up/method-of-follow-up.page';
import { ReasonTypePage } from './system-controls/reason-type/reason-type.page';
import { SeverityLevelPage } from './system-controls/severity-level/severity-level.page';
import { CreateEditWorkflowMatrixComponent } from './system-controls/workflow-matrix/create-edit-workflow-matrix/create-edit-workflow-matrix.component';
import { WorkflowMatrixPage } from './system-controls/workflow-matrix/workflow-matrix.page';
import { ChangeRequestPssrTasksComponent } from './tasks/change-request-pssr-tasks/change-request-pssr-tasks.component';
import { ChangeRequestImplementationTasksComponent } from './tasks/implementation/implementation.component';

const routes: Routes = [
    { path: "", redirectTo: "dashboard", pathMatch: "full" },
    {
        path: "dashboard",
        component: DashboardPage,
        data: {
            pageTitle: "[ISOQuest].[Dashboard].PageTitle",
            show: true,
            icon: "fa fa-home"
        }
    },
    {
        path: "change-request",
        data: {
            pageTitle: "[MOCQuest].[SiteMap].ChangeRequests",
            show: true,
            icon: "fab fa-gg",
            // roles: ["ISOQuestSuperAdministrator"]
        },
        children: [
            //{
            //    path: "all-overdue-changes",
            //    component: AllOverdueProjectsPage,
            //    data: {
            //        pageTitle: "[MOCQuest].[SiteMap].AllOverdueChanges", -- nelokalizirana
            //        show: false
            //    }
            //},
            {
                path: "my-change-requests",
                component: MyChangeRequestPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].MyChangeRequests",
                    show: true
                }
            },
            {
                path: "my-overdue-changes",
                component: MyOverdueChangeRequestPage,
                data: {
                    pageTitle: "[MOCQuest].[ChangeRequest].MyOverdueChangeRequests",
                    show: true
                }
            },
            {
                path: "my-facility-change-requests",
                component: MyFacilityChangeRequestPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].MyFacilityChangeRequests",
                    show: true
                }
            },
            {
                path: "my-waiting-for-approval-change-requests",
                component: MyWaitingForApprovalChangeRequestComponent,
                data: {
                    pageTitle: "[MOCQuest].[ChangeRequest].MyWaitingForApprovalChangeRequests",
                    show: true
                }
            },
            {
                path: "all-change-requests",
                component: AllChangeRequestPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].AllChangeRequests",
                    show: true
                }
            },
            {
                path: "details/:id",
                component: ChangeRequestDetailsComponent,
                data: {
                    pageTitle: "[MOCQuest].[ChangeRequest].ChangeRequestDetails",
                    show: false
                }
            }
        ]
    },
    {
        path: "reports",
        data: {
            pageTitle: "[ISOQuest].[Common].Reports",
            show: true,
            icon: "fad fa-chart-pie"
        },
        children: []
    },
    {
        path: "tasks",
        data: {
            pageTitle: "[ISOQuest].[Common].TMS.Tasks",
            show: true,
            icon: "fa fa-tasks"
        },
        children: [
            {
                path: "implementation-tasks",
                component: ChangeRequestImplementationTasksComponent,
                data: {
                    pageTitle: "[MOCQuest].[ChangeRequest].Status.Implementation",
                    show: true
                },
            },
            {
                path: "pssr-tasks",
                component: ChangeRequestPssrTasksComponent,
                data: {
                    pageTitle: "[MOCQuest].[ChangeRequest].Status.Approval",
                    show: true
                },
            }
        ]
    },
    {
        path: "system-controls",
        data: {
            pageTitle: "[ISOQuest].[SiteMap].SystemControls",
            show: false,
            icon: "fa fa-list"
        },
        children: [
            {
                path: "change-request-type",
                component: ChangeRequestTypePage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.ChangeRequestTypes",
                    show: true,
                }
            },
            {
                path: "reason-type",
                component: ReasonTypePage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.ReasonTypes",
                    show: true,
                }
            },
            {
                path: "severity-levels",
                component: SeverityLevelPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SeverityLevels",
                    show: true
                }
            },
            {
                path: "workflow-matrix",
                component: WorkflowMatrixPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].WorkflowMatrices",
                    show: true
                },
                // pathMatch: "full",
                // children: [
                //     {
                //         path: "edit/:id",
                //         component: CreateEditWorkflowMatrixComponent,
                //         data: {
                //             pageTitle: "[MOCQuest].[SystemControls].WorkflowMatrix.Edit",
                //             show: false
                //         },
                //         pathMatch: "full"
                //     },
                //     {
                //         path: "create",
                //         component: CreateEditWorkflowMatrixComponent,
                //         data: {
                //             pageTitle: "[MOCQuest].[SystemControls].WorkflowMatrix.New",
                //             show: false
                //         },
                //         pathMatch: "full"
                //     }
                // ]
            },
            {
                path: "edit-workflow-matrix/:id",
                component: CreateEditWorkflowMatrixComponent,
                data: {
                    pageTitle: "[MOCQuest].[SystemControls].WorkflowMatrix.Edit",
                    show: false
                }
            },
            {
                path: "create-workflow-matrix",
                component: CreateEditWorkflowMatrixComponent,
                data: {
                    pageTitle: "[MOCQuest].[SystemControls].WorkflowMatrix.New",
                    show: false
                }
            },
            {
                path: "checklist-groups",
                component: ChecklistGroupsPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].ChecklistGroups",
                    show: true
                }
            },
            {
                path: "personnel-group",
                component: PersonnelGroupPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.PersonnelGroups",
                    show: true,
                    moduleType: ISOQuestModuleType.MOCQuest,
                    departmentAdministratorType: DepartmentAdministratorType.MOCQuest,
                    departmentPickerRoles: [ISOQuestRoles.ISOQuestSuperAdministrator, ISOQuestRoles.MOCQuestAdministrator]
                }
            },
            {
                path: "new-checklist-group",
                component: ManageChecklistGroupPage,
                data: {
                    pageTitle: "[MOCQuest].[ChecklistGroup].New",
                    show: false
                }
            },
            {
                path: "edit-checklist-group/:id",
                component: ManageChecklistGroupPage,
                data: {
                    pageTitle: "[MOCQuest].[Common].Buttons.ChecklistGroup.Edit",
                    show: false
                }
            },
            {
                path: "follow-up-result",
                component: FollowUpResultPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.FollowUpResults",
                    show: true
                }
            },
            {
                path: "method-of-follow-up",
                component: MethodOfFollowUpPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.MethodsOfFollowUp",
                    show: true
                }
            },
            {
                path: "change-request-tag",
                component: ChangeRequestTagPage,
                data: {
                    pageTitle: "[MOCQuest].[SiteMap].SystemControls.ChangeRequestTags",
                    show: true
                }
            },
        ]
    },
    {
        path: "settings",
        component: SettingsComponent,
        data: {
            pageTitle: "[ISOQuest].[SiteMap].Settings",
            show: true,
            icon: "fas fa-cog",
            roles: ["ISOQuestSuperAdministrator", "MOCQuestAdministrator"]
        }
    },

    {
        path: "my-profile",
        component: MyProfileComponent,
        data: {
            pageTitle: "[ISOQuest].[Common].Localization.MyProfile",
            show: false
        }
    },
    {
        path: "retake-self-provisioning",
        component: RetakeSelfProvisioningPage,
        data: {
            pageTitle: "[ISOQuest].[Common].RetakeSelfProvisioning",
            show: false,
        }
    },

    { path: "insufficient-permissions", component: InsufficientPermissionsPage },
    { path: "**", component: PageNotFoundComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { anchorScrolling: 'enabled', scrollOffset: [0, 64] })],
    exports: [RouterModule]
})
export class AppRoutingModule { }
