import { Injectable } from "@angular/core";
import { IdentityLookupViewModel, LookupViewModel } from "codera-shared";
import { BehaviorSubject, Observable, Subject } from "rxjs";
import { map, shareReplay, switchMap, tap } from "rxjs/operators";
import { ChecklistGroup, ChoiceOutcomeType } from "src/app/system-controls/checklist-groups/types";
import { ChangeRequestService } from "../../change-request.service";
import { AnalysisConfigModel, UpdateChoiceOnChangeModel, UpdateOnChangeType } from "../../types";

@Injectable({
    providedIn: "root"
})
export class AnalysisService {
    private fetchAnalysisSubject = new Subject();
    private fetchAnalysisAlertsSubject = new Subject();
    private fetchPreStartupReviewAnalysisAlertsSubject = new Subject();
    private approvalInProgressBS = new BehaviorSubject(false);
    private analysisConfig: AnalysisConfigModel;
    private expandedState: { [key: string]: boolean }[] = [];
    private groups: ChecklistGroup[] = [];

    fetchPreStartupReviewAnalysisAlerts$ = this.fetchPreStartupReviewAnalysisAlertsSubject.asObservable();

    alerts$ = this.fetchAnalysisAlertsSubject.pipe(
        switchMap(() => {
            return this.changeRequestService.getChangeRequestAnalysisAlerts(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType)
        }),
        shareReplay(1)
    );

    groups$ = this.fetchAnalysisSubject.pipe(
        tap(() => {
            const groups = this.groups;
            groups.forEach(group => {
                this.expandedState[group.Id] = group.expanded;
                group.Questions.forEach(question => {
                    this.expandedState[question.Id] = question.expanded;
                    question.Choices.forEach(choice => {
                        this.expandedState[choice.Id] = choice.expanded;
                        choice.OutcomeTypes
                            .filter(outcomeType => outcomeType.OutcomeType.Id == ChoiceOutcomeType.NestedChecklist)
                            .forEach(outcomeType => {
                                if (outcomeType.NestedChecklistConfig && outcomeType.NestedChecklistConfig.Checklist) {
                                    this.expandedState[outcomeType.NestedChecklistConfig.Checklist.Id] = outcomeType.NestedChecklistConfig.Checklist.expanded;
                                    outcomeType.NestedChecklistConfig.Checklist.Questions.forEach(nestedQuestion => {
                                        this.expandedState[nestedQuestion.Id] = nestedQuestion.expanded;
                                        nestedQuestion.Choices.forEach(nestedChoice => {
                                            this.expandedState[nestedChoice.Id] = nestedChoice.expanded;
                                        });
                                    });
                                }
                            });
                    });
                });
            });
        }),
        switchMap(() => {
            return this.changeRequestService.getAnalysis(this.analysisConfig.GetAnalysisUrl, this.analysisConfig.ObjectId)
        }),
        map((groups) => {
            groups.forEach(group => {
                group.expanded = this.expandedState[group.Id];
                group.Questions.forEach(question => {
                    question.expanded = this.expandedState[question.Id];
                    question.Choices.forEach(choice => {
                        choice.expanded = this.expandedState[choice.Id];
                        choice.OutcomeTypes
                            .filter(outcomeType => outcomeType.OutcomeType.Id == ChoiceOutcomeType.NestedChecklist)
                            .forEach(outcomeType => {
                                if (outcomeType.NestedChecklistConfig && outcomeType.NestedChecklistConfig.Checklist) {
                                    outcomeType.NestedChecklistConfig.Checklist.expanded = this.expandedState[outcomeType.NestedChecklistConfig.Checklist.Id];
                                    outcomeType.NestedChecklistConfig.Checklist.Questions.forEach(nestedQuestion => {
                                        nestedQuestion.expanded = this.expandedState[nestedQuestion.Id];
                                        nestedQuestion.Choices.forEach(nestedChoice => {
                                            nestedChoice.expanded = this.expandedState[nestedChoice.Id];
                                        });
                                    });
                                }
                            });
                    });
                });
            });
            return groups;
        }),
        tap((groups) => {
            this.groups = groups;
            this.fetchAnalysisAlertsSubject.next();
            this.fetchPreStartupReviewAnalysisAlertsSubject.next();
            this.approvalInProgressBS.next(groups.some(group => group.ApprovalStatus.Id == 1 || group.ApprovalStatus.Id == 2));
            this.changeRequestService.fetchChangeRequestAlerts(this.analysisConfig.ObjectId);
        })
    );

    approvalInProgress$ = this.approvalInProgressBS.asObservable();

    constructor(private changeRequestService: ChangeRequestService) { }

    fetchChecklistGroups() {
        this.fetchAnalysisSubject.next();
    }

    fetchAnalysisAlerts() {
        this.fetchAnalysisAlertsSubject.next();
    }

    setAnalysisConfig(analysisConfig: AnalysisConfigModel) {
        this.analysisConfig = analysisConfig;
    }

    updateOnChange(groupId: string, type: UpdateOnChangeType, value: string | boolean | IdentityLookupViewModel | LookupViewModel, questionId?: string, choiceId?: string, outcomeTypeConfigId?: string): Observable<boolean> {
        const model = new UpdateChoiceOnChangeModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, groupId, questionId, choiceId, outcomeTypeConfigId, type, value);
        return this.changeRequestService.updateChoiceOnChange(this.analysisConfig.UpdateChoiceOnChangeUrl, model);
    }
}
