import { Component, ComponentRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewContainerRef } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { AttachmentsMultipleModeComponent, AttachmentViewModel, BulkOperationExecutionResultComponent, CancelAction, ComponentControl, CustomAction, DynamicService, IdentityLookupViewModel, InputControl, IqDatePickerComponent, IqPersonnelPickerComponent, IsoQuestEventManagerService, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl, TextareaControl } from 'codera-shared';
import { ToastrService } from "ngx-toastr";
import { RecommendedActionStatusPickerComponent } from '../recommended-action-status-picker/recommended-action-status-picker.component';
import { RecommendedActionService } from '../recommended-action.service';
import { RecommendedActionViewModel, Status } from '../types';

@Component({
    selector: 'create-update-recommended-action',
    templateUrl: './create-update-recommended-action.component.html'
})
export class CreateUpdateRecommendedActionComponent implements OnInit, OnDestroy {
    @Input() recommendedActionId: string;
    @Input() changeRequestId: string;

    @Output() onCreateUpdate: EventEmitter<string | boolean>;

    model: RecommendedActionViewModel;
    createRecommendedActionModalFormConfig: ModalFormConfig;
    component: ComponentRef<ModalFormComponent>;
    attachmentsModel: AttachmentViewModel;

    constructor(
        private recommendedActionService: RecommendedActionService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private translate: TranslateService,
        private toastr: ToastrService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.onCreateUpdate = new EventEmitter<string | boolean>();
    }

    ngOnDestroy(): void {
    }

    ngOnInit() {
        const modalTitle = this.recommendedActionId
            ? "[MOCQuest].[Common].Buttons.RecommendedAction.Edit"
            : "[MOCQuest].[Common].Buttons.RecommendedAction.New";

        this.prepareModalFormConfig(modalTitle);

        if (this.recommendedActionId) {
            this.recommendedActionService.getById(this.recommendedActionId).subscribe((response: RecommendedActionViewModel) => {
                this.component = this.summonDialog();
                this.model = response;
                this.component.instance.model = response;
            });
        } else {
            this.component = this.summonDialog();
        }
    }

    prepareModalFormConfig(title: string) {
        const that = this;
        this.model = new RecommendedActionViewModel();

        this.createRecommendedActionModalFormConfig = new ModalFormConfig(title,
            [
                new RowControl([
                    new InputControl({
                        type: 'text',
                        field: 'Title',
                        label: '[ISOQuest].[Common].Title',
                        required: true,
                        css: 'col-md-12',
                        min: 0,
                        max: 250
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        field: "AssignedTo",
                        componentClass: IqPersonnelPickerComponent,
                        css: "col-md-6",
                        data: [
                            { "label": "[ISOQuest].[Common].Assignee" },
                            { "isRequired": true },
                            { "isMultiple": false },
                            { "onlyActive": true },
                            { "type": "Personnel" },
                            { "moduleName": 'MOCQuest' },
                            { "targetModule": 'MOCQuest' }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: RecommendedActionStatusPickerComponent,
                        label: "[MOCQuest].[RecommendedAction].Status",
                        field: "Status",
                        required: false,
                        css: "col-md-6",
                        data: [
                            { "isRequired": true },
                            {
                                "modelChange": (status: IdentityLookupViewModel) => {
                                    let modalFormConfig = this.createRecommendedActionModalFormConfig as ModalFormConfig;
                                    let completionDateComponent = modalFormConfig.getControlByField("CompletionDate") as ComponentControl;
                                    if (status && status.Id == Status.Completed && completionDateComponent.formControl) {
                                        completionDateComponent.formControl.enable();
                                        completionDateComponent.getDataByKey("isRequired").isRequired = true;
                                        completionDateComponent.setRequired(true);
                                    }
                                    else {
                                        completionDateComponent.formControl.disable();
                                        completionDateComponent.getDataByKey("isRequired").isRequired = false;
                                        completionDateComponent.setRequired(false);
                                        this.model.CompletionDate = null;
                                    }
                                }
                            }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        field: "DueDate",
                        componentClass: IqDatePickerComponent,
                        css: "col-md-6",
                        data: [
                            { "label": "[ISOQuest].[Common].DueDate" },
                            { "minDate": new Date() },
                            { "isRequired": true },
                            { "maxDate": null }
                        ],
                        show: true
                    }),
                    new ComponentControl({
                        field: "CompletionDate",
                        componentClass: IqDatePickerComponent,
                        css: "col-md-6",
                        data: [
                            { "label": "[ISOQuest].[Common].CompletionDate" },
                            { "isRequired": false },
                            { "minDate": null },
                            { "maxDate": null },
                            {
                                "modelChange": (completionDate: Date) => {
                                    if (completionDate) {
                                        this.model.Status = new IdentityLookupViewModel(2, "Completed", true)
                                    }
                                }
                            }
                        ],
                        show: true
                    })
                ]),
                new RowControl([
                    new TextareaControl({
                        field: "Description",
                        label: "[ISOQuest].[Common].Description"
                    })
                ]),
                new RowControl([
                    new InputControl({
                        field: "RequiredBeforeLaunch",
                        type: "checkbox",
                        css: "col-md-6",
                        label: "[MOCQuest].[RecommendedAction].RequiredBeforeLaunch",
                        required: false
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: AttachmentsMultipleModeComponent,
                        field: "Attachments",
                        data: [
                            { isMultiple: true },
                            { url: `RecommendedAction/UploadAttachment/${this.changeRequestId}` },
                            { deleteUrl: 'RecommendedAction/DeleteAttachment' }
                        ]
                    })
                ])
            ],
            [
                new CustomAction({
                    actionType: "submit",
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    callback: () => {
                        that.recommendedActionService.createOrUpdate(that.model, this.changeRequestId).subscribe((response) => {
                            let recommendedActionId = null;
                            if (typeof response === "string") {
                                that.model.Id = response;
                            }

                            recommendedActionId = that.model.Id;
                            if (this.recommendedActionId)
                                that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                            else
                                that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));

                            that.uploadAttachments(recommendedActionId);
                            that.onCreateUpdate.emit(response);
                        }, (error) => {
                            that.isoQuestEventManager.notify("manuallyTriggerServerValidation", error);
                        });
                    },
                    css: "btn btn-primary"
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    callback: () => {
                        that.onCreateUpdate.emit();
                    },
                    css: "btn btn-default"
                })
            ]);
    }

    summonDialog(): ComponentRef<ModalFormComponent> {
        let component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
            [
                { config: this.createRecommendedActionModalFormConfig },
                { model: this.model },
                {
                    modalOptions: {
                        size: "md"
                    }
                }
            ]
        );
        return component as ComponentRef<ModalFormComponent>;
    }

    uploadAttachments(recommendedActionId: string) {
        this.attachmentsModel = new AttachmentViewModel();
        this.isoQuestEventManager.subscribe(this, "iq-no-attachments-provided", () => {
            this.createRecommendedActionModalFormConfig.modalRef && this.createRecommendedActionModalFormConfig.modalRef.close();
        });

        let succeeded = [];
        let failed = [];
        this.isoQuestEventManager.subscribe(this, "iq-single-attachment-success", (payload) => {
            if (payload.data.title) {
                succeeded.push({ Name: payload.data.title, Reason: '', IsSucceed: true });
            }
        });
        this.isoQuestEventManager.subscribe(this, "iq-single-attachment-error", (payload) => {
            if (payload.data.title) {
                var errorParsed = JSON.parse(payload.data.error);
                failed.push({ Name: payload.data.title, Reason: errorParsed.Errors[""][0], IsSucceed: false });
            }
        });

        this.isoQuestEventManager.subscribe(this, "attachmentsUploadFinished", () => {
            this.isoQuestEventManager.unsubscribe(this, "iq-no-attachments-provided");
            this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-success");
            this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-error");
            this.isoQuestEventManager.unsubscribe(this, "attachmentsUploadFinished");

            if (failed.length) {
                var all = { Succeed: succeeded, Failed: failed };
                this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
                    { "model": all },
                    { "modalOptions": { size: "sm" } },
                    {
                        "callback": () => {
                            this.createRecommendedActionModalFormConfig.modalRef && this.createRecommendedActionModalFormConfig.modalRef.close();
                        }
                    }
                ]);

                this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
            }
            else {
                this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
                this.createRecommendedActionModalFormConfig.modalRef && this.createRecommendedActionModalFormConfig.modalRef.close();
            }
        });

        this.attachmentsModel.ObjectId = recommendedActionId;
        this.isoQuestEventManager.notify("attachmentsSubmitted", this.attachmentsModel);
    }
}
