import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { IdentityLookupViewModel, WorkflowDefinitionByTagViewModel, WorkflowService } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { SettingsService } from '../settings.service';
import { MOCQuestSettingsViewModel } from '../types';

@Component({
    selector: 'app-approval-settings',
    templateUrl: './approval-settings.component.html'
})
export class ApprovalSettingsComponent implements OnInit {
    @Input() viewModel: MOCQuestSettingsViewModel;
    @Output() viewModelChanged: EventEmitter<MOCQuestSettingsViewModel>;
    submitted: boolean;
    @Input() model: any;
    @Input() tag: string;
    @Input() module: string;
    @Input() controllerName: string;
    pickerData: IdentityLookupViewModel[];
    wfDefinitionByTag: WorkflowDefinitionByTagViewModel;
    approvalTypeForm = new FormGroup({
        'approvalType': new FormControl()
    });
    get approvalType() { return this.approvalTypeForm.get('approvalType'); }

    constructor(
        private toastr: ToastrService,
        private translate: TranslateService,
        private settingsService: SettingsService,
        private workflowService: WorkflowService
    ) {
        this.pickerData = [];
        this.viewModelChanged = new EventEmitter<MOCQuestSettingsViewModel>();
    }

    ngOnInit() {
        const that = this;

        this.settingsService.getSettings().subscribe((viewModel: MOCQuestSettingsViewModel) => {
            that.viewModel = viewModel;

            this.workflowService.getWorkflowDefinitionByTag("MOCQuest.ChangeRequest.InitialApproval", "MOCQuest", "Workflow/GetWorkflowDefinitionByTag").subscribe((response) => {
                this.wfDefinitionByTag = response;
                for (let i = 0; i < response.AvailableApprovalTypes.length; i++) {
                    const item = response.AvailableApprovalTypes[i];
                    this.pickerData.push({
                        Id: item.Id,
                        Name: this.translate.instant(`[ISOQuest].[Workflow].Approval.ApprovalType.${item.Name}`),
                        Status: true
                    });
                }
            });
        });
    }

    updateSettings() {
        this.settingsService.setSettings(this.viewModel).subscribe((viewModel) => {
            this.viewModelChanged.emit(viewModel);
            this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
        });
    }
}

