import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { AddEditFollowUpViewModel, FollowUpPermissionsViewModel, FollowUpViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class FollowUpService {
  private apiUrl: string;

  constructor(
    private http: HttpClient,
    private applicationService: ApplicationService
  ) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  add(model: FollowUpViewModel): Observable<string | boolean> {
    const url = `${this.apiUrl}/ChangeRequest/AddFollowUp`;
    return this.http.post<string | boolean>(url, model);
  }

  submit(model: AddEditFollowUpViewModel): Observable<string | boolean> {
    const url = `${this.apiUrl}/ChangeRequest/SubmitFollowUp`;
    if (model.HasNextFollowUp) {
      if (model.NextFollowUp == null) {
        model.NextFollowUp = new AddEditFollowUpViewModel();
        model.NextFollowUp.ChangeRequestId = model.ChangeRequestId;
        model.NextFollowUp.AssignedTo = model.NextFollowUpAssignedTo;
        model.NextFollowUp.DueDate = model.NextFollowUpDueDate;
        model.NextFollowUp.MethodOfFollowUp = model.NextFollowUpMethodOfFollowUp;
      }
    }
    else {
      model.NextFollowUp = null;
    }
    return this.http.post<string | boolean>(url, model);
  }

  getChangeRequestFollowUps(changeRequestId: string): Observable<FollowUpViewModel[]> {
    const url = `${this.apiUrl}/ChangeRequest/GetChangeRequestFollowUps`;
    return this.http.post<FollowUpViewModel[]>(url, { id: changeRequestId });
  }

  getById(changeRequestId: string, followUpId: string): Observable<FollowUpViewModel> {
    const url = `${this.apiUrl}/ChangeRequest/GetFollowUpById`;
    return this.http.post<FollowUpViewModel>(url, { id: changeRequestId, followUpId });
  }

  getLatestFollowUp(changeRequestId: string): Observable<FollowUpViewModel> {
    const url = `${this.apiUrl}/ChangeRequest/GetLatestFollowUp`;
    return this.http.post<FollowUpViewModel>(url, { id: changeRequestId });
  }

  delete(changeRequestId: string): Observable<boolean> {
    const url = `${this.apiUrl}/ChangeRequest/DeleteFollowUp`;
    return this.http.post<boolean>(url, { id: changeRequestId });
  }

  getPermissions(changeRequestId: string): Observable<FollowUpPermissionsViewModel> {
    const url = `${this.apiUrl}/ChangeRequest/GetFollowUpPermissions`;
    return this.http.post<FollowUpPermissionsViewModel>(url, { id: changeRequestId });
  }
}
