import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FluentTabConfig, RenderButtonsPosition } from 'codera-shared';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { TmsTaskGridComponent } from 'src/app/task-management-system/tms-task-grid/tms-task-grid.component';
import { ChangeRequestImplementationTasksCountsViewModel } from '../types';

@Component({
  selector: 'implementation',
  templateUrl: './implementation.component.html',
  styleUrls: ['./implementation.component.scss']
})
export class ChangeRequestImplementationTasksComponent implements OnInit {

  fluentTabsConfig: FluentTabConfig[];
  taskCounts: ChangeRequestImplementationTasksCountsViewModel;
  renderButtonsTypes = RenderButtonsPosition;
  pageTitle: string;
  constructor(
    private route: ActivatedRoute,
    private translate: TranslateService,
    private changeRequestService: ChangeRequestService
  ) { }

  ngOnInit() {
    const that = this;

    this.pageTitle = this.route.snapshot.data["pageTitle"];
    this.changeRequestService.getChangeRequestImplementationTasksCounts().toPromise().then(response => {
      this.taskCounts = response;
    });

    this.fluentTabsConfig = [
      new FluentTabConfig({
        componentClass: TmsTaskGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.AllTasks >= 0)
            count = that.taskCounts.AllTasks.toString();

            return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.AllTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span> `;
        },
        data: [
          { source: "ChangeRequest/AllTasksFromChangeRequestsImplementationStageGrid" }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.AllOpenTasks >= 0)
            count = that.taskCounts.AllOpenTasks.toString();

            return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.AllOpenTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span>`;
        },
        data: [
          { source: "ChangeRequest/AllOpenTasksFromChangeRequestsImplementationStageGrid" }
        ]
      }),
      new FluentTabConfig({
        componentClass: TmsTaskGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.MyOpenTasks >= 0)
            count = that.taskCounts.MyOpenTasks.toString();

            return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.MyOpenTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span> `;
        },
        data: [
          { source: "ChangeRequest/MyOpenTasksFromChangeRequestsImplementationStageGrid" }
        ]
      })
    ]
  }

}
