import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TMSSearchModel } from '../types';

@Component({
  selector: 'tms-saved-searches-picker',
  templateUrl: './tms-saved-searches-picker.component.html'
})
export class TmsSavedSearchesPickerComponent implements OnInit {
  @Input() model: TMSSearchModel;
  @Output() modelChange: EventEmitter<TMSSearchModel>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() enableItemCreation: boolean;
  @Input() excludeUsedItems: boolean;
  @Input() objectType: string;
  @Input() objectId: string;
  @Input() defaultSearchOption: string

  controllerName: string;
  hasPermission: boolean;
  showNewItemButton: boolean;

  constructor() {
    this.modelChange = new EventEmitter<TMSSearchModel>();
  }

  ngOnInit() {
    this.controllerName = `/TaskManagementSystem/GetSavedSearches?objectType=${this.objectType}&objectId=${this.objectId}&defaultSearchOption=${this.defaultSearchOption}`;

    if (!this.label)
      this.label = "[ISOQuest].[TaskManagementSystem].Search.SavedSearches";
  }

  onChange(newValue: TMSSearchModel) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
