import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { Status } from '../types';
import { TranslateService } from '@ngx-translate/core';
@Component({
    selector: 'recommended-action-status-picker',
    templateUrl: './recommended-action-status-picker.component.html'
})
export class RecommendedActionStatusPickerComponent implements OnInit {
    private _model: IdentityLookupViewModel | IdentityLookupViewModel[];
    @Input() set model(value: IdentityLookupViewModel | IdentityLookupViewModel[]) {
        this._model = value;
        this.modelChange.emit(this._model);
    }
    get model() {
        return this._model;
    }
    
    @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

    @Input() label: string;
    @Input() controllerName: string;
    @Input() placeholder: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;

    staticData: IdentityLookupViewModel[];

    constructor(
        private translate: TranslateService
    ) {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        this.staticData = Object.keys(Status).filter((item) => {
            return isNaN(Number(item));
        }).map(status => {
            return new IdentityLookupViewModel(Status[status], this.translate.instant("[MOCQuest].[RecommendedAction].Status." + status), true);
        }).sort((left, right) => Number(left.Id) - Number(right.Id));

        if (!this.isMultiple && this.isRequired && !this.model) {
            this.onChange(this.staticData[0]);
        }
    }

    ngOnDestroy() { }

    onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }
}
