import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { UpdateLocalizationItemViewModel, LocalizationItemViewModel } from './types';
import { TranslateService } from "@ngx-translate/core";

@Injectable({
  providedIn: 'root'
})
export class LocalizationService {
  private hostUrl: string;
  private siteKey: string;

  constructor(private httpClinet: HttpClient, private applicationService: ApplicationService, private translate: TranslateService) {
    this.hostUrl = applicationService.getHostUrl();
    this.siteKey = applicationService.getCurrentSiteKey();
  }

  getLocalizationItemsById(id: string, isExternal: boolean): Observable<LocalizationItemViewModel[]> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/GetLocalizationItemsById`;
    return this.httpClinet.post<LocalizationItemViewModel[]>(url, { id: id, isExternal: isExternal });
  }

  create(model: UpdateLocalizationItemViewModel): Observable<UpdateLocalizationItemViewModel> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/Insert`;
    return this.httpClinet.post<UpdateLocalizationItemViewModel>(url, model);
  }

  update(model: UpdateLocalizationItemViewModel): Observable<UpdateLocalizationItemViewModel> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/Update`;
    return this.httpClinet.post<UpdateLocalizationItemViewModel>(url, model);
  }

  delete(id: string, isExternal: boolean): Observable<boolean> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/Delete`;
    return this.httpClinet.post<boolean>(url, { id: id, isExternal: isExternal });
  }

  publishParallel(): Observable<boolean> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/PublishParallel`;
    return this.httpClinet.post<boolean>(url, {});
  }

  publishModuleLocalizations(moduleKey: string): Observable<boolean> {
    const url = `${this.hostUrl}/isoquest/api/sites/${this.siteKey}/Localizations/PublishModuleLocalizations`;
    return this.httpClinet.post<boolean>(url, { moduleKey: moduleKey });
  }
}
