import { Component, OnInit } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, FluentTabConfig } from 'codera-shared';
import { ApprovalSettingsComponent } from './approval/approval-settings.component';
import { GeneralSettingsComponent } from './general/general-settings.component';
import { NotificationsComponent } from './notifications/notifications.component';
import { ReportsComponent } from './reports/reports.component';
import { SettingsService } from './settings.service';
import { MOCQuestSettingsViewModel } from './types';
import { WorkflowStagesComponent } from './workflow-stages/workflow-stages.component';

@Component({
    selector: 'app-settings',
    templateUrl: './settings.component.html',
    styleUrls: ['./settings.component.scss']
})

export class SettingsComponent implements OnInit {
    viewModel: MOCQuestSettingsViewModel;
    tabsConfig: FluentTabConfig[];
    pageTitle: string;

    constructor(
        private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private settingsService: SettingsService,
        private applicationService: ApplicationService
    ) {
    }

    ngOnInit() {
        const that = this;

        this.pageTitle = this.route.snapshot.data["pageTitle"];
        let translatedTitle;
        this.translate.get(this.pageTitle).subscribe(translated => {
            translatedTitle = translated;
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translatedTitle}`);
        });

        this.settingsService.getSettings().subscribe((viewModel: MOCQuestSettingsViewModel) => {
            this.pageTitle = "[MOCQuest].[SiteMap].MOCSettings";
            that.viewModel = viewModel;

            this.tabsConfig = [
                new FluentTabConfig({
                    title: () => {
                        return `<span><i class='fa fa-cog'></i>${that.translate.instant("[MOCQuest].[Settings].General")}</span>`;
                    },
                    show: true,
                    componentClass: GeneralSettingsComponent,
                    data: [
                        { "viewModel": that.viewModel },
                        {
                            viewModelChanged: (viewModel) => {
                                that.viewModel = viewModel;
                            }
                        }
                    ]
                }),
                new FluentTabConfig({
                    title: () => {
                        return `<span><i class='far fa-check-square'></i>${that.translate.instant("[ISOQuest].[Workflow].Approval.ApprovalSettings")}</span>`;
                    },
                    show: true,
                    componentClass: ApprovalSettingsComponent
                }),
                new FluentTabConfig({
                    title: () => {
                        return `<span><i class='fa fa-bell'></i>${that.translate.instant("[MOCQuest].[Settings].Notifications")}</span>`;
                    },
                    show: true,
                    componentClass: NotificationsComponent,
                    data: [
                        { "viewModel": that.viewModel },
                        {
                            viewModelChanged: (viewModel) => {
                                that.viewModel = viewModel;
                            }
                        }
                    ]
                }),
                new FluentTabConfig({
                    title: () => {
                        return `<span><i class='fad fa-stream'></i>${that.translate.instant("[MOCQuest].[Settings].WorkflowStages")}</span>`;
                    },
                    componentClass: WorkflowStagesComponent,
                    data: [
                        { "viewModel": that.viewModel }
                    ]
                }),
                new FluentTabConfig({
                    title: () => {
                        return `<span><i class='far fa-chart-bar'></i>${that.translate.instant("[ISOQuest].[Common].Reports")}</span>`;
                    },
                    componentClass: ReportsComponent,
                    data: [
						{ "viewModel": that.viewModel },
						{
							"onReportActionEvent": () => {
								this.settingsService.getSettings().subscribe((settings: MOCQuestSettingsViewModel) => {
									this.viewModel = settings;
									this.tabsConfig.forEach((config: FluentTabConfig) => {
										var settingsData = config.data.find(item => item.hasOwnProperty("viewModel"));
										if (settingsData != null)
											settingsData.settings = settings;
									})
								})
							}
						}
					]

                })

            ];
        });
    }
}
