import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { TMSTagViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class TmsTaskTopicService {
  private isoQuestApiUrl: string;

  constructor(private httpClient: HttpClient, private applicationService: ApplicationService) {
    this.isoQuestApiUrl = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}`;
  }

  delete(id: string): Observable<boolean> {
    const url = `${this.isoQuestApiUrl}/TMSTag/Delete`;
    return this.httpClient.post<boolean>(url, { id: id });
  }

  createOrUpdate(model: TMSTagViewModel): Observable<string | boolean> {
    const url = `${this.isoQuestApiUrl}/TMSTag/CreateOrUpdate`;
    return this.httpClient.post<string | boolean>(url, model);
  }

  getById(id: string): Observable<TMSTagViewModel> {
    const url = `${this.isoQuestApiUrl}/TMSTag/GetById`;
    return this.httpClient.post<TMSTagViewModel>(url, { id: id });
  }
}
