import { Component, Input, OnDestroy, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, DynamicApprovalType, DynamicService, IdentityLookupViewModel, LookupViewModel, TextConfirmationModalComponent } from 'codera-shared';
import { DragulaService, Group } from 'ng2-dragula';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { AnalysisConfigModel, UpdateChoiceOutcomeTypeNotifiers, UpdateChoicesOrderViewModel, UpdateOnChangeType, UpdateQuestionsOrderViewModel } from 'src/app/change-request/types';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { OutcomeConfigEvent } from 'src/app/system-controls/checklist-groups/manage-checklist-group/outcome-config/types';
import { AddChoiceModel, AddChoiceOutcomeTypeViewModel, AddQuestionModel, ChecklistGroup, Choice, ChoiceNewDocumentConfig, ChoiceOutcomeConfigUpdateModel, ChoiceOutcomeType, ChoiceSpecificationConfig, ChoiceUpdateDocumentsConfig, OutcomeTypeConfigViewModel, Question, RemoveChoiceModel, RemoveChoiceOutcomeTypeViewModel, RemoveQuestionModel, UpdateChecklistGroupAssigneesModel } from 'src/app/system-controls/checklist-groups/types';
import { AnalysisService } from '../../analysis.service';
@Component({
  selector: 'manage-mode-checklist-group',
  templateUrl: './manage-mode-checklist-group.component.html',
  styleUrls: ['./manage-mode-checklist-group.component.scss']
})
export class ManageModeChecklistGroupComponent implements OnInit, OnDestroy {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;
  @Input() allowOutcome: boolean;

  settings: MOCQuestSettingsViewModel;
  allowedApprovalTypes: IdentityLookupViewModel[];
  changeType = UpdateOnChangeType;

  choicesGroup: Group;
  dragulaChoices: string = "Choices";

  questionsGroup: Group;
  dragulaQuestions: string = "Questions";
  destroy$: Subject<boolean> = new Subject<boolean>();
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private dragulaService: DragulaService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private analysisService: AnalysisService,
    private applicationService: ApplicationService,
    private changeRequestService: ChangeRequestService
  ) { }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }

  ngOnInit() {
    this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;
    this.initDragula();
    this.allowedApprovalTypes = [];
    if (this.settings.AllowParallelApprovalType) {
      this.allowedApprovalTypes.push(
        new IdentityLookupViewModel(
          DynamicApprovalType.Parallel,
          this.translate.instant(`[ISOQuest].[Workflow].Approval.ApprovalType.${DynamicApprovalType[DynamicApprovalType.Parallel]}`),
          true)
      );
    }
    if (this.settings.AllowParallelCancelApprovalType) {
      this.allowedApprovalTypes.push(
        new IdentityLookupViewModel(
          DynamicApprovalType.ParallelCancelOnFirstRejection,
          this.translate.instant(`[ISOQuest].[Workflow].Approval.ApprovalType.${DynamicApprovalType[DynamicApprovalType.ParallelCancelOnFirstRejection]}`),
          true)
      );
    }
    if (this.settings.AllowSerialApprovalType) {
      this.allowedApprovalTypes.push(
        new IdentityLookupViewModel(
          DynamicApprovalType.Serial,
          this.translate.instant(`[ISOQuest].[Workflow].Approval.ApprovalType.${DynamicApprovalType[DynamicApprovalType.Serial]}`),
          true)
      );
    }
    if (this.settings.AllowVoteApprovalType) {
      this.allowedApprovalTypes.push(
        new IdentityLookupViewModel(
          DynamicApprovalType.Vote,
          this.translate.instant(`[ISOQuest].[Workflow].Approval.ApprovalType.${DynamicApprovalType[DynamicApprovalType.Vote]}`),
          true)
      );
    }
  }

  addQuestion(gId: number): void {
    this.changeRequestService.addAnalysisQuestion(this.analysisConfig.AddAnalysisQuestionUrl, new AddQuestionModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, this.group.Id))
      .pipe(takeUntil(this.destroy$))
      .subscribe((questionId: string) => {
        this.analysisService.fetchChecklistGroups();
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
      });
  }

  removeQuestion(questionId: string): void {
    this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
      { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
      { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
      {
        "modalOptions": {
          size: "sm"
        }
      },
      {
        successCallback: () => {
          this.changeRequestService.removeAnalysisQuestion(this.analysisConfig.RemoveAnalysisQuestionUrl, new RemoveQuestionModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, this.group.Id, questionId))
            .pipe(takeUntil(this.destroy$))
            .subscribe(() => {
              this.analysisService.fetchChecklistGroups();
              this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'), '', { timeOut: 1500 });
            });
        }
      }
    ], true);
  }

  addChoice(questionId: string) {
    const choiceModel = new AddChoiceModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, this.group.Id, questionId);
    this.changeRequestService.addAnalysisChoice(this.analysisConfig.AddAnalysisChoiceUrl, choiceModel)
      .pipe(takeUntil(this.destroy$))
      .subscribe((choiceId: string) => {
        this.analysisService.fetchChecklistGroups();
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
      });
  }

  removeChoice(questionId: string, choiceId: string) {
    this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
      { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
      { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
      {
        "modalOptions": {
          size: "sm"
        }
      },
      {
        successCallback: () => {
          this.changeRequestService.removeAnalysisChoice(this.analysisConfig.RemoveAnalysisChoiceUrl, new RemoveChoiceModel(this.analysisConfig.ObjectId,this.analysisConfig.ObjectType, this.group.Id, questionId, choiceId))
            .pipe(takeUntil(this.destroy$))
            .subscribe(() => {
              this.analysisService.fetchChecklistGroups();
              this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'), '', { timeOut: 1500 });
            });
        }
      }
    ], true);
  }

  addOutcomeType(questionId: string, choiceId: string) {
    const model = new AddChoiceOutcomeTypeViewModel(this.analysisConfig.ObjectType, this.analysisConfig.ObjectId, this.group.Id, questionId, choiceId);
    this.changeRequestService.addChoiceOutcomeType(model)
      .pipe(takeUntil(this.destroy$))
      .subscribe((response) => {
        this.analysisService.fetchChecklistGroups();
      });
  }

  removeOutcomeType(questionId: string, choiceId: string, outcomeTypeConfigId: string) {
    const that = this;
    this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
      { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
      { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
      {
        "modalOptions": {
          size: "sm"
        }
      },
      {
        successCallback: (modalRef) => {
          const model = new RemoveChoiceOutcomeTypeViewModel(that.analysisConfig.ObjectType, this.analysisConfig.ObjectId, this.group.Id, questionId, choiceId, outcomeTypeConfigId);
          that.changeRequestService.removeChoiceOutcomeType(model)
            .pipe(takeUntil(this.destroy$))
            .subscribe((response) => {
              that.toastr.success(that.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'));
              this.analysisService.fetchChecklistGroups();
            });
        }
      }
    ], true);
  }

  updateAssignees(assignees: []): void {
    let model = new UpdateChecklistGroupAssigneesModel(this.analysisConfig.ObjectId, this.group.Id, this.analysisConfig.ObjectType, assignees);
    this.changeRequestService.updateChecklistGroupAssignees(model)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
        this.analysisService.fetchChecklistGroups();
      }, (error) => {
        var errorMessage = error.error.Errors[""][0];
        this.toastr.error(this.translate.instant(errorMessage));
        this.analysisService.fetchChecklistGroups();
      });
  }

  updateChoiceOutcomeTypeNotifiers(groupId: string, questionId: string, choiceId: string, outcomeTypeConfigId: string, notifiers: []): void {
    var model = new UpdateChoiceOutcomeTypeNotifiers(this.analysisConfig.ObjectId, groupId, questionId, choiceId, outcomeTypeConfigId, this.analysisConfig.ObjectType, notifiers);
    this.changeRequestService.updateChoiceOutcomeTypeNotifiers(model)
      .pipe(takeUntil(this.destroy$))
      .subscribe(x => {
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'));
        this.analysisService.fetchChecklistGroups();
      }, (error) => {
        var errorMessage = error.error.Errors[""][0];
        this.toastr.error(this.translate.instant(errorMessage));
        this.analysisService.fetchChecklistGroups();
      });
  }

  onQuestionsOrderChange(questions: Question[], group: ChecklistGroup) {
    questions.forEach((question, index) => {
      question.Position = index;
    });
    group.Questions = questions;

    const updateQuestionsOrderModel = new UpdateQuestionsOrderViewModel(this.analysisConfig.ObjectId, group.Id, group.Questions);
    this.changeRequestService.updateQuestionsOrder(this.analysisConfig.UpdateQuestionsOrderUrl, updateQuestionsOrderModel)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.analysisService.fetchChecklistGroups();
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
      });
  }

  onChoicesOrderChange(choices: Choice[], question: Question, group: ChecklistGroup) {
    choices.forEach((choice, index) => {
      choice.Position = index;
    });
    question.Choices = choices;

    const updateQuestionsOrderModel = new UpdateChoicesOrderViewModel(this.analysisConfig.ObjectId, group.Id, question.Id, choices);
    this.changeRequestService.updateChoicesOrder(this.analysisConfig.UpdateChoicesOrderUrl, updateQuestionsOrderModel)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.analysisService.fetchChecklistGroups();
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
      })
  }

  updateOnChange(groupId: string, type: UpdateOnChangeType, value: string | boolean | IdentityLookupViewModel | LookupViewModel, questionId?: string, choiceId?: string, outcomeTypeConfigId?: string): void {
    this.analysisService.updateOnChange(groupId, type, value, questionId, choiceId, outcomeTypeConfigId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response) => {
          this.analysisService.fetchChecklistGroups();
          this.analysisService.fetchAnalysisAlerts();
          this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
        },
        error: (error) => {
          if (error && error.error && error.error.Errors && error.error.Errors[""] && error.error.Errors[""][0]) {
            var errorMessage = error.error.Errors[""][0];
            this.toastr.error(this.translate.instant(errorMessage));
          }
          this.analysisService.fetchChecklistGroups();
        }
      })
  }

  closePopover(event: OutcomeConfigEvent, popover: any, questionId: string, choiceId: string) {
    if (event.Cancel) {
      popover.close();
    }
    else {
      const outcomeTypeConfig = event.Model;
      let model = new ChoiceOutcomeConfigUpdateModel(this.analysisConfig.ObjectId, this.group.Id, questionId, choiceId, null, null, outcomeTypeConfig.Id, null, outcomeTypeConfig.OutcomeType.Id, outcomeTypeConfig.UpdateDocumentsConfig || null, outcomeTypeConfig.NewDocumentConfig || null, outcomeTypeConfig.SpecificationConfig || null);
      this.changeRequestService.updateOutcomeConfig(this.analysisConfig.UpdateOutcomeConfigUrl, model)
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => {
          this.analysisService.fetchChecklistGroups();
          this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
          popover.close();
        });
    }
  }

  validateQuestion(question: Question) {
    let title = question.Title;
    if (!title || title.length === 0)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

    let hasDuplicateTitles = this.group.Questions.filter(item => {
      if (item.Title == title) {
        return item;
      }
    }).length;

    if (hasDuplicateTitles > 1)
      return this.translate.instant('[MOCQuest].[Exceptions].Question.AlreadyExists');

    if (question.Choices.length === 0)
      return this.translate.instant('[MOCQuest].[Exceptions].Question.EmptyChoices');

    return '';
  }


  validateChoice(question: Question, choice: Choice): string {
    const title = choice.Title;
    if (!title || title.length === 0) {
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
    }

    const hasDuplicateTitles = question.Choices.filter(item => {
      if (item.Title == title) {
        return item;
      }
    }).length;

    if (hasDuplicateTitles > 1) {
      return this.translate.instant('[MOCQuest].[Exceptions].Choice.AlreadyExists');
    }

    return '';
  }

  validateConfig(outcomeTypeConfig: OutcomeTypeConfigViewModel): string {
    switch (outcomeTypeConfig.OutcomeType.Id as ChoiceOutcomeType) {
      case ChoiceOutcomeType.DocumentUpdate:
        return !outcomeTypeConfig.UpdateDocumentsConfig ||
          outcomeTypeConfig.UpdateDocumentsConfig.Title == null ||
          outcomeTypeConfig.UpdateDocumentsConfig.Title.length === 0
          ? '[MOCQuest].[Exceptions].ChangeRequest.SpecifyUpdateDocumentsConfig'
          : '';

      case ChoiceOutcomeType.NewDocument:
        return !outcomeTypeConfig.NewDocumentConfig ||
          outcomeTypeConfig.NewDocumentConfig.Title == null ||
          outcomeTypeConfig.NewDocumentConfig.Title && outcomeTypeConfig.NewDocumentConfig.Title.length === 0
          ? '[MOCQuest].[Exceptions].ChangeRequest.SpecifyCreateDocumentConfig'
          : '';

      case ChoiceOutcomeType.Specify:
        return !outcomeTypeConfig.SpecificationConfig ||
          outcomeTypeConfig.SpecificationConfig.Title == null ||
          outcomeTypeConfig.SpecificationConfig.Title && outcomeTypeConfig.SpecificationConfig.Title.length === 0
          ? '[MOCQuest].[Exceptions].ChangeRequest.SpecifySpecificationConfig'
          : '';
    }

    return '';
  }


  validateTaskConfigTitle(title: string): string {
    if (!title || title.length === 0)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

    return '';
  }

  validateDocumentTitle(title: string): string {
    if (!title || title.length === 0)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

    return '';
  }

  validateActivity(title: string): string {
    if (!title || title.length === 0)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');

    return '';
  }

  validateChecklist(checklist: IdentityLookupViewModel) {
    if (!checklist)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
  }

  validateSeverityLevel(severityLevel: LookupViewModel) {
    if (!severityLevel)
      return this.translate.instant('[ISOQuest].[Validation].RequiredFiled');
  }

  initDragula() {
    this.questionsGroup = this.dragulaService.find(this.dragulaQuestions);
    if (this.questionsGroup == null) {
      this.questionsGroup = this.dragulaService.createGroup(this.dragulaQuestions, {
        moves: (el, container, handle) => {
          return handle.className.includes("question-handle-class");
        },
        accepts: (el, target, source) => {
          return target.id === source.id;
        }
      });
    }

    this.choicesGroup = this.dragulaService.find(this.dragulaChoices);
    if (this.choicesGroup == null) {
      this.choicesGroup = this.dragulaService.createGroup(this.dragulaChoices, {
        moves: (el, container, handle) => {
          return handle.className.includes("choice-handle-class");
        },
        accepts: (el, target, source) => {
          return target.id === source.id;
        }
      });
    }
  }
}
