import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { DynamicService, ApplicationService, ModalFormConfig, RowControl, SubmitAction, CancelAction, ModalFormComponent, LabelControl } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'app-share-permalink',
  templateUrl: './share-permalink.component.html',
  styleUrls: ['./share-permalink.component.scss']
})
export class SharePermalinkComponent implements OnInit {
  @Input()
  objectType: string;
  @Input()
  objectId: string;
  @Input()
  taskId: string;

  permalink: string;

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private taskManagementSystemService: TaskManagementSystemService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) { }

  ngOnInit() {
    const that = this;
    this.taskManagementSystemService.getTaskManagementSystemInstance(this.objectType, this.objectId).subscribe(instance => {
      this.permalink = this.applicationService.getBaseUrl() + "/" + instance.Url + "/" + this.taskId

      const modalFormConfig = new ModalFormConfig(
        "[ISOQuest].[TaskManagementSystem].SendAsLink",
        [
          new RowControl([
            new LabelControl({
              text: "[ISOQuest].[TaskManagementSystem].PermalinkMessage"
            })
          ]),
          new RowControl([
            new LabelControl({
              text: that.permalink,
              css: "col-12 font-weight-bold",
            })
          ])
        ],
        [
          new SubmitAction({
            field: "Submit",
            value: "[ISOQuest].[TaskManagementSystem].Task.Copy",
            callback(formSnapshot) {
              var tempInput = document.createElement("input");
              tempInput.style.position = "absolute";
              tempInput.style.left = "-1000px";
              tempInput.style.top = "absolute";
              tempInput.value = that.permalink;
              document.body.appendChild(tempInput);
              tempInput.select();
              document.execCommand("copy");
              document.body.removeChild(tempInput);
              that.translate.get("[ISOQuest].[Common].CopyLinkSuccess").subscribe(translation => {
                that.toastr.success(translation);
              });
            },
            css: "btn btn-primary"
          }),
          new CancelAction({
            field: "Cancel",
            value: "[ISOQuest].[Common].Buttons.Cancel",
            callback() { },
            css: "btn btn-default"
          })]
      );

      this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
        [
          { config: modalFormConfig },
          {
            modalOptions: {
              size: "sm"
            }
          }
        ]);
    });
  }

}
