import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApplicationService } from 'codera-shared';
import { SeverityLevelViewModel } from './types';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SeverityLevelService {
  private apiUrl: string;

  constructor(
    private http: HttpClient,
    private applicationService: ApplicationService
  ) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  getById(id: string): Observable<SeverityLevelViewModel> {
    const url = `${this.apiUrl}/SeverityLevel/GetById`;
    return this.http.post<SeverityLevelViewModel>(url, { id: id });
  }

  update(model: SeverityLevelViewModel): Observable<boolean> {
    const url = `${this.apiUrl}/SeverityLevel/Update`;
    return this.http.post<boolean>(url, model);
  }
}
