import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ApplicationService, ApprovalStatus, WorkflowPathsModel } from 'codera-shared';
import { Subscription } from 'rxjs';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { AnalysisObjectType, ChecklistGroup } from '../../../../system-controls/checklist-groups/types';
import { ChangeRequestService } from '../../../change-request.service';
import { AnalysisConfigModel } from '../../../types';
import { AnalysisService } from '../analysis.service';

@Component({
    selector: 'answer-mode-analysis',
    templateUrl: './answer-mode-analysis.component.html',
    styleUrls: ['./answer-mode-analysis.component.scss']
})
export class AnswerModeAnalysisComponent implements OnInit, OnDestroy {
    @Input() groups: ChecklistGroup[];
    @Input() analysisConfig: AnalysisConfigModel;
    @Input() workflowObjectType: string;
    @Input() workflowPaths: WorkflowPathsModel;

    approvalStatus = ApprovalStatus;
    analysisObjectType = AnalysisObjectType;
    backendErrors: string[];
    settings: MOCQuestSettingsViewModel;
    approvalInProgress: boolean;
    subscriptions: Subscription[];

    constructor(
        private analysisService: AnalysisService,
        private applicationService: ApplicationService,
        private changeRequestService: ChangeRequestService,
    ) { }

    ngOnDestroy(): void {
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
    }

    ngOnInit() {
        this.subscriptions = [];
        this.backendErrors = [];
        this.settings = this.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel;
        this.subscriptions.push(
            this.analysisService.approvalInProgress$.subscribe(approvalInProgress => {
                this.approvalInProgress = approvalInProgress;
            })
        );
    }

    exportAnalysis() {
        this.changeRequestService.exportChangeRequestAnalysisXlsx(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);
    }

    exportAnalysisForPrint() {
        this.changeRequestService.exportChangeRequestAnalysisForPrintXlsx(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);
    }

    groupsTrackByFn(index: number, item: ChecklistGroup) {
        return item.Id;
    };
}
