import { Component, ComponentRef, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, BulkChangeStatusComponent, BulkDeleteComponent, ComponentControl, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderDropdownItem, HeaderItemsConfig, InputControl, IqStatusPickerComponent, ModalFormConfig, RenderButtonsPosition, RowControl, TextConfirmationModalComponent, ModalFormComponent } from 'codera-shared';
import { ReasonTypeService } from './reason-type.service';
import { ReasonTypeGridModel, ReasonTypeViewModel } from './types';
import { ReasonTypeFactory } from './reason-type.factory';

@Component({
    selector: 'reason-type',
    templateUrl: './reason-type.page.html'
})
export class ReasonTypePage implements OnInit {

    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    tableSource: string;
    modalConfig: ModalFormConfig;
    model: ReasonTypeViewModel;
    renderButtonsTypes = RenderButtonsPosition;
    headerItemsConfig: HeaderItemsConfig;

    constructor(
        private router: Router,
        private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private reasonTypeService: ReasonTypeService,
        private reasonTypeFactory: ReasonTypeFactory,
        private applicationService: ApplicationService
    ) {
        this.model = new ReasonTypeViewModel();
    }

    ngOnInit() {
        const that = this;

        this.tableSource = `ReasonType/Grid`;
        this.pageTitle = this.route.snapshot.data["pageTitle"];

        this.headerItemsConfig = new HeaderItemsConfig([
            new HeaderButtonItem({
                name: "new-reason-type",
                icon: "fa fa-plus",
                label: "[MOCQuest].[Common].Buttons.ReasonType.New",
                action: that.summonDialog
            }),
            new HeaderDropdownItem({
                name: "bulk-actions",
                icon: "",
                label: "[ISOQuest].[Common].Buttons.WithSelected",
                dropdownActions: [
                    new HeaderButtonItem({
                        name: "change-status",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.ChangeStatus",
                        action: that.bulkChangeStatus
                    }),
                    new HeaderButtonItem({
                        name: "bulk-delete",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.Delete",
                        action: that.bulkDelete
                    })
                ]
            })
        ]);

        // set the title in the tab
        this.translate.get(this.pageTitle).subscribe(translated => {
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translated}`);
        });

        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqStatusPickerComponent,
                        field: "Status",
                        label: "[ISOQuest].[Common].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });

        // table config
        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "string",
                    flexGrow: 2
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "status"
                },
                {
                    field: "ModifiedBy",
                    title: "[ISOQuest].[Common].ModifiedBy",
                    sortable: "ModifiedBy",
                    type: "personnel"
                },
                {
                    field: "ModifiedAt",
                    title: "[ISOQuest].[Common].ModifiedAt",
                    sortable: "ModifiedAt",
                    type: "date"
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: ReasonTypeGridModel) {
                        that.reasonTypeService.getById(row.Id).subscribe((response: ReasonTypeViewModel) => {
                            that.model = response;
                            that.modalConfig = that.reasonTypeFactory.getModalConfig(that.model, () => { that.fluentTableConfig.notifyDataChanged('item-updated'); });

                            that.modalConfig.modalHeader = "[MOCQuest].[Common].Buttons.ReasonType.Edit";
                            that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
                                { "config": that.modalConfig },
                                { "model": that.model },
                                {
                                    "modalOptions": {
                                        size: "sm"
                                    }
                                }
                            ], true);
                        });
                    },
                    render(row: ReasonTypeGridModel) {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
                    },
                    flexGrow: 0.28
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: ReasonTypeGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.reasonTypeService.delete(row.Id)
                                        .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                                }
                            }
                        ], true);
                    },
                    render(row: ReasonTypeGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.28
                }
            ],
            selection: {
                mode: "checkbox"
            },
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });
    }

    summonDialog = () => {
        this.model = new ReasonTypeViewModel();
        this.modalConfig = this.reasonTypeFactory.getModalConfig(this.model, () => { this.fluentTableConfig.notifyDataChanged('item-created'); });
        this.modalConfig.modalHeader = "[MOCQuest].[Common].Buttons.ReasonType.New";
        this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
            { "config": this.modalConfig },
            { "model": this.model },
            { "modalOptions": { size: "sm" } }
        ], true);
    }

    bulkChangeStatus = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "ReasonType/BulkChangeStatus" },
            { "modalOptions": { size: "sm" } }
        ]);
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "ReasonType/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }
}
