import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { DocumentCallbacks, DocumentPermissions, IsoQuestEventManagerService } from 'codera-shared';
import { ChangeRequestService } from '../../change-request.service';

@Component({
    selector: 'change-request-documents',
    templateUrl: './change-request-documents.component.html'
})
export class ChangeRequestDocumentsComponent implements OnInit, OnDestroy {
    @Input() objectId: string;
    callbacks: DocumentCallbacks;
    documentsPermissions: DocumentPermissions;
    @Output() refreshDocumentsCounter: EventEmitter<void>;

    constructor(private changeRequestService: ChangeRequestService,
                private isoQuestEventManager: IsoQuestEventManagerService)
    {
        this.refreshDocumentsCounter = new EventEmitter();
    }

    ngOnInit() {
        this.changeRequestService.getPermissions(this.objectId).subscribe((permissions) => {
            this.documentsPermissions = {
                CanPickAndRemoveDocuments: permissions.CanManageChangeRequestDocuments,
                CanDelete: permissions.CanManageChangeRequestDocuments
            };
        });

        this.callbacks = {
            AddDocuments: this.changeRequestService.addDocuments,
            RemoveDocument: this.changeRequestService.removeDocument,
            ListDocuments: this.changeRequestService.listDocuments,
            DocumentsChanged: (): void => {
                this.refreshDocumentsCounter.emit();
            }
        };
    }

    ngOnDestroy() { }

}
