import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';

@Component({
  selector: 'tms-task-priority-icon-picker',
  templateUrl: './tms-task-priority-icon-picker.component.html',
  styleUrls: ['./tms-task-priority-icon-picker.component.scss']
})
export class TmsTaskPriorityIconPickerComponent implements OnInit {
  @Input()
  model: IdentityLookupViewModel;
  @Input()
  label: string;
  @Input()
  isDisabled: boolean;
  @Input()
  isRequired: boolean;

  @Output()
  modelChange: EventEmitter<IdentityLookupViewModel>;

  staticData: IdentityLookupViewModel[];
  constructor() {
    this.modelChange = new EventEmitter();
    this.staticData = [];
  }

  ngOnInit() {
    const that = this;

    const allowedIcons = [
      "fa fa-angle-double-down",
      "fa fa-angle-double-up",
      "fa fa-angle-down",
      "fa fa-angle-up",
      "fa fa-arrow-circle-down",
      "fa fa-arrow-circle-up",
      //"fa fa-arrow-circle-o-down",
      //"fa fa-arrow-circle-o-up",
      "fa fa-arrow-down",
      "fa fa-arrow-up",
      "fa fa-caret-down",
      "fa fa-caret-up",
      //"fa fa-caret-square-o-down",
      //"fa fa-caret-square-o-up",
      "fa fa-chevron-circle-down",
      "fa fa-chevron-circle-up",
      "fa fa-chevron-down",
      "fa fa-chevron-up",
      //"fa fa-exchange",
      //"fa fa-long-arrow-down",
      //"fa fa-long-arrow-up"
    ];

    allowedIcons.forEach((icon, index) => {
      this.staticData.push(new IdentityLookupViewModel(index, icon, true));
    });

    setTimeout(() => {
      if (this.isRequired && !this.model)
        this.onSelect(this.staticData[0]);
    })
  }

  onSelect(newValue: IdentityLookupViewModel) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
