import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, CancelAction, DynamicService, FluentTabConfig, InputControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, WorkflowPathsModel } from 'codera-shared';
import { AnalysisConfigModel, UpdateOnChangeType } from 'src/app/change-request/types';
import { MOCQuestSettingsViewModel } from 'src/app/settings/types';
import { ChecklistGroup } from 'src/app/system-controls/checklist-groups/types';
import { AnalysisService } from '../../analysis.service';
import { ManageModeChecklistGroupApprovalsComponent } from '../manage-mode-checklist-group-approvals/manage-mode-checklist-group-approvals.component';
import { ManageModeChecklistGroupComponent } from '../manage-mode-checklist-group/manage-mode-checklist-group.component';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'manage-mode-checklist-group-wrapper',
  templateUrl: './manage-mode-checklist-group-wrapper.component.html',
  styleUrls: ['./manage-mode-checklist-group-wrapper.component.scss']
})
export class ManageModeChecklistGroupWrapperComponent implements OnInit {
  @Input() group: ChecklistGroup;
  @Input() analysisConfig: AnalysisConfigModel;
  @Input() workflowObjectType: string;
  @Input() workflowPaths: WorkflowPathsModel;

  fluentTabs: FluentTabConfig[];
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private analysisService: AnalysisService,
    private applicationService: ApplicationService,
  ) { }

  ngOnInit() {
    const that = this;

    this.fluentTabs = [
      new FluentTabConfig({
        componentClass: ManageModeChecklistGroupComponent,
        title: "[ISOQuest].[Common].General",
        data: [
          { group: that.group },
          { analysisConfig: that.analysisConfig }
        ]
      }),
      new FluentTabConfig({
        componentClass: ManageModeChecklistGroupApprovalsComponent,
        title: (tab) => {
          return `<span><i class='fa fa-sitemap'></i>&nbsp;${that.translate.instant("[ISOQuest].[Workflow].Common.Approvals")}</span>`;
        },
        data: [
          { group: that.group },
          { objectId: that.group.Id },
          { objectType: that.workflowObjectType },
          { workflowPaths: that.workflowPaths }
        ],
        show: (that.applicationService.getCurrentModuleSettings() as MOCQuestSettingsViewModel).AllowChecklistApproval
      })
    ];
  }

  summonUpdateTitleModal() {
    const that = this;
    const title = this.group.Title;
    const modalConfig = new ModalFormConfig(
      "[MOCQuest].[Common].UpdateTitle",
      [
        new RowControl([
          new InputControl({
            field: "Title",
            type: "text",
            css: "col-12",
            label: "[ISOQuest].[Common].Title",
            required: true
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          css: "btn btn-primary",
          callback: (formValueSnapshot) => {
            that.analysisService.updateOnChange(that.group.Id, UpdateOnChangeType.GroupTitle, formValueSnapshot.Title).subscribe(response => {
              that.toastr.success(that.translate.instant('[ISOQuest].[Common].Notifications.ItemUpdated'));
              that.analysisService.fetchChecklistGroups();
            });
          }
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          css: "btn btn-default",
          callback: () => {

          }
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { config: modalConfig },
      { model: { Title: title } },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }
}
