import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, CancelAction, ComponentControl, CustomAction, DynamicService, FacilityPickerComponent, InputControl, IqPersonnelPickerComponent, IsoQuestEventManagerService, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl } from 'codera-shared';
import { WorkflowStages } from 'src/app/settings/types';
import { ChangeRequestTypePickerComponent } from 'src/app/system-controls/change-request-type/picker/change-request-type-picker.component';
import { SeverityLevelLevelPickerComponent } from 'src/app/system-controls/severity-level/picker/severity-level-level-picker/severity-level-level-picker.component';
import { WorkflowMatrixViewModel, WorkflowStage } from '../../types';
import { WorkflowMatrixService } from '../../workflow-matrix.service';
import { WorkflowStageMatrixComponent } from '../../workflow-stage-matrix/workflow-stage-matrix.component';

@Component({
    selector: 'app-create-update-workflow-matrix',
    templateUrl: './create-update-workflow-matrix.component.html',
    styleUrls: ['./create-update-workflow-matrix.component.scss']
})
export class CreateUpdateWorkflowMatrixComponent implements OnInit {
    @Input()
    id: string;

    @Output()
    onCreateUpdate: EventEmitter<WorkflowMatrixViewModel>;

    model: WorkflowMatrixViewModel;
    modalFormConfig: ModalFormConfig;
    component: ComponentRef<ModalFormComponent>;

    constructor(
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private workflowMatrixService: WorkflowMatrixService,
        private isoQuestEventManager: IsoQuestEventManagerService
    ) {
        this.onCreateUpdate = new EventEmitter<WorkflowMatrixViewModel>();
    }

    ngOnInit() {
        this.summonDialog();
        if (this.id) {
            this.workflowMatrixService.getById(this.id).subscribe((response: WorkflowMatrixViewModel) => {
                this.model = response;
                this.component.instance.model = response;
                const changeRequestTypeControl = this.modalFormConfig.getControlByField("ChangeRequestType") as ComponentControl;
                changeRequestTypeControl.getDataByKey("facility").facility = this.component.instance.model.Facility;
                this.isoQuestEventManager.notify("invalidateData", null);
            });
        }
    }

    summonDialog() {
        const that = this;

        this.model = new WorkflowMatrixViewModel();
        let requiredStages = [
            WorkflowStages[WorkflowStages.Requested],
            WorkflowStages[WorkflowStages.Implementation],
            WorkflowStages[WorkflowStages.Launch],
            WorkflowStages[WorkflowStages.Closure]
        ];

        this.model.WorkflowStageMatrix = Object.keys(WorkflowStages).filter((item) => {
            return isNaN(Number(item));
        }).filter((item) => {
            return item !== WorkflowStages[WorkflowStages.Terminate];
        }).map((stageName: string) => {
            let requiredStage = requiredStages.includes(stageName);
            let stage: WorkflowStage = undefined;
            if (requiredStage) {
                stage = new WorkflowStage(null, WorkflowStages[stageName], stageName, true, true, true, true);
            } else {
                stage = new WorkflowStage(null, WorkflowStages[stageName], stageName, false, false, false);
            }
            return stage;
        });

        let title = this.id
            ? "[MOCQuest].[SystemControls].WorkflowMatrix.Edit"
            : "[MOCQuest].[SystemControls].WorkflowMatrix.New";

        this.modalFormConfig = new ModalFormConfig(
            title,
            [
                new RowControl(
                    [
                        new ComponentControl({
                            componentClass: FacilityPickerComponent,
                            show: this.applicationService.getCurrentSite().IsMultiSite,
                            field: "Facility",
                            data: [
                                { "label": "[ISOQuest].[Common].Facility" },
                                { "onlyActive": true },
                                { "isRequired": true },
                                {
                                    "modelChange": (newValue: LookupViewModel) => {
                                        this.onFacilityChange(newValue);
                                    }
                                }
                            ]
                        })
                    ],
                    this.applicationService.getCurrentSite().IsMultiSite
                ),
                new RowControl([
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        data: [
                            { "label": "[MOCQuest].[SystemControls].ChangeRequestType" },
                            { "onlyActive": true },
                            { "isRequired": true },
                            { "isDisabled": this.applicationService.getCurrentSite().IsMultiSite && !this.id },
                            { "facility": null },
                            { "itemsForWorkflowMatrix": true }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "ChangeCoordinators",
                        data: [
                            { targetModule: "MOCQuest" },
                            { label: "[MOCQuest].[ChangeRequest].ChangeCoordinators" },
                            { isMultiple: true },
                            { onlyActive: true }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: SeverityLevelLevelPickerComponent,
                        field: "SeverityLevel",
                        data: [
                            { label: "[MOCQuest].[SystemControls].WorkflowMatrix.DefaultSeverityLevel" },
                            { isRequired: true }
                        ]
                    })
                ]),
                new RowControl([
                    new InputControl({
                        field:"AllowImplementationAndPssrToBeConductedAtSameTime",
                        type:"checkbox",
                        label:"[MOCQuest].[SystemControls].AllowImplementationAndPssrToBeConductedAtSameTime"
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: WorkflowStageMatrixComponent,
                        field: "WorkflowStageMatrix",
                        data: [
                            { submitted: false }
                        ]
                    })
                ]),
                new RowControl([
                    new InputControl({
                        type: "checkbox",
                        field: "Status",
                        label: "[ISOQuest].[Common].Choices.Active",
                        css: "col-md-12"
                    })
                ])
            ],
            [
                new CustomAction({
                    actionType: "button",
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    css: "btn btn-primary",
                    callback: (value, valid) => {
                        let workflowStageMatrix: ComponentControl = that.modalFormConfig.getControlByField("WorkflowStageMatrix") as ComponentControl;
                        workflowStageMatrix.getDataByKey("submitted").submitted = true;
                        that.isoQuestEventManager.notify("invalidateData", null);
                        that.component.instance.config.submitted = true;
                        if (valid) {
                            that.workflowMatrixService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
                                if (typeof response === "string")
                                    that.model.Id = response;

                                that.onCreateUpdate.emit(that.model);
                                that.modalFormConfig && that.modalFormConfig.modalRef && that.modalFormConfig.modalRef.close();
                            }, (error) => {
                                that.isoQuestEventManager.notify("manuallyTriggerServerValidation", error);
                            });
                        }
                    }
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    css: "btn btn-default",
                    callback: () => {

                    }
                })
            ]);

        this.component = this.dynamicService.addComponent(
            this.viewContainer,
            ModalFormComponent,
            [
                { "config": this.modalFormConfig },
                { "model": this.model },
                {
                    "modalOptions":
                        { "size": "md" }
                }
            ]) as ComponentRef<ModalFormComponent>;
    }

    /**
    * This method updates the input on facility change on the facility dependent pickers
    * @param newValue
    */
    private onFacilityChange(newValue: LookupViewModel): void {
        const changeRequestTypeControl = this.modalFormConfig.getControlByField("ChangeRequestType") as ComponentControl;
        if (newValue) {
            changeRequestTypeControl.getDataByKey("facility").facility = newValue;
            changeRequestTypeControl.getDataByKey("isDisabled").isDisabled = false;
        }
        else {
            changeRequestTypeControl.getDataByKey("isDisabled").isDisabled = true;
        }
        this.model.ChangeRequestType = null;
    }
}
