import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LookupViewModel, IdentityLookupViewModel } from 'codera-shared';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ChangeRequestStatus } from '../../types';

@Component({
  selector: 'app-status-picker',
  templateUrl: './status-picker.component.html'
})
export class ChangeRequestStatusPickerComponent implements OnInit {
  @Input() model: LookupViewModel | LookupViewModel[];
  @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

  @Input() label: string;
  @Input() controllerName: string;
  @Input() placeholder: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() excludedStatuses: IdentityLookupViewModel[]

  staticData: IdentityLookupViewModel[];

  constructor(
    private translate: TranslateService,
    private router: Router) {
    this.modelChange = new EventEmitter();
    this.excludedStatuses = [];
  }

  ngOnInit() {
    if (!this.label) {
      this.translate.get("[MOCQuest].[ChangeRequest].Status").subscribe(localized => this.label = localized);
    }

    this.staticData = Object.keys(ChangeRequestStatus).filter((item) => {
      return isNaN(Number(item));
    }).map(changeRequestStatusValue => {
      return new IdentityLookupViewModel(ChangeRequestStatus[changeRequestStatusValue], this.translate.instant("[MOCQuest].[ChangeRequest].Status." + changeRequestStatusValue), true);
    }).sort((left, right) => Number(left.Id) - Number(right.Id));

    if (this.excludedStatuses.length > 0) {
      let excludedStatusesIds = this.excludedStatuses.map(x => x.Id);
      this.staticData = this.staticData.filter((item) => {
        return !excludedStatusesIds.some(x => x === item.Id);
      });
    }
  }

  onChange(newValue: LookupViewModel | LookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
