import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { DynamicService, ModalFormConfig, RowControl, ComponentControl, SubmitAction, LabelControl, CancelAction, ModalFormComponent, BulkOperationExecutionResultComponent } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { TaskStatusPickerComponent } from '../../task/task-status-picker/task-status-picker.component';
import { TmsTaskColorPickerComponent } from '../../tms-task-colors/tms-task-color-picker/tms-task-color-picker.component';
import { BulkChangeColorViewModel, BulkChangeTaskStatusViewModel } from '../../types';

@Component({
  selector: 'bulk-change-tms-task-status',
  template: ''
})
export class BulkChangeTmsTaskStatusComponent implements OnInit {
  @Input()
  selectedIds: string[];

  @Output()
  onSuccess: EventEmitter<void>;
  model: BulkChangeTaskStatusViewModel;
  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private taskManagementSystemService: TaskManagementSystemService
  ) {
    this.onSuccess = new EventEmitter<void>();
  }


  ngOnInit() {
    const that = this;

    if (this.selectedIds == null || this.selectedIds.length == 0)
      throw new Error("Please provide TMS Task ids");

    this.model = new BulkChangeTaskStatusViewModel(this.selectedIds, null);
    const modalFormConfig = new ModalFormConfig(
      "[ISOQuest].[TaskManagementSystem].ChangeTasksStatus",
      [
        new RowControl([
          new ComponentControl({
            componentClass: TaskStatusPickerComponent,
            field: "Status",
            data: [
              { isRequired: true },
              { onlyActive: true }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formValueSnapshot) {
            that.taskManagementSystemService.bulkChangeTaskStatus(that.model).subscribe((response) => {
              that.dynamicService.addComponent(that.viewContainer, BulkOperationExecutionResultComponent, [
                { model: response },
                {
                  modalOptions: {
                    size: "md"
                  }
                }
              ]);
              that.onSuccess.emit();
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback(formValueSnapshot) { },
          css: "btn btn-default"
        })
      ]
    );

    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { config: modalFormConfig },
      { model: this.model },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }
}
