import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { FluentTableConfig, DynamicService, ApplicationService, TextConfirmationModalComponent } from 'codera-shared';
import { WorkflowStageGridModel } from '../types';
import { TranslateService } from '@ngx-translate/core';
import { CreateUpdateLocalizationItemComponent } from 'src/app/localization/create-update/create-update-localization.component';
import { LocalizationService } from 'src/app/localization/localization.service';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

@Component({
  selector: 'app-workflow-stages',
  templateUrl: './workflow-stages.component.html',
  styleUrls: ['./workflow-stages.component.scss']
})
export class WorkflowStagesComponent implements OnInit {

  fluentTableConfig: FluentTableConfig;
  tableSource: string;
  siteKey: string;
  constructor(
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private localizationService: LocalizationService,
    private http: HttpClient
  ) {
    this.siteKey = this.applicationService.getCurrentSiteKey();
  }

  ngOnInit() {
    const that = this;

    this.tableSource = "/Settings/WorkflowStagesGrid";
    this.fluentTableConfig = new FluentTableConfig({
      columns: [
        {
          title: "[MOCQuest].[Settings].WorkflowStages.StageName",
          field: "StageName",
          type: "custom",
          render: (row: WorkflowStageGridModel) => {
            var translatedStageName = "-";
            that.translate.get(`[MOCQuest].[WorkflowStage].StageName.${row.StageName}`).subscribe((translation: string) => {
              translatedStageName = translation;
            });

            return translatedStageName;
          }
        },
        {
          title: "[ISOQuest].[Common].Label",
          field: "Label",
          type: "custom",
          render: (row: WorkflowStageGridModel) => {
            let translatedLabel = "-";
            let workflowStageLabel = `[MOCQuest].[WorkflowStage].${row.StageName}.Label.${this.siteKey}`;
            that.translate.get(workflowStageLabel).subscribe((translation: string) => {
              if (translation === workflowStageLabel) // this mean there is no translation for this stage so we need to get default 
                that.translate.get(`[MOCQuest].[WorkflowStage].${row.StageName}.Default`).subscribe((translation: string) => {
                  translatedLabel = translation;
                });
              else
                translatedLabel = translation;
            });

            return `<span>${translatedLabel}</span>`;
          }
        },
        {
          field: "editAction",
          title: "",
          type: "action",
          handler: (row: WorkflowStageGridModel) => {
            that.dynamicService.addComponent(that.viewContainer, CreateUpdateLocalizationItemComponent, [
              { "isExternal": true },
              { "id": row.LabelId },
              { "key": `[MOCQuest].[WorkflowStage].${row.StageName}.Label.${this.siteKey}` },
              { "sectionId": "d6e8a7fb-5362-4303-96b9-f2db4304b0b0" },
              { "moduleId": 18 },
              { "javaScript": true },
              {
                "onCreateUpdate": () => {
                  that.fluentTableConfig.notifyDataChanged("item-updated");
                  that.fetchLocalization();
                }
              }
            ]);
          },
          render(row: WorkflowStageGridModel) {
            let translatedLabel = "-";
            that.translate.get("[ISOQuest].[Common].Buttons.Edit").subscribe((translation: string) => translatedLabel = translation);
            return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${translatedLabel}</span></a>`;
          },
          flexGrow: 0.28
        },
        {
          field: "deleteAction",
          title: "",
          type: "action",
          handler(row: WorkflowStageGridModel) {
            that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
              { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
              { "modalText": "[MOCQuest].[Settings].WorkflowStages.LocalizationLabelDelete" },
              {
                "modalOptions": {
                  size: "sm"
                }
              },
              {
                "successCallback": () => {
                  that.localizationService.delete(row.LabelId, true)
                    .subscribe(() => {
                      that.fluentTableConfig.notifyDataChanged("item-deleted");
                      that.fetchLocalization();
                    });
                }
              }
            ], true);
          },
          render(row: WorkflowStageGridModel) {
            if (!row.LabelId)
              return "";

            let translatedLabel = "-";
            that.translate.get("[ISOQuest].[Common].RestoreToDefault").subscribe((translation: string) => translatedLabel = translation);
            return `<a class="btn btn-sm btn-default blue-stripe pull-right btn-overlay"><i class="far fa-undo"></i>&nbsp;<span>${translatedLabel}</span></a>`;
          },
          flexGrow: 0.28
        }
      ],
      sorting: {
        "Order": "asc"
      },
      footer: {
        show: true,
        showExports: false,
        showPageSize: true,
        showPaging: true,
        showRefreshButton: true
      }
    });

  }

  fetchLocalization() {
    this.localizationService.publishModuleLocalizations(this.applicationService.getCurrentModuleKey()).subscribe(() => {
      const date = new Date();
      if (location.port) {
        //angular cli
        this.translate.currentLoader = new TranslateHttpLoader(this.http, '/assets/locales/', `.json?ts=${date.getTime()}`);
      } else {
        //iis
        this.translate.currentLoader = new TranslateHttpLoader(this.http, '../../src/assets/locales/', `.json?ts=${date.getTime()}`);
      }
      this.translate.reloadLang(this.translate.currentLang);
    });
  }
}
