import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, DynamicService, InputControl, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { TmsTaskCategoryService } from '../tms-task-category.service';
import { TMSTaskCategoryViewModel } from '../types';

@Component({
  selector: 'app-create-update-tms-task-category',
  templateUrl: './create-update-tms-task-category.component.html',
  styleUrls: ['./create-update-tms-task-category.component.scss']
})
export class CreateUpdateTmsTaskCategoryComponent implements OnInit {
  @Input()
  id: string;
  @Input()
  objectType: string;
  @Input()
  objectId: string;

  @Output()
  onCreateUpdate: EventEmitter<LookupViewModel>;

  modalFormConfig: ModalFormConfig;
  model: TMSTaskCategoryViewModel;
  private componentRef: ComponentRef<ModalFormComponent>;

  constructor(
    private tmsTaskCategoryService: TmsTaskCategoryService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef
  ) {
    this.onCreateUpdate = new EventEmitter<LookupViewModel>();
  }

  ngOnInit() {
    if (!this.objectType)
      throw new Error("objectType is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    this.model = new TMSTaskCategoryViewModel();
    let title = this.id
      ? "[ISOQuest].[TMSTaskCategory].EditTaskCategory"
      : "[ISOQuest].[TMSTaskCategory].NewTaskCategory";

    this.prepareModalFormConfig(title);
    this.componentRef = this.summonDialog();

    if (this.id)
      this.getById();
  }

  prepareModalFormConfig(title: string) {
    const that = this;
    this.modalFormConfig = new ModalFormConfig(
      title,
      [
        new RowControl([
          new InputControl({
            type: "text",
            field: "Title",
            label: "[ISOQuest].[Common].Title",
            required: true,
            css: "col-md-12",
            min: 0,
            max: 250
          })
        ]),
        new RowControl([
          new InputControl({
            type: "checkbox",
            field: "Status",
            label: "[ISOQuest].[Common].Choices.Active",
            css: "col-md-12"
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            that.tmsTaskCategoryService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
              if (typeof response === "string")
                that.onCreateUpdate.emit(new LookupViewModel(response, that.model.Title, that.model.Status));
              else if (typeof response === "boolean")
                that.onCreateUpdate.emit(new LookupViewModel(that.model.Id, that.model.Title, that.model.Status));
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );
  }

  summonDialog(): ComponentRef<ModalFormComponent> {
    this.model = new TMSTaskCategoryViewModel();
    this.model.ObjectId = this.objectId;
    this.model.ObjectType = this.objectType;

    let component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
      [
        { config: this.modalFormConfig },
        { model: this.model },
        {
          modalOptions: {
            size: "sm"
          }
        }
      ]
    );
    return component as ComponentRef<ModalFormComponent>;
  }

  getById() {
    this.tmsTaskCategoryService.getById(this.id).subscribe((response: TMSTaskCategoryViewModel) => {
      this.model = response;
      this.componentRef.instance.model = response;
    });
  }
}
