import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ApplicationService, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderItemsConfig, RenderButtonsPosition } from "codera-shared";
import { ChangeRequestService } from '../change-request.service';
import { CreateUpdateChangeRequestComponent } from '../create-update/create-update-change-request.component';
import { ChangeRequestPermissionsModel } from '../types';

@Component({
    selector: 'my-facility-change-request',
    templateUrl: './my-facility-change-request.page.html',
    styleUrls: ['./my-facility-change-request.page.scss']
})
export class MyFacilityChangeRequestPage implements OnInit {
    pageTitle: string;
    renderButtonsTypes = RenderButtonsPosition;
    headerItemsConfig: HeaderItemsConfig;

    tableSource: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentTableConfig: FluentTableConfig;
    fluentSearchConfig: FluentSearchConfig;

    constructor(private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private changeRequestService: ChangeRequestService) { }

    ngOnInit() {
        const that = this;
        this.pageTitle = this.route.snapshot.data["pageTitle"];
        let translatedTitle;

        this.translate.get(this.pageTitle).subscribe(translated => {
            translatedTitle = translated
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translatedTitle}`);
        });

        this.changeRequestService.getPermissions().subscribe((permissions: ChangeRequestPermissionsModel) => {
            this.headerItemsConfig = new HeaderItemsConfig([
                new HeaderButtonItem({
                    name: "new-change-request",
                    icon: "fa fa-plus",
                    label: "[MOCQuest].[ChangeRequest].New",
                    action: that.summonDialog.bind(this),
                    show: permissions.CanCreateChangeRequest
                })
            ]);
        });

        this.tableSource = `ChangeRequest/MyFacilityChangeRequestsGrid`;
    }

    summonDialog() {
        this.dynamicService.addComponent(this.viewContainer, CreateUpdateChangeRequestComponent, [{}], true);
    }
}
