import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ApplicationService, IdentityLookupViewModel } from 'codera-shared';
import { ChangeRequestTmsGrouping } from '../../types';

@Component({
    selector: 'change-request-tms-group-by-picker',
    templateUrl: './change-request-tms-group-by-picker.component.html',
    styleUrls: ['./change-request-tms-group-by-picker.component.scss']
})
export class ChangeRequestTmsGroupByPickerComponent implements OnInit {
    @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
    @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

    @Input() label: string;
    @Input() controllerName: string;
    @Input() placeholder: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;
    @Input() defaultSelected: ChangeRequestTmsGrouping;

    staticData: IdentityLookupViewModel[];

    constructor(
        private applicationService: ApplicationService
    ) {
        this.staticData = [];
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        if (!this.label) {
            this.label = '[ISOQuest].[Common].GroupBy';
        }

        this.staticData.push(new IdentityLookupViewModel(ChangeRequestTmsGrouping.ChangeRequest, "[MOCQuest].[SiteMap].ChangeRequest", true));
        if (this.applicationService.getCurrentSite().IsMultiSite)
            this.staticData.push(new IdentityLookupViewModel(ChangeRequestTmsGrouping.Facility, "[ISOQuest].[Common].Facility", true));
        this.staticData.push(new IdentityLookupViewModel(ChangeRequestTmsGrouping.Assignee, "[ISOQuest].[Common].Assignee", true));
        this.staticData.push(new IdentityLookupViewModel(ChangeRequestTmsGrouping.TaskStatus, "[ISOQuest].[Common].Status", true));
        if (!(this.defaultSelected === undefined || this.defaultSelected === null)) {
            let defaultValue = this.staticData.find(x => x.Id === this.defaultSelected as number);
            if (!(defaultValue === undefined || defaultValue === null))
                this.model = defaultValue;
        }
    }

    onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }
}
