import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { RecommendedActionsCountViewModel, RecommendedActionViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class RecommendedActionService {

    private apiUrl: string;

    constructor(
        private httpClient: HttpClient,
        private applicationService: ApplicationService
    ) {
        this.apiUrl = this.applicationService.getApiUrl();
    }

    getById(id: string): Observable<RecommendedActionViewModel> {
        const url = `${this.apiUrl}/RecommendedAction/GetById`;
        return this.httpClient.post(url, { id: id });
    }

    createOrUpdate(model: RecommendedActionViewModel, changeRequestId: string): Observable<string | boolean> {
        const url = `${this.apiUrl}/RecommendedAction/CreateOrUpdate`;
        return this.httpClient.post<string | boolean>(url, { model: model, changeRequestId: changeRequestId });
    }

    delete(id: string, changeRequestId: string): Observable<boolean> {
        const url = `${this.apiUrl}/RecommendedAction/Delete`;
        return this.httpClient.post<boolean>(url, { id: id, changeRequestId: changeRequestId });
    }
    
    getRecommendedActionsCount(changeRequestId: string): Observable<RecommendedActionsCountViewModel> {
        const url = `${this.apiUrl}/RecommendedAction/GetRecommendedActionsCount`;
        return this.httpClient.post<RecommendedActionsCountViewModel>(url, { changeRequestId: changeRequestId });
    }
    
}
