import { Component, OnInit, Input, ViewChild, TemplateRef, ViewContainerRef, HostListener } from '@angular/core';
import { ModalFormConfig, DynamicService, RowControl, ComponentControl, StaticInfoComponent, CancelAction, ModalFormComponent } from 'codera-shared';
import { ReasonTypeViewModel } from '../types';
import { ReasonTypeService } from '../reason-type.service';

@Component({
  selector: 'reason-type-info',
  templateUrl: './reason-type-info.component.html',
  styleUrls: ['./reason-type-info.component.scss']
})
export class ReasonTypeInfoComponent implements OnInit {

  @Input() id: string;

  private modalConfig: ModalFormConfig;
  model: ReasonTypeViewModel;

  @ViewChild('statusTemplate') public statusTemplate: TemplateRef<any>;

  constructor(
    private reasonTypeService: ReasonTypeService,
    private viewContainer: ViewContainerRef,
    private dynamicService: DynamicService
  ) { }

  ngOnInit() {
    this.modalConfig = new ModalFormConfig("[MOCQuest].[SiteMap].SystemControls.ReasonType.Single", [
      new RowControl([
        new ComponentControl({
          componentClass: StaticInfoComponent,
          field: "Title",
          css: "col-sm-6",
          data: [
            { label: "[ISOQuest].[Common].Title" }
          ]
        }),
        new ComponentControl({
          componentClass: StaticInfoComponent,
          field: "Status",
          css: "col-sm-6",
          data: [
            { label: "[ISOQuest].[Common].Status" },
            { template: this.statusTemplate }
          ]
        })
      ])
    ], [
      new CancelAction({
        field: "Cancel",
        value: "[ISOQuest].[Common].Buttons.OK",
        callback() { },
        css: "btn btn-primary"
      })
    ]);
  }

  @HostListener("click")
  onClick() {
    this.reasonTypeService.getById(this.id).subscribe(model => {
      this.model = model;

      this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
        { "config": this.modalConfig },
        { "model": model },
        { "modalOptions": { size: "md" } }
      ], true);
    });
  }

}
