import { Component, ComponentRef, EventEmitter, Input, NgZone, OnDestroy, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, AttachmentsMultipleModeComponent, AttachmentViewModel, BulkOperationExecutionResultComponent, CancelAction, ComponentControl, CustomAction, DynamicService, FacilityPickerComponent, FrequencySchedulePickerComponent, IdentityLookupViewModel, InputControl, IqDatePickerComponent, IqDepartmentPickerComponent, IqNumberComponent, IqPersonnelPickerComponent, IsoQuestEvent, IsoQuestEventManagerService, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextEditorComponent } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';
import { CalculatePropertyValuesModel, TMSSettingsViewModel, TMSTaskStatus, TMSTaskViewModel } from '../../types';
import { TaskStatusPickerComponent } from '../task-status-picker/task-status-picker.component';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { TmsTaskTopicPickerComponent } from '../../tms-task-topics/tms-task-topic-picker/tms-task-topic-picker.component';
import { TmsTaskCategoryPickerComponent } from '../../tms-task-categories/tms-task-category-picker/tms-task-category-picker.component';
import { TmsTaskPriorityPickerComponent } from '../../tms-task-priorities/tms-task-priority-picker/tms-task-priority-picker.component';
import { TmsTaskColorPickerComponent } from '../../tms-task-colors/tms-task-color-picker/tms-task-color-picker.component';

@Component({
  selector: 'create-update-tms-task',
  templateUrl: './create-update-tms-task.component.html',
  styleUrls: ['./create-update-tms-task.component.scss']
})
export class CreateUpdateTmsTaskComponent implements OnInit, OnDestroy {
  modalFormConfig: ModalFormConfig;
  model: TMSTaskViewModel;
  component: ComponentRef<ModalFormComponent>;
  lock: boolean;

  @Input()
  objectType: string;
  @Input()
  objectId: string;
  @Input()
  tmsSettings: TMSSettingsViewModel;
  @Input()
  id: string;
  @Output()
  onUpdateTask: EventEmitter<string>;
  @Output()
  onCreateTask: EventEmitter<string>;
  @Output()
  onUploadFinished: EventEmitter<void>;

  constructor(
    private taskManagementSystemService: TaskManagementSystemService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private isoQuestEventManager: IsoQuestEventManagerService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) {
    this.onUpdateTask = new EventEmitter<string>();
    this.onCreateTask = new EventEmitter<string>();
    this.onUploadFinished = new EventEmitter<void>();
    this.lock = false;
  }

  ngOnDestroy(): void {
  }

  ngOnInit() {
    const that = this;

    const currentModule = this.applicationService.getCurrentModuleKey();

    if (!this.objectId)
      throw new Error("objectId is required");

    if (!this.objectType)
      throw new Error("objectType is required");

    this.modalFormConfig = new ModalFormConfig(
      that.id ? "[ISOQuest].[TaskManagementSystem].Task.Edit" : "[ISOQuest].[Common].Buttons.AddNewTask",
      [
        new RowControl([
          new InputControl({
            field: "Title",
            type: "text",
            required: true,
            label: "[ISOQuest].[Common].Title",
            css: "col-6"
          }),
          new ComponentControl({
            componentClass: TaskStatusPickerComponent,
            field: "Status",
            data: [
              { isDisabled: false },
              {
                modelChange: (status: IdentityLookupViewModel) => {
                  if (that.lock)
                    return;

                  if (status.Id === TMSTaskStatus.Archived || status.Id === TMSTaskStatus.Completed) { //Completed or Archived
                    if (that.model.CompletionDate == null)
                      that.model.CompletionDate = new Date();

                    that.model.PercentDone = 100;
                  } else if (status.Id === TMSTaskStatus.NotStarted) { //NotStarted
                    that.model.CompletionDate = null;
                    that.model.PercentDone = 0;
                  } else {
                    that.model.CompletionDate = null;
                    if (that.model.PercentDone === 100) {
                      that.model.PercentDone = 0;
                    }
                  }
                }
              }
            ],
            css: "col-6"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskTopicPickerComponent,
            field: "Tag",
            data: [
              { objectId: this.objectId },
              { objectType: this.objectType }
            ],
            css: "col-6"
          }),
          new ComponentControl({
            componentClass: TmsTaskCategoryPickerComponent,
            field: "TaskCategory",
            data: [
              { objectId: this.objectId },
              { objectType: this.objectType }
            ],
            css: "col-6"
          })
        ]),

        new RowControl([
          new ComponentControl({
            componentClass: TmsTaskPriorityPickerComponent,
            field: "TaskPriority",
            data: [
              { objectId: this.objectId },
              { objectType: this.objectType }
            ],
            css: "col-6"
          }),
          new ComponentControl({
            componentClass: TmsTaskColorPickerComponent,
            field: "Color",
            data: [
              { objectId: this.objectId },
              { objectType: this.objectType }
            ],
            css: "col-6"
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "Owner",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].Owner" },
              { type: "Personnel" },
              { targetModule: currentModule }
            ]
          }),
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "AssignedTo",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].Assignee" },
              { type: "Personnel" },
              { targetModule: currentModule }
            ]
          }),
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "NotificationRecipients",
            css: "col-6",
            data: [
              { onlyActive: true },
              { isMultiple: true },
              { type: "All" },
              { targetModule: currentModule },
              { label: "[ISOQuest].[TaskManagementSystem].NotificationRecipients" },
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: IqPersonnelPickerComponent,
            field: "RestrictedReadership",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[TaskManagementSystem].RestrictedReadership" },
              { type: "All" },
              { targetModule: currentModule }
            ]
          }),
          new ComponentControl({
            componentClass: FacilityPickerComponent,
            field: "Facility",
            data: [
              { isRequired: true },
              { label: "[ISOQuest].[Common].Facility" },
              {
                modelChange: (model) => {
                  const departmentComponentControl = that.modalFormConfig.getControlByField("Department") as ComponentControl;
                  if (model) {
                    departmentComponentControl.getDataByKey("facility").facility = model;
                    departmentComponentControl.getDataByKey("isDisabled").isDisabled = false;
                  } else {
                    departmentComponentControl.getDataByKey("isDisabled").isDisabled = true;
                  }
                }
              }
            ],
            css: "col-6",
            show: that.applicationService.getCurrentSite().IsMultiSite && !that.tmsSettings.DisableFacility
          }),
          new ComponentControl({
            componentClass: IqDepartmentPickerComponent,
            field: "Department",
            data: [
              { label: "[ISOQuest].[Common].Department" },
              { facility: null },
              { isDisabled: false }
            ],
            css: "col-6"
          })
        ]),
        new RowControl([
          new InputControl({
            field: "MarkAsRecurringTask",
            type: "checkbox",
            label: "[ISOQuest].[BryntumGantt].MarkAsRecurringTask",
            events: [
              {
                eventName: "change",
                eventCallback: () => {
                  const frequencySchedule = this.modalFormConfig.getControlByField("FrequencySchedule") as ComponentControl;
                  frequencySchedule.show = that.model.MarkAsRecurringTask;
                }
              }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: FrequencySchedulePickerComponent,
            field: "FrequencySchedule",
            show: false,
            validators: [FrequencySchedulePickerComponent.validator]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: IqDatePickerComponent,
            field: "StartDate",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].StartDate" },
              { maxDate: null },
              {
                modelChange: (date: Date) => {
                  const endDate = that.modalFormConfig.getControlByField("EndDate") as ComponentControl;
                  endDate.getDataByKey("minDate").minDate = date;
                  if (!that.lock)
                    that.calculatePropertyValues("StartDate", date);
                }
              }
            ]
          }),
          new ComponentControl({
            componentClass: IqDatePickerComponent,
            field: "EndDate",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].DueDate" },
              { minDate: null },
              {
                modelChange: (date: Date) => {
                  const startDate = that.modalFormConfig.getControlByField("StartDate") as ComponentControl;
                  startDate.getDataByKey("maxDate").maxDate = date;
                  if (!that.lock)
                    that.calculatePropertyValues("DueDate", date);
                }
              }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: IqDatePickerComponent,
            field: "ActualStartDate",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].ActualStartDate" },
              { maxDate: null },
              {
                modelChange: (date: Date) => {
                  const completionDate = that.modalFormConfig.getControlByField("CompletionDate") as ComponentControl;
                  completionDate.getDataByKey("minDate").minDate = date;
                }
              }
            ]
          }),
          new ComponentControl({
            componentClass: IqDatePickerComponent,
            field: "CompletionDate",
            css: "col-6",
            data: [
              { label: "[ISOQuest].[Common].CompletionDate" },
              { minDate: null },
              {
                modelChange: (date: Date) => {
                  if (that.lock)
                    return;

                  const actualStartDate = that.modalFormConfig.getControlByField("ActualStartDate") as ComponentControl;
                  actualStartDate.getDataByKey("maxDate").maxDate = date;
                  if (date) {
                    if (that.model.Status.Id !== TMSTaskStatus.Archived) {
                      that.model.Status = new IdentityLookupViewModel(
                        TMSTaskStatus.Completed,
                        `[ISOQuest].[Common].TMS.Status.${TMSTaskStatus[TMSTaskStatus.Completed]}`,
                        true);
                    }

                    that.model.PercentDone = 100;
                  } else {
                    if (that.model.Status.Id === TMSTaskStatus.Archived ||
                      that.model.Status.Id === TMSTaskStatus.Completed) {
                      that.model.Status = new IdentityLookupViewModel(
                        TMSTaskStatus.NotStarted,
                        `[ISOQuest].[Common].TMS.Status.${TMSTaskStatus[TMSTaskStatus.NotStarted]}`,
                        true);
                    }

                    that.model.PercentDone = 0;
                  }
                }
              }
            ]
          })
        ]),
        new RowControl([
          new LabelControl({
            text: "",
            css: "col-6"
          }),
          new ComponentControl({
            componentClass: IqNumberComponent,
            field: "Duration",
            label: "[ISOQuest].[Common].Duration",
            css: "col-6",
            data: [
              { min: 0 },
              {
                modelChange: (value) => {
                  if (!that.lock)
                    that.calculatePropertyValues("Duration", value);
                }
              }
            ],
            validators: [
              IqNumberComponent.validator.min(0)
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: IqNumberComponent,
            field: "ComingDueDays",
            label: "[ISOQuest].[Common].ComingDueDays",
            css: "col-6",
            data: [
              { min: 0 }
            ]
          }), new ComponentControl({
            componentClass: IqNumberComponent,
            field: "PercentDone",
            label: "[ISOQuest].[TaskManagementSystem].Columns.PercentDone",
            css: "col-6",
            data: [
              { min: 0 },
              { max: 100 },
              {
                modelChange: () => {
                  if (that.lock)
                    return;

                  if (isNaN(that.model.PercentDone))
                    return;

                  if (that.model.PercentDone == 100) {
                    that.model.Status = new IdentityLookupViewModel(
                      TMSTaskStatus.Completed,
                      `[ISOQuest].[Common].TMS.Status.${TMSTaskStatus[TMSTaskStatus.Completed]}`,
                      true);

                    that.model.CompletionDate = new Date();
                  } else if (that.model.PercentDone == 0 &&
                    (that.model.Status.Id === TMSTaskStatus.Completed ||
                      that.model.Status.Id === TMSTaskStatus.Archived)) {
                    that.model.Status = new IdentityLookupViewModel(
                      TMSTaskStatus.NotStarted,
                      `[ISOQuest].[Common].TMS.Status.${TMSTaskStatus[TMSTaskStatus.NotStarted]}`,
                      true);

                    that.model.CompletionDate = null;
                  } else {
                    that.model.Status = new IdentityLookupViewModel(
                      TMSTaskStatus.InProgress,
                      `[ISOQuest].[Common].TMS.Status.${TMSTaskStatus[TMSTaskStatus.InProgress]}`,
                      true);

                    that.model.CompletionDate = null;
                  }
                }
              }
            ],
            validators: [
              IqNumberComponent.validator.min(0),
              IqNumberComponent.validator.max(100)
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: TextEditorComponent,
            field: "Notes",
            data: [
              { label: "[ISOQuest].[Common].Notes" },
              { editorId: "notes" }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: AttachmentsMultipleModeComponent,
            field: "Attachments",
            data: [
              { "isMultiple": true },
              { "url": "/TaskManagementSystem/UploadAttachment" },
              { "deleteUrl": "/TaskManagementSystem/DeleteAttachment" },
              {
                onDeleteAttachment: () => {
                  that.onUploadFinished.emit();
                }
              }
            ]
          })
        ])
      ],
      [
        new CustomAction({
          field: "Submit",
          actionType: "submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            that.taskManagementSystemService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
              if (typeof response === "string") {
                that.onCreateTask.emit(response);
                that.uploadAttachments(response);
              } else {
                that.onUpdateTask.emit(that.id);
                that.uploadAttachments(that.id);
              }
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.model = new TMSTaskViewModel(this.objectType, this.objectId);
    const restrictedReadershipComponent = this.modalFormConfig.getControlByField("RestrictedReadership") as ComponentControl;

    if (this.id) {
      this.taskManagementSystemService.getTMSTaskAttachments(this.id).subscribe(attachments => {
        this.taskManagementSystemService.getTMSTaskById(this.id).subscribe(task => {
          restrictedReadershipComponent.show = task.Instance.Settings.RestrictedReadership;
          this.lock = true;
          this.model = task;
          this.model.Attachments = attachments;
          setTimeout(() => {
            this.lock = false;
          }, 0);
          this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
            [
              { model: this.model },
              { config: this.modalFormConfig },
              {
                modalOptions: {
                  size: "md"
                }
              }
            ]) as ComponentRef<ModalFormComponent>;
        });
      })
    } else {
      this.taskManagementSystemService.getTaskManagementSystemInstance(this.objectType, this.objectId).toPromise().then(viewModel => {
        restrictedReadershipComponent.show = viewModel.Settings.RestrictedReadership;
        this.lock = true;
        this.model.Status = new IdentityLookupViewModel(TMSTaskStatus.NotStarted, `[ISOQuest].[Common].TMS.Status.NotStarted`, true);
        this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
          [
            { model: this.model },
            { config: this.modalFormConfig },
            {
              modalOptions: {
                size: "md"
              }
            }
          ]) as ComponentRef<ModalFormComponent>;
        setTimeout(() => {
          this.lock = false;
        }, 0);
      });
    }
  }

  calculatePropertyValues(changedProperty: string, value: any): void {
    this.lock = true;

    var currentPropertyValues = new CalculatePropertyValuesModel(
      this.model.Id,
      this.model.Duration,
      0,
      this.model.StartDate,
      this.model.EndDate,
      false,
      changedProperty
    );
    currentPropertyValues[changedProperty] = value;

    this.taskManagementSystemService.calculatePropertyValues(currentPropertyValues).subscribe(response => {
      this.model.StartDate = response.StartDate;
      this.model.EndDate = response.DueDate;
      this.model.Duration = response.Duration;
      setTimeout(() => {
        this.lock = false;
      }, 0);
    });
  };

  uploadAttachments(tmsTaskId: string) {
    let attachmentsModel = new AttachmentViewModel();
    this.isoQuestEventManager.subscribe(this, "iq-no-attachments-provided", () => {
      this.unsubscribeFromEvents();
      this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
      this.modalFormConfig.modalRef.close();
    });

    let succeeded = [];
    let failed = [];
    this.isoQuestEventManager.subscribe(this, "iq-single-attachment-success", (payload: IsoQuestEvent) => {
      if (payload.data.title) {
        succeeded.push({ Name: payload.data.title, Reason: '', IsSucceed: true });
      }
    });
    this.isoQuestEventManager.subscribe(this, "iq-single-attachment-error", (payload: IsoQuestEvent) => {
      if (payload.data.title) {
        var errorParsed = JSON.parse(payload.data.error);
        failed.push({ Name: payload.data.title, Reason: errorParsed.Errors[""][0], IsSucceed: false });
      }
    });

    this.isoQuestEventManager.subscribe(this, "attachmentsUploadFinished", () => {
      this.unsubscribeFromEvents();

      if (failed.length) {
        var all = { Succeed: succeeded, Failed: failed };
        this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
          { "model": all },
          { "modalOptions": { size: "sm" } },
        ]);

        this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
      }
      else {
        this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemSaved"));
        this.modalFormConfig.modalRef.close();
      }
      this.onUploadFinished.emit();
    });

    attachmentsModel.ObjectId = tmsTaskId;
    this.isoQuestEventManager.notify("attachmentsSubmitted", attachmentsModel);
  }

  unsubscribeFromEvents() {
    this.isoQuestEventManager.unsubscribe(this, "iq-no-attachments-provided");
    this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-success");
    this.isoQuestEventManager.unsubscribe(this, "iq-single-attachment-error");
    this.isoQuestEventManager.unsubscribe(this, "attachmentsUploadFinished");
  }
}
