import { Injectable } from "@angular/core";
import { ApplicationService, BulkOperationExecutionResultItem, CancelAction, ComponentControl, FacilityPickerComponent, InputControl, IqPersonnelPickerComponent, IsoQuestEventManagerService, ModalFormConfig, RowControl, SubmitAction } from "codera-shared";
import { ChangeRequestTypePickerComponent } from "../change-request-type/picker/change-request-type-picker.component";
import { SeverityLevelLevelPickerComponent } from "../severity-level/picker/severity-level-level-picker/severity-level-level-picker.component";
import { CopyWorkflowMatrixViewModel } from './types';
import { WorkflowMatrixService } from './workflow-matrix.service';

@Injectable({ providedIn: "root" })
export class WorkflowMatrixFactory {

    constructor(
        private workflowMatrixService: WorkflowMatrixService,
        private applicationService: ApplicationService,
        private isoQuestEventManagerService: IsoQuestEventManagerService
    ) { }

    getModalConfig(model: CopyWorkflowMatrixViewModel, onCopy?: (result: BulkOperationExecutionResultItem) => void): ModalFormConfig {
        const that = this;
        const copyModalConfig = new ModalFormConfig(
            '[MOCQuest].[SystemControls].WorkflowMatrix.CopyWorkflowMatrix',
            [
                new RowControl([
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "Facilities",
                        css: "col-md-10",
                        data: [
                            { "label": "[ISOQuest].[Common].Facility" },
                            { "isRequired": true },
                            { "isMultiple": true },
                            { "isDisabled": false },
                            { "onlyActive": true },
                            { "currentUserFacilities": true },
                            { "itemsToExclude": [] }
                        ],
                        show: that.applicationService.getCurrentSite().IsMultiSite
                    }),
                    new InputControl({
                        type: 'checkbox',
                        field: 'AllFacilities',
                        label: '[MOCQuest].[SystemControls].WorkflowMatrix.AllFacilities',
                        css: 'col-md-2',
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        events: [
                            {
                                eventName: "change",
                                eventCallback: () => {
                                    this.onAllFacilitiesChecked(copyModalConfig, model);
                                }
                            }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        css: "col-md-6",
                        data: [
                            { "label": "[MOCQuest].[SystemControls].ChangeRequestType" },
                            { "isRequired": false },
                            { "isMultiple": false },
                            { "isDisabled": true },
                            { "onlyActive": true }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: SeverityLevelLevelPickerComponent,
                        field: "SeverityLevel",
                        css: "col-md-6",
                        data: [
                            { "label": "[MOCQuest].[SystemControls].WorkflowMatrix.DefaultSeverityLevel" },
                            { "isRequired": false },
                            { "isMultiple": false },
                            { "isDisabled": true },
                            { "onlyActive": true }
                        ]
                    })
                ]),
                new RowControl([
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "ChangeCoordinators",
                        css: "col-md-12",
                        data: [
                            { "label": "[MOCQuest].[ChangeRequest].ChangeCoordinators" },
                            { "isRequired": false },
                            { "isMultiple": true },
                            { "isDisabled": true },
                            { "onlyActive": true }
                        ]
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: 'Submit',
                    value: '[ISOQuest].[Common].Buttons.Submit',
                    callback() {
                        that.workflowMatrixService.copyWorkflowMatrix(model).subscribe((result: BulkOperationExecutionResultItem) => {
                            if (onCopy) {
                                onCopy(result);
                            }
                        }, (error: any) => {
                            this.isoQuestEventManagerService.notify("manuallyTriggerServerValidation", error);
                        });
                    },
                    css: 'btn btn-primary'
                }),
                new CancelAction({
                    field: 'Cancel',
                    value: '[ISOQuest].[Common].Buttons.Cancel',
                    callback() { },
                    css: 'btn btn-default'
                })
            ]
        );

        return copyModalConfig;
    }

    private onAllFacilitiesChecked(copyModalConfig: ModalFormConfig, model: CopyWorkflowMatrixViewModel): void {
        const facility = copyModalConfig.getControlByField("Facilities") as ComponentControl;
        if (model.AllFacilities) {
            facility.getDataByKey("isDisabled").isDisabled = true;
            facility.getDataByKey("isRequired").isRequired = false;
            model.Facilities = null;
            this.isoQuestEventManagerService.notify("invalidateData", null);
            setTimeout(() => {
                if (facility.formControl)
                    facility.formControl.disable();
            });
        }
        else {
            facility.getDataByKey("isDisabled").isDisabled = false;
            facility.getDataByKey("isRequired").isRequired = true;

            if (facility.formControl)
                facility.formControl.enable();

            this.isoQuestEventManagerService.notify("invalidateData", null);
        }
    }
}
