import { AfterViewInit, Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { ApplicationSite, ApplicationService, DynamicService, HelpersService, FluentTableConfig, TextConfirmationModalComponent, BulkDeleteComponent, LookupViewModel, IsoQuestEventManagerService, PersonnelGroupPickerViewModel } from 'codera-shared';
import { BryntumGanttInstanceConfig } from 'src/app/bryntum-gantt/types';
import { TaskManagementSystemService } from '../task-management-system.service';
import { CreateUpdateTmsTaskComponent } from '../task/create-update/create-update-tms-task.component';
import { SendTaskAsLinkComponent } from '../task/send-as-link/send-task-as-link.component';
import { SharePermalinkComponent } from '../task/share-permalink/share-permalink.component';
import { TMSSearchModel, TMSPermissionsModel, TMSSettingsViewModel, TMSSearchModelHelper, FilterConfigViewModel, SystemSearchType, TMSColumnItemViewModel, TMSTaskViewModel } from '../types';
import { UpdateTmsTaskAssigneeComponent } from '../update-tms-task-assignee/update-tms-task-assignee.component';
import { UpdateTmsTaskNotificationRecipientsComponent } from '../update-tms-task-notification-recipients/update-tms-task-notification-recipients.component';
import { UpdateTmsTaskCategoryComponent } from '../update-tms-task-category/update-tms-task-category.component';
import { UpdateTmsTaskColorComponent } from '../update-tms-task-color/update-tms-task-color.component';
import { UpdateTmsTaskOwnerComponent } from '../update-tms-task-owner/update-tms-task-owner.component';
import { UpdateTmsTaskPriorityComponent } from '../update-tms-task-priority/update-tms-task-priority.component';
import { UpdateTmsTaskTopicComponent } from '../update-tms-task-topic/update-tms-task-topic.component';
import { TranslateService } from '@ngx-translate/core';
import { cloneDeep } from 'lodash';
import { ToastrService } from 'ngx-toastr'
import { TMSTaskInfoComponent } from '../task/info/tms-task-info.component';
import { UpdateTmsTaskNotesComponent } from '../update-tms-task-notes/update-tms-task-notes.component';
import { BulkChangeTmsTaskColorComponent } from '../bulk-actions/bulk-change-tms-task-color/bulk-change-tms-task-color.component';
import { BulkChangeTmsTaskStatusComponent } from '../bulk-actions/bulk-change-tms-task-status/bulk-change-tms-task-status.component';
import { BulkChangeTmsTaskOwnerComponent } from '../bulk-actions/bulk-change-tms-task-owner/bulk-change-tms-task-owner.component';
import { BulkChangeTmsTaskAssigneeComponent } from '../bulk-actions/bulk-change-tms-task-assignee/bulk-change-tms-task-assignee.component';
import { BulkChangeTmsTaskCategoryComponent } from '../bulk-actions/bulk-change-tms-task-category/bulk-change-tms-task-category.component';
import { BulkChangeTmsTaskPriorityComponent } from '../bulk-actions/bulk-change-tms-task-priority/bulk-change-tms-task-priority.component';
import { BulkDeleteTmsTaskComponent } from '../bulk-actions/bulk-delete-tms-task/bulk-delete-tms-task.component';
import { UpdateTmsTaskDepartmentComponent } from '../update-tms-task-department/update-tms-task-department.component';
@Component({
  selector: 'tms-gantt',
  templateUrl: './tms-gantt.component.html',
  styleUrls: ['./tms-gantt.component.scss']
})
export class TmsGanttComponent implements OnInit, AfterViewInit {

  @Input()
  url: string;
  @Input()
  objectId: string;
  @Input()
  objectType: string;

  instanceConfig: BryntumGanttInstanceConfig;
  defaultSearch: TMSSearchModel;
  permissions: TMSPermissionsModel;
  settings: TMSSettingsViewModel;
  warningMessage: string;
  model: TMSSearchModelHelper;
  showing: boolean;
  currentModule: string;
  submitted: boolean;
  currentSite: ApplicationSite;
  searchCache: TMSSearchModelHelper;
  createMode: boolean;
  error: string;
  activeTabHtmlElement: any;

  constructor(
    private applicationService: ApplicationService,
    private taskManagementSystemService: TaskManagementSystemService,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private helperService: HelpersService,
    private toastr: ToastrService,
    private isoQuestEventManager: IsoQuestEventManagerService
  ) {
    this.currentModule = this.applicationService.getCurrentModuleKey();
    this.currentSite = this.applicationService.getCurrentSite();
  }

  ngAfterViewInit(): void {
    const tmsElement = document.getElementsByTagName("task-management-system") as HTMLCollection;
    if (tmsElement.length > 0) {
      const activeTab = tmsElement[0].getElementsByClassName("nav-link active");
      if (activeTab.length > 0) {
        this.activeTabHtmlElement = activeTab[0];
      }
    }
  }

  ngOnInit() {
    this.model = new TMSSearchModelHelper();
    this.model.localSearch = new TMSSearchModel();
    this.model.localSearch.FilterConfig = new FilterConfigViewModel();
    this.model.localSearch.ObjectId = this.objectId;
    this.model.localSearch.ObjectType = this.objectType;

    this.taskManagementSystemService.getPermissions(this.objectType, this.objectId).subscribe(permissions => {
      this.permissions = permissions;
      this.setupInstance();
    });

    this.taskManagementSystemService.getTaskManagementSystemSettings(this.objectType, this.objectId).subscribe(settings => {
      this.settings = settings;
      this.setSearch(this.defaultSearch);
      this.setupInstance();
    });

    this.taskManagementSystemService.getDefaultSearch(this.objectType, this.objectId, SystemSearchType[SystemSearchType.AllTasks]).subscribe(defaultSearch => {
      this.defaultSearch = defaultSearch;
      this.setSearch(this.defaultSearch);
      this.setupInstance();
    });
  }

  setupInstance(): void {
    if (this.permissions === undefined || this.settings === undefined || this.defaultSearch === undefined)
      return;

    this.instanceConfig = new BryntumGanttInstanceConfig();
    this.instanceConfig.currentSite = this.applicationService.getCurrentSite();
    this.instanceConfig.currentUser = this.applicationService.getCurrentUser();
    this.instanceConfig.siteKey = this.applicationService.getCurrentSiteKey();
    this.instanceConfig.objectId = this.objectId;
    this.instanceConfig.objectType = this.objectType;
    this.instanceConfig.objectName = "";
    this.instanceConfig.permissions = this.permissions;
    this.instanceConfig.requestOptions = this.defaultSearch;
    this.instanceConfig.defaultLoadNumberOfItemsInPickers = 20;//this.applicationService.getApplicationSettings().DefaultLoadNumberOfItemsInPickers
    this.instanceConfig.settings = this.settings;
    this.instanceConfig.defaultSearchOption = SystemSearchType[SystemSearchType.AllTasks];
    this.instanceConfig.translate = (key: string) => {
      return this.translate.instant(key);
    };

    this.instanceConfig.actions = {
      showWarningMessage: (resposnse) => {
        this.warningMessage = resposnse;
      },
      addNewTask: () => {
        this.dynamicService.addComponent(this.viewContainer, CreateUpdateTmsTaskComponent, [
          { objectType: this.objectType },
          { objectId: this.objectId },
          { tmsSettings: this.settings },
          {
            onCreateTask: (taskId) => {
              this.instanceConfig.reloadGanttChart();
              this.instanceConfig.onAddRecord(taskId);
              //this.isoQuestEventManager.notify("reload-tms-tasks-count", null);
            }
          },
          {
            onUploadFinished: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      changeSavedSearch: (data) => {
        this.setSearch(JSON.parse(JSON.stringify(data)));
        this.search(true);
      },
      updateTask: (taskId: string) => {
        this.dynamicService.addComponent(this.viewContainer, CreateUpdateTmsTaskComponent, [
          { objectType: this.objectType },
          { objectId: this.objectId },
          { tmsSettings: this.settings },
          { id: taskId },
          {
            onUpdateTask: () => {
              this.instanceConfig.reloadGanttChart();
              this.instanceConfig.onAddRecord(taskId);
              //this.isoQuestEventManager.notify("reload-tms-tasks-count", null);
            }
          },
          {
            onUploadFinished: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      sendAsLink: (taskId: string) => {
        this.dynamicService.addComponent(this.viewContainer, SendTaskAsLinkComponent, [
          { objectType: this.objectType },
          { objectId: this.objectId },
          { taskId: taskId }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      permalink: (taskId: string) => {
        this.dynamicService.addComponent(this.viewContainer, SharePermalinkComponent, [
          { objectType: this.objectType },
          { objectId: this.objectId },
          { taskId: taskId }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      exportToExcel: (requestOptions) => {
        const url = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}/TaskManagementSystem/GetExportToExcel`;
        this.helperService.downloadFile(
          {
            url: url,
            data: requestOptions,
            onError: function (error) {
              var err = JSON.parse(error);
              this.toastr.error(this.translate.instant(err.Errors[""][0]));
            }
          }
        );
      },
      exportAllColumnsToExcel: (requestOptions) => {
        const url = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}/TaskManagementSystem/GetExportAllColumsToExcel`;
        this.helperService.downloadFile(
          {
            url: url,
            data: requestOptions,
            onError: function (error) {
              var err = JSON.parse(error);
              this.toastr.error(this.translate.instant(err.Errors[""][0]));
            }
          }
        );
      },
      updateOwner: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskOwnerComponent, [
          { value: value },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateAssignee: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskAssigneeComponent, [
          { value: value },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateNotificationRecipients: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskNotificationRecipientsComponent, [
          { value: value },
          {
            onSubmit: (notificationRecipients: PersonnelGroupPickerViewModel[]) => {
              this.instanceConfig.updateField(taskId, field, notificationRecipients);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateTaskCategory: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskCategoryComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateTaskPriority: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskPriorityComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateTag: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskTopicComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateColor: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskColorComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateDepartment: (taskId: string, field: string, value: any, facility: LookupViewModel) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskDepartmentComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          { facility: facility },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      onSelectGrouping: (groupingId: any) => {
        if (this.model && this.model.localSearch)
          this.model.localSearch.GroupingId = groupingId;
      },
      toggleSearch: (callback: (showing: boolean) => void) => {
        this.showing = !this.showing;
        callback(this.showing);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      taskInfo: (id: string) => {
        this.dynamicService.addComponent(this.viewContainer, TMSTaskInfoComponent, [
          { id: id }
        ]);

        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      updateNotes: (taskId: string, field: string, value: any) => {
        this.dynamicService.addComponent(this.viewContainer, UpdateTmsTaskNotesComponent, [
          { value: value },
          { objectType: this.objectType },
          { objectId: this.objectId },
          {
            onSubmit: (personnelId: string) => {
              this.instanceConfig.updateField(taskId, field, personnelId);
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangeColor: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskColorComponent, [
          { objectId: this.objectId },
          { objectType: this.objectType },
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangeStatus: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskStatusComponent, [
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangeOwner: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskOwnerComponent, [
          { targetModule: this.currentModule },
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangeAssignee: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskAssigneeComponent, [
          { targetModule: this.currentModule },
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangeCategory: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskCategoryComponent, [
          { objectId: this.objectId },
          { objectType: this.objectType },
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkChangePriority: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeTmsTaskPriorityComponent, [
          { objectId: this.objectId },
          { objectType: this.objectType },
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      bulkDelete: (selectedTasks: any[]) => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteTmsTaskComponent, [
          { selectedIds: selectedTasks.map(x => x.id) },
          {
            onSuccess: () => {
              this.instanceConfig.reloadGanttChart();
            }
          }
        ]);
        if (this.activeTabHtmlElement)
          this.activeTabHtmlElement.click();
        else
          (document.getElementsByClassName("nav-link active")[0] as any).click();
      },
      reloadTmsTasksCount: () => {
        this.isoQuestEventManager.notify("reload-tms-tasks-count", null);
      }
    };
  }

  setSearch(search): void {
    if (search) {
      this.model.savedSearch = cloneDeep(search);
      this.model.localSearch = cloneDeep(search);
    }

    if (!this.model.localSearch.ColumnConfig) {
      this.model.localSearch.ColumnConfig = this.settings.Columns || [];
    }
  }

  search(preventUpdatePicker?): void {
    this.instanceConfig.reloadGanttChart(this.model.localSearch, preventUpdatePicker);
  };

  clearSearchFields(): void {
    this.model.localSearch.FilterConfig.Title = null;
    this.model.localSearch.FilterConfig.StartDate = null;
    this.model.localSearch.FilterConfig.EndDate = null;
    this.model.localSearch.FilterConfig.ActualStartDate = null;
    this.model.localSearch.FilterConfig.CompletionDate = null;
    this.model.localSearch.FilterConfig.Priority = null;
    this.model.localSearch.FilterConfig.Category = null;
    this.model.localSearch.FilterConfig.Status = null;
    this.model.localSearch.FilterConfig.Owner = null;
    this.model.localSearch.FilterConfig.Assignee = null;
    this.model.localSearch.FilterConfig.MaturityStatus = null;
    this.model.localSearch.FilterConfig.Tags = null;
    this.model.localSearch.FilterConfig.Color = [];
    this.model.localSearch.FilterConfig.TaskType = null;
    this.model.localSearch.FilterConfig.Facility = null;
    this.model.localSearch.FilterConfig.Department = [];

    this.instanceConfig.setGrouping(0);

    this.search();
  };

  new(): void {
    this.searchCache = cloneDeep(this.model);
    this.resetSearch();
    document.getElementById("search-title").focus();
    this.createMode = true;
  };

  resetSearch(): void {
    this.model.localSearch = new TMSSearchModel();
    this.model.localSearch.FilterConfig = new FilterConfigViewModel();
    this.model.savedSearch = null;
    this.model.localSearch.ObjectId = this.objectId;
    this.model.localSearch.ObjectType = this.objectType;
    this.model.localSearch.ColumnConfig = this.settings.Columns;
    this.instanceConfig.setGrouping(0);
    this.submitted = false;
    this.search();
  }

  cancel(): void {
    this.model = cloneDeep(this.searchCache);
    if (this.model && this.model.localSearch) {
      var groupingId = this.model.localSearch.GroupingId;
      this.instanceConfig.setGrouping(groupingId);
    }
    this.searchCache = null;
    this.createMode = false;
    this.search();
  }

  save(): void {
    const update = !!this.model.localSearch.Id;

    var messageKey = update ? '[ISOQuest].[Common].Notifications.ItemUpdated' : '[ISOQuest].[Common].Notifications.ItemSaved';

    this.error = null;
    this.submitted = true;

    if (!this.model.localSearch.Title) {
      return;
    }

    if (update) {
      this.taskManagementSystemService.updateSearch(this.model.localSearch).subscribe((response) => {
        this.model.localSearch.Id = response;
        this.model.localSearch.Personnel = null;
        this.model.savedSearch = cloneDeep(this.model.localSearch);
        this.submitted = false;
        this.toastr.success(this.translate.instant(messageKey));
        this.search();
        this.createMode = false;
      }, (response) => {
        this.error = response.error.Errors[""][0];
      });
    } else {
      this.taskManagementSystemService.saveSearch(this.model.localSearch).subscribe((response) => {
        this.model.localSearch.Id = response;
        this.model.localSearch.Personnel = null;
        this.model.savedSearch = cloneDeep(this.model.localSearch);
        this.submitted = false;
        this.toastr.success(this.translate.instant(messageKey));
        this.search();
        this.createMode = false;
      }, (response) => {
        this.error = response.error.Errors[""][0];
      });
    }
  }

  savedSearchChange(savedSearch: TMSSearchModel): void {
    if (savedSearch) {
      this.setSearch(savedSearch);
      this.search();
      this.createMode = false;
    } else {
      this.resetSearch();
    }
  }

  resetToDefaultSystemSearch(): void {
    this.taskManagementSystemService.resetToDefaultSystemSearch(this.model.localSearch.Id, this.instanceConfig.defaultSearchOption).subscribe(response => {
      if (response) {
        this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemUpdated'));
        this.setSearch(response);
        this.search();
      }
    });
  }

  delete(): void {
    this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
      { modalHeader: "[ISOQuest].[Common].ConfirmDialog.Title" },
      { modalText: "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
      {
        successCallback: () => {
          this.taskManagementSystemService.deleteSearch(this.model.localSearch.Id).subscribe((response) => {
            this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemDeleted'));
            this.setSearch(response);
            this.search();
          });
        }
      }
    ]);
  }
}
