import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ApplicationService, ApplicationSite } from 'codera-shared';
import { SettingsService } from '../settings.service';
import { MOCQuestSettingsViewModel } from '../types';

@Component({
    selector: 'app-general-settings',
    templateUrl: './general-settings.component.html',
    styleUrls: ['./general-settings.component.scss']
})
export class GeneralSettingsComponent implements OnInit {
    @Input() viewModel: MOCQuestSettingsViewModel;
    @Output() viewModelChanged: EventEmitter<MOCQuestSettingsViewModel>;

    currentSite: ApplicationSite;
    constructor(
        private settingsService: SettingsService,
        private applicationService: ApplicationService
    ) {
        this.viewModelChanged = new EventEmitter<MOCQuestSettingsViewModel>();
        this.currentSite = this.applicationService.getCurrentSite();
    }

    ngOnInit() {
        const that = this;

    }

    updateSettings() {
        this.settingsService.setSettings(this.viewModel).subscribe((viewModel: MOCQuestSettingsViewModel) => {
            this.viewModelChanged.emit(viewModel);
            window.location.reload();
        });
    }

}
