        //                field: 'Status',
        //                label: '[ISOQuest].[Common].Choices.Active',
        //                css: 'col-md-12'
        //            })
        //        ])
        //    ],
        //    [
        //        new SubmitAction({
        //            field: 'Submit',
        //            value: '[ISOQuest].[Common].Buttons.Submit',
        //            callback() {
        //                that.reasonTypeService.saveOrUpdate(model).subscribe((reasonTypeId) => {
        //                    if (onSave) {
        //                        onSave(new LookupViewModel(reasonTypeId as string, model.Title, model.Status));
