import {
    Component,
    ComponentRef,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ViewContainerRef,
} from "@angular/core";
import { Router } from "@angular/router";
import {
    ApplicationService,
    ApplicationSettings,
    ApplicationSite,
    FluentSearchConfig,
    FluentTableConfig,
    ModalFormConfig,
    DynamicService,
    FluentTableComponent,
    ModalFormComponent,
    CancelAction,
    CustomAction,
    InputControl,
    RowControl,
    TextConfirmationModalComponent,
    ComponentControl,
    IqStatusPickerComponent,
    FluentTabConfig,
} from "codera-shared";
import { MOCQuestSettingsViewModel } from "./settings/types";
import { ToastrService } from "ngx-toastr";
import { ChangeRequestTypeGridModel } from "./system-controls/change-request-type/types";
import { CreateUpdateTmsTaskCategoryComponent } from "./task-management-system/tms-task-categories/create-update/create-update-tms-task-category.component";
import { TMSTaskCategoryViewModel } from "./task-management-system/tms-task-categories/types";
import { StatusPickerComponent } from "./status-picker/status-picker.component";
import { ChangeRequestDocumentsComponent } from "./change-request/details/documents/change-request-documents.component";
import { ApprovalSettingsComponent } from "./settings/approval/approval-settings.component";
import { Category } from "./category";
import { CategoryService } from "./category.service";
import { TestComponent } from "./test/test.component";
import { TestSecondComponent } from "./test-second/test-second.component";

@Component({
    selector: "app-root",
    templateUrl: "./app.component.html",
    styleUrls: ["./app.component.scss"],
})
export class AppComponent implements OnInit {
    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    source: { [key: string]: any }[] | string = [];
    modalConfig: ModalFormConfig;
    model: Category;
    @Input() changeRequestId: string;
    canManageRecommendedActions: boolean;
    settings: any;
    moduleSettings: MOCQuestSettingsViewModel;
    applicationSettings: ApplicationSettings;
    enableIeMessage: boolean = false;
    iEMessage: string;
    currentSite: ApplicationSite;
    showSidebar = false;
    @Output() onCreateUpdate: EventEmitter<string | boolean>;
    tabsConfig: FluentTabConfig[];

    constructor(
        private router: Router,
        private applicationService: ApplicationService,
        private dynamicService: DynamicService,
        private viewContainerRef: ViewContainerRef,
        private toastr: ToastrService,
        private categoryService: CategoryService
    ) {
        this.onCreateUpdate = new EventEmitter<string | boolean>();
    }
    prepareTabs() {
        const that = this;

        this.tabsConfig = [
            new FluentTabConfig({
                title: () => {
                    return `<span><i class='far fa-check-square'></i>Category</span>`;
                },
                show: true,
                componentClass: TestComponent
            }),
            new FluentTabConfig({
                title: () => {
                    return `<span><i class='far fa-check-square'></i>Product</span>`;
                },
                show: true,
                componentClass: TestSecondComponent
            })
        ]
    }
    ngOnInit() {
        this.source = "Category/GetAllTable";
        this.fluentTableConfig = this.getFluentTableConfig();
        this.applicationSettings =
            this.applicationService.getApplicationSettings();
        this.moduleSettings =
            this.applicationService.getCurrentModuleSettings();
        this.currentSite = this.applicationService.getCurrentSite();
        this.configModal();
        this.prepareTabs();
    }
    configModal() {
        const that = this;
        that.modalConfig = new ModalFormConfig(
            "Category",
            [
                new RowControl([
                    //0
                    new InputControl({
                        type: "text",
                        field: "Name",
                        label: "Name",
                        min: 1,
                        max: 255,
                    })
                ]),
                new RowControl([
                    //1
                    new ComponentControl({
                        componentClass: StatusPickerComponent,
                        field: "Status",
                        data: [{label: "1"}, { isRequired: true },]
                    })
                ])
            ],
            [
                new CustomAction({
                    actionType: "submit",
                    field: "Submit",
                    value: "Submit",
                    callback: (row: Category) => {
                        that.model.Status
                        that.categoryService
                            .createOrUpdate(that.model)
                            .subscribe(
                                (response) => {
                                    that.onCreateUpdate.emit(response);
                                    that.modalConfig.modalRef.close();
                                    that.fluentTableConfig.notifyDataChanged(
                                        "item-updated"
                                    );
                                },
                                (error) => {
                                    that.toastr.error(
                                        "Error",
                                        error
                                    );
                                }
                            );
                    },
                    css: "btn btn-primary",
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "Cancel",
                    callback: () => {},
                    css: "btn btn-default",
                }),
            ]
        );
    }
    openModal() {
        const that = this;
        that.dynamicService.addComponent(
            that.viewContainerRef,
            ModalFormComponent,
            [{ config: that.modalConfig }, { model: new Category() }]
        );
    }

    getFluentTableConfig() {
        const that = this;
        return new FluentTableConfig({
            columns: [
                {
                    field: "Name",
                    title: "Name",
                    type: "string",
                    sortable: "Name"
                },
                {
                    field: "Description",
                    title: "Description",
                    type: "string"
                },
                {
                    field: "Status",
                    title: "Status",
                    type: "string"
                },
                {
                    field: "editAction",
                    title: "Category",
                    type: "action",
                    handler(row: Category) {
                        that.categoryService
                            .getCategory(row.Id)
                            .subscribe((x) =>
                            {
                                that.model = x;
                                that.dynamicService.addComponent(
                                    that.viewContainerRef,
                                    ModalFormComponent,
                                    [
                                        { config: that.modalConfig },
                                        { model: that.model },
                                    ]
                                );

                            }
                            );
                    },
                    render(row: Category) {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>Edit</span></a>`;
                    },
                    flexGrow: 0.28,
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: Category) {
                        that.dynamicService.addComponent(
                            that.viewContainerRef,
                            TextConfirmationModalComponent,
                            [
                                { modalHeader: "Title" },
                                { modalText: "DeleteMessage" },
                                {
                                    modalOptions: {
                                        size: "sm",
                                    },
                                },
                                {
                                    successCallback: () => {
                                        that.categoryService
                                            .delete(row.Id)
                                            .subscribe(() =>
                                                that.fluentTableConfig.notifyDataChanged(
                                                    "item-deleted"
                                                )
                                            );
                                    },

                                },
                            ],
                            true
                        );
                    },
                    render(row: ChangeRequestTypeGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>Delete</span></a>`;
                    },
                    flexGrow: 0.28,
                },
            ],
            sorting: {
                Name: "asc",
            }
        });
    }
}
