import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LookupViewModel } from 'codera-shared';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'change-request-picker',
    templateUrl: './change-request-picker.component.html',
    styles: [`.search-change-request-container { width:300px; }`]
})
export class ChangeRequestPickerComponent implements OnInit {
    @Input() model: LookupViewModel | LookupViewModel[];
    @Output() modelChange: EventEmitter<LookupViewModel | LookupViewModel[]>;

    @Input() label: string;
    @Input() controllerName: string;
    @Input() placeholder: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;
    @Input() searchMode: boolean;
    
    constructor(
        private translate: TranslateService,
        private router: Router) {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        if (!this.controllerName) {
            this.controllerName = "/ChangeRequest/GetAvailableItems";
        }
    }

    onChange(newValue: LookupViewModel | LookupViewModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }
}
