import { Component, ComponentRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BulkDeleteComponent, ComponentControl, DynamicService, FluentSearchConfig, FluentTableConfig, InputControl, IqPersonnelGroupInfoComponent, IqPersonnelInfoComponent, IqPersonnelPickerComponent, IqYesNoPickerComponent, IsoQuestEventManagerService, ModalFormConfig, RowControl, TextConfirmationModalComponent } from 'codera-shared';
import { ChangeRequestService } from '../../../change-request.service';
import { ChangeRequestPermissionsModel } from '../../../types';
import { BulkChangeStatusPickerComponent } from './bulk-change-status-picker/bulk-change-status-picker.component';
import { CreateUpdateRecommendedActionComponent } from './create-update/create-update-recommended-action.component';
import { RecommendedActionStatusPickerComponent } from './recommended-action-status-picker/recommended-action-status-picker.component';
import { RecommendedActionService } from './recommended-action.service';
import { RecommendedActionGridModel, RecommendedActionViewModel } from './types';

@Component({
    selector: 'recommended-action',
    templateUrl: './recommended-action.component.html'
})
export class RecommendedActionComponent implements OnInit, OnDestroy {
    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    tableSource: string;
    modalConfig: ModalFormConfig;
    model: RecommendedActionViewModel;
    @Input() changeRequestId: string;
    canManageRecommendedActions: boolean;
    @Output() refreshRecommendedActionCounter: EventEmitter<void>;

    constructor(
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private changeRequestService: ChangeRequestService,
        private isoQuestEventManager: IsoQuestEventManagerService,
        private recommendedActionService: RecommendedActionService
    ) {
        this.refreshRecommendedActionCounter = new EventEmitter();
    }

    ngOnDestroy(): void {
    }

    ngOnInit() {
        const that = this;

        this.isoQuestEventManager.subscribe(this, "workflow-changed", () => {
            this.getPermissions();
        });

        this.isoQuestEventManager.subscribe(this, "bulk-items-deleted", () => {
            this.refreshRecommendedActionCounter.emit();
        });

        this.tableSource = `RecommendedAction/Grid?changeRequestId=${this.changeRequestId}`;
        this.pageTitle = this.route.snapshot.data["pageTitle"];

        this.getPermissions();

        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        type: "text",
                        field: "Title",
                        label: "[ISOQuest].[Common].Title",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "AssignedTo",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "label": "[ISOQuest].[Common].Assignee" },
                            { "isRequired": false },
                            { "isDisabled": false },
                            { "isMultiple": true },
                            { "onlyActive": true },
                            { "type": "Personnel" },
                            { "moduleName": 'MOCQuest' },
                            { "targetModule": 'MOCQuest' }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: RecommendedActionStatusPickerComponent,
                        field: "Status",
                        label: "[MOCQuest].[RecommendedAction].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqYesNoPickerComponent,
                        field: "RequiredBeforeLaunch",
                        label: "[MOCQuest].[RecommendedAction].RequiredBeforeLaunch",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });
    }

    getPermissions() {
        const that = this;

        this.fluentTableConfig = null;
        this.changeRequestService.getPermissions(this.changeRequestId).subscribe((permissions: ChangeRequestPermissionsModel) => {
            that.canManageRecommendedActions = permissions.CanManageRecommendedActions;
            that.fluentTableConfig = that.getFluentTableConfig();
        });
    }

    getFluentTableConfig() {
        const that = this;
        return new FluentTableConfig({
            columns: [
                {
                    field: "",
                    title: " ",
                    type: "custom",
                    render(row: RecommendedActionGridModel) {
                        if (row.HasAttachments) {
                            return `<i class="fa fa-paperclip pull-right"></i>`;
                        }
                        return "";
                    },
                    flexGrow: 0.25
                },
                {
                    field: "Title",
                    title: "[ISOQuest].[Common].Title",
                    sortable: "Title",
                    type: "string",
                    cellClass: "inline-column"
                },
                {
                    field: "AssignedTo",
                    title: "[ISOQuest].[Common].Assignee",
                    sortable: "AssignedTo",
                    type: "action",
                    handler(row: RecommendedActionGridModel) {
                        if (row.AssignedTo !== null) {
                            if (!row.AssignedTo.IsGroup) {
                                const personnelId = row.AssignedTo.Personel.Id;
                                that.dynamicService.addComponent(that.viewContainer, IqPersonnelInfoComponent, [
                                    { "personnelId": personnelId }
                                ], false);
                            }
                            else {
                                const groupId = row.AssignedTo.Group.Id;
                                that.dynamicService.addComponent(that.viewContainer, IqPersonnelGroupInfoComponent, [
                                    { "groupId": groupId }
                                ], false);
                            }
                        }
                    },
                    render(row: RecommendedActionGridModel) {
                        if (row.AssignedTo !== null) {
                            if (!row.AssignedTo.IsGroup) {
                                return `<a title="${row.AssignedTo.Personel.FullName}" href="javascript:void(0);">${row.AssignedTo.Personel.FullName}</a>`;
                            }
                            else {
                                return `<a title="${row.AssignedTo.Group.Title}" href="javascript:void(0);">${row.AssignedTo.Group.Title}</a>`;
                            }
                        }
                        else {
                            return "";
                        }
                    }
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "custom",
                    render: (row: RecommendedActionGridModel) => {
                        if (!row.Status) {
                            return null;
                        } else {
                            const translatedStatus = that.translate.instant(row.Status.Name);
                            switch (row.Status.Id) {
                                case 0:
                                    return `<i class='far fa-circle-notch' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                case 1:
                                    return `<i class='fa fa-spinner' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                case 2:
                                    return `<i class='fa fa-check-circle' style='font-size: 15px;'></i>&nbsp;&nbsp;${translatedStatus}`;
                                default:
                                    return null;
                            }
                        }
                    },
                    flexGrow: 1
                },
                {
                    field: "DueDate",
                    title: "[ISOQuest].[Common].DueDate",
                    sortable: "DueDate",
                    type: "date"
                },
                {
                    field: "CompletionDate",
                    title: "[ISOQuest].[Common].CompletionDate",
                    sortable: "CompletionDate",
                    type: "date"
                },
                {
                    field: "Description",
                    title: "[ISOQuest].[Common].Description",
                    sortable: "Description",
                    type: "string",
                    cellClass: "inline-column"
                },
                {
                    field: "RequiredBeforeLaunch",
                    title: "[MOCQuest].[RecommendedAction].RequiredBeforeLaunch",
                    sortable: "RequiredBeforeLaunch",
                    type: "yesNo"
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: RecommendedActionGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, CreateUpdateRecommendedActionComponent, [
                            { changeRequestId: that.changeRequestId },
                            { recommendedActionId: row.Id },
                            {
                                onCreateUpdate: () => {
                                    that.fluentTableConfig.notifyDataChanged();
                                    that.refreshRecommendedActionCounter.emit();
                                    that.isoQuestEventManager.notify("get-change-request-alerts", null);
                                }
                            }
                        ], true);
                    },
                    render(row: RecommendedActionGridModel) {
                        return row.CanEdit ? `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>` : "";
                    },
                    flexGrow: 0.4
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: RecommendedActionGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.recommendedActionService.delete(row.Id, that.changeRequestId).subscribe(() => {
                                        that.fluentTableConfig.notifyDataChanged("item-deleted");
                                        that.refreshRecommendedActionCounter.emit();
                                        that.isoQuestEventManager.notify("get-change-request-alerts", null);
                                    });
                                }
                            }
                        ], true);
                    },
                    render() {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.4,
                    show: that.canManageRecommendedActions
                }
            ],
            selection: {
                mode: that.canManageRecommendedActions ? "checkbox" : "none"
            },
            sorting: {
                "Title": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });
    }

    summonDialog() {
        const that = this;
        this.dynamicService.addComponent(this.viewContainer, CreateUpdateRecommendedActionComponent, [
            { changeRequestId: this.changeRequestId },
            {
                onCreateUpdate: () => {
                    that.fluentTableConfig.notifyDataChanged();
                    that.refreshRecommendedActionCounter.emit();
                    that.isoQuestEventManager.notify("get-change-request-alerts", null);
                }
            }
        ], true);
    }

    bulkChangeStatus = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusPickerComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "RecommendedAction/BulkChangeStatus" },
            { "modalOptions": { size: "sm" } },
            {
                onSuccess: () => {
                    this.refreshRecommendedActionCounter.emit();
                }
            }
        ]);
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "RecommendedAction/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }
}
