import { HttpClient } from '@angular/common/http';
import { Injectable } from "@angular/core";
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { ChecklistGroup, CopyChecklistGroupViewModel, SelectedChecklistGroupsModel } from './types';

@Injectable({ providedIn: 'root' })
export class ChecklistGroupService {
    private apiUrl: string;

    constructor(
        private http: HttpClient,
        private applicationService: ApplicationService
    ) {
        this.apiUrl = this.applicationService.getApiUrl();
    }

    getById(checklistGroupId: string): Observable<ChecklistGroup> {
        const url = `${this.apiUrl}/ChecklistGroup/GetById`;
        return this.http.post<ChecklistGroup>(url, { id: checklistGroupId });
    }

    saveOrUpdate(model: ChecklistGroup): Observable<string | boolean> {
        const url = `${this.apiUrl}/ChecklistGroup/CreateOrUpdate`;
        return this.http.post<string | boolean>(url, model);
    }

    delete(checklistGroupId: string): Observable<boolean> {
        const url = `${this.apiUrl}/ChecklistGroup/Delete`;
        return this.http.post<boolean>(url, { id: checklistGroupId });
    }

    getSelectedItems(model: SelectedChecklistGroupsModel): Observable<ChecklistGroup[]> {
        const url = `${this.apiUrl}/ChecklistGroup/GetSelectedItems`;
        return this.http.post<ChecklistGroup[]>(url, model);
    }

    copy(model: CopyChecklistGroupViewModel): Observable<boolean> {
        const url = `${this.apiUrl}/ChecklistGroup/Copy`;
        return this.http.post<boolean>(url, model);
    }
}
