import { Component, Input, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, ComponentControl, DynamicService, FacilityInfoComponent, FacilityPickerComponent, FluentSearchConfig, FluentTableComponent, FluentTableConfig, FluentTableRequestExportType, IdentityLookupViewModel, InputControl, IqPersonnelInfoComponent, IqPersonnelPickerComponent, RowControl } from 'codera-shared';
import { ChangeRequestStatusPickerComponent } from 'src/app/change-request/picker/status-picker/status-picker.component';
import { ChangeRequestTmsGroupByPickerComponent } from 'src/app/tasks/pickers/change-request-tms-group-by-picker/change-request-tms-group-by-picker.component';
import { ChangeRequestTmsGrouping } from 'src/app/tasks/types';
import { TmsMaturityStatusPickerComponent } from '../../change-request/picker/tms-maturity-status-picker/tms-maturity-status-picker.component';
import { TMSTaskInfoComponent } from '../task/info/tms-task-info.component';
import { TaskStatusPickerComponent } from '../task/task-status-picker/task-status-picker.component';
import { TmsTaskGridModel } from '../types';
import { ChangeRequestTypePickerComponent } from '../../system-controls/change-request-type/picker/change-request-type-picker.component';

@Component({
    selector: 'tms-task-grid',
    templateUrl: './tms-task-grid.component.html',
    styleUrls: ['./tms-task-grid.component.scss']
})
export class TmsTaskGridComponent implements OnInit {

    @Input()
    source: string;
    fluentTableConfig: FluentTableConfig;
    fluentSearchConfig: FluentSearchConfig;
    currentFilter: { [key: string]: any }[] = [];
    @ViewChild(FluentTableComponent) fluentTableComponent: FluentTableComponent;

    constructor(
        private router: Router,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService
    ) { }

    ngOnInit() {
        const that = this;

        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "ChangeRequestFacility",
                    type: "action",
                    sortable: "ChangeRequestFacility",
                    title: "[MOCQuest].[ChangeRequest].ChangeRequestFacility",
                    render: (row: TmsTaskGridModel) => {
                        return `<a href="javascript:void(0);">${row.ChangeRequestFacility.Name}</a>`
                    },
                    handler: (row: TmsTaskGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, FacilityInfoComponent, [
                            { facilityId: row.ChangeRequestFacility.Id }
                        ]);
                    },
                    show: that.applicationService.getCurrentSite().IsMultiSite
                },
                {
                    field: "ChangeRequest",
                    type: "action",
                    sortable: "ChangeRequestName",
                    title: "[MOCQuest].[SiteMap].ChangeRequest",
                    render: (row: TmsTaskGridModel) => {
                        const siteKey = that.applicationService.getCurrentSiteKey();
                        if (row && row.ChangeRequest && row.ChangeRequest.Name)
                            return `<a href="/mocquest/sites/${siteKey}/change-request/details/${row.ChangeRequest.Id}">${row.ChangeRequest.Name}</a>`;
                        return "";
                    },
                    handler: (row: TmsTaskGridModel) => {
                        that.router.navigate(["/change-request/details", row.ChangeRequest.Id]);
                    }
                },
                {
                    field: "ChangeRequestStatus",
                    type: "custom",
                    sortable: "ChangeRequestStatus",
                    title: "[MOCQuest].[ChangeRequest].ChangeRequestStatus",
                    render: (row: TmsTaskGridModel) => {
                        return `${that.translate.instant(row.ChangeRequestStatus.Name)}`
                    }
                },
                {
                    field: "Title",
                    type: "action",
                    sortable: "Title",
                    title: "[MOCQuest].[Common].TaskTitle",
                    render: (row: TmsTaskGridModel) => {
                        return `<a href='javascript:void(0);'>${row.Title}</a>`;
                    },
                    handler: (row: TmsTaskGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, TMSTaskInfoComponent, [
                            { id: row.Id },
                            {
                                onUpdateTask: () => {
                                    that.fluentTableConfig.notifyDataChanged();
                                }
                            }
                        ])
                    }
                },
                {
                    field: "Status",
                    type: "custom",
                    sortable: "Status",
                    title: "[ISOQuest].[Common].Status",
                    render: (row: TmsTaskGridModel) => {
                        return `${that.translate.instant(row.Status.Name)}`
                    }
                },
                {
                    field: "MaturityStatus",
                    type: "custom",
                    sortable: "MaturityStatus",
                    title: "[ISOQuest].[Common].MaturityStatus",
                    render: (row: TmsTaskGridModel) => {
                        return `${that.translate.instant(row.MaturityStatus.Name)}`
                    }
                },
                {
                    field: "StartDate",
                    type: "date",
                    sortable: "StartDate",
                    title: "[ISOQuest].[Common].StartDate",
                },
                {
                    field: "DueDate",
                    type: "date",
                    sortable: "EndDate",
                    title: "[ISOQuest].[Common].DueDate",
                }, {
                    field: "ActualStartDate",
                    type: "date",
                    sortable: "ActualStartDate",
                    title: "[ISOQuest].[Common].ActualStartDate"
                },
                {
                    field: "CompletionDate",
                    type: "date",
                    sortable: "CompletionDate",
                    title: "[ISOQuest].[Common].CompletionDate",
                },
                {
                    field: "Owner",
                    type: "action",
                    title: "[ISOQuest].[Common].Owner",
                    render: (row: TmsTaskGridModel) => {
                        if (row.Owner && row.Owner.Name)
                            return `<a href="javascript:void(0);">${row.Owner.Name}</a>`;
                        return "-";
                    },
                    handler: (row: TmsTaskGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, IqPersonnelInfoComponent, [
                            { personnelId: row.Owner.Id }
                        ]);
                    }
                },
                {
                    field: "Assignee",
                    type: "action",
                    title: "[ISOQuest].[Common].Assignee",
                    render: (row: TmsTaskGridModel) => {
                        if (row.Assignee && row.Assignee.Name)
                            return `<a href="javascript:void(0);">${row.Assignee.Name}</a>`;
                        return "-";
                    },
                    handler: (row: TmsTaskGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, IqPersonnelInfoComponent, [
                            { personnelId: row.Assignee.Id }
                        ]);
                    }
                }
            ],
            sorting: { Title: "asc" },
            selection: {
                mode: "none"
            },
            grouping: {
                field: "ChangeRequest",
                render: (row) => {
                    if (row.key) {
                        return that.translate.instant(row.key);
                    } else {
                        return that.translate.instant("[ISOQuest].[Common].NotAvailable");
                    }
                },
                expandGroups: true
            },
            export: [
                {
                    exportType: FluentTableRequestExportType.Xls,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });

        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new InputControl({
                        field: "ChangeRequestName",
                        type: "text",
                        label: "[MOCQuest].[SiteMap].ChangeRequest",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: IqPersonnelPickerComponent,
                        field: "Assignee",
                        data: [
                            { targetModule: "MOCQuest" },
                            { type: "Personnel" },
                            { label: "[ISOQuest].[Common].Assignee" }
                        ],
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "ChangeRequestFacility",
                        data: [
                            { label: "[ISOQuest].[Common].Facility" }
                        ],
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: TmsMaturityStatusPickerComponent,
                        field: "MaturityStatus",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: TaskStatusPickerComponent,
                        field: "TaskStatus",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestStatusPickerComponent,
                        field: "ChangeRequestStatus",
                        data: [
                            { label: "[MOCQuest].[ChangeRequest].ChangeRequestStatus" }
                        ],
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTmsGroupByPickerComponent,
                        field: "GroupBy",
                        data: [
                            {
                                modelChange: (model: IdentityLookupViewModel) => {
                                    let field = "";
                                    let property = "";
                                    if (model != null && model.Id != null) {

                                        switch (model.Id) {
                                            case ChangeRequestTmsGrouping.ChangeRequest:
                                                field = "ChangeRequest";
                                                break;

                                            case ChangeRequestTmsGrouping.Facility:
                                                field = "ChangeRequestFacility";
                                                break;

                                            case ChangeRequestTmsGrouping.Assignee:
                                                field = "Assignee";
                                                break;

                                            case ChangeRequestTmsGrouping.TaskStatus:
                                                field = "Status";
                                                break;

                                            default:
                                                field = undefined;
                                                property = undefined;
                                                break;
                                        }

                                        that.fluentTableConfig.grouping = {
                                            field: field,
                                            property: property,
                                            render: (row) => {
                                                if (row.key) {
                                                    return that.translate.instant(row.key);
                                                } else {
                                                    return that.translate.instant("[ISOQuest].[Common].NotAvailable");
                                                }
                                            },
                                            expandGroups: true
                                        };
                                    } else {
                                        that.fluentTableConfig.grouping = null;
                                        that.fluentTableConfig.sorting = { Title: "asc" };
                                    }

                                    that.fluentTableConfig.notifyDataChanged();
                                },
                            },
                            { defaultSelected: ChangeRequestTmsGrouping.ChangeRequest }
                        ],
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new InputControl({
                        type: "text",
                        field: "ChangeRequestId",
                        label: "[MOCQuest].[ChangeRequest].ChangeRequestId",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "isMultiple": true }
                        ]
                    }),
                    new InputControl({
                        type: "text",
                        field: "ProjectNumber",
                        label: "[MOCQuest].[ChangeRequest].ProjectNumber",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        show: that.applicationService.getCurrentModuleSettings().EnableProjectNumber
                    }),
                ])
            ],
            searchAction: () => {
                that.fluentTableConfig.applyFilterAndGrouping(that.currentFilter);
            },
            resetAction: () => {
                that.fluentTableConfig.grouping = undefined;
                that.fluentTableConfig.sorting = [{ Title: "asc" }]
                that.fluentTableConfig.applyFilterAndGrouping(that.currentFilter);
                that.fluentTableComponent.table.groupedRows = undefined;
                that.fluentTableComponent.table.sorts = [{ prop: "Title", dir: "asc" }];
            }
        });
    }

}
