import { Injectable, ViewContainerRef } from '@angular/core';
import { Validators } from '@angular/forms';
import { AttachmentsMultipleModeComponent, CancelAction, ComponentControl, CustomAction, DynamicService, InputControl, IqDatePickerComponent, IqPersonnelPickerComponent, LabelControl, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextareaControl } from 'codera-shared';
import { FollowUpResultPickerComponent } from 'src/app/system-controls/follow-up-result/picker/follow-up-result-picker.component';
import { MethodOfFollowUpPickerComponent } from 'src/app/system-controls/method-of-follow-up/picker/method-of-follow-up-picker.component';
import { FollowUpService } from './follow-up.service';
import { AddEditFollowUpViewModel } from './types';


@Injectable({
  providedIn: 'root'
})
export class FollowUpFactory {
  constructor(
    private followUpService: FollowUpService
  ) {

  }

  getNewFollowUpModalConfig(model: AddEditFollowUpViewModel, onSave?: (result: AddEditFollowUpViewModel) => void, onCancel?: () => void): ModalFormConfig {
    const that = this;

    const modalConfig = new ModalFormConfig(
      "[MOCQuest].[ChangeRequest].FollowUp.SetupFollowUp",
      [
        new RowControl([
          new ComponentControl({
            field: "DueDate",
            componentClass: IqDatePickerComponent,
            data: [
              { "label": "[ISOQuest].[Common].DueDate" },
              { "isRequired": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "AssignedTo",
            componentClass: IqPersonnelPickerComponent,
            data: [
              { "label": "[ISOQuest].[Common].AssignedTo" },
              { "targetModule": "MOCQuest" },
              { "isRequired": true },
              { "isMultiple": true },
              { "onlyActive": true },
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "MethodOfFollowUp",
            componentClass: MethodOfFollowUpPickerComponent,
            data: [
              { "isRequired": true },
              { "onlyActive": true }
            ]
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback() {
            that.followUpService.submit(model).subscribe((eventTypeId) => {
              if (onSave) {
                onSave(model);
              }
            })
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() {
            if (onCancel) {
              onCancel();
            }
          },
          css: "btn btn-default"
        })
      ]
    );

    return modalConfig;
  }

  getFollowUpModalConfig(model: AddEditFollowUpViewModel, dynamicService: DynamicService, viewContainer: ViewContainerRef, onSave?: (result: AddEditFollowUpViewModel) => void, onCancel?: () => void): ModalFormConfig {
    const that = this;

    const modalConfig = new ModalFormConfig(
      "[MOCQuest].[ChangeRequest].FollowUp.Conduct",
      [
        new RowControl([
          new ComponentControl({
            field: "DueDate",
            componentClass: IqDatePickerComponent,
            data: [
              { "label": "[ISOQuest].[Common].DueDate" },
              { "isRequired": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "DateCompleted",
            componentClass: IqDatePickerComponent,
            data: [
              { "label": "[MOCQuest].[Common].DateCompleted" },
              { "isRequired": true },
              { "maxDate": new Date() }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "AssignedTo",
            componentClass: IqPersonnelPickerComponent,
            data: [
              { "label": "[ISOQuest].[Common].AssignedTo" },
              { "isRequired": true },
              { "isMultiple": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "MethodOfFollowUp",
            componentClass: MethodOfFollowUpPickerComponent,
            data: [
              { "label": "[MOCQuest].[Common].MethodOfFollowUp" },
              { "isRequired": true },
              { "onlyActive": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "FollowUpResult",
            componentClass: FollowUpResultPickerComponent,
            data: [
              { "label": "[MOCQuest].[Common].FollowUpResult" },
              { "isRequired": true },
              { "onlyActive": true }
            ]
          })
        ]),
        new RowControl([
          new TextareaControl({
            field: "Comment",
            label: "[ISOQuest].[Common].Comment",
            required: true,
            rows: 5,
            validators: [
              Validators.required
            ]
          })
        ]),
        new RowControl([
          new InputControl({
            type: "checkbox",
            field: "HasNextFollowUp",
            label: "[MOCQuest].[ChangeRequest].FollowUp.ConductAnotherFollowUp",
            tip: "[MOCQuest].[ChangeRequest].FollowUp.ConductAnotherFollowUp.Tip",
            events: [
              {
                eventName: "change",
                eventCallback: () => {
                  var nextFollowUpDueDate = modalConfig.getControlByField("NextFollowUpDueDate") as ComponentControl;
                  var nextFollowUpAssignedTo = modalConfig.getControlByField("NextFollowUpAssignedTo") as ComponentControl;
                  var nextFollowUpMethodOfFollowUp = modalConfig.getControlByField("NextFollowUpMethodOfFollowUp") as ComponentControl;

                  if (model.HasNextFollowUp) {
                    nextFollowUpDueDate.show = true;
                    nextFollowUpAssignedTo.show = true;
                    nextFollowUpMethodOfFollowUp.show = true;

                    nextFollowUpDueDate.setRequired(true);
                    nextFollowUpAssignedTo.setRequired(true);
                    nextFollowUpMethodOfFollowUp.setRequired(true);
                  }
                  else {
                    nextFollowUpDueDate.show = false;
                    nextFollowUpAssignedTo.show = false;
                    nextFollowUpMethodOfFollowUp.show = false;

                    nextFollowUpDueDate.setRequired(false);
                    nextFollowUpAssignedTo.setRequired(false);
                    nextFollowUpMethodOfFollowUp.setRequired(false);
                  }

                  model.NextFollowUpAssignedTo = [];

                  (nextFollowUpAssignedTo as ComponentControl).value = [];
                  model.NextFollowUpDueDate = null;
                  model.NextFollowUpMethodOfFollowUp = null;
                }
              }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "NextFollowUpDueDate",
            componentClass: IqDatePickerComponent,
            show: false,
            data: [
              { "label": "[ISOQuest].[Common].DueDate" },
              { "isRequired": true },
              { "minDate": model.DueDate }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "NextFollowUpAssignedTo",
            componentClass: IqPersonnelPickerComponent,
            show: false,
            data: [
              { "label": "[ISOQuest].[Common].AssignedTo" },
              { "isRequired": true },
              { "isMultiple": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            field: "NextFollowUpMethodOfFollowUp",
            componentClass: MethodOfFollowUpPickerComponent,
            show: false,
            data: [
              { "label": "[MOCQuest].[Common].MethodOfFollowUp" },
              { "isRequired": true },
              { "onlyActive": true }
            ]
          })
        ]),
        new RowControl([
          new ComponentControl({
            componentClass: AttachmentsMultipleModeComponent,
            field: "Attachments",
            data: [
              { isMultiple: true },
              { url: `ChangeRequest/UploadFollowUpAttachment/${model.ChangeRequestId}` },
              { deleteUrl: 'ChangeRequest/DeleteFollowUpAttachment' }
            ]
          })
        ])
      ],
      [
        new CustomAction({
          actionType: "submit",
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback() {
            const confirmationModalConfig = that.getConductConfirmationModal(model, () => {
              that.followUpService.submit(model).subscribe((response) => {
                if (typeof response === "string")
                  model.Id = response;

                onSave(model);
              });
            });
            dynamicService.addComponent(viewContainer, ModalFormComponent, [
              { "config": confirmationModalConfig },
              { "model": model },
              {
                "modalOptions": {
                  size: "md"
                }
              }
            ], true);
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() {
            if (onCancel) {
              onCancel();
            }
          },
          css: "btn btn-default"
        })
      ]
    );

    return modalConfig;
  }

  getConductConfirmationModal(model: AddEditFollowUpViewModel, onSave?: (result: AddEditFollowUpViewModel) => void, onCancel?: () => void): ModalFormConfig {
    const that = this;
    const modalConfig = new ModalFormConfig(
      "[ISOQuest].[Common].ConfirmDialog.Title",
      [
        new RowControl([
          new LabelControl({
            text: model.HasNextFollowUp ? "[MOCQuest].[Common].FollowUp.NextFollowUpConfirmation" : "[MOCQuest].[Common].FollowUp.ChangeRequestCloseConfirmation",
            css: "col-md-12"
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Confirm",
          callback() {
            if (onSave) {
              onSave(model);
            }
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() {
            if (onCancel) {
              onCancel();
            }
          },
          css: "btn btn-default"
        })
      ]
    );

    return modalConfig;
  }
}
