import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ApplicationService, DynamicService, LookupViewModel, ModalFormComponent } from 'codera-shared';
import { ChangeRequestTagFactory } from '../change-request-tag.factory';
import { ChangeRequestTagViewModel } from '../types';

@Component({
  selector: 'change-request-tag-picker',
  templateUrl: './change-request-tag-picker.component.html',
  styleUrls: ['./change-request-tag-picker.component.scss']
})
export class ChangeRequestTagPickerComponent implements OnInit {
    @Input() model: any;
    @Output() modelChange: EventEmitter<any>;

    @Input() label: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() enableAdding: boolean;
    @Input() allowTags: boolean;

    @Output() onSelect: EventEmitter<any>;
    @Output() onRemove: EventEmitter<any>;

    controllerName: string;
    hasPermission: boolean;
    showNewItemButton: boolean;
    data: { [key: string]: any } = {};

    constructor(
        private applicationService: ApplicationService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private changeRequestTagFactory: ChangeRequestTagFactory
    ) {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        this.hasPermission = this.applicationService.hasAnyRole(["ISOQuestSuperAdministrator", "MOCQuestAdministrator"]);
        this.updateControllerName();

        if (!this.label) {
            this.label = this.isMultiple
                ? "[MOCQuest].[SiteMap].SystemControls.ChangeRequestTag.Multiple"
                : "[MOCQuest].[SiteMap].SystemControls.ChangeRequestTag.Single";
        }

        if (this.enableAdding && this.hasPermission)
            this.showNewItemButton = true;
    }

    onChange(newValue: any) { 
        this.model = newValue;
        this.modelChange.emit(this.model);
    }

    onNewItemClick() {
        let onSave = (result: any) => {
            if (this.isMultiple) {
                if (this.model) {
                    this.model = this.model.concat([result]);
                } else {
                    this.model = [result];
                }
            } else {
                this.model = result;
            }

            this.modelChange.emit(this.model);
        };

        let model = new ChangeRequestTagViewModel();
        let config = this.changeRequestTagFactory.getModalConfig(model, onSave);

        this.dynamicService.addComponent(this.viewContainer,
            ModalFormComponent,
            [
                { model: model },
                { config: config },
                { modalOptions: { size: 'sm' } }
            ], true
        );
    }

    private updateControllerName() {
        let controllerName = "/ChangeRequestTag/GetAvailableItems";
        this.controllerName = controllerName;
    }
}
