import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { CancelAction, ComponentControl, CustomAction, DynamicService, IqSelectPickerComponent, ModalFormComponent, ModalFormConfig, SelectDocumentsComponent } from 'codera-shared';
import { DocumentActivityModel } from '../system-controls/checklist-groups/types';

@Component({
    selector: 'documents-picker',
    templateUrl: './documents-picker.component.html'
})
export class DocumentsPickerComponent implements OnInit {
    @Input() model: DocumentActivityModel[];
    @Input() label: string;
    @Input() isMultiple: boolean;
    @Input() isRequired: boolean;
    @Input() isDisabled: boolean;
    @Input() onlyActive: boolean;

    @Output() modelChange: EventEmitter<DocumentActivityModel[]>;
    modalConfig: ModalFormConfig;

    constructor(private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef) {
        this.modelChange = new EventEmitter();
    }

    ngOnInit() {
        var that = this;

        if (!this.label) {
            this.label = "[ISOQuest].[Common].Documents";
        }

        this.modalConfig = new ModalFormConfig("[ISOQuest].[Common].Buttons.PickDocuments", [
            new ComponentControl({
                componentClass: SelectDocumentsComponent,
                field: "model",
                css: "col-sm-12 col-md-12 col-lg-12",
                data: [
                    { "selectedDocuments": new Array<DocumentActivityModel>() },
                    { "rootDocumentIds": new Array<string>() }
                ]
            })
        ], [
            new CustomAction({
                actionType: "button",
                field: "Submit",
                value: "[ISOQuest].[Common].Buttons.Submit",
                callback() {
                    const grid = that.model as any[];
                    grid.splice(0, grid.length);

                    const selectedDocuments = (that.modalConfig.getControlByField("model") as ComponentControl).getDataByKey("selectedDocuments").selectedDocuments;
                    for (let i = 0; i < selectedDocuments.length; i++) {
                        const selectedDocument = selectedDocuments[i];

                        grid.push({
                            RootDocumentId: selectedDocument.RootDocumentId,
                            ReferenceNumber: selectedDocument.ReferenceNumber,
                            Id: selectedDocument.Id,
                            RevisionNumber: selectedDocument.RevisionNumber,
                            Title: selectedDocument.Title,
                            ContentType: selectedDocument.ContentType,
                            Department: selectedDocument.Department.Name,
                            DocumentType: selectedDocument.DocumentType.Name,
                            IssuedDate: selectedDocument.IssuedDate,
                            Facility: selectedDocument.Facility,
                            CanRemove: true,
                            Name: selectedDocument.Title,
                            Status: true
                        });
                    }

                    that.modelChange.emit(grid);
                    that.modalConfig.modalRef.close();
                },
                css: "btn btn-primary"
            }),
            new CancelAction({
                field: "Cancel",
                value: "[ISOQuest].[Common].Buttons.Cancel",
                callback() { },
                css: "btn btn-default"
            })
        ]);
    }

    onChange(newValue: DocumentActivityModel[]) {
        this.model = newValue;
        this.modelChange.emit(this.model);
    }

    openDocumentsPicker(): void {
        const grid = this.model as any[];
        const selectedDocuments = (this.modalConfig.getControlByField("model") as ComponentControl).getDataByKey("selectedDocuments").selectedDocuments;
        const rootDocumentIds = (this.modalConfig.getControlByField("model") as ComponentControl).getDataByKey("rootDocumentIds").rootDocumentIds;

        selectedDocuments.splice(0, selectedDocuments.length);
        rootDocumentIds.splice(0, rootDocumentIds.length);

        for (let i = 0; i < grid.length; i++) {
            const document = grid[i];
            selectedDocuments.push({
                RootDocumentId: document.RootDocumentId,
                ReferenceNumber: document.ReferenceNumber,
                Id: document.Id,
                RevisionNumber: document.RevisionNumber,
                Title: document.Title,
                ContentType: document.ContentType,
                Department: { Name: document.Department },
                DocumentType: { Name: document.DocumentType },
                IssuedDate: document.IssuedDate,
                Facility: document.Facility,
                CanManage: true
            });
            rootDocumentIds.push(document.RootDocumentId);
        }

        this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
            { config: this.modalConfig },
            { modalOptions: { size: "lg" } }
        ], true);
    }
}
