import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IdentityLookupViewModel, ISOQuestModuleType, ISOQuestRoles, LookupViewModel, WorkflowPathsModel, WorkflowUiOptionsModel } from 'codera-shared';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UpdateOnChangeType } from 'src/app/change-request/types';
import { ChecklistGroup } from 'src/app/system-controls/checklist-groups/types';
import { AnalysisService } from '../../analysis.service';

@Component({
  selector: 'manage-mode-checklist-group-approvals',
  templateUrl: './manage-mode-checklist-group-approvals.component.html',
  styleUrls: ['./manage-mode-checklist-group-approvals.component.scss']
})
export class ManageModeChecklistGroupApprovalsComponent implements OnInit, OnDestroy {
  moduleType = ISOQuestModuleType;
  moduleRoles = ISOQuestRoles;
  changeType = UpdateOnChangeType;

  @Input() group: ChecklistGroup;
  @Input() objectId: string;
  @Input() objectType: string;
  @Input() workflowPaths: WorkflowPathsModel;

  workflowUiOptionsModel: WorkflowUiOptionsModel;

  destroy$: Subject<boolean> = new Subject<boolean>();
  constructor(
    private toastr: ToastrService,
    private translate: TranslateService,
    private analysisService: AnalysisService
  ) { }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }

  ngOnInit() { }

  updateOnChange(groupId: string, type: UpdateOnChangeType, value: string | boolean | IdentityLookupViewModel | LookupViewModel, questionId?: string, choiceId?: string, outcomeTypeConfigId?: string): void {
    this.analysisService.updateOnChange(groupId, type, value, questionId, choiceId, outcomeTypeConfigId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response) => {
          this.analysisService.fetchChecklistGroups();
          this.analysisService.fetchAnalysisAlerts();
          this.toastr.success(this.translate.instant('[ISOQuest].[Common].Notifications.ItemSaved'), '', { timeOut: 1500 });
        },
        error: (error) => {
          if (error && error.error && error.error.Errors && error.error.Errors[""] && error.error.Errors[""][0]) {
            var errorMessage = error.error.Errors[""][0];
            this.toastr.error(this.translate.instant(errorMessage));
          }
          this.analysisService.fetchChecklistGroups();
        }
      })
  }
}
