import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { FollowUpResultViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class FollowUpResultService {

  private apiUrl: string;

  constructor(
    private http: HttpClient,
    private applicationService: ApplicationService
  ) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  saveOrUpdate(model: FollowUpResultViewModel): Observable<string> {
    const url = `${this.apiUrl}/FollowUpResult/CreateOrUpdate`;
    return this.http.post<string>(url, model);
  }

  getById(followUpResultId: string): Observable<FollowUpResultViewModel> {
    const url = `${this.apiUrl}/FollowUpResult/GetById`;
    return this.http.post<FollowUpResultViewModel>(url, { id: followUpResultId });
  }

  delete(followUpResultId: string): Observable<boolean> {
    const url = `${this.apiUrl}/FollowUpResult/Delete`;
    return this.http.post<boolean>(url, { id: followUpResultId });
  }
}
