import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService, AttachmentViewModel, BulkDeleteViewModel, BulkOperationExecutionResult } from 'codera-shared';
import { Observable } from 'rxjs';
import { BulkChangeColorViewModel, BulkChangeTaskAssigneeViewModel, BulkChangeTaskCategoryViewModel, BulkChangeTaskOwnerViewModel, BulkChangeTaskPriorityViewModel, BulkChangeTaskStatusViewModel, CalculatePropertyValuesModel, SendTaskLinkOrEmailViewModel, TMSInstanceViewModel, TMSPermissionsModel, TMSSearchModel, TMSSettingsViewModel, TMSTaskViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class TaskManagementSystemService {
  private isoQuestApiUrl: string;

  constructor(private httpClient: HttpClient, private applicationService: ApplicationService) {
    this.isoQuestApiUrl = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}`;
  }

  getTaskManagementSystemInstance(objectType: string, objectId: string): Observable<TMSInstanceViewModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetTaskManagementSystemInstance`;
    return this.httpClient.post<TMSInstanceViewModel>(url, { objectType: objectType, objectId: objectId });
  }

  getDefaultSearch(objectType: string, objectId: string, defaultSearchOption?: string): Observable<TMSSearchModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetDefaultSearch`;
    return this.httpClient.post<TMSSearchModel>(url, { objectType: objectType, objectId: objectId, defaultSearchOption: defaultSearchOption });
  }

  getPermissions(objectType: string, objectId: string): Observable<TMSPermissionsModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetPermissions`;
    return this.httpClient.post<TMSPermissionsModel>(url, { objectType: objectType, objectId: objectId });
  }

  createOrUpdate(model: TMSTaskViewModel, copyTask?: boolean): Observable<string | boolean> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/CreateOrUpdate`;
    return this.httpClient.post<string | boolean>(url, { model: model, copyTask: copyTask });
  }

  getTaskManagementSystemSettings(objectType: string, objectId: string): Observable<TMSSettingsViewModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetSettings`;
    return this.httpClient.post<TMSSettingsViewModel>(url, { objectType: objectType, objectId: objectId });
  }

  getTMSTaskById(id: string): Observable<TMSTaskViewModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetById`;
    return this.httpClient.post<TMSTaskViewModel>(url, { id: id });
  }

  getTMSTaskAttachments(id: string): Observable<AttachmentViewModel[]> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/GetTaskAttachments`;
    return this.httpClient.post<AttachmentViewModel[]>(url, { id: id });
  }

  sendTaskAsLink(model: SendTaskLinkOrEmailViewModel): Observable<boolean> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/SendTaskAsLink`;
    return this.httpClient.post<boolean>(url, model);
  }

  calculatePropertyValues(model: CalculatePropertyValuesModel): Observable<CalculatePropertyValuesModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/CalculatePropertyValues`;
    return this.httpClient.post<CalculatePropertyValuesModel>(url, model);
  }

  saveSearch(model: TMSSearchModel): Observable<string> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/SaveSearch`;
    return this.httpClient.post<string>(url, model);
  }

  updateSearch(model: TMSSearchModel): Observable<string> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/UpdateSearch`;
    return this.httpClient.post<string>(url, model);
  }

  deleteSearch(id: string): Observable<TMSSearchModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/DeleteSearch`;
    return this.httpClient.post<TMSSearchModel>(url, { id: id });
  }

  resetToDefaultSystemSearch(id: string, defaultSearchOption?: string): Observable<TMSSearchModel> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/ResetToDefaultSystemSearch`;
    return this.httpClient.post<TMSSearchModel>(url, { id: id, defaultSearchOption: defaultSearchOption });
  }

  bulkChangeTaskColor(model: BulkChangeColorViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeColor`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkChangeTaskStatus(model: BulkChangeTaskStatusViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeStatus`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkChangeTaskOwner(model: BulkChangeTaskOwnerViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeOwner`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkChangeTaskAssignee(model: BulkChangeTaskAssigneeViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeAssignee`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkChangeTaskCategory(model: BulkChangeTaskCategoryViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeTaskCategory`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkChangeTaskPriority(model: BulkChangeTaskPriorityViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkChangeTaskPriority`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }

  bulkDelete(model: BulkDeleteViewModel): Observable<BulkOperationExecutionResult> {
    const url = `${this.isoQuestApiUrl}/TaskManagementSystem/BulkDelete`;
    return this.httpClient.post<BulkOperationExecutionResult>(url, model);
  }
}
