import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { ChangeRequestTypeViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class ChangeRequestTypeService {

    private apiUrl: string;

    constructor(
        private http: HttpClient,
        private applicationService: ApplicationService
    ) {
        this.apiUrl = this.applicationService.getApiUrl();
    }

    saveOrUpdate(model: ChangeRequestTypeViewModel): Observable<string | boolean> {
        const url = `${this.apiUrl}/ChangeRequestType/CreateOrUpdate`;
        return this.http.post<string | boolean>(url, model);
    }

    getById(changeTypeId: string): Observable<ChangeRequestTypeViewModel> {
        const url = `${this.apiUrl}/ChangeRequestType/GetById`;
        return this.http.post<ChangeRequestTypeViewModel>(url, { id: changeTypeId });
    }

    delete(changeTypeId: string): Observable<boolean> {
        const url = `${this.apiUrl}/ChangeRequestType/Delete`;
        return this.http.post<boolean>(url, { id: changeTypeId });
    }
}
