import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { TMSColorViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class TmsTaskColorService {
  private isoQuestApiUrl: string;

  constructor(private httpClient: HttpClient, private applicationService: ApplicationService) {
    this.isoQuestApiUrl = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}`;
  }

  delete(id: string): Observable<boolean> {
    const url = `${this.isoQuestApiUrl}/TMSColor/Delete`;
    return this.httpClient.post<boolean>(url, { id: id });
  }

  createOrUpdate(model: TMSColorViewModel): Observable<string | boolean> {
    const url = `${this.isoQuestApiUrl}/TMSColor/CreateOrUpdate`;
    return this.httpClient.post<string | boolean>(url, model);
  }

  getById(id: string): Observable<TMSColorViewModel> {
    const url = `${this.isoQuestApiUrl}/TMSColor/GetById`;
    return this.httpClient.post<TMSColorViewModel>(url, { id: id });
  }
}
