import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { LaunchViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class LaunchService {
  private apiUrl: string;

  constructor(private http: HttpClient, private applicationService: ApplicationService) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  getLaunch(changeRequestId: string): Observable<LaunchViewModel> {
    const url = `${this.apiUrl}/ChangeRequest/GetLaunch`;
    return this.http.post<LaunchViewModel>(url, { changeRequestId: changeRequestId });
  }

  updateLaunch(model: LaunchViewModel): Observable<boolean> {
    const url = `${this.apiUrl}/ChangeRequest/UpdateLaunch`;
    return this.http.post<boolean>(url, model);
  }
}
