import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FluentTabConfig, RenderButtonsPosition } from 'codera-shared';
import { ChangeRequestPssrTasksGridComponent } from 'src/app/change-request/change-request-pssr-tasks-grid/change-request-pssr-tasks-grid.component';
import { ChangeRequestService } from 'src/app/change-request/change-request.service';
import { ChangeRequestPssrTasksCountsViewModel } from '../types';

@Component({
  selector: 'change-request-pssr-tasks',
  templateUrl: './change-request-pssr-tasks.component.html',
  styleUrls: ['./change-request-pssr-tasks.component.scss']
})
export class ChangeRequestPssrTasksComponent implements OnInit {

  fluentTabsConfig: FluentTabConfig[];
  taskCounts: ChangeRequestPssrTasksCountsViewModel;
  renderButtonsTypes = RenderButtonsPosition;
  pageTitle: string;
  constructor(
    private route: ActivatedRoute,
    private translate: TranslateService,
    private changeRequestService: ChangeRequestService
  ) { }

  ngOnInit() {
    const that = this;

    this.pageTitle = this.route.snapshot.data["pageTitle"];
    this.getTaskCounts();

    this.fluentTabsConfig = [
      new FluentTabConfig({
        componentClass: ChangeRequestPssrTasksGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.AllTasks >= 0)
            count = that.taskCounts.AllTasks.toString();

          return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.AllTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span> `;
        },
        data: [
          {
            source: "ChangeRequest/AllTasksFromChangeRequestsPssrStageGrid",
            onDeleteTask: () => that.getTaskCounts()
          }
        ]
      }),
      new FluentTabConfig({
        componentClass: ChangeRequestPssrTasksGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.AllOpenTasks >= 0)
            count = that.taskCounts.AllOpenTasks.toString();

            return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.AllOpenTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span>`;
        },
        data: [
          {
            source: "ChangeRequest/AllOpenTasksFromChangeRequestsPssrStageGrid",
            onDeleteTask: () => that.getTaskCounts()
          }
        ]
      }),
      new FluentTabConfig({
        componentClass: ChangeRequestPssrTasksGridComponent,
        title: () => {
          let count = "-";
          if (that.taskCounts && that.taskCounts.MyOpenTasks >= 0)
            count = that.taskCounts.MyOpenTasks.toString();

            return `${that.translate.instant('[ISOQuest].[TaskManagementSystem].SystemDefinedSearch.MyOpenTasks')}&nbsp;<span class='badge badge-success rounded-circle'>${count}</span> `;
        },
        data: [
          {
            source: "ChangeRequest/MyOpenTasksFromChangeRequestsPssrStageGrid",
            onDeleteTask: () => that.getTaskCounts()
          }
        ]
      })
    ]
  }

  getTaskCounts() {
    this.changeRequestService.getChangeRequestPssrTasksCounts().toPromise().then(response => {
      this.taskCounts = response;
    });
  }
}
