import { Component, OnInit, Input, ViewContainerRef, ComponentRef, ViewChild, ElementRef, AfterViewInit, OnDestroy, Output, EventEmitter } from '@angular/core';
import { ModalFormConfig, DynamicService, ModalFormComponent, ApplicationService, IqLanguage, IsoQuestEventManagerService, IdentityLookupViewModel, LookupViewModel } from 'codera-shared';
import { UpdateLocalizationItemViewModel, ValuePerLanguage, LocalizationItemViewModel } from '../types';
import { NgbModalRef, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import { LocalizationService } from '../localization.service';

@Component({
  selector: 'app-create-update-localization',
  templateUrl: './create-update-localization.component.html',
  styleUrls: ['./create-update-localization.component.scss']
})
export class CreateUpdateLocalizationItemComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild("modalContent") modalContent: ElementRef; // template variable

  @Input()
  id: string;

  @Input()
  key: string;

  @Input()
  isExternal: boolean;

  @Input()
  sectionId: string;

  @Input()
  moduleId: number;

  @Input()
  javaScript: boolean;

  @Output()
  onCreateUpdate: EventEmitter<UpdateLocalizationItemViewModel>

  model: UpdateLocalizationItemViewModel;
  modalFormConfig: ModalFormConfig;
  component: ComponentRef<ModalFormComponent>;
  languagesModel: ValuePerLanguage[];
  submitted: boolean;
  title: string;
  private modalRef: NgbModalRef;
  private backendErrors: string[];

  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private isoQuestEventManager: IsoQuestEventManagerService,
    private modalService: NgbModal,
    private localizationService: LocalizationService
  ) {
    this.onCreateUpdate = new EventEmitter<UpdateLocalizationItemViewModel>();
    this.backendErrors = [];
  }

  ngOnInit() {
    this.submitted = false;
    this.languagesModel = this.applicationService.getAvailableLanguages().map((item: IqLanguage) => {
      return new ValuePerLanguage("", item.Id, "", item.ShortTitle);
    });
    this.model = new UpdateLocalizationItemViewModel();
    if (this.isExternal) {
      this.model.IsExternal = this.isExternal;
      this.model.Key = this.key;
      this.model.JavaScript = this.javaScript;
      this.model.Module = new IdentityLookupViewModel(this.moduleId, "", false);
      this.model.Section = new LookupViewModel(this.sectionId, "", false);
    }
    this.model.Values = this.languagesModel;
    this.title = this.id ? "[ISOQuest].[Localization].EditItem.DialogTitle" : "[ISOQuest].[Localization].NewItem.DialogTitle"
    if (this.id) {
      this.localizationService.getLocalizationItemsById(this.id, this.isExternal).subscribe((response: LocalizationItemViewModel[]) => {
        this.model.Values = response.map((item: LocalizationItemViewModel) => {
          return new ValuePerLanguage(item.Id, item.Language.Id, item.Value, item.Language.ShortTitle);
        });
      });
    }
  }

  ngAfterViewInit(): void {
    setTimeout(() => this.modalRef = this.modalService.open(this.modalContent, { size: "lg", centered: true, backdrop: "static" }));
  }

  ngOnDestroy(): void {
  }

  submit(localizationForm: NgForm) {
    this.submitted = true;
    if (localizationForm.invalid)
      return;

    this.subscribeOnSubmitEvents();

    if (this.id)
      this.localizationService.update(this.model).subscribe(() => {
        this.onCreateUpdate.emit(this.model);
      });
    else
      this.localizationService.create(this.model).subscribe(() => {
        this.onCreateUpdate.emit(this.model);
      });
  }

  cancel() {
    this.modalRef.close();
  }

  private subscribeOnSubmitEvents() {
    this.isoQuestEventManager.subscribe(this, "on-response-success", (eventData) => {
      this.backendErrors = [];

      this.unsubscribeFromAllSubmitEvents();
      this.modalRef.close();
    });

    this.isoQuestEventManager.subscribe(this, "on-response-error", (eventData) => {
      this.backendErrors = [];

      const errors = eventData.data.error.Errors;
      Object.keys(errors).forEach((key: string) => this.backendErrors = this.backendErrors.concat(errors[key]));

      this.unsubscribeFromAllSubmitEvents();
    });
  }

  private unsubscribeFromAllSubmitEvents() {
    this.isoQuestEventManager.unsubscribe(this, "on-response-success");
    this.isoQuestEventManager.unsubscribe(this, "on-response-error");
  }
}
