import { Component, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { ModalFormConfig, RowControl, LabelControl, SubmitAction, CancelAction, ModalFormComponent, DynamicService, BulkDeleteViewModel, BulkOperationExecutionResultComponent } from 'codera-shared';
import { TaskManagementSystemService } from '../../task-management-system.service';

@Component({
  selector: 'bulk-delete-tms-task',
  template: ''
})
export class BulkDeleteTmsTaskComponent implements OnInit {
  @Input()
  selectedIds: string[];

  @Output()
  onSuccess: EventEmitter<void>;

  model: BulkDeleteViewModel;
  constructor(
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private taskManagementSystemService: TaskManagementSystemService
  ) {
    this.onSuccess = new EventEmitter<void>();
  }

  ngOnInit() {
    const that = this;

    this.model = new BulkDeleteViewModel(this.selectedIds);
    const modalForm = new ModalFormConfig(
      "[ISOQuest].[Common].ConfirmDialog.Title",
      [
        new RowControl([
          new LabelControl({
            text: `[ISOQuest].[Common].ConfirmDialog.DeleteMessage`
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.OK",
          css: "btn btn-primary",
          callback: () => {
            that.taskManagementSystemService.bulkDelete(that.model).subscribe(response => {
              that.dynamicService.addComponent(that.viewContainer, BulkOperationExecutionResultComponent, [
                { model: response },
                {
                  modalOptions: {
                    size: "md"
                  }
                }
              ]);
              that.onSuccess.emit();
            });
          }
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          css: "btn btn-default",
          callback: () => {

          }
        })
      ]
    );

    that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
      { config: modalForm },
      { model: that.model },
      {
        modalOptions: {
          size: "md"
        }
      }
    ]);
  }

}
