import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';

@Component({
  selector: 'tms-task-priority-color',
  templateUrl: './tms-task-priority-color.component.html',
  styleUrls: ['./tms-task-priority-color.component.scss']
})
export class TmsTaskPriorityColorComponent implements OnInit {
  @Input()
  model: IdentityLookupViewModel;
  @Output()
  modelChange: EventEmitter<IdentityLookupViewModel>;

  constructor() {
    this.modelChange = new EventEmitter<IdentityLookupViewModel>();
  }

  ngOnInit() {
  }

  onColorPickerChange(color: string): void {
    this.model.Name = color;
    this.modelChange.emit(this.model);
  }
}
