import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, BulkChangeStatusComponent, BulkDeleteComponent, ComponentControl, DynamicService, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderDropdownItem, HeaderItemsConfig, InputControl, IqStatusPickerComponent, ModalFormComponent, ModalFormConfig, RenderButtonsPosition, RowControl, TextConfirmationModalComponent } from 'codera-shared';
import { FollowUpResultFactory } from './follow-up-result.factory';
import { FollowUpResultService } from './follow-up-result.service';
import { FollowUpResultGridModel, FollowUpResultViewModel } from './types';

@Component({
  templateUrl: './follow-up-result.page.html',
})
export class FollowUpResultPage implements OnInit {
  pageTitle: string;
  currentFilter: { [key: string]: any }[] = [];
  fluentSearchConfig: FluentSearchConfig;
  fluentTableConfig: FluentTableConfig;
  tableSource: string;
  modalConfig: ModalFormConfig;
  model: FollowUpResultViewModel;
  renderButtonsTypes = RenderButtonsPosition;
  headerItemsConfig: HeaderItemsConfig;

  constructor(
    private titleService: Title,
    private route: ActivatedRoute,
    private translate: TranslateService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private applicationService: ApplicationService,
    private followUpResultService: FollowUpResultService,
    private followUpResultFactory: FollowUpResultFactory,
  ) {
    this.model = new FollowUpResultViewModel();
  }

  ngOnInit() {
    const that = this;

    this.tableSource = `FollowUpResult/Grid`;
    this.pageTitle = this.route.snapshot.data["pageTitle"];

    this.headerItemsConfig = new HeaderItemsConfig([
      new HeaderButtonItem({
        name: "new-follow-up-result",
        icon: "fa fa-plus",
        label: "[MOCQuest].[Common].Buttons.FollowUpResult.New",
        action: that.summonDialog
      }),
      new HeaderDropdownItem({
        name: "bulk-actions",
        icon: "",
        label: "[ISOQuest].[Common].Buttons.WithSelected",
        dropdownActions: [
          new HeaderButtonItem({
            name: "change-status",
            icon: "",
            label: "[ISOQuest].[Common].BulkActions.ChangeStatus",
            action: that.bulkChangeStatus
          }),
          new HeaderButtonItem({
            name: "change-status",
            icon: "",
            label: "[ISOQuest].[Common].BulkActions.Delete",
            action: that.bulkDelete
          })
        ]
      })
    ]);

    // set the title in the tab
    this.translate.get(this.pageTitle).subscribe(translated => {
      this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translated}`);
    });

    // filter config
    this.fluentSearchConfig = new FluentSearchConfig({
      controls: [
        new RowControl([
          new InputControl({
            type: "text",
            field: "Title",
            label: "[ISOQuest].[Common].Title",
            css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
          }),
          new ComponentControl({
            componentClass: IqStatusPickerComponent,
            field: "Status",
            label: "[ISOQuest].[Common].Status",
            css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
          })
        ])
      ],
      searchAction() {
        that.fluentTableConfig.applyFilter(that.currentFilter);
      },
      resetAction() {
        that.fluentTableConfig.applyFilter(that.currentFilter);
      }
    });

    // table config
    this.fluentTableConfig = new FluentTableConfig({
      columns: [
        {
          field: "Title",
          title: "[ISOQuest].[Common].Title",
          sortable: "Title",
          type: "string",
          flexGrow: 1.5
        },
        {
          field: "Status",
          title: "[ISOQuest].[Common].Status",
          sortable: "Status",
          type: "status"
        },
        {
          field: "ModifiedBy",
          title: "[ISOQuest].[Common].ModifiedBy",
          sortable: "ModifiedBy",
          type: "personnel"
        },
        {
          field: "ModifiedAt",
          title: "[ISOQuest].[Common].ModifiedAt",
          sortable: "ModifiedAt",
          type: "date"
        },
        {
          field: "editAction",
          title: "",
          type: "action",
          handler(row: FollowUpResultGridModel) {
            that.followUpResultService.getById(row.Id).subscribe((response: FollowUpResultViewModel) => {
              that.model = response;
              that.modalConfig = that.followUpResultFactory.getModalConfig(that.model, () => { that.fluentTableConfig.notifyDataChanged('item-updated'); });
              that.modalConfig.modalHeader = "[MOCQuest].[Common].Buttons.FollowUpResult.Edit";
              that.dynamicService.addComponent(that.viewContainer, ModalFormComponent, [
                { "config": that.modalConfig },
                { "model": that.model },
                {
                  "modalOptions": {
                    size: "sm"
                  }
                }
              ], true);
            });
          },
          render(row: FollowUpResultGridModel) {
            return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
          },
          flexGrow: 0.4
        },
        {
          field: "deleteAction",
          title: "",
          type: "action",
          handler(row: FollowUpResultGridModel) {
            that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
              { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
              { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" }, ,
              {
                "modalOptions": {
                  size: "sm"
                }
              },
              {
                "successCallback": () => {
                  that.followUpResultService.delete(row.Id)
                    .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                }
              }
            ], true);
          },
          render(row: FollowUpResultGridModel) {
            return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
          },
          flexGrow: 0.4
        }
      ],
      selection: {
        mode: "checkbox"
      },
      sorting: {
        "Title": "asc"
      },
      export: [
        {
          exportType: 2,
          label: "[ISOQuest].[Common].ExportToExcel"
        }
      ]
    });
  }

  summonDialog = () => {
    this.model = new FollowUpResultViewModel();
    this.modalConfig = this.followUpResultFactory.getModalConfig(this.model, () => { this.fluentTableConfig.notifyDataChanged('item-created'); });
    this.modalConfig.modalHeader = "[MOCQuest].[Common].Buttons.FollowUpResult.New";
    this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
      { "config": this.modalConfig },
      { "model": this.model },
      { "modalOptions": { size: "sm" } }
    ], true);
  }

  bulkChangeStatus = () => {
    this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusComponent, [
      { "fluentTable": this.fluentTableConfig },
      { "url": "FollowUpResult/BulkChangeStatus" },
      { "modalOptions": { size: "sm" } }
    ]);
  }

  bulkDelete = () => {
    this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
      { "fluentTable": this.fluentTableConfig },
      { "url": "FollowUpResult/BulkDelete" },
      { "modalOptions": { size: "sm" } }
    ], true);
  }
}
