import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NgbModalOptions, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ApplicationService, FluentTableConfig } from 'codera-shared';
import { BulkChangeStatusIdentityLookupViewModel } from "../types";

@Component({
    selector: 'bulk-change-status-picker',
    templateUrl: './bulk-change-status-picker.component.html',
    styleUrls: ['./bulk-change-status-picker.component.scss']
})
export class BulkChangeStatusPickerComponent implements OnInit {
    model: BulkChangeStatusIdentityLookupViewModel;
    @Input() fluentTable: FluentTableConfig;
    @Input() url: string;
    @Input() modalOptions: NgbModalOptions;
    @Output() onSuccess: EventEmitter<void>;

    private modalRef: NgbModalRef;

    constructor(private http: HttpClient, private applicationService: ApplicationService) {
        this.model = new BulkChangeStatusIdentityLookupViewModel();
        this.onSuccess = new EventEmitter<void>();
    }

    ngOnInit() {
        this.model.SelectedIds = this.fluentTable.selection.selected.map(x => x.Id);
    }

    onInit(modalRef) {
        this.modalRef = modalRef;
    }

    onSubmit(result) {
        this.model.SelectedIds = this.fluentTable.selection.selected.map(x => x.Id);
        this.http.post(`${this.applicationService.getApiUrl()}/${this.url}`, this.model)
            .subscribe(() => {
                this.fluentTable.notifyDataChanged("bulk-items-updated");
                this.onSuccess.emit();
                this.modalRef.close();
            });
    }

    onCancel(result) {
        this.modalRef.dismiss();
    }
}
