import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService, BulkOperationExecutionResultItem } from 'codera-shared';
import { Observable } from 'rxjs';
import { CopyWorkflowMatrixViewModel, WorkflowMatrixViewModel } from './types';

@Injectable({
    providedIn: 'root'
})
export class WorkflowMatrixService {
    private apiUrl: string;

    constructor(
        private http: HttpClient,
        private applicationService: ApplicationService
    ) {
        this.apiUrl = this.applicationService.getApiUrl();
    }

    createOrUpdate(model: WorkflowMatrixViewModel): Observable<string | boolean> {
        const url = `${this.apiUrl}/WorkflowMatrix/CreateOrUpdate`;
        return this.http.post<string | boolean>(url, model);
    }    

    getById(changeTypeId: string): Observable<WorkflowMatrixViewModel> {
        const url = `${this.apiUrl}/WorkflowMatrix/GetById`;
        return this.http.post<WorkflowMatrixViewModel>(url, { id: changeTypeId });
    }

    delete(changeTypeId: string): Observable<boolean> {
        const url = `${this.apiUrl}/WorkflowMatrix/Delete`;
        return this.http.post<boolean>(url, { id: changeTypeId });
    }

    getWorkflowMatrixByChangeRequestTypeId(changeRequestTypeId: string, facilityId: string): Observable<WorkflowMatrixViewModel> {
        const url = `${this.apiUrl}/WorkflowMatrix/GetWorkflowMatrixByChangeRequestTypeId`;
        return this.http.post<WorkflowMatrixViewModel>(url, { changeRequestTypeId: changeRequestTypeId, facilityId: facilityId });
    }

    copyWorkflowMatrix(model: CopyWorkflowMatrixViewModel): Observable<BulkOperationExecutionResultItem> {
        const url = `${this.apiUrl}/WorkflowMatrix/CopyWorkflowMatrix`;
        return this.http.post<BulkOperationExecutionResultItem>(url, model);
    }
}
