import { Component, Input, OnDestroy, OnInit, ViewContainerRef } from '@angular/core';
import { DynamicService, IsoQuestEventManagerService } from 'codera-shared';
import { ChangeRequestStatus } from '../../types';
import { LaunchService } from './launch.service';
import { LaunchViewModel } from './types';
import { UpdateLaunchComponent } from './update-launch/update-launch.component';

@Component({
    selector: 'launch',
    templateUrl: './launch.component.html',
    styleUrls: ['./launch.component.scss']
})
export class LaunchComponent implements OnInit, OnDestroy {
    @Input()
    changeRequestId: string;

    model: LaunchViewModel;
    changeRequestStatus = ChangeRequestStatus;
    constructor(
        private launchService: LaunchService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private isoQuestEventManager: IsoQuestEventManagerService) {
    }

    ngOnDestroy(): void {
    }

    ngOnInit() {
        const that = this;
        if (this.changeRequestId === undefined || this.changeRequestId === null)
            throw new Error("changeRequestId is required");

        this.model = new LaunchViewModel(this.changeRequestId);
        this.getLaunch();
        this.isoQuestEventManager.subscribe(this, "fetch-change-request", () => {
            that.getLaunch();
        });
    }

    getLaunch(): void {
        this.launchService.getLaunch(this.changeRequestId).subscribe(viewModel => {
            this.model = viewModel;
        });
    }

    editLaunch() {
        this.dynamicService.addComponent(this.viewContainer, UpdateLaunchComponent, [
            { changeRequestId: this.changeRequestId },
            {
                onUpdate: () => {
                    this.getLaunch();
                }
            }
        ]);
    }
}
