import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IdentityLookupViewModel } from 'codera-shared';
import { TMSTaskType } from '../types';

@Component({
  selector: 'tms-task-type-picker',
  templateUrl: './tms-task-type-picker.component.html'
})
export class TmsTaskTypePickerComponent implements OnInit {
  @Input() model: IdentityLookupViewModel | IdentityLookupViewModel[];
  @Output() modelChange: EventEmitter<IdentityLookupViewModel | IdentityLookupViewModel[]>;

  @Input() label: string;
  @Input() isMultiple: boolean;
  @Input() isRequired: boolean;
  @Input() isDisabled: boolean;
  @Input() onlyActive: boolean;
  @Input() searchMode: boolean;
  taskType = TMSTaskType;

  staticData: IdentityLookupViewModel[];
  statusConfig: { [key: string]: any }
  constructor() {
    this.modelChange = new EventEmitter<IdentityLookupViewModel>();
    this.staticData = [];
  }

  ngOnInit() {
    if (!this.label)
      this.label = "[ISOQuest].[TaskManagementSystem].TaskType";

    this.staticData = Object.keys(TMSTaskType).filter((item) => {
      return isNaN(Number(item));
    }).map(taskType => {
      return new IdentityLookupViewModel(TMSTaskType[taskType], "[ISOQuest].[TaskManagementSystem].RecurringPicker." + taskType, true);
    }).sort((left, right) => Number(left.Id) - Number(right.Id));
  }

  onChange(newValue: IdentityLookupViewModel | IdentityLookupViewModel[]) {
    this.model = newValue;
    this.modelChange.emit(this.model);
  }
}
