import { ViewportScroller } from '@angular/common';
import { Component, EventEmitter, HostListener, Input, OnDestroy, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { CancelAction, ComponentControl, DynamicService, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction, TextConfirmationModalComponent, WorkflowPathsModel } from 'codera-shared';
import { DragulaService, Group } from 'ng2-dragula';
import { ToastrService } from "ngx-toastr";
import { Subscription } from 'rxjs';
import { ChecklistGroupAssigneesComponent } from 'src/app/system-controls/checklist-groups/checklist-group-assignees/checklist-group-assignees.component';
import { ChecklistGroup, RemoveChecklistGroupModel } from 'src/app/system-controls/checklist-groups/types';
import { ChecklistGroupPickerComponent } from '../../../system-controls/checklist-groups/picker/checklist-group-picker.component';
import { ChangeRequestService } from '../../change-request.service';
import { AnalysisConfigModel, AnalysisMode, UpdateChangeRequestAnalysisViewModel, UpdateGroupsOrderViewModel } from '../../types';
import { AnalysisService } from './analysis.service';
import { AnswerModeAnalysisComponent } from './answer-mode-analysis/answer-mode-analysis.component';
import { ManageModeAnalysisComponent } from './manage-mode-analysis/manage-mode-analysis.component';
import { ViewModeAnalysisComponent } from './view-mode-analysis/view-mode-analysis.component';

@Component({
    templateUrl: './analysis.component.html',
    styleUrls: ['./analysis.component.scss'],
    providers: [AnalysisService]
})
export class AnalysisComponent implements OnInit, OnDestroy {
    analysisMode = AnalysisMode;
    showToTop: boolean;
    activeMode: AnalysisMode;
    expandedGroups: boolean;
    model: ChecklistGroup[];
    manageModeHidden: boolean;
    viewModeHidden: boolean;
    answerModeHidden: boolean;
    modesHidden: boolean;
    workflowObjectType: string;
    workflowPaths: WorkflowPathsModel;

    checklistGroup: Group;
    dragulaChecklistGroups: string;
    subscriptions: Subscription[];
    @Input() analysisConfig: AnalysisConfigModel;
    @Input() onAnalysisSave: EventEmitter<void>;

    @ViewChild('manageMode') manageMode: ManageModeAnalysisComponent;
    @ViewChild('viewMode') viewMode: ViewModeAnalysisComponent;
    @ViewChild('answerMode') answerMode: AnswerModeAnalysisComponent;

    constructor(
        private toastr: ToastrService,
        private translate: TranslateService,
        private dragulaService: DragulaService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private analysisService: AnalysisService,
        private viewportScroller: ViewportScroller,
        private changeRequestService: ChangeRequestService,
    ) {
        this.activeMode = AnalysisMode.View;
        this.expandedGroups = true;
        this.manageModeHidden = true;
        this.viewModeHidden = true;
        this.answerModeHidden = true;
        this.modesHidden = true;

        this.dragulaChecklistGroups = "Groups";
        this.checklistGroup = this.dragulaService.find(this.dragulaChecklistGroups);
        if (this.checklistGroup == null) {
            this.checklistGroup = this.dragulaService.createGroup(this.dragulaChecklistGroups, {
                moves: (el, container, handle) => {
                    return handle.className.includes("group-handle-class");
                },
            });
        }
        this.onAnalysisSave = new EventEmitter<void>();
    }
    ngOnDestroy(): void {
        this.subscriptions.forEach(subscription => subscription.unsubscribe());
        if (this) // bug in isoquesteventmanager probably
            this.dragulaService.destroy(this.dragulaChecklistGroups);
    }

    ngOnInit() {
        this.subscriptions = [];
        if (this.analysisConfig == null)
            throw new Error("Please provide Analysis component config");

        Object.keys(this.analysisConfig).forEach(key => {
            if (key == null)
                throw new Error(`Please provide value for ${key}`);
        });

        this.workflowObjectType = "MOCQuest.ChecklistGroup";
        this.workflowPaths = {
            GetWorkflowDetailsSettings: "MOCQuestWorkflow/GetWorkflowDetailsSettings",
            GetWorkflowSettings: "MOCQuestWorkflow/GetWorkflowSettings",
            GetWorkflowDefinitionByTag: "Workflow/GetWorkflowDefinitionByTag",
            UpdateWorkflowSettings: "MOCQuestWorkflow/UpdateWorkflowSettings",
            ResetWorkflowSettings: "MOCQuestWorkflow/ResetWorkflowSettings",
            GetApprovalHistory: "MOCQuestWorkflow/GetApprovalHistory",
            GetApprovalHistoryDetails: "MOCQuestWorkflow/GetApprovalHistoryDetails",
            GetApprovalHistoryDetailsAll: "MOCQuestWorkflow/GetApprovalHistoryDetailsAll",
            GetWorkflowUiOptions: "Workflow/GetChecklistGroupApprovalWorkflowUiOptions",
            ExecuteTransition: "Workflow/ExecuteTransition",
            PreExecuteTransition: "Workflow/PreExecuteTransition",
            GetAvailableTransitions: "Workflow/GetAvailableTransitions",
            RequestApproval: `ChangeRequest/RequestChecklistApproval?changeRequestId=${this.analysisConfig.ObjectId}`,
        };

        this.getAnalysisStateMode();
        this.analysisService.groups$.subscribe(groups => {
            this.model = groups;
        });
        this.analysisService.setAnalysisConfig(this.analysisConfig);
        this.analysisService.fetchChecklistGroups();
        this.subscriptions.push(
            this.changeRequestService.statusChangeSubject.subscribe((statusViewModel) => {
                this.getAnalysisStateMode();
                this.analysisService.fetchChecklistGroups();
            })
        );
    }

    getAnalysisStateMode() {
        this.subscriptions.push(
            this.changeRequestService.getAnalysisState(this.analysisConfig.GetAnalysisStateUrl, this.analysisConfig.ObjectId).subscribe(viewModel => {
                this.manageModeHidden = viewModel.ManageModeHidden;
                this.viewModeHidden = viewModel.ViewModeHidden;
                this.answerModeHidden = viewModel.AnswerModeHidden;

                this.modesHidden =
                    (this.manageModeHidden && this.viewModeHidden) ||
                    (this.manageModeHidden && this.answerModeHidden) ||
                    (this.viewModeHidden && this.answerModeHidden);

                this.activateMode(viewModel.ActiveMode);
            }));
    }

    activateMode(mode: AnalysisMode): void {
        this.activeMode = mode;
    }

    goToTop() {
        window.scroll({ top: 0, left: 0, behavior: 'smooth' });
    }

    @HostListener('window:scroll')
    checkScroll(): void {
        const scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
        this.showToTop = scrollPosition >= 100;
    }

    addGroup(): void {
        const that = this;
        const updateChangeRequestAnalysisViewModel = new UpdateChangeRequestAnalysisViewModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType);

        const updateChangeRequestAnalysisModalFormConfig = new ModalFormConfig(
            "[MOCQuest].[ChangeRequest].Analysis.AddGroup",
            [
                new RowControl([
                    new ComponentControl({
                        componentClass: ChecklistGroupPickerComponent,
                        field: "ChecklistGroup",
                        css: "col-12",
                        data: [
                            { isRequired: true },
                            { allowOutcome: that.analysisConfig.AllowOutcome }
                        ]
                    })
                ])
            ],
            [
                new SubmitAction({
                    field: "Submit",
                    value: "[ISOQuest].[Common].Buttons.Submit",
                    css: "btn btn-primary",
                    callback: () => {
                        that.subscriptions.push(
                            that.changeRequestService.updateAnalysis(that.analysisConfig.UpdateAnalysisUrl, updateChangeRequestAnalysisViewModel).subscribe(() => {
                                that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
                                that.analysisService.fetchChecklistGroups();
                            })
                        );
                    }
                }),
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Cancel",
                    css: "btn btn-default",
                    callback: () => { }
                })
            ]
        );

        this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
            { config: updateChangeRequestAnalysisModalFormConfig },
            { model: updateChangeRequestAnalysisViewModel },
            {
                modalOptions: {
                    size: "md"
                }
            }
        ]);
    }

    removeGroup(group: ChecklistGroup) {
        const that = this;
        const removeQuestionGroupModel = new RemoveChecklistGroupModel(this.analysisConfig.ObjectId, this.analysisConfig.ObjectType, group.Id);

        this.dynamicService.addComponent(this.viewContainer, TextConfirmationModalComponent, [
            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
            { "modalText": "[ISOQuest].[Common].ConfirmDialog.RemoveMessage" },
            {
                "modalOptions": {
                    size: "sm"
                }
            },
            {
                successCallback: () => {
                    that.changeRequestService.removeAnalysisQuestionGroup(that.analysisConfig.RemoveAnalysisQuestionGroupUrl, removeQuestionGroupModel).subscribe(() => {
                        that.toastr.success(that.translate.instant("[ISOQuest].[Common].Notifications.ItemRemoved"));
                        that.analysisService.fetchChecklistGroups();
                    })
                }
            }
        ], true);
    }

    goToGroup(groupId: string): void {
        this.viewportScroller.scrollToAnchor(groupId);
    }

    onGroupOrderChange(groups: ChecklistGroup[]) {
        groups.forEach((group, index) => group.Position = index);

        const updateGroupsOrderModel = new UpdateGroupsOrderViewModel(this.analysisConfig.ObjectId, groups);
        this.changeRequestService.updateGroupsOrder(this.analysisConfig.UpdateGroupsOrderUrl, updateGroupsOrderModel).subscribe(() => {
            this.toastr.success(this.translate.instant("[ISOQuest].[Common].Notifications.ItemUpdated"));
            this.analysisService.fetchChecklistGroups();
        });
    }

    onAnalysisSaveEvent() {
        this.onAnalysisSave.emit();
    }

    showAssignees(checklistGroupId: string) {
        let modalFormConfig = new ModalFormConfig(
            "[ISOQuest].[Common].Assignees",
            [
                new ComponentControl({
                    componentClass: ChecklistGroupAssigneesComponent,
                    field: "Assignees",
                    data: [
                        { changeRequestId: this.analysisConfig.ObjectId },
                        { checklistGroupId: checklistGroupId },
                        { analysisObjectType: this.analysisConfig.ObjectType }
                    ]
                })
            ],
            [
                new CancelAction({
                    field: "Cancel",
                    value: "[ISOQuest].[Common].Buttons.Close",
                    css: "btn btn-default",
                    callback: () => { }
                })
            ]
        );

        this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
            { config: modalFormConfig },
            { modalOptions: { size: "sm" } }
        ]);
    }
}
