import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { TMSTaskCategoryViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class TmsTaskCategoryService {
  private isoQuestApiUrl: string;

  constructor(private httpClient: HttpClient, private applicationService: ApplicationService) {
    this.isoQuestApiUrl = `${this.applicationService.getHostUrl()}/isoquest/api/sites/${this.applicationService.getCurrentSiteKey()}`;
  }

  delete(id: string): Observable<boolean> {
    const url = `${this.isoQuestApiUrl}/TMSTaskCategory/Delete`;
    return this.httpClient.post<boolean>(url, { id: id });
  }

  createOrUpdate(model: TMSTaskCategoryViewModel): Observable<string | boolean> {
    const url = `${this.isoQuestApiUrl}/TMSTaskCategory/CreateOrUpdate`;
    return this.httpClient.post<string | boolean>(url, model);
  }

  getById(id: string): Observable<TMSTaskCategoryViewModel> {
    const url = `${this.isoQuestApiUrl}/TMSTaskCategory/GetById`;
    return this.httpClient.post<TMSTaskCategoryViewModel>(url, { id: id });
  }
}
