import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApplicationService } from 'codera-shared';
import { Observable } from 'rxjs';
import { ReasonTypeViewModel } from './types';

@Injectable({
  providedIn: 'root'
})
export class ReasonTypeService {

  private apiUrl: string;

  constructor(
    private http: HttpClient,
    private applicationService: ApplicationService
  ) {
    this.apiUrl = this.applicationService.getApiUrl();
  }

  saveOrUpdate(model: ReasonTypeViewModel): Observable<string | boolean> {
    const url = `${this.apiUrl}/ReasonType/CreateOrUpdate`;
    return this.http.post<string | boolean>(url, model);
  }

  getById(changeTypeId: string): Observable<ReasonTypeViewModel> {
    const url = `${this.apiUrl}/ReasonType/GetById`;
    return this.http.post<ReasonTypeViewModel>(url, { id: changeTypeId });
  }

  delete(changeTypeId: string): Observable<boolean> {
    const url = `${this.apiUrl}/ReasonType/Delete`;
    return this.http.post<boolean>(url, { id: changeTypeId });
  }
}
