import { Component, Input, OnInit, ViewContainerRef } from '@angular/core';
import { IAllowApprovalTypeSettings } from './types';
import { TranslateService } from '@ngx-translate/core';
import { ModalFormConfig, RowControl, ComponentControl, CancelAction, ModalFormComponent, DynamicService, WorkflowService } from 'codera-shared';
import { DisableApprovalTypeInfoComponent } from '../disable-approval-type-info/disable-approval-type-info.component';
import { SettingsService } from '../../settings.service';

@Component({
    selector: 'allowed-approval-types',
    templateUrl: './allowed-approval-types.component.html',
    styleUrls: ['./allowed-approval-types.component.css']
})
export class AllowedApprovalTypesComponent implements OnInit {
    @Input() moduleSettings: IAllowApprovalTypeSettings;

    constructor(
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private workflowService: WorkflowService,
        private settingsService: SettingsService
    ) { }

    ngOnInit() {
    }

    onChange(approvalTypeId: number, newValue: boolean) {
        if (!newValue) {
            this.settingsService.canDisableApprovalType(approvalTypeId).subscribe((response) => {
                if (response.length) {
                    var approvalTypeKey = "";
                    if (approvalTypeId === 1) {
                        this.moduleSettings.AllowParallelApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.Parallel');
                    }
                    else if (approvalTypeId === 2) {
                        this.moduleSettings.AllowSerialApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.Serial');
                    }
                    else if (approvalTypeId === 3) {
                        this.moduleSettings.AllowVoteApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.Vote');
                    }
                    else if (approvalTypeId === 4) {
                        this.moduleSettings.AllowParallelCancelApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.ParallelCancelOnFirstRejection');
                    }
                    else if (approvalTypeId === 5) {
                        this.moduleSettings.AllowVoteCancelOnFirstRejectionApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.VoteCancelOnFirstRejection');
                    }
                    else if (approvalTypeId === 6) {
                        this.moduleSettings.AllowVoteWaitAllToFinishApprovalType = true;
                        approvalTypeKey = this.translate.instant('[ISOQuest].[Workflow].Approval.ApprovalType.VoteWaitAllToFinish');
                    }

                    var message = this.translate.instant("[ISOQuest].[Workflow].Common.ApprovalTypeDisableFailedInfo");
                    var approvalType = this.translate.instant(approvalTypeKey);
                    var failedMessage = message.replace("{0}", approvalType);

                    let disableApprovalTypeInfoModalFormConfig = new ModalFormConfig(
                        "[ISOQuest].[Common].InfoDialog.WarningTitle",
                        [
                            new RowControl([
                                new ComponentControl({
                                    componentClass: DisableApprovalTypeInfoComponent,
                                    field: "model",
                                    data: [
                                        { infoDetails: response },
                                        { failedMessage: failedMessage }
                                    ]
                                })
                            ])
                        ],
                        [
                            new CancelAction({
                                callback: () => { },
                                field: "Cancel",
                                value: "[ISOQuest].[Common].Buttons.Close",
                                css: "btn btn-default"
                            })
                        ]);

                    this.dynamicService.addComponent(this.viewContainer,
                        ModalFormComponent,
                        [
                            { config: disableApprovalTypeInfoModalFormConfig },
                            {
                                modalOptions: {
                                    size: 'md'
                                }
                            }
                        ]);
                }
            });
        }
    }
}
