import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationService, BulkChangeStatusComponent, BulkDeleteComponent, BulkOperationExecutionResultComponent, ComponentControl, DynamicService, FacilityInfoComponent, FacilityPickerComponent, FluentSearchConfig, FluentTableConfig, HeaderButtonItem, HeaderDropdownItem, HeaderItemsConfig, IqStatusPickerComponent, ModalFormComponent, ModalFormConfig, RenderButtonsPosition, RowControl, TextConfirmationModalComponent } from 'codera-shared';
import { ChangeRequestTypePickerComponent } from '../change-request-type/picker/change-request-type-picker.component';
import { CopyWorkflowMatrixViewModel, WorkflowMatrixGridModel, WorkflowMatrixViewModel } from './types';
import { WorkflowMatrixFactory } from './workflow-matrix.factory';
import { WorkflowMatrixService } from './workflow-matrix.service';

@Component({
    selector: 'app-workflow-matrix',
    templateUrl: './workflow-matrix.page.html',
    styleUrls: ['./workflow-matrix.page.scss']
})
export class WorkflowMatrixPage implements OnInit {
    pageTitle: string;
    currentFilter: { [key: string]: any }[] = [];
    fluentSearchConfig: FluentSearchConfig;
    fluentTableConfig: FluentTableConfig;
    tableSource: string;
    modalConfig: ModalFormConfig;
    model: WorkflowMatrixViewModel;
    copyWorkflowMatrixModel: CopyWorkflowMatrixViewModel;
    renderButtonsTypes = RenderButtonsPosition;
    headerItemsConfigs: HeaderItemsConfig;

    constructor(
        private router: Router,
        private titleService: Title,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private dynamicService: DynamicService,
        private viewContainer: ViewContainerRef,
        private applicationService: ApplicationService,
        private workflowMatrixFactory: WorkflowMatrixFactory,
        private workflowMatrixService: WorkflowMatrixService
    ) {
        this.model = new WorkflowMatrixViewModel();
    }

    ngOnInit() {
        const that = this;

        this.tableSource = `WorkflowMatrix/Grid`;
        this.pageTitle = this.route.snapshot.data["pageTitle"];

        this.headerItemsConfigs = new HeaderItemsConfig([
            new HeaderButtonItem({
                name: "copy-workflow-matrix",
                icon: "fa fa-copy",
                disabled: true,
                show: this.applicationService.getCurrentSite().IsMultiSite,
                label: "[MOCQuest].[SystemControls].WorkflowMatrix.Copy",
                title: "[MOCQuest].[SystemControls].WorkflowMatrix.CopyButtonTitle",
                action: that.copyWorkflowMatrix.bind(this)
            }),
            new HeaderButtonItem({
                name: "new-workflow-matrix",
                icon: "fa fa-plus",
                label: "[MOCQuest].[SystemControls].WorkflowMatrix.New",
                action: that.summonDialog.bind(this)
            }),
            new HeaderDropdownItem({
                name: "bulk-actions",
                icon: "",
                label: "[ISOQuest].[Common].Buttons.WithSelected",
                dropdownActions: [
                    new HeaderButtonItem({
                        name: "change-status",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.ChangeStatus",
                        action: that.bulkChangeStatus
                    }),
                    new HeaderButtonItem({
                        name: "bulk-delete",
                        icon: "",
                        label: "[ISOQuest].[Common].BulkActions.Delete",
                        action: that.bulkDelete
                    })
                ]
            })
        ]);

        this.translate.get(this.pageTitle).subscribe(translated => {
            this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translated}`);
        });

        // filter config
        this.fluentSearchConfig = new FluentSearchConfig({
            controls: [
                new RowControl([
                    new ComponentControl({
                        componentClass: FacilityPickerComponent,
                        field: "Facility",
                        show: that.applicationService.getCurrentSite().IsMultiSite,
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "label": "[ISOQuest].[Common].Facility" }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: ChangeRequestTypePickerComponent,
                        field: "ChangeRequestType",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2",
                        data: [
                            { "label": "[MOCQuest].[SystemControls].ChangeRequestType" }
                        ]
                    }),
                    new ComponentControl({
                        componentClass: IqStatusPickerComponent,
                        field: "Status",
                        label: "[ISOQuest].[Common].Status",
                        css: "col-xs-12 col-sm-12 col-md-2 col-lg-2"
                    })
                ])
            ],
            searchAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            },
            resetAction() {
                that.fluentTableConfig.applyFilter(that.currentFilter);
            }
        });

        // table config
        this.fluentTableConfig = new FluentTableConfig({
            columns: [
                {
                    field: "Facility",
                    title: "[ISOQuest].[Common].Facility",
                    sortable: "Facility",
                    type: "action",
                    show: that.applicationService.getCurrentSite().IsMultiSite,
                    render: (row: WorkflowMatrixGridModel) => {
                        if (row.Facility)
                            return `<a href="javascript:void(0);">${row.Facility.Name}</a>`;
                        else
                            return "-";
                    },
                    handler: (row: WorkflowMatrixGridModel) => {
                        that.dynamicService.addComponent(that.viewContainer, FacilityInfoComponent, [{ "facilityId": row.Facility.Id }]);
                    }
                },
                {
                    field: "ChangeRequestType",
                    title: "[MOCQuest].[SystemControls].ChangeRequestType",
                    sortable: "ChangeRequestType",
                    type: "custom",
                    render: (row: WorkflowMatrixGridModel) => {
                        return `${row.ChangeRequestType.Name}`;
                    },
                },
                {
                    field: "Status",
                    title: "[ISOQuest].[Common].Status",
                    sortable: "Status",
                    type: "status"
                },
                {
                    field: "copyAction",
                    title: "",
                    type: "action",
                    handler(row: WorkflowMatrixGridModel) {
                        that.copyWorkflowMatrix(row.Id);
                    },
                    render() {
                        return that.applicationService.getCurrentSite().IsMultiSite ? `<a class="btn btn-sm btn-default blue-stripe pull-right btn-overlay"><i class="fas fa-copy"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Copy")}</span></a>` : '';
                    },
                    flexGrow: 0.28
                },
                {
                    field: "editAction",
                    title: "",
                    type: "action",
                    handler(row: WorkflowMatrixGridModel) {
                        that.router.navigate(['/system-controls/edit-workflow-matrix', row.Id]);
                    },
                    render(row: WorkflowMatrixGridModel) {
                        return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Edit")}</span></a>`;
                    },
                    flexGrow: 0.25
                },
                {
                    field: "deleteAction",
                    title: "",
                    type: "action",
                    handler(row: WorkflowMatrixGridModel) {
                        that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
                            { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
                            { "modalText": "[ISOQuest].[Common].ConfirmDialog.DeleteMessage" },
                            {
                                "modalOptions": {
                                    size: "sm"
                                }
                            },
                            {
                                "successCallback": () => {
                                    that.workflowMatrixService.delete(row.Id)
                                        .subscribe(() => that.fluentTableConfig.notifyDataChanged("item-deleted"));
                                }
                            }
                        ], true);
                    },
                    render(row: WorkflowMatrixGridModel) {
                        return `<a class="btn btn-sm btn-default red-stripe pull-right btn-overlay"><i class="far fa-trash-alt"></i>&nbsp;<span>${that.translate.instant("[ISOQuest].[Common].Buttons.Delete")}</span></a>`;
                    },
                    flexGrow: 0.25
                }
            ],
            selection: {
                mode: "checkbox"
            },
            sorting: {
                "ChangeRequestType": "asc"
            },
            export: [
                {
                    exportType: 2,
                    label: "[ISOQuest].[Common].ExportToExcel"
                }
            ]
        });
    }

    summonDialog = () => {
        this.router.navigate(["/system-controls/create-workflow-matrix"]);
    }

    bulkChangeStatus = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkChangeStatusComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "WorkflowMatrix/BulkChangeStatus" },
            { "modalOptions": { size: "sm" } }
        ]);
    }

    bulkDelete = () => {
        this.dynamicService.addComponent(this.viewContainer, BulkDeleteComponent, [
            { "fluentTable": this.fluentTableConfig },
            { "url": "WorkflowMatrix/BulkDelete" },
            { "modalOptions": { size: "sm" } }
        ], true);
    }

    copyWorkflowMatrix = (id) => {
        var workflowMatrixId = id ? id : this.fluentTableConfig.selection.selected[0].Id;
        this.workflowMatrixService.getById(workflowMatrixId).subscribe((response: WorkflowMatrixViewModel) => {
            this.model = response;
            var copyWorkflowMatrixViewModel = new CopyWorkflowMatrixViewModel();
            copyWorkflowMatrixViewModel.OriginalWorkflowMatrixFacility = this.model.Facility;
            copyWorkflowMatrixViewModel.ChangeRequestType = this.model.ChangeRequestType;
            copyWorkflowMatrixViewModel.Status = this.model.Status;
            copyWorkflowMatrixViewModel.SeverityLevel = this.model.SeverityLevel;
            copyWorkflowMatrixViewModel.AllowImplementationAndPssrToBeConductedAtSameTime = this.model.AllowImplementationAndPssrToBeConductedAtSameTime;
            copyWorkflowMatrixViewModel.WorkflowStageMatrix = this.model.WorkflowStageMatrix;
            copyWorkflowMatrixViewModel.ChangeCoordinators = this.model.ChangeCoordinators;
            this.modalConfig = this.workflowMatrixFactory.getModalConfig(copyWorkflowMatrixViewModel, (result) => {
                this.fluentTableConfig.notifyDataChanged('item-created');
                this.headerItemsConfigs.getItemByName("copy-workflow-matrix").disabled = true;
                this.headerItemsConfigs.getItemByName("copy-workflow-matrix").title = "[MOCQuest].[SystemControls].WorkflowMatrix.CopyButtonTitle";
                this.modalConfig.modalRef.close();

                // summon the other results modal
                this.dynamicService.addComponent(this.viewContainer, BulkOperationExecutionResultComponent, [
                    { "model": result },
                    { "modalOptions": { size: "sm" } }
                ]);
            });

            this.dynamicService.addComponent(this.viewContainer, ModalFormComponent, [
                { "config": this.modalConfig },
                { "model": copyWorkflowMatrixViewModel },
                { "modalOptions": { size: "md" } }
            ], true);
        });
    }

    onSelectRows(selectedRows) {
        var copyWorklflowMatrixButton = this.headerItemsConfigs.getItemByName("copy-workflow-matrix");
        copyWorklflowMatrixButton.disabled = this.applicationService.getCurrentSite().IsMultiSite && selectedRows.length == 1 ? false : true;
        copyWorklflowMatrixButton.title = this.applicationService.getCurrentSite().IsMultiSite && selectedRows.length == 1 ? "" : "[MOCQuest].[SystemControls].WorkflowMatrix.CopyButtonTitle";
    }
}
