import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApplicationService } from "codera-shared";
import { Observable } from "rxjs";
import { CanDisableApprovalTypeResultModel, MOCQuestSettingsViewModel } from "./types";

@Injectable()
export class SettingsService {
    private apiUrl: string;

    constructor(private http: HttpClient, private applicationService: ApplicationService) {
        this.apiUrl = ""//this.applicationService.getApiUrl();
    }

    getSettings(): Observable<MOCQuestSettingsViewModel> {
        const url = `${this.apiUrl}/Settings/GetSettings`;
        return this.http.post<MOCQuestSettingsViewModel>(url, null);
    }

    setSettings(model: MOCQuestSettingsViewModel): Observable<MOCQuestSettingsViewModel> {
        const url = `${this.apiUrl}/Settings/SetSettings`;
        return this.http.post<MOCQuestSettingsViewModel>(url, model);
    }

    canDisableApprovalType(approvalTypeId: number): Observable<CanDisableApprovalTypeResultModel[]> {
        const url = `${this.apiUrl}/MOCQuestWorkflow/CanDisableApprovalType`;
        return this.http.post<CanDisableApprovalTypeResultModel[]>(url, { dynamicApprovalType: approvalTypeId });
    }
}
