import { Component, ComponentRef, EventEmitter, Input, OnInit, Output, ViewContainerRef } from '@angular/core';
import { CancelAction, DynamicService, InputControl, LookupViewModel, ModalFormComponent, ModalFormConfig, RowControl, SubmitAction } from 'codera-shared';
import { TmsTaskTopicService } from '../tms-task-topic.service';
import { TMSTagViewModel } from '../types';

@Component({
  selector: 'create-update-tms-task-topic',
  templateUrl: './create-update-tms-task-topic.component.html',
  styleUrls: ['./create-update-tms-task-topic.component.scss']
})
export class CreateUpdateTmsTaskTopicComponent implements OnInit {
  @Input()
  id: string;
  @Input()
  objectType: string;
  @Input()
  objectId: string;

  @Output()
  onCreate: EventEmitter<TMSTagViewModel>;
  @Output()
  onUpdate: EventEmitter<TMSTagViewModel>;

  model: TMSTagViewModel;
  private component: ComponentRef<ModalFormComponent>;

  constructor(
    private tmsTaskTopicService: TmsTaskTopicService,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
  ) {
    this.model = new TMSTagViewModel();
    this.onCreate = new EventEmitter<TMSTagViewModel>();
    this.onUpdate = new EventEmitter<TMSTagViewModel>();
  }

  ngOnInit() {
    if (!this.objectType)
      throw new Error("objectType is required");

    if (!this.objectId)
      throw new Error("objectId is required");

    this.summonDialog();
    if (this.id)
      this.getById();
  }

  summonDialog(): void {
    const that = this;

    this.model.ObjectType = this.objectType;
    this.model.ObjectId = this.objectId;
    let title = this.id
      ? "[ISOQuest].[TaskManagementSystem].Topic.EditTopic"
      : "[ISOQuest].[TaskManagementSystem].Topic.NewTopic";

    const modalFormConfig = new ModalFormConfig(
      title,
      [
        new RowControl([
          new InputControl({
            type: "text",
            field: "Title",
            label: "[ISOQuest].[Common].Title",
            required: true,
            css: "col-md-12",
            min: 0,
            max: 250
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            that.tmsTaskTopicService.createOrUpdate(that.model).subscribe((response: string | boolean) => {
              if (typeof response === "string")
                that.onCreate.emit(new TMSTagViewModel(response, that.model.Title, that.objectType, that.objectId));
              else if (typeof response === "boolean")
                that.onUpdate.emit(new TMSTagViewModel(that.model.Id, that.model.Title, that.objectType, that.objectId));

            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    );

    this.component = this.dynamicService.addComponent(this.viewContainer, ModalFormComponent,
      [
        { config: modalFormConfig },
        { model: this.model },
        {
          modalOptions: {
            size: "md"
          }
        }
      ]) as ComponentRef<ModalFormComponent>;
  }

  getById(): void {
    this.tmsTaskTopicService.getById(this.id).subscribe(topic => {
      this.model = topic;
      this.component.instance.model = topic;
    });
  }
}
