import { Component, OnInit, ViewContainerRef, ComponentRef, Input } from '@angular/core';
import { SeverityLevelGridModel, SeverityLevelViewModel, SeverityLevelLevel } from './types';
import { FluentTableConfig, DynamicService, ModalFormConfig, RowControl, InputControl, SubmitAction, CancelAction, ModalFormComponent, ComponentControl, ApplicationService, TextConfirmationModalComponent } from 'codera-shared';
import { TranslateService } from "@ngx-translate/core";
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { SeverityLevelService } from './severity-level.service';
import { SeverityLevelLevelPickerComponent } from './picker/severity-level-level-picker/severity-level-level-picker.component';
import { CreateUpdateLocalizationItemComponent } from 'src/app/localization/create-update/create-update-localization.component';
import { LocalizationService } from 'src/app/localization/localization.service';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-severity-level',
  templateUrl: './severity-level.page.html'
})
export class SeverityLevelPage implements OnInit {
  modalFormConfig: ModalFormConfig;
  fluentTableConfig: FluentTableConfig;
  tableSource: string;
  pageTitle: string;
  model: SeverityLevelViewModel;
  siteKey: string;

  constructor(
    private route: ActivatedRoute,
    private dynamicService: DynamicService,
    private viewContainer: ViewContainerRef,
    private translate: TranslateService,
    private titleService: Title,
    private severityLevelService: SeverityLevelService,
    private applicationService: ApplicationService,
    private localizationService: LocalizationService,
    private http: HttpClient
  ) {
    this.siteKey = applicationService.getCurrentSiteKey();
  }

  ngOnInit() {
    const that = this;

    this.tableSource = `SeverityLevel/Grid`;
    this.pageTitle = this.route.snapshot.data["pageTitle"];
    this.translate.get(this.pageTitle).subscribe(translated => {
      this.titleService.setTitle(`${this.applicationService.getCurrentModuleType()} // ${translated}`);
    });

    // table config
    this.fluentTableConfig = new FluentTableConfig({
      columns: [
        {
          field: "Level",
          title: "[ISOQuest].[Common].Level",
          sortable: "Level",
          type: "custom",
          render: (row: SeverityLevelGridModel) => {
            var translateLevel = "-";
            that.translate.get(row.Level.Name).subscribe((response: string) => {
              translateLevel = response;
            });
            return translateLevel;
          }
        },
        {
          field: "Title",
          title: "[ISOQuest].[Common].Title",
          type: "custom",
          render: (row: SeverityLevelGridModel) => {
            let translateTitle = "-";
            if (row.LabelId) {
              that.translate.get(`${row.Level.Name}.Label.${that.siteKey}`).subscribe((translation: string) => translateTitle = translation);
            } else {
              that.translate.get(`${row.Level.Name}.Label.Default`).subscribe((translation: string) => translateTitle = translation);
            }
            return translateTitle;
          }
        },
        {
          field: "editAction",
          title: "",
          type: "action",
          handler: (row: SeverityLevelGridModel) => {
            let level = SeverityLevelLevel[row.Level.Id];
            that.dynamicService.addComponent(that.viewContainer, CreateUpdateLocalizationItemComponent, [
              { "isExternal": true },
              { "id": row.LabelId },
              { "key": `[MOCQuest].[SystemControls].SeverityLevel.Level.${level}.Label.${this.siteKey}` },
              { "sectionId": "62CB5360-580D-41C9-854D-B4B727CE5BA7" },
              { "moduleId": 18 },
              { "javaScript": true },
              {
                "onCreateUpdate": () => {
                  that.fluentTableConfig.notifyDataChanged("item-updated");
                  that.publishAndFetchLocalization();
                }
              }
            ]);
          },
          render(row: SeverityLevelGridModel) {
            let translatedLabel = "-";
            that.translate.get("[ISOQuest].[Common].Buttons.Edit").subscribe((translation: string) => translatedLabel = translation);
            return `<a class="btn btn-sm btn-default yellow-stripe pull-right btn-overlay"><i class="fa fa-pencil"></i>&nbsp;<span>${translatedLabel}</span></a>`;
          },
          flexGrow: 0.28
        },
        ,
        {
          field: "deleteAction",
          title: "",
          type: "action",
          handler(row: SeverityLevelGridModel) {
            that.dynamicService.addComponent(that.viewContainer, TextConfirmationModalComponent, [
              { "modalHeader": "[ISOQuest].[Common].ConfirmDialog.Title" },
              { "modalText": "[MOCQuest].[Settings].WorkflowStages.LocalizationLabelDelete" },
              {
                "modalOptions": {
                  size: "sm"
                }
              },
              {
                "successCallback": () => {
                  that.localizationService.delete(row.LabelId, true)
                    .subscribe(() => {
                      that.fluentTableConfig.notifyDataChanged("item-deleted");
                      that.publishAndFetchLocalization();
                    });
                }
              }
            ], true);
          },
          render(row: SeverityLevelGridModel) {
            if (!row.LabelId)
              return "";

            let translatedLabel = "-";
            that.translate.get("[ISOQuest].[Common].RestoreToDefault").subscribe((translation: string) => translatedLabel = translation);
            return `<a class="btn btn-sm btn-default blue-stripe pull-right btn-overlay"><i class="far fa-undo"></i>&nbsp;<span>${translatedLabel}</span></a>`;
          },
          flexGrow: 0.28
        }
      ],
      selection: {
        mode: "none"
      },
      sorting: {
        "Level": "asc"
      },
      footer: {
        show: false
      }
    });

    // modal config
    this.modalFormConfig = new ModalFormConfig(
      "[MOCQuest].[SystemControls].SeverityLevel.Edit",
      [
        new RowControl([
          new ComponentControl({
            componentClass: SeverityLevelLevelPickerComponent,
            field: "Level",
            data: [
              { "isDisabled": true }
            ]
          })
        ]),
        new RowControl([
          new InputControl({
            field: "Title",
            type: "text",
            label: "[ISOQuest].[Common].Title",
            max: 255
          })
        ])
      ],
      [
        new SubmitAction({
          field: "Submit",
          value: "[ISOQuest].[Common].Buttons.Submit",
          callback(formSnapshot) {
            that.severityLevelService.update(that.model).subscribe((response: boolean) => {
              that.fluentTableConfig.notifyDataChanged("item-updated");
            });
          },
          css: "btn btn-primary"
        }),
        new CancelAction({
          field: "Cancel",
          value: "[ISOQuest].[Common].Buttons.Cancel",
          callback() { },
          css: "btn btn-default"
        })
      ]
    )
  }

  publishAndFetchLocalization() {
    this.localizationService.publishModuleLocalizations(this.applicationService.getCurrentModuleKey()).subscribe(() => {
      const date = new Date();
      if (location.port) {
        //angular cli
        this.translate.currentLoader = new TranslateHttpLoader(this.http, '/assets/locales/', `.json?ts=${date.getTime()}`);
      } else {
        //iis
        this.translate.currentLoader = new TranslateHttpLoader(this.http, '../../src/assets/locales/', `.json?ts=${date.getTime()}`);
      }
      this.translate.reloadLang(this.translate.currentLang);
    });
  }
}


